import { Canvas } from '../..';
import { IPlugin } from '../..';
import ActivityIndicatorRenderer from './ActivityIndicatorRenderer';
import ActivityIndicatorComponentDefinition from './ActivityIndicatorComponentDefinition';

export default class ActivityIndicatorRendererPlugin implements IPlugin {
  name = 'ActivityIndicatorRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'activityIndicator',
      ActivityIndicatorRenderer,
      ActivityIndicatorComponentDefinition
    );
  }

  deactivate() {}
}
