import * as React from 'react';
import { Layer } from 'models/Layer';
import Svg, { Path, Defs, Mask, LinearGradient, Stop } from 'svgs';
import { generateSVGPath } from '@builderx-core/conversions';
import { generateRandomString } from '../../models/utils';
import { isEmpty } from 'lodash';

const rerenderProps = ['layout', 'children', 'props', 'style', 'visible'];
export default class PathRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;

  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this);
  }

  get svgPath() {
    const layer: Layer = this.props.layer;
    let pathProps = layer.props.getAll();
    return generateSVGPath(
      pathProps.points,
      pathProps.strokeWidth,
      pathProps.isClosed,
      pathProps.viewBox
    );
  }
  get maskPath() {
    const layer: Layer = this.props.layer;
    let imageMask = layer.props.get('mask');
    if (!imageMask) {
      return '';
    }
    return generateSVGPath(
      imageMask || [],
      1,
      true,
      layer.props.get('viewBox')
    );
  }
  render() {
    const layer: Layer = this.props.layer;
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    let transform = layer.rotation ? `rotate(${layer.rotation}deg)` : undefined;
    let pathStyle = {
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
      transform,
    };
    delete pathStyle['backgroundImage'];

    const gradient = layer.style.get('gradient') || {};
    const mask = layer.props.get('mask');
    let maskId = generateRandomString(6);
    const gradientId = generateRandomString(6);
    return (
      <Svg
        // @ts-ignore
        id={layerId}
        style={pathStyle}
        viewBox={layerProps.viewBox}
        preserveAspectRatio="none"
      >
        {!isEmpty(gradient) || (mask && !isEmpty(mask)) ? (
          <Defs>
            {mask && !isEmpty(mask) && (
              <Mask id={maskId} x="0" y="0">
                <Path
                  fill="white" // DOC: hard-coded acc. to the masking behaviour in sketch
                  d={this.maskPath}
                />
              </Mask>
            )}
            {!isEmpty(gradient) && (
              <LinearGradient
                id={gradientId}
                x1={gradient.from.x}
                y1={gradient.from.y}
                x2={gradient.to.x}
                y2={gradient.to.y}
              >
                {gradient.stops.map((stop: any, index: any) => {
                  return (
                    <Stop
                      key={`gradient-${gradient.id}-stop-${index}`}
                      offset={stop.offset}
                      stopColor={stop.stopColor}
                    />
                  );
                })}
              </LinearGradient>
            )}
          </Defs>
        ) : null}
        <Path
          stroke={layerProps.stroke}
          // transform={layerProps.rotation ? transform : undefined}
          fill={!isEmpty(gradient) ? `url(#${gradientId})` : layerProps.fill}
          strokeWidth={layerProps.strokeWidth}
          fillOpacity={layerProps.fillOpacity}
          strokeOpacity={layerProps.strokeOpacity}
          clipPath={layerProps.clipPath}
          d={this.svgPath}
          // rotation={layerProps.rotation}
          mask={mask && !isEmpty(mask) ? `url(#${maskId})` : undefined}
        />
      </Svg>
    );
  }
}
