import { IPlugin, IContext, Canvas } from '../..';
import getSiblingPoints from './getSiblingPoints';
import getParentSnapPoints from './getParentSnapPoints';
import getGutterSnapPoints from './getGutterPoints';
import snapToGrid from './snapToGrid';
import snapPoint, { snapToPoints } from './snapPoint';
import getSnapPoints from './getSnapPoints';
import Config from './interfaces/Config';

export default class SnapPointsCalculator implements IPlugin {
  name = 'SnapPointsCalculator';
  canvas!: Canvas;
  config!: Config;
  snapBound: number = 5;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.config = {
      snap: false,
      parentSnap: false,
      siblingSnap: false,
      gutterSnap: false,
      gridSnap: false,
      rulerSnap: false,
    };
  }

  setSnapBound = (context: IContext, bound: any) => {
    this.snapBound = bound;
  };
  getSnapBound = (context: IContext) => {
    return this.snapBound;
  };

  getConfig = (): Config => {
    return this.config;
  };

  setConfig = (context: IContext, config: Config) => {
    this.config = {
      // snap: config.snap ? config.snap : this.config.snap,
      // parentSnap: config.parentSnap
      //   ? config.parentSnap
      //   : this.config.parentSnap,
      // siblingSnap: config.siblingSnap
      //   ? config.siblingSnap
      //   : this.config.siblingSnap,
      // gutterSnap: config.gutterSnap
      //   ? config.gutterSnap
      //   : this.config.gutterSnap,
      // gridSnap: config.gridSnap ? config.gridSnap : this.config.gridSnap,
      // rulerSnap: config.rulerSnap ? config.rulerSnap : this.config.rulerSnap,
      ...this.config,
      ...config,
    };
  };

  activate() {
    this.canvas.addFunction('getSnapConfig', this.getConfig);
    this.canvas.addFunction('setSnapConfig', this.setConfig);

    this.canvas.addFunction('getSiblingSnapPoints', (...args: any[]) =>
      // @ts-ignore
      getSiblingPoints(this.config, ...args)
    );
    this.canvas.addFunction('getParentSnapPoints', (...args: any[]) =>
      // @ts-ignore
      getParentSnapPoints(this.config, ...args)
    );
    this.canvas.addFunction('getGutterSnapPoints', (...args: any[]) =>
      // @ts-ignore
      getGutterSnapPoints(this.config, ...args)
    );
    this.canvas.addFunction('snapToGrid', (...args: any[]) =>
      // @ts-ignore
      snapToGrid(this.config, ...args)
    );
    this.canvas.addFunction('snapToPoints', snapToPoints);
    this.canvas.addFunction('getSnapPoints', (...args: any[]) =>
      // @ts-ignore
      getSnapPoints(this.config, ...args)
    );
    this.canvas.addFunction('setSnapBound', this.setSnapBound);
    this.canvas.addFunction('getSnapBound', this.getSnapBound);
  }

  deactivate() {
    this.canvas.removeFunction('getSiblingSnapPoints');
    this.canvas.removeFunction('getParentSnapPoints');
    this.canvas.removeFunction('getGutterSnapPoints');
    this.canvas.removeFunction('snapToGrid');
    this.canvas.removeFunction('snapToPoints');
    this.canvas.removeFunction('getSnapPoints');
    this.canvas.removeFunction('setSnapBound');
    this.canvas.removeFunction('getSnapBound');
  }
}
