import Ruler from './Ruler';

export default class HorizontalRuler extends Ruler {
  drawRuler(start: any, ctx: any) {
    let {
      fontColor,
      bgColor,
      fontScale,
      width,
      height,
      ratio,
      perWidth,
      scale,
      zoom,
    } = this.props;
    ctx.fillStyle = bgColor;
    ctx.fillRect(0, 0, width, height);
    ctx.translate(-start * ratio, 0);
    ctx.beginPath();
    ctx.fillStyle = fontColor;

    const startX = start - (start % perWidth);
    for (let i = startX; i < startX + width / ratio; i += perWidth) {
      let tempX = ((i >> 0) + 0.5) * ratio;
      ctx.lineWidth = 0.5;
      ctx.moveTo(tempX, height);
      let text: any = i / zoom;
      text = Math.round(text / scale) * scale;

      if (text % (scale * 10) === 0) {
        ctx.save();
        ctx.translate((i + 2) * ratio, 0);
        ctx.scale(fontScale, fontScale);
        ctx.fillText(text, 2 * ratio, height / 2);
        ctx.restore();
        ctx.lineTo(tempX, 0);
      } else if (text % (scale * 5) === 0) {
        ctx.lineTo(tempX, height / 2);
      } else {
        ctx.lineTo(tempX, (height * 2) / 3);
      }
      ctx.stroke();
    }
    ctx.closePath();
    ctx.translate(start * ratio, 0);
  }
}
