import { Layer } from '../../models/Layer';
import ISnapPoints from './interfaces/ISnapPoints';
import drawLineForAxis from './drawLineForAxis';
import { Canvas } from 'models/Canvas';
import ModelUtils from '../../models/utils';
import { get, forEach } from 'lodash';

export default function drawLinesForSnap(
  canvas: Canvas,
  layer: Layer,
  boundingBox: any,
  meta?: any,
  className?: string
) {
  if (!className) {
    className = 'snap-lines';
  }

  let parent = layer.parent ? layer.parent : meta && meta.parent;
  if (!parent) {
    return;
  }

  // // @ts-ignore
  let snapLinesX = document.querySelectorAll('.' + className + '-x');
  let snapLinesY = document.querySelectorAll('.' + className + '-y');
  // @ts-ignore
  for (let snapLine of snapLinesX) {
    snapLine.style.visibility = 'hidden';
  }
  // @ts-ignore
  for (let snapLine of snapLinesY) {
    snapLine.style.visibility = 'hidden';
  }

  if (
    !isNaN(get(meta, 'snappedSide.x.snappedSide.snappedPoint.rootValue', NaN))
  ) {
    let point = meta.snappedSide.x.snappedSide.snappedPoint.rootValue;
    if (
      ModelUtils.pointsOverlap(point, boundingBox.x) ||
      ModelUtils.pointsOverlap(point, boundingBox.width / 2 + boundingBox.x) ||
      ModelUtils.pointsOverlap(point, boundingBox.width + boundingBox.x)
    ) {
      // TODO: remove using architecture instead of DOM
      let snapLines = document.querySelectorAll('.' + className + '-y');
      // @ts-ignore
      for (let snapLine of snapLines) {
        snapLine.style.visibility = 'visible';
        snapLine.style.left = point + 'px';

        const parentAbsoluteRotation = parent.getAbsoluteRotation();
        const parentDims = parent.getRotatedPolygon();
        let offset = Math.max(boundingBox.width, boundingBox.height);
        snapLine.style.transform = `rotate(${parentAbsoluteRotation}deg)`;
        snapLine.style.transformOrigin = `${parentDims.centroid[0] -
          point}px ${parentDims.centroid[1] + offset}px`;
      }
    }
  }

  if (
    !isNaN(get(meta, 'snappedSide.y.snappedSide.snappedPoint.rootValue', NaN))
  ) {
    let point = meta.snappedSide.y.snappedSide.snappedPoint.rootValue;
    // jugaad: cheking if bounding box is touches snap line

    if (
      ModelUtils.pointsOverlap(point, boundingBox.y) ||
      ModelUtils.pointsOverlap(point, boundingBox.height / 2 + boundingBox.y) ||
      ModelUtils.pointsOverlap(point, boundingBox.height + boundingBox.y)
    ) {
      let snapLines = document.querySelectorAll('.' + className + '-x');
      // @ts-ignore
      for (let snapLine of snapLines) {
        snapLine.style.visibility = 'visible';
        snapLine.style.top = point + 'px';

        // transform changes
        const parentAbsoluteRotation = parent.getAbsoluteRotation();
        const parentDims = parent.getRotatedPolygon();
        let offset = Math.max(boundingBox.width, boundingBox.height);
        snapLine.style.transform = `rotate(${parentAbsoluteRotation}deg)`;
        snapLine.style.transformOrigin = `${parentDims.centroid[0] +
          offset}px ${parentDims.centroid[1] - point}px`;
      }
    }
  }
}

function drawLine(
  canvas: any,
  layer: any,
  point: any,
  boundingBox: any,
  axis: 'x' | 'y',
  meta?: any,
  className?: string
) {
  let parent = layer.parent ? layer.parent : meta && meta.parent;

  if (!parent) {
    return;
  }
  const parentAbsoluteRotation = parent.getAbsoluteRotation();
  const parentDims = parent.getRotatedPolygon();
  let offset = Math.max(boundingBox.width, boundingBox.height);

  const value = point;
  let extraStyle: any = {
    // visibility: 'hidden',
  };
  if (axis === 'y') {
    extraStyle = {
      ...extraStyle,
      transform: `rotate(${parentAbsoluteRotation}deg)`,
      transformOrigin: `${parentDims.centroid[0] - value}px ${parentDims
        .centroid[1] + offset}px`,
    };
  } else {
    extraStyle = {
      ...extraStyle,
      transform: `rotate(${parentAbsoluteRotation}deg)`,
      transformOrigin: `${parentDims.centroid[0] + offset}px ${parentDims
        .centroid[1] - value}px`,
    };
  }

  canvas.executeFunction(
    'drawLine',
    axis,
    value,
    className + '-' + axis,
    -offset,
    undefined,
    undefined,
    undefined,
    extraStyle
  );
}
export function drawAllLinesForSnap(
  canvas: Canvas,
  layer: Layer,
  boundingBox: any,
  snapLines: any,
  className: string = 'snap-lines',
  meta?: any
) {
  drawLine(canvas, layer, -100, boundingBox, 'y', meta, className);
  drawLine(canvas, layer, -100, boundingBox, 'x', meta, className);
}
