import * as React from 'react';
import ReactDOM from 'react-dom';
import { Canvas, IPlugin } from '../..';
import RulerScaleWrapper from './RulerScaleWrapper';

export default class RulerScalePlugin implements IPlugin {
  name = 'RulerScalePlugin';
  canvas!: Canvas;
  dispose: any;
  show!: boolean;
  wrapperLayer: any;
  selectedLineIndex: any;
  ghostImageDOM?: any;
  initialPoint: { x: number; y: number } = { x: 0, y: 0 };
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
    this.show = true;
  }
  showRulers() {
    this.show = true;
    this.renderScale();
    this.wrapperLayer.style.display = '';
  }
  hideRulers() {
    this.show = false;
    this.wrapperLayer.style.display = 'none';
    ReactDOM.unmountComponentAtNode(this.wrapperLayer);
  }
  setInitialPoint(context: any, point: { x: number; y: number }) {
    this.initialPoint = point;
    if (this.show) {
      this.renderScale();
    }
  }
  handleGhostImage(e: any) {
    var crt = e.target.cloneNode(true);
    crt.style.backgroundColor = 'red';
    crt.style.opacity = 0;
    crt.style.pointerEvents = 'none';
    document.body.appendChild(crt);
    this.ghostImageDOM = crt;
    e.dataTransfer.setDragImage(crt, 0, 0);
  }

  disposeGhostImage() {
    if (this.ghostImageDOM) this.ghostImageDOM.remove();
  }
  calculateScaleFromZoom(zoom: number) {
    let scale = 1;
    if (zoom <= 0.01) {
      scale = 1000;
    } else if (zoom <= 0.03) {
      scale = 500;
    } else if (zoom <= 0.06) {
      scale = 200;
    } else if (zoom <= 0.12) {
      scale = 100;
    } else if (zoom <= 0.25) {
      scale = 50;
    } else if (zoom > 0.25 && zoom <= 0.5) {
      scale = 20;
    } else if (zoom > 0.5 && zoom <= 1) {
      scale = 10;
    } else if (zoom > 1 && zoom <= 2) {
      scale = 5;
    } else if (zoom > 2 && zoom <= 4) {
      scale = 2;
    }
    return scale;
  }
  onDragStartHorizontal = (e: any) => {
    this.handleGhostImage(e);
    const dims = {
      x: 0,
      y: 19,
      height: 1,
      width: 1000,
      rotation: 0,
    };
    this.canvas.executeFunction('HtmlDragToWorld.dragStart', e, dims);
    this.selectedLineIndex = this.canvas.executeFunction(
      'RulerLines.addLine',
      'x',
      19
    );
  };
  onDragEndHorizontal = (e: any) => {
    this.disposeGhostImage();
    e.preventDefault();
    let dragVal = this.canvas.executeFunction('HtmlDragToWorld.dragEnd', e);
    this.canvas.executeFunction(
      'RulerLines.setPosition',
      dragVal.session.current.y,
      this.selectedLineIndex
    );
    this.selectedLineIndex = undefined;
  };
  onDragHorizontal = (e: any) => {
    if (e.pageX && e.pageY) {
      let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
      if (
        dragVal.session.current.deltaX !== 0 ||
        dragVal.session.current.deltaY !== 0
      ) {
        if (dragVal.session.current.y > 18) {
          this.canvas.executeFunction(
            'RulerLines.setPosition',
            dragVal.session.current.y,
            this.selectedLineIndex
          );
        }
      }
    }
  };
  onDragStartVertical = (e: any) => {
    this.handleGhostImage(e);
    const dims = {
      x: 19,
      y: 0,
      height: 1000,
      width: 1,
      rotation: 0,
    };
    this.canvas.executeFunction('HtmlDragToWorld.dragStart', e, dims);
    this.selectedLineIndex = this.canvas.executeFunction(
      'RulerLines.addLine',
      'y',
      19
    );
  };
  onDragEndVertical = (e: any) => {
    this.disposeGhostImage();
    e.preventDefault();
    let dragVal = this.canvas.executeFunction('HtmlDragToWorld.dragEnd', e);
    this.canvas.executeFunction(
      'RulerLines.setPosition',
      dragVal.session.current.x,
      this.selectedLineIndex
    );
    this.selectedLineIndex = undefined;
  };
  onDragVertical = (e: any) => {
    if (e.pageX && e.pageY) {
      let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
      if (
        dragVal.session.current.deltaX !== 0 ||
        dragVal.session.current.deltaY !== 0
      ) {
        if (dragVal.session.current.x > 18) {
          this.canvas.executeFunction(
            'RulerLines.setPosition',
            dragVal.session.current.x,
            this.selectedLineIndex
          );
        }
      }
    }
  };
  renderScale() {
    let zoom: any = this.canvas.getZoom();
    let scale = this.calculateScaleFromZoom(zoom);
    let perWidth = scale * zoom;
    ReactDOM.render(
      <RulerScaleWrapper
        canvas={this.canvas}
        initialPoint={this.initialPoint}
        thick={0.5}
        perWidth={perWidth}
        zoom={zoom}
        scale={scale}
        horLineArr={[1, 1]}
        verLineArr={[1, 1]}
        bgColor={'#252a31'}
        fgColor={'#6F727D'}
        fontColor={'#6F727D'}
        lineColor={'#FF0000'}
        shadowColor={'#6F727D'}
        horLineValue={[100, 200]}
        verLineValue={[100, 200]}
        shadow={{
          x: 200,
          y: 100,
          width: 100,
          height: 400,
        }}
        onDragStartHorizontal={this.onDragStartHorizontal}
        onDragEndHorizontal={this.onDragEndHorizontal}
        onDragHorizontal={this.onDragHorizontal}
        onDragStartVertical={this.onDragStartVertical}
        onDragEndVertical={this.onDragEndVertical}
        onDragVertical={this.onDragVertical}
        // onContextMenu={this.props.onContextMenu}
      />,
      this.wrapperLayer
    );
  }
  activate() {
    if (this.show) {
      this.wrapperLayer = document.createElement('div');
      this.dispose = this.canvas.addDomToViewport(this.wrapperLayer);
      this.wrapperLayer.style.position = 'absolute';
      this.wrapperLayer.style.top = '0px';
      this.wrapperLayer.style.left = '0px';
      this.wrapperLayer.style.bottom = '0px';
      this.wrapperLayer.style.right = '0px';
      this.wrapperLayer.style.display = this.show ? '' : 'none';
      this.wrapperLayer.id = 'ruler-wrapper';
      this.wrapperLayer.style.zIndex = '1';
      this.wrapperLayer.style.pointerEvents = 'none';
      if (this.show) {
        this.renderScale();
      }
      this.canvas.on('zoom', () => {
        if (this.show) {
          this.renderScale();
        }
      });
    }
    this.canvas.addFunction(
      'RulerScale.setInitialPoint',
      this.setInitialPoint.bind(this)
    );
    this.canvas.addFunction('RulerScale.show', this.showRulers.bind(this));
    this.canvas.addFunction('RulerScale.hide', this.hideRulers.bind(this));
  }

  deactivate() {
    this.dispose();
    this.canvas.removeFunction('RulerScale.setInitialPoint');
    this.canvas.removeFunction('RulerScale.show');
    this.canvas.removeFunction('RulerScale.hide');
  }
}
