import { Canvas, IPlugin, Layer, IContext } from '../..';
const path = require('path');
export default class DataStore implements IPlugin {
  name = 'DataStore';
  canvas!: Canvas;
  dataMap = new Map<Layer, any>();

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction('setData', this.setData.bind(this));
    this.canvas.addFunction('getData', this.getData.bind(this));
    this.canvas.addFunction('removeData', this.removeData.bind(this));
  }

  setData(context: IContext, key: any, value: any) {
    //
    const layer = context.layer;
    let layerData = this.dataMap.get(layer);

    if (layerData) {
      layerData = { ...layerData, [key]: value };
    } else {
      layerData = {
        [key]: value,
      };
    }
    // console.log(layerData, layer, 'layer data here');

    this.dataMap.set(layer, layerData);
  }

  getData(context: IContext, key?: any) {
    const layer = context.layer;
    let layerData = this.dataMap.get(layer);
    if (layerData) {
      if (key) {
        return layerData[key];
      } else {
        return layerData;
      }
    }
  }

  removeData(context: IContext, key?: any) {
    const layer = context.layer;
    let layerData = this.dataMap.get(layer);
    if (key) {
      delete layerData[key];
    } else {
      this.dataMap.delete(layer);
    }
  }

  deactivate() {
    this.canvas.removeFunction('setData');
    this.canvas.removeFunction('getData');
    this.canvas.removeFunction('removeData');
  }
}
