import * as React from 'react';
//@ts-ignore
import paper from 'paper';
import {
  getPaperPathFromPoints,
  convertPaperPointToBxObj,
} from '@builderx-core/conversions';
import { PathEvent, MouseEvent } from '../../';
import { get } from 'lodash';

type PathEditorProps = {
  onEvent: (event: any) => void;
  pathProps?: any;
  newPath?: boolean;
  bounds: any;
  dimensions: any;
  canvas: any;
  layer: any;
};

export default class PathEditor extends React.PureComponent<
  PathEditorProps,
  any
> {
  paper: any = paper;
  path!: paper.Path | null;
  segment!: paper.Segment | null | any;
  tool!: paper.Tool | null;
  handle!: string | null;
  xFactor: number = 1;
  yFactor: number = 1;
  oldBounds: any;
  isFirstClick: boolean = true;
  onUpdate = (keys: any) => {
    this.forceUpdate();
  };
  componentDidMount() {
    const canvas = document.getElementById('path-editor-canvas');
    if (!canvas) {
      return;
    }
    this.props.canvas.on('zoom', this.onUpdate);
    this.paper.setup(canvas as HTMLCanvasElement);
    this.init();
    let tool = new paper.Tool();
    tool.onMouseDrag = this.onMouseDrag;
    tool.onMouseDown = this.onMouseDown;
    tool.onMouseUp = this.onMouseUp;
    tool.onMouseMove = this.onMouseMove;
    this.tool = tool;
    this.paper.settings.handleSize = 7 / this.props.canvas.getZoom();
    this.ready();
  }

  ready() {
    let pointMode = '';
    if (this.segment) {
      if (this.segment.handleIn.isZero() || this.segment.handleOut.isZero()) {
        pointMode = 'straight';
      } else {
        pointMode = 'disconnected';
      }
    }
    const event = new PathEvent('pathEditorOnReady', this.props.layer, {
      actions: {
        deleteSelectedPoint: this.deleteSelectedPoint,
        straightenSelectedPoint: this.straightenSelectedPoint,
        disconnectSelectedPoint: this.disconnectSelectedPoint,
        finishEditing: this.finishEditing,
        closePath: this.closePath,
      },
      pointMode,
    });
    this.props.onEvent(event);
  }

  componentDidUpdate(prevProps: PathEditorProps) {
    this.paper.settings.handleSize = 7 / this.props.canvas.getZoom();
    this.init();
  }

  componentWillUnmount() {
    if (this.tool) {
      this.tool.remove();
    }
    this.pathEditorOnEnd(true);
    this.paper.project.clear();
    this.paper.project.remove();
  }

  init() {
    let { layer, dimensions, newPath } = this.props;
    let path: paper.Path | any;
    if (newPath) {
      path = new paper.Path();
      this.segment = path.add(new paper.Point(0, 0));
      path.selectedColor = '#A3A3A3';
      path.strokeColor = '#A3A3A3';
      path.strokeWidth = 1 / this.props.canvas.getZoom();
      this.paper.project.clear();
      let paperLayer = new paper.Layer([path]);
      this.paper.project.addLayer(paperLayer);
    } else {
      if (this.path) {
        this.path.remove();
      }
      this.isFirstClick = false;
      let pathProps = layer.props.getAll();
      let points = pathProps.points;
      let isClosed = pathProps.isClosed;
      let pathJSON = getPaperPathFromPoints(points, isClosed);
      path = pathJSON;
      this.xFactor = dimensions.width / path.bounds.width;
      this.yFactor = dimensions.height / path.bounds.height;
      this.oldBounds = path.bounds;
      path.applyMatrix = true;
      path.bounds.x = 0;
      path.bounds.y = 0;
      path.translate(new paper.Point(dimensions.rootX, dimensions.rootY));
      path.scale(this.xFactor, this.yFactor, path.bounds.topLeft);
      const rotation = this.getRotation();
      if (rotation) {
        path.rotate(rotation);
      }
      path.selectedColor = '#A3A3A3';
      path.strokeColor = '#A3A3A3';
      path.fillColor = pathProps.fill;
      this.paper.project.clear();
      let paperLayer = new paper.Layer([path]);
      this.paper.project.addLayer(paperLayer);
    }
    path.selected = true;
    //@ts-ignore
    this.paper.view.draw();
    this.paper.view.update();
    this.path = path;
  }

  getRotation = () => {
    // TODO: handle rotation here
    // const rotation = get(
    //   this.props.layer,
    //   'domainCompiledRefs[0].renderCompiledRef.rotation'
    // );
    return 0;
  };

  finishEditing = () => {
    if (this.segment) {
      this.segment.remove();
      this.segment = null;
    }
  };

  closePath = () => {
    this.finishEditing();
    if (this.path) {
      this.path.closePath();
    }
  };

  deleteSelectedPoint = () => {
    if (this.segment) {
      this.segment.remove();
      this.ready();
    }
  };

  straightenSelectedPoint = () => {
    if (this.segment) {
      this.segment.clearHandles();
      this.ready();
    }
  };

  disconnectSelectedPoint = () => {
    if (this.segment) {
      if (this.segment.handleIn.isZero()) {
        this.segment.smooth();
      }
      if (this.segment.handleOut.isZero()) {
        this.segment.smooth();
      }
      this.ready();
    }
  };

  emitUpdatedJSON = () => {
    if (this.path) {
      if (this.props.newPath) {
        let tempPath = this.path.clone({ insert: false, deep: true });
        tempPath.translate(
          new paper.Point(-this.path.bounds.x, -this.path.bounds.y)
        );

        let convertedJSON = convertPaperPointToBxObj(tempPath);
        const emitEvent = new PathEvent(
          'pathEditorOnChange',
          this.props.layer,
          {
            pathProps: convertedJSON,
            bounds: this.path.bounds,
          }
        );
        this.props.onEvent(emitEvent);
      } else {
        let tempPath = this.path.clone({ insert: false, deep: true });
        tempPath.translate(
          new paper.Point(-this.props.dimensions.x, -this.props.dimensions.y)
        );
        let tempPath2 = this.path.clone({ insert: false, deep: true });

        const rotation = this.getRotation();
        if (rotation) {
          tempPath.rotate(-rotation);
          tempPath2.rotate(-rotation);
        }
        tempPath.bounds = this.oldBounds;
        let convertedJSON = convertPaperPointToBxObj(tempPath);
        const emitEvent = new PathEvent(
          'pathEditorOnChange',
          this.props.layer,
          {
            pathProps: convertedJSON,
            bounds: tempPath2.bounds,
          }
        );
        this.props.onEvent(emitEvent);
      }
    }
  };

  render() {
    return (
      <canvas
        width={this.props.bounds.width}
        height={this.props.bounds.height}
        style={{
          cursor: 'crosshair',
          position: 'absolute',
          pointerEvents: 'auto',
          // border: '2px solid red',
        }}
        id="path-editor-canvas"
      />
    );
  }

  private onMouseUp = (event: paper.ToolEvent) => {
    let { point } = event;
    let canvasEvent = new MouseEvent(
      'PaperEvent',
      this.props.canvas,
      (event as any).event
    );
    point.x = canvasEvent.worldX;
    point.y = canvasEvent.worldY;
    if (this.props.newPath && this.path) {
      if (this.segment) {
        this.segment.selected = false;
      }
      const hitResult = this.paper.project.hitTest(point, {
        segments: true,
        tolerance: 5,
      });
      if (
        (hitResult &&
          hitResult.segment &&
          hitResult.segment.index === 0 &&
          hitResult.segment.next) ||
        get(event, 'event.detail') === 2
      ) {
        this.closePath();
        this.emitUpdatedJSON();
      } else {
        this.segment = this.path.add(point);
        this.segment.selected = true;
      }
    }
    this.handle = null;
  };
  private onMouseDrag = (event: paper.ToolEvent) => {
    let { point, downPoint } = event;
    if (downPoint) {
      let canvasEvent = new MouseEvent(
        'PaperEvent',
        this.props.canvas,
        (event as any).event
      );
      point.x = canvasEvent.worldX;
      point.y = canvasEvent.worldY;
      if (this.props.newPath && this.segment) {
        this.segment.handleIn = downPoint.subtract(point);
        this.segment.handleOut = point.subtract(downPoint);
      } else {
        if (this.segment) {
          switch (this.handle) {
            case 'segment':
              this.segment.point = point;
              this.emitUpdatedJSON();
              break;
            case 'handle-in':
              this.segment.handleIn = point.subtract(this.segment.point);
              this.emitUpdatedJSON();
              break;
            case 'handle-out':
              this.segment.handleOut = point.subtract(this.segment.point);
              this.emitUpdatedJSON();
              break;
            default:
              break;
          }
        }
      }
    }
  };
  private pathEditorOnEnd = (isLastUpdate: boolean = false) => {
    if (!isLastUpdate) {
      this.emitUpdatedJSON();
    }
    const emitEvent = new PathEvent('pathEditorOnEnd', this.props.layer);
    this.props.onEvent(emitEvent);
  };
  private onMouseDown = (event: paper.ToolEvent) => {
    if (this.isFirstClick) {
      const emitEvent = new MouseEvent(
        'pathEditor.selectParent',
        this.props.canvas,
        (event as any).event
      );
      this.props.onEvent(emitEvent);
      this.isFirstClick = false;
    }
    if (this.path) {
      let downPoint = event.downPoint;
      let canvasEvent = new MouseEvent(
        'PaperEvent',
        this.props.canvas,
        (event as any).event
      );
      downPoint.x = canvasEvent.worldX;
      downPoint.y = canvasEvent.worldY;
      if (!this.props.newPath) {
        const hitResult = this.paper.project.hitTest(downPoint, {
          stroke: true,
          segments: true,
          handles: true,
          tolerance: 5,
        });
        if (this.segment) {
          this.segment.selected = false;
        }
        if (hitResult) {
          this.handle = hitResult.type;
          switch (hitResult.type) {
            case 'stroke':
              if (hitResult.location) {
                const { location } = hitResult;
                const newCurve = location.curve.divideAt(location);
                if (newCurve) {
                  this.segment = newCurve.segment1;
                  this.segment!.selected = true;
                  this.handle = 'segment';
                }
              }
              break;
            case 'segment':
            case 'handle-in':
            case 'handle-out':
              this.segment = hitResult.segment;
              this.segment.selected = true;
              break;
            default:
              break;
          }
          this.ready();
        } else {
          if (!this.path.contains(downPoint)) {
            this.pathEditorOnEnd();
          }
        }
      }
    }
  };
  private onMouseMove = (event: paper.ToolEvent) => {
    let { newPath } = this.props;
    let { point } = event;
    let canvasEvent = new MouseEvent(
      'PaperEvent',
      this.props.canvas,
      (event as any).event
    );
    point.x = canvasEvent.worldX;
    point.y = canvasEvent.worldY;
    if (newPath && this.segment) {
      this.segment.point.set(point);
    }
  };
}
