import {
  Canvas,
  IPlugin,
  Layer,
  IContext,
  MouseEvent as DesignerMouseEvent,
  Event as DesignerEvent,
  ArtboardEvent,
  stopRerender,
  startRerender,
} from '../..';
import { union } from 'lodash';
import {
  forEach,
  isEmpty,
  indexOf,
  findLast,
  intersection,
  filter,
} from 'lodash';
import SelectLayers from '../../events/SelectLayers';
import ResetResizerEvent from '../../events/ResetResizerEvent';
import CommandEvent from '../../events/CommandEvent';
import { generateRandomString } from '../../models/utils';

export default class StageDesigner implements IPlugin {
  name = 'StageDesigner';
  canvas!: Canvas;
  artboards = new Map<string, Layer>();
  selectedArtboard?: Layer;
  topLayerSelectionMode: boolean = false;
  previousMovedLayers: Array<Layer> = [];

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction('addArtboard', this.addArtboard.bind(this));
    this.canvas.addFunction('removeArtboard', this.removeArtboard.bind(this));
    this.canvas.addFunction('selectArtboard', this.selectArtboard.bind(this));
    this.canvas.addFunction(
      'getSelectedArtboard',
      this.getSelectedArtboard.bind(this)
    );
    this.canvas.addFunction('getArtboard', this.getArtboard.bind(this));
    this.canvas.addFunction('focusArtboard', this.focusArtboard.bind(this));
    this.canvas.addFunction('getAllArtboards', this.getAllArtboards.bind(this));
    this.canvas.addFunction('getArtboardById', this.getArtboardById.bind(this));
    this.canvas.addFunction(
      'getSelectedArtboardId',
      this.getSelectedArtboardId.bind(this)
    );

    this.canvas.addFunction(
      'getNewArtboardPosition',
      this.getNewArtboardPosition.bind(this)
    );
    this.canvas.addFunction('getHitArtboard', this.getHitArtboard.bind(this));

    this.canvas.on('selectedLayers', () => {
      if (
        this.canvas.selectedLayers[0] &&
        this.canvas.selectedLayers[0].parent &&
        this.canvas.selectedLayers[0].parent.parent &&
        this.canvas.selectedLayers[0].parent.parent.type === 'canvas' &&
        this.canvas.selectedLayers[0].type !== 'scrollView'
      ) {
        // TODO: heavy jugaad
        // if root layer is selected, select artboard instead
        stopRerender();
        const artboardLayer = this.canvas.selectedLayers[0].parent;
        this.canvas.selectLayers(artboardLayer);

        if (artboardLayer.children[0].children.length === 0) {
          artboardLayer.executeFunction('LayerResizer.showLayer');
        } else {
          artboardLayer.executeFunction('LayerResizer.hideLayer');
        }

        startRerender();
      }
    });

    // Filter layers in hitRect other that direct children of root layer in selected artboard
    this.canvas.addMiddleware(
      'MouseRect.getHitRectResult',
      (next, hitRectResult: Array<Layer>) => {
        const selectedArtboard = this.canvas.executeFunction(
          'getSelectedArtboard'
        );
        // console.log(hitRectResult, 'Layer.getHitRectResult original *(*(*(');
        if (selectedArtboard) {
          let selectedArtboardLayers: Array<Layer> = [];
          let lowestIndexArtboardLayers: Array<Layer> = [];
          const allArtboards = Array.from(this.artboards.values());
          let lowestIndex = allArtboards.length - 1;

          const ignoredHitRectLayers: Array<Layer> = [];
          hitRectResult.forEach(layer => {
            if (
              layer.parent &&
              layer.parent.style.get('pointerEvents') === 'none'
            ) {
              ignoredHitRectLayers.push(layer.parent);
            }
          });

          const hitRectResultWithIgnored = union(
            hitRectResult,
            ignoredHitRectLayers
          );

          hitRectResultWithIgnored.forEach((layer: Layer): any => {
            if (layer.type === 'canvas') return;
            const currentLayerArtboard = layer.executeFunction('getArtboard');

            if (!currentLayerArtboard) {
              return;
            }

            let modLayers; // Layers to consider for comparing. Should be a direct child of root layer
            // or should be a child of ignored layer which is a direct child of root layer
            if (
              currentLayerArtboard.children[0] &&
              currentLayerArtboard.children[0].children.includes(layer)
            ) {
              if (layer.style.get('pointerEvents') !== 'none') {
                modLayers = [layer];
              } else {
                // if ignored layer, then consider its children and add to modLayers
                modLayers = intersection(hitRectResult, layer.children);
              }
            }

            if (modLayers) {
              if (
                // Layer belongs to selectedArtboard
                currentLayerArtboard === selectedArtboard
              ) {
                selectedArtboardLayers = union(
                  selectedArtboardLayers,
                  modLayers
                );
              }
              // Check if layer belongs to lowestIndexArtboard
              else {
                const index = indexOf(allArtboards, currentLayerArtboard);
                if (index < lowestIndex) {
                  // If index is lesser than previous, createNewArray and change index
                  lowestIndexArtboardLayers = [];

                  lowestIndexArtboardLayers = union(
                    lowestIndexArtboardLayers,
                    modLayers
                  );
                  lowestIndex = index;
                } else if (index === lowestIndex) {
                  lowestIndexArtboardLayers = union(
                    lowestIndexArtboardLayers,
                    modLayers
                  );
                }
              }
            }
          });
          if (selectedArtboardLayers.length) {
            return next(selectedArtboardLayers);
          } else {
            return next(lowestIndexArtboardLayers);
          }
        } else {
          return next(hitRectResult);
        }
      }
    );

    // Modify event.target in Canvas.dispatchEventOnHitLayer according to selected layer,
    this.canvas.addMiddleware(
      'Canvas.dispatchEventOnHitLayer',
      (next, event) => {
        if (event.name === 'click') {
          this.modifyEventForSelectableLayers(event);
          return next(event);
        } else if (event.name === 'mousemove') {
          this.modifyEventForSelectableLayers(event);
          return next(event);
        }

        return next(event);
      }
    );

    this.canvas.addMiddleware('LayerDrag.dragStart', (next, event) => {
      const target = event.target;
      if (
        target.type === 'canvas' ||
        // (target.parent && target.parent.type === 'canvas') ||
        (target.parent.parent && target.parent.parent.type === 'canvas') ||
        this.canvas.moveWithGrabHandle
      ) {
        return;
      }
      // const allRootLayers: Array<Layer> = [];

      //   const artboardArray = Array.from(this.artboards.values());

      //   artboardArray.forEach((artboard: Layer) => {
      //     const rootLayer = artboard.children[0];

      //     if (rootLayer) {
      //       allRootLayers.push(rootLayer);
      //     }
      //   });

      //   if (allRootLayers.includes(hitLayer)) {
      //     next(event);
      //   }

      this.modifyEventForSelectableLayers(event, true);

      return next(event);
    });
    // this.canvas.on('selectedLayers', () => {
    //   if (this.canvas.selectedLayers[0]) {
    //     const artboard = this.canvas.selectedLayers[0].executeFunction(
    //       'getArtboard'
    //     );
    //     if (this.selectedArtboard !== artboard) {
    //       this.canvas.dispatchEvent(
    //         'selectArtboard',
    //         new Event('selectArtboard', artboard)
    //       );
    //       this.selectArtboard(
    //         { canvas: this.canvas, layer: this.canvas },
    //         artboard
    //       );
    //     }
    //   }
    // });

    this.canvas.addEventListener('dblclick', (e: DesignerMouseEvent) => {
      let parentLayer = e.target.parent;
      const hitLayers = e.target.getHitResult(e.worldX, e.worldY);

      let currentLayer: Layer | null = e.target;
      while (
        currentLayer &&
        currentLayer.type !== 'canvas' &&
        !this.canvas.selectedLayers.includes(currentLayer)
      ) {
        if (this.canvas.selectedLayers.includes(currentLayer.parent as Layer)) {
          const filteredLayer = filterIgnoredLayers(currentLayer, hitLayers);

          if (filteredLayer) {
            this.canvas.dispatchEvent(
              'selectLayers',
              new SelectLayers('selectLayers', this.canvas, [filteredLayer])
            );
          }
          break;
        }
        currentLayer = currentLayer.parent;
      }
    });

    this.canvas.addMiddleware('MouseRect.dragStart', (next, event) => {
      if (this.canvas.moveWithGrabHandle) {
        return;
      }
      const hitLayer = this.canvas.getHitLayer(event.worldX, event.worldY);
      if (
        hitLayer &&
        (hitLayer.locked ||
          (hitLayer.type === 'canvas' &&
            event.target &&
            event.target.type === 'canvas'))
      ) {
        next(event);
      } else {
        const allRootLayers: Array<Layer> = [];

        const artboardArray = Array.from(this.artboards.values());

        artboardArray.forEach((artboard: Layer) => {
          const rootLayer = artboard.children[0];

          if (rootLayer) {
            allRootLayers.push(rootLayer);
          }
        });

        if (allRootLayers.includes(hitLayer)) {
          next(event);
        }
      }
    });

    this.canvas.addMiddleware('LayerResizer.dragStart', (next, event) => {
      if (this.canvas.moveWithGrabHandle) {
        return;
      } else {
        next(event);
      }
    });
    this.canvas.addMiddleware('LayerResizer.drag', (next, event) => {
      if (this.canvas.moveWithGrabHandle) {
        return;
      } else {
        next(event);
      }
    });
    this.canvas.addMiddleware('LayerResizer.dragEnd', (next, event) => {
      if (this.canvas.moveWithGrabHandle) {
        return;
      } else {
        next(event);
      }
    });
    // this.canvas.addEventListener('layerDrag', this.handleMoveLayerArtboard);
    // this.canvas.addEventListener(
    //   'LayerResizer.drag',
    //   this.handleMoveLayerArtboard
    // );
    this.canvas.addEventListener('keyUp', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 91) {
        this.setTopLayerSelectionMode(false);
      }
    });
    this.canvas.addEventListener('keyDown', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 91) {
        this.setTopLayerSelectionMode(true);
      }
    });
    this.canvas.addMiddleware(
      `LayerResizer.getHandles`,
      (next: any, layers: Array<Layer>) => {
        if (layers?.[0]?.parent?.type === 'canvas') {
          // if layer is an artboard
          const handles = next(layers);

          const handlesWithoutRotation = filter(handles, h => h !== 'ROTATE');
          return handlesWithoutRotation;
        } else {
          return next(layers);
        }
      }
    );

    // this.canvas.addEventListener('drag', (e: DesignerMouseEvent) => {
    //   this.setScroll(
    //     { canvas: this.canvas, layer: this.canvas },
    //     {
    //       x:
    //         (this.canvas.layout.get('width') - e.worldX) /
    //         this.canvas.layout.get('width'),
    //       y:
    //         (this.canvas.layout.get('height') - e.worldY) /
    //         this.canvas.layout.get('height'),
    //     }
    //   );
    // });

    this.canvas.addEventListener('keyDown', (event: any) => {
      event = event.originalEvent ? event.originalEvent : event;
      if (event.keyCode === 32) {
        this.canvas.enableMoveWithGrabHandle();
      }
    });

    this.canvas.addEventListener('keyUp', (event: any) => {
      event = event.originalEvent ? event.originalEvent : event;
      if (event.keyCode === 32) {
        this.canvas.disableMoveWithGrabHandle();
      }
    });
  }

  getNewRootLayer = (targetDims: any, currentArtboard: any) => {
    const { rootX, rootY, width, height } = targetDims;

    const layersHit = this.canvas.getHitRectResult(rootX, rootY, width, height);
    const currentRootLayer = currentArtboard.children[0];

    const newRootLayersHit: Array<Layer> = filter(layersHit, (layer: Layer) => {
      if (
        layer !== currentRootLayer &&
        layer.parent &&
        layer.parent.parent &&
        layer.parent.parent.type === 'canvas'
      ) {
        return true;
      }
      return false;
    });
    let newRootLayer: Layer | undefined;
    newRootLayersHit.forEach((layer: any) => {
      const dims = layer.getBoundingClientRect();
      const x_overlap = Math.max(
        0,
        Math.min(dims.rootX + dims.width, targetDims.rootX + targetDims.width) -
          Math.max(dims.rootX, targetDims.rootX)
      );
      const y_overlap = Math.max(
        0,
        Math.min(
          dims.rootY + dims.height,
          targetDims.rootY + targetDims.height
        ) - Math.max(dims.rootY, targetDims.rootY)
      );
      const overlapArea = x_overlap * y_overlap;
      newRootLayer =
        overlapArea > (targetDims.width * targetDims.height) / 2
          ? layer
          : undefined;
    });
    return newRootLayer;
  };

  handleMoveLayerArtboard = (event: any) => {
    if (indexOf(this.previousMovedLayers, event.target) !== -1) {
      return;
    }
    this.canvas.executeFunction('Hover.hideAllHoverBoxes');
    const layerDims = event.target.getBoundingClientRect();
    const currentArtboard = event.target.executeFunction('getArtboard');
    const currentSelectedLayers: Array<Layer> = filter(
      this.canvas.selectedLayers,
      (layer: Layer) => {
        if (layer.parent && layer.parent.type === 'canvas') {
          return false;
        }
        return true;
      }
    );
    let hitRectParams = {};
    if (currentSelectedLayers.length > 1) {
      const boundingBox = this.canvas.getBoundingBox(currentSelectedLayers);
      hitRectParams = {
        rootX: boundingBox.x,
        rootY: boundingBox.y,
        width: boundingBox.width,
        height: boundingBox.height,
      };
    } else {
      hitRectParams = {
        rootX: layerDims.rootX,
        rootY: layerDims.rootY,
        width: layerDims.width,
        height: layerDims.height,
      };
    }
    const newRootLayer: Layer | undefined = this.getNewRootLayer(
      hitRectParams,
      currentArtboard
    );

    if (newRootLayer) {
      if (
        currentSelectedLayers.length > 0 &&
        currentSelectedLayers.includes(event.target)
      ) {
        let newToSelectLayers: Array<Layer> = [];
        currentSelectedLayers.forEach((layer: Layer) => {
          this.previousMovedLayers.push(layer);
          const newLayer: Layer | undefined = this.moveLayer(
            layer,
            newRootLayer
          );
          if (newLayer) {
            newToSelectLayers.push(newLayer);
          }
        });

        this.canvas.selectLayers(newToSelectLayers);
        this.canvas.dispatchEvent(
          'resetResizer',
          new ResetResizerEvent(
            'resetResizer',
            this.canvas,
            event.originalEvent
          )
        );
      } else {
        // Synthetic Mouse Up event
        const mouseUpEvent = new DesignerMouseEvent(
          'mouseup',
          this.canvas,
          event.originalEvent,
          this.canvas
        );
        this.canvas.dispatchEventOnHitLayer('mouseup', mouseUpEvent);

        this.moveLayer(event.target, newRootLayer);

        // Synthetic mouseDown event for LayerDrag
        const mouseDownEvent = new DesignerMouseEvent(
          'mousedown',
          this.canvas,
          event.originalEvent,
          this.canvas
        );
        this.canvas.dispatchEventOnHitLayer('mousedown', mouseDownEvent);
      }
    }
    this.canvas.executeFunction('Hover.showAllHoverBoxes');
  };

  moveLayer = (layer: Layer, newRootLayer: Layer) => {
    const fromIndex = indexOf(layer.parent?.children, layer);
    const toIndex = newRootLayer.children.length;
    this.canvas.dispatchEvent(
      'moveLayer',
      new CommandEvent('moveLayer', this.canvas, {
        fromParent: layer.parent,
        toParent: newRootLayer,
        fromIndex: fromIndex,
        toIndex: toIndex,
      })
    );
    const newLayer: Layer = newRootLayer.children[toIndex];
    const dims = layer.getBoundingClientRect();
    const newRootLayerDims = newRootLayer.getBoundingClientRect();
    const newLayerDims = {
      ...layer.getBoundingClientRect(),
      x: dims.rootX - newRootLayerDims.rootX,
      y: dims.rootY - newRootLayerDims.rootY,
    };
    if (newLayer) {
      forEach(newLayer.convertLayerDimensions(newLayerDims), (value, key) => {
        this.canvas.dispatchEvent(
          'setStyleAttribute',
          new CommandEvent('setStyleAttribute', this.canvas, {
            layer: newLayer,
            key,
            value,
            stylePropName: 'style',
          })
        );
      });
    }
    return newLayer;
  };

  addArtboard(context: IContext, filePath: string): Layer {
    const position = this.getNewArtboardPosition();
    const artboard = this.canvas.createLayer();
    this.canvas.addChild(artboard);
    artboard.setLayout({
      top: position.artTop,
      left: position.artLeft,
      height: 600,
      width: 300,
      position: 'absolute',
    });
    artboard.setStyle('backgroundColor', 'white');
    artboard.setProp('id', generateRandomString(20));
    this.artboards.set(filePath, artboard);
    artboard.on('layout', () => {
      this.canvas.dispatchEvent(
        'artboardLayoutChanges',
        new DesignerEvent('artboardLayoutChanges', this.canvas, this.canvas)
      );
    });
    artboard.on('updateDom', () => {
      this.canvas.dispatchEvent(
        'artboardLayoutChanges',
        new DesignerEvent('artboardLayoutChanges', this.canvas, this.canvas)
      );
    });

    this.canvas.dispatchEvent(
      'artboardLayoutChanges',
      new DesignerEvent('artboardLayoutChanges', this.canvas, this.canvas)
    );

    return artboard;
  }

  removeArtboard(context: IContext, filePath: string) {
    const artboard = this.artboards.get(filePath);
    if (artboard) {
      this.artboards.delete(filePath);
      this.canvas.removeChild(artboard);
    }

    this.canvas.dispatchEvent(
      'artboardLayoutChanges',
      new DesignerEvent('artboardLayoutChanges', this.canvas, this.canvas)
    );
  }

  selectArtboard(context: IContext, artboard: Layer) {
    this.selectedArtboard = artboard;

    if (this.selectedArtboard) {
      const artboardDims = this.selectedArtboard.getBoundingClientRect();

      // set grid configuration
      this.canvas.executeFunction('setGridOffset', {
        x: artboardDims.rootX,
        y: artboardDims.rootY,
      });
      this.canvas.executeFunction(
        'setGridSize',
        this.selectedArtboard.executeFunction('getGridSize')
      );
    }

    this.canvas.dispatchEvent(
      'artboardSelected',
      new ArtboardEvent('artboardSelected', this.canvas, artboard, this.canvas)
    );
  }

  focusArtboard(context: IContext) {
    const canvasDims = this.canvas.getBoundingClientRect();
    const artboardDims = context.layer.getBoundingClientRect();

    const scrollDims = {
      x: (artboardDims.x + artboardDims.width / 2) / canvasDims.width,
      y: (artboardDims.y + artboardDims.height / 2) / canvasDims.height,
    };

    this.canvas.executeFunction('setScroll', scrollDims);
  }

  getHitArtboard(context: IContext, x: number, y: number) {
    let hitLayers = this.canvas.getHitResult(x, y);
    const artboardArray = Array.from(this.artboards.values());
    let artboards: Array<Layer> = [];

    forEach(hitLayers.reverse(), layer => {
      if (artboardArray.includes(layer)) {
        artboards.push(layer);
      }
    });

    if (isEmpty(artboards)) {
      return null;
    }

    return artboards[artboards.length - 1];
  }

  getSelectedArtboard(context: IContext, filePath: string) {
    return this.selectedArtboard;
  }
  getSelectedArtboardId(context: IContext) {
    return this.selectedArtboard
      ? this.selectedArtboard.props.get('id')
      : undefined;
  }
  getAllArtboards(context: IContext) {
    return this.artboards;
  }

  getArtboardById(context: IContext, path: string) {
    return this.artboards.get(path);
  }

  getArtboard(context: IContext) {
    let layer = context.layer;
    if (layer.type !== 'canvas') {
      const artboardArray = Array.from(this.artboards.values());
      while (!artboardArray.includes(layer) && layer.parent) {
        layer = layer.parent;
      }
      if (artboardArray.includes(layer)) {
        return layer;
      } else {
        return null;
      }
    } else {
      throw new Error('getArtboard should be called on layer only');
    }
  }

  getNewArtboardPosition = () => {
    let left = Number.MIN_VALUE;
    let artLeft = Number.MIN_VALUE;
    let artTop = Number.MIN_VALUE;
    let top = Number.MIN_VALUE;
    const zoom = 1; // TODO: get actual zoom later

    if (this.artboards.size === 0) {
      return { left: 300, top: 50, artTop: 50, artLeft: 300 };
    }
    this.artboards.forEach((artboard: Layer) => {
      const artboardClientRect = artboard.getBoundingClientRect();
      const artMaxX = artboardClientRect.x + artboardClientRect.width;
      const artMidY = artboardClientRect.y + artboardClientRect.height / 2;
      if (left < artMaxX) {
        left = artMaxX;
        top = artMidY;
        artTop = artboardClientRect.y;
      }
    });
    artLeft = left + 50;
    left = left * zoom + 50;
    top = top * zoom - 40;
    return { left, top, artTop, artLeft };
  };

  modifyEventForSelectableLayers(event: any, forLayerDrag: boolean = false) {
    // Traverse up the chain to all the parents and modify e.target as soon as you find a layer that matches one of the following conditions
    // - It is a sibling of selected layers,
    // - It is one of the direct ancestors of the selected layer upto the root layer, siblings of those parents, or a direct children of root layer of any artboard

    // console.log(
    //   event.target,
    //   'Canvas.dispatchEventOnHitLayer click original *(*(*('
    // );

    if (event.target && event.target.type === 'canvas') {
      // event.target = null;
      return;
    }

    const hitLayers = (event.target as Layer).getHitResult(
      event.worldX,
      event.worldY
    );

    const selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    let currentRootLayer: Layer | undefined;
    if (selectedArtboard) {
      currentRootLayer = selectedArtboard.children[0];
    }

    let allRootLayerChildren: Array<Layer> = [];
    const allRootLayers: Array<Layer> = [];

    const artboardArray = Array.from(this.artboards.values());

    artboardArray.forEach((artboard: Layer) => {
      const rootLayer = artboard.children[0];

      if (rootLayer) {
        allRootLayerChildren = allRootLayerChildren.concat(rootLayer.children);
        // allRootLayerChildren.push(rootLayer);
        allRootLayers.push(rootLayer);
      }
    });

    // Because artboard should be draggable (when dragging its label) but not clickable or hoverable
    if (artboardArray.includes(event.target) && forLayerDrag) {
      return;
    }

    let currentLayer: Layer | null = event.target;
    while (
      currentLayer &&
      currentLayer.type !== 'canvas'
      // !this.canvas.selectedLayers.includes(currentLayer)
    ) {
      if (this.topLayerSelectionMode) {
        // Don't modify target, select top layer
        break;
      }

      // If currentLayer is a sibling of selected Layer
      if (
        this.canvas.selectedLayers.length > 0 &&
        this.canvas.selectedLayers[0].parent &&
        this.canvas.selectedLayers[0].parent.children.includes(
          currentLayer as Layer
        )
      ) {
        const filteredLayer = filterIgnoredLayers(currentLayer, hitLayers);
        if (filteredLayer) {
          event.target = filteredLayer;
        }
        // console.log(currentLayer, 'is being hovered *(*(*(*');
        break;
      }

      if (allRootLayerChildren.includes(currentLayer)) {
        const filteredLayer = filterIgnoredLayers(currentLayer, hitLayers);
        if (filteredLayer) {
          event.target = filteredLayer;
        }
        break;
      }

      if (allRootLayers.includes(currentLayer)) {
        const filteredLayer = filterIgnoredLayers(currentLayer, hitLayers);
        if (filteredLayer) {
          event.target = filteredLayer;
        }
        break;
      }

      const ancestorsList = [];

      let parent: Layer | null = this.canvas.selectedLayers[0];
      while (
        currentRootLayer &&
        parent &&
        !currentRootLayer.children.includes(parent) &&
        parent.parent
      ) {
        parent = parent.parent;
        ancestorsList.push(parent);
      }

      let ancestorSiblingsList: Layer[] = [];
      ancestorsList.forEach((ancestor: Layer) => {
        if (ancestor.parent) {
          ancestorSiblingsList = ancestorSiblingsList.concat(
            ancestor.parent.children
          );
        }
      });

      if (union(ancestorsList, ancestorSiblingsList).includes(currentLayer)) {
        const filteredLayer = filterIgnoredLayers(currentLayer, hitLayers);
        if (filteredLayer) {
          event.target = filteredLayer;
        }
        break;
      }

      currentLayer = currentLayer.parent;
      // while (currentLayer && !hitLayers.includes(currentLayer?.parent)) {
      //   currentLayer = currentLayer.parent;
      // }

      event.target = null;
    }
  }

  setTopLayerSelectionMode(value: boolean) {
    this.topLayerSelectionMode = value;
  }

  deactivate() {
    this.canvas.removeFunction('addArtboard');
    this.canvas.removeFunction('removeArtboard');
    this.canvas.removeFunction('getArtboardById');
    this.canvas.removeFunction('getNewArtboardPosition');
    this.canvas.removeFunction('getSelectedArtboardId');
  }
}

function filterIgnoredLayers(currentLayer: Layer, hitLayers: Array<Layer>) {
  if (hitLayers.includes(currentLayer)) {
    return currentLayer;
  } else if (currentLayer.style.get('pointerEvents') === 'none') {
    const topChildInHitLayers = findLast(currentLayer.children, child => {
      if (hitLayers.includes(child)) {
        return true;
      }
      return false;
    });

    if (topChildInHitLayers) {
      return topChildInHitLayers;
    }
  }
  return;
}
