import * as React from 'react';
import { Layer } from '../../models/Layer';
import { Canvas } from '../../models/Canvas';
import ReactDOM from 'react-dom';
import { isNil } from 'lodash';

type RootRendererProps = {
  canvas: Canvas;
  layer: Layer;
};
export default class RootRenderer extends React.PureComponent<
  RootRendererProps,
  any
> {
  onUpdate = () => {
    this.forceUpdate();
  };

  componentDidMount() {
    this.props.layer.on(['type'], this.onUpdate);
  }
  componentWillUnmount() {
    this.props.layer.off(['type'], this.onUpdate);
  }

  render() {
    const layer = this.props.layer;
    const TheChosenOne = this.props.canvas.getRenderer(layer.type);
    return (
      <TheChosenOne
        RootRenderer={RootRenderer}
        layer={layer}
        canvas={this.props.canvas}
      />
    );
  }
}
