import { Event, Layer } from './../../..';

export default class DrawEvent extends Event {
  dimensions: {
    worldX: number;
    worldY: number;
    height?: number;
    width?: number;
  };
  meta?: any;

  constructor(
    name: string,
    target: Layer,
    payload: { x: number; y: number; height?: number; width?: number },
    currentTarget?: Layer,
    meta?: any
  ) {
    super(name, target, currentTarget);
    this.dimensions = {
      worldX: payload.x,
      worldY: payload.y,
      height: payload.height,
      width: payload.width,
    };
    this.meta = meta;
  }
}
