import { IPlugin, IContext } from 'interfaces';
import { Canvas } from 'models/Canvas';
import { LineDrawerDragEvent } from './events';

export default class LineDrawer implements IPlugin {
  name = 'LineDrawer';
  canvas!: Canvas;
  namespace = 'LineDrawer';
  ghostImageDOM?: any;
  dispose: any;
  handleGhostImage(e: any) {
    var crt = e.target.cloneNode(true);
    crt.style.backgroundColor = 'red';
    crt.style.opacity = 0;
    crt.style.pointerEvents = 'none';
    document.body.appendChild(crt);
    this.ghostImageDOM = crt;
    e.dataTransfer.setDragImage(crt, 0, 0);
  }

  disposeGhostImage() {
    if (this.ghostImageDOM) this.ghostImageDOM.remove();
  }

  drawLine(
    context: IContext,
    axis: 'x' | 'y',
    axisCoordinate: number,
    className: string | null,
    startCoordinate?: number,
    endCoordinate?: number,
    draggable?: boolean,
    label?: string,
    style: any = {}
  ): HTMLDivElement {
    const lineWrappedLayer = document.createElement('div');
    this.dispose = this.canvas.addDomToWorld(lineWrappedLayer, true, 103);
    let zoom = this.canvas.getZoom();
    let lineWidth = 1 / zoom;
    let lineWrapperDim = 8 / zoom;
    let line = document.createElement('div');
    this.canvas.on('zoom', (e: any) => {
      zoom = this.canvas.getZoom();
      lineWidth = 1 / zoom;
      lineWrapperDim = 8 / zoom;
      if (line && lineWrappedLayer) {
        if (axis === 'x') {
          line.style.height = lineWidth + 'px';
          lineWrappedLayer.style.height = `${lineWrapperDim}px`;
        } else {
          lineWrappedLayer.style.width = `${lineWrapperDim}px`;
          line.style.width = lineWidth + 'px';
        }
      }
    });
    if (className) {
      lineWrappedLayer.className = className;
    }

    let labelBox;
    let boxWidth = 30;
    let boxHeight = 15;
    let fontSize = 10;
    let labelSpacing = 10;
    let length;

    startCoordinate = startCoordinate ? startCoordinate : 0;

    line.style.position = 'absolute';
    lineWrappedLayer.style.position = 'absolute';
    line.style.backgroundColor = '#22b9d1';
    lineWrappedLayer.style.pointerEvents = 'none';

    lineWrappedLayer.style.transform = style.transform;
    lineWrappedLayer.style.transformOrigin = style.transformOrigin;
    lineWrappedLayer.style.visibility = style.visibility;

    const lineLength = Math.max(
      this.canvas.getBoundingClientRect().width,
      this.canvas.getBoundingClientRect().height
    );

    if (draggable) {
      lineWrappedLayer.style.pointerEvents = 'auto';
      lineWrappedLayer.draggable = true;
      let x, y;

      if (axis == 'x') {
        endCoordinate = endCoordinate ? endCoordinate : lineLength;
        x = Math.min(startCoordinate, endCoordinate);
        y = axisCoordinate;
        lineWrappedLayer.onmouseover = (e: any) => {
          if (!this.canvas.executeFunction('LayerDrag.isDragging'))
            this.canvas.setMouseCursor('row-resize');
        };
      } else {
        endCoordinate = endCoordinate ? endCoordinate : lineLength;
        y = Math.min(startCoordinate, endCoordinate);
        x = axisCoordinate;
        lineWrappedLayer.onmouseover = (e: any) => {
          if (!this.canvas.executeFunction('LayerDrag.isDragging'))
            this.canvas.setMouseCursor('col-resize');
        };
      }
      lineWrappedLayer.onmouseleave = (e: any) => {
        this.canvas.setMouseCursor('default');
      };
      const dims = {
        x,
        y,
        height: lineWidth,
        width: Math.abs(startCoordinate - endCoordinate),
        rotation: 0,
      };
      lineWrappedLayer.ondragstart = e => {
        this.handleGhostImage(e);
        let dragVal = this.canvas.executeFunction(
          'HtmlDragToWorld.dragStart',
          e,
          dims
        );
        this.canvas.dispatchEvent(
          `${this.namespace}.dragStart`,
          new LineDrawerDragEvent(
            `${this.namespace}.dragStart`,
            dragVal.startValues,
            dragVal.session,
            dragVal.event,
            this.canvas,
            undefined,
            axis
          )
        );
      };
      lineWrappedLayer.ondrag = e => {
        if (e.pageX && e.pageY) {
          let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
          if (
            dragVal.session.current.deltaX !== 0 ||
            dragVal.session.current.deltaY !== 0
          ) {
            this.canvas.dispatchEvent(
              `${this.namespace}.onDrag`,
              new LineDrawerDragEvent(
                `${this.namespace}.onDrag`,
                dragVal.startValues,
                dragVal.session,
                dragVal.event,
                this.canvas,
                undefined,
                axis
              )
            );
          }
        }
      };
      lineWrappedLayer.ondragend = e => {
        this.disposeGhostImage();
        e.preventDefault();
        let dragVal = this.canvas.executeFunction('HtmlDragToWorld.dragEnd', e);
        this.canvas.dispatchEvent(
          `${this.namespace}.dragEnd`,
          new LineDrawerDragEvent(
            `${this.namespace}.dragEnd`,
            dragVal.startValues,
            dragVal.session,
            dragVal.event,
            this.canvas,
            undefined,
            axis
          )
        );
      };
    }

    switch (axis) {
      case 'x':
        endCoordinate = endCoordinate ? endCoordinate : lineLength;
        length = Math.abs(startCoordinate - endCoordinate);
        line.style.height = lineWidth + 'px';
        line.style.width = length + 'px';
        lineWrappedLayer.style.width = length + 'px';
        lineWrappedLayer.style.height = `${lineWrapperDim}px`;
        lineWrappedLayer.style.left =
          Math.min(startCoordinate, endCoordinate) + 'px';
        lineWrappedLayer.style.top = axisCoordinate + 'px';
        lineWrappedLayer.appendChild(line);
        labelBox = this.createLabelBox(
          label,
          'x',
          axisCoordinate,
          length / 2 - boxWidth / 2,
          boxHeight,
          boxWidth,
          labelSpacing,
          fontSize
        );
        if (labelBox) {
          lineWrappedLayer.appendChild(labelBox);
        }
        break;

      case 'y':
        endCoordinate = endCoordinate ? endCoordinate : lineLength;
        length = Math.abs(startCoordinate - endCoordinate);
        line.style.width = lineWidth + 'px';
        line.style.height = length + 'px';
        lineWrappedLayer.style.height = length + 'px';
        lineWrappedLayer.style.width = `${lineWrapperDim}px`;
        lineWrappedLayer.style.top =
          Math.min(startCoordinate, endCoordinate) + 'px';
        lineWrappedLayer.style.left = axisCoordinate + 'px';
        lineWrappedLayer.appendChild(line);
        labelBox = this.createLabelBox(
          label,
          'y',
          axisCoordinate,
          length / 2 - boxHeight / 2,
          boxHeight,
          boxWidth,
          labelSpacing,
          fontSize
        );
        if (labelBox) {
          lineWrappedLayer.appendChild(labelBox);
        }
        break;

      default:
        break;
    }

    return lineWrappedLayer;
  }
  setLabelBoxStyle(
    labelBox: any,
    height: any,
    width: any,
    axis: any,
    labelSpacing: any,
    pointOnAxis: any,
    text: any,
    fontSize: any
  ) {
    const zoom = this.canvas.getZoom();
    height = height / zoom;
    width = width / zoom;
    labelSpacing = labelSpacing / zoom;

    labelBox.style.width = width + 'px';
    labelBox.style.height = height + 'px';
    labelBox.style.borderRadius = `${2 / zoom}px`;

    if (axis === 'x') {
      if (height - labelSpacing <= 0) {
        labelBox.style.top = labelSpacing + 'px';
      } else {
        labelBox.style.top = -(labelSpacing + height) + 'px';
      }
      labelBox.style.left = pointOnAxis + 'px';
    } else {
      if (width - labelSpacing <= 0) {
        labelBox.style.left = -(labelSpacing + width) + 'px';
      } else {
        labelBox.style.left = labelSpacing + 'px';
      }
      labelBox.style.top = pointOnAxis + 'px';
    }
    text.style.fontSize = fontSize / zoom + 'px';
  }
  createLabelBox(
    label: string | undefined,
    axis: 'x' | 'y',
    coordinate: number,
    pointOnAxis: number,
    boxHeight: number,
    boxWidth: number,
    labelSpacing: number,
    fontSize: number
  ): HTMLDivElement | undefined {
    if (!label) return;

    let labelBox = document.createElement('div');
    labelBox.style.position = 'absolute';
    labelBox.style.backgroundColor = '#22b9d1';

    let text = document.createElement('span');
    text.style.backgroundColor = 'transparent';
    text.style.color = 'white';
    text.style.left = 0 + 'px';
    text.style.right = 0 + 'px';
    text.style.top = 0 + 'px';
    text.style.bottom = 0 + 'px';
    text.style.position = 'absolute';
    text.style.display = 'flex';
    text.style.textAlign = 'center';
    text.style.justifyContent = 'center';
    text.style.alignItems = 'center';
    this.setLabelBoxStyle(
      labelBox,
      boxHeight,
      boxWidth,
      axis,
      labelSpacing,
      pointOnAxis,
      text,
      fontSize
    );
    text.innerHTML = label;
    labelBox.appendChild(text);
    this.canvas.on('zoom', () => {
      this.setLabelBoxStyle(
        labelBox,
        boxHeight,
        boxWidth,
        axis,
        labelSpacing,
        pointOnAxis,
        text,
        fontSize
      );
    });
    return labelBox;
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addFunction('drawLine', this.drawLine.bind(this));
  }

  deactivate() {
    this.canvas.removeFunction('drawLine');
    this.dispose();
  }
}
