import Ruler from './Ruler';

export default class VerticalRuler extends Ruler {
  constructor(props: any) {
    super(props);
    this.vertical = true;
  }
  drawRuler(start: any, ctx: any) {
    const {
      fontColor,
      bgColor,
      fontScale,
      width,
      height,
      ratio,
      perWidth,
      scale,
      zoom,
    } = this.props;

    ctx.fillStyle = bgColor;
    ctx.fillRect(0, 0, width, height);
    ctx.translate(0, -start * ratio);
    ctx.beginPath();
    ctx.fillStyle = fontColor;
    const perHeight = perWidth;
    const startY = start - (start % perHeight);
    for (let i = startY; i < startY + height / ratio; i += perHeight) {
      let tempY = ((i >> 0) + 0.5) * ratio;
      let text: any = i / zoom;
      text = Math.round(text / scale) * scale;
      ctx.moveTo(width, tempY);
      if (text % (scale * 10) === 0) {
        ctx.save();
        ctx.translate(width, (i - 2) * ratio);
        ctx.rotate(-Math.PI / 2);
        ctx.scale(fontScale, fontScale);
        ctx.fillText(text, 2 * ratio, (-width / 3) * 2);
        ctx.restore();
        ctx.lineTo(0, tempY);
      } else if (text % (scale * 5) === 0) {
        ctx.lineTo(width / 2, tempY);
      } else {
        ctx.lineTo((width * 2) / 3, tempY);
      }
      ctx.stroke();
    }
    ctx.closePath();
    ctx.translate(0, start * ratio);
  }
}
