import { Canvas, IPlugin, MouseEvent } from '../../';
import { IDimensionWithRotation, IPosition } from 'interfaces';
import ModelUtils from './../../models/utils';
export default class HtmlDragToWorldPlugin implements IPlugin {
  name = 'HtmlDragToWorldPlugin';
  namespace = 'HtmlDragToWorld';
  canvas!: Canvas;
  resizerHandleLayersRef!: Map<string, any>;
  initPos: IPosition = { x: 0, y: 0 };
  relPos: IPosition = { x: 0, y: 0 };
  lastPos: IPosition = { x: 0, y: 0 };
  startValues = { x: 0, y: 0, height: 0, width: 0, rotation: 0 };
  dragSession = {
    start: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
    current: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
    end: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
  };
  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  setStartValues = (dims: IDimensionWithRotation) => {
    this.startValues = dims;
  };
  startDragSession = (x: number, y: number) => {
    this.dragSession.start = { x, y, deltaX: 0, deltaY: 0 };
    this.dragSession.current = { x, y, deltaX: 0, deltaY: 0 };
    this.dragSession.end = { x, y, deltaX: 0, deltaY: 0 };
  };
  endDragSession = () => {
    this.dragSession.end = {
      x: this.dragSession.current.x,
      y: this.dragSession.current.y,
      deltaX: this.dragSession.current.x - this.dragSession.start.x,
      deltaY: this.dragSession.current.y - this.dragSession.start.y,
    };
  };
  updateDragSession = (dx: number, dy: number) => {
    this.dragSession.current = {
      x: this.dragSession.current.x + dx,
      y: this.dragSession.current.y + dy,
      deltaX: dx,
      deltaY: dy,
    };
  };
  dragStart(context: any, e: any, initialDims: IDimensionWithRotation) {
    let event = new MouseEvent(`${this.namespace}.dragStart`, this.canvas, e);
    this.initPos = { x: event.worldX, y: event.worldY };
    this.setStartValues(initialDims);
    this.startDragSession(event.worldX, event.worldY);
    this.lastPos = { x: 0, y: 0 };
    return {
      event,
      startValues: { ...this.startValues },
      session: { ...this.dragSession },
    };
  }
  drag(context: any, e: any) {
    e.preventDefault();
    let event = new MouseEvent(`${this.namespace}.drag`, this.canvas, e);
    this.relPos = {
      x: event.worldX - this.initPos.x,
      y: event.worldY - this.initPos.y,
    };
    let deltaX = this.relPos.x - this.lastPos.x;
    let deltaY = this.relPos.y - this.lastPos.y;
    // let rotatedDelta = ModelUtils.rotatePoint(
    //   [deltaX, deltaY],
    //   360 - this.startValues.rotation,
    //   [0, 0]
    // );
    this.updateDragSession(deltaX, deltaY);
    this.lastPos = { ...this.relPos };
    return {
      event,
      startValues: { ...this.startValues },
      session: { ...this.dragSession },
    };
  }
  dragEnd(context: any, e: any) {
    e.preventDefault();
    let event = new MouseEvent(`${this.namespace}.dragEnd`, this.canvas, e);
    this.setStartValues({ x: 0, y: 0, height: 0, width: 0, rotation: 0 });
    this.endDragSession();
    this.initPos = { x: 0, y: 0 };
    return {
      event,
      startValues: { ...this.startValues },
      session: { ...this.dragSession },
    };
  }
  activate() {
    this.canvas.addFunction(
      `${this.namespace}.dragStart`,
      this.dragStart.bind(this)
    );
    this.canvas.addFunction(`${this.namespace}.drag`, this.drag.bind(this));
    this.canvas.addFunction(
      `${this.namespace}.dragEnd`,
      this.dragEnd.bind(this)
    );
  }

  deactivate() {
    //
  }
}
