import { Canvas, IPlugin } from '../..';

export default class UndoRedoPlugin implements IPlugin {
  name = 'UndoRedoPlugin';
  canvas!: Canvas;

  store: Array<any> = [];

  init(canvas: Canvas) {
    this.canvas = canvas;
    // //@ts-ignore
    // window['undoRedoStore'] = this.store;
  }

  activate() {
    this.canvas.addMiddleware('Layer.setStyle', (next: any, data: any) => {
      next(data);

      this.store.push({
        type: 'Layer.setStyle',
        payload: data,
      });
    });
  }

  deactivate() {}
}
