import * as React from 'react';
import { Image } from 'react-native-web';
import { Layer } from 'models/Layer';

const rerenderProps = ['layout', 'children', 'props', 'style', 'visible'];
export default class MapViewRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;

  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }

  render() {
    const layer: Layer = this.props.layer;
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    const mapViewStyles = { ...layer.style.getAll(), ...layer.layout.getAll() };
    let mapStyle = layerProps.customMapStyle
      ? layerProps.customMapStyle
      : 'Standard';

    const canvas = layer.getCanvas();

    try {
      return (
        <Image
          ref={(c: any) => {
            this._ref = c;
          }}
          {...layerProps}
          style={{
            resizeMode: 'cover',
            ...mapViewStyles,
          }}
          source={{ uri: `${canvas.assetURL}/${mapStyle}.png` }}
          nativeID={layerId}
        />
      );
    } catch (Error) {
      return (
        <Image
          ref={(c: any) => {
            this._ref = c;
          }}
          {...layerProps}
          style={{
            resizeMode: 'cover',
            ...mapViewStyles,
          }}
          source={{ uri: `${canvas.assetURL}/Standard.png` }}
          nativeID={layerId}
        />
      );
    }
  }
}
