import { Canvas, Layer } from '../../';
import { isEmpty, intersection, isNil, forEach, round } from 'lodash';
import { ResizerRendererDragEvent } from '../ResizerRenderer/ResizerRendererDragEvent';
import ModelUtils, {
  computeOrientation,
  calculateRotation,
  computeOrientationMultiple,
} from './../../models/utils';
import { IDimensionWithRotation, IPlugin, IPosition } from 'interfaces';
import { LayerResizerEvent } from './LayerResizerEvent';
import ResetResizerEvent from '../../events/ResetResizerEvent';

export default class LayerResizerPlugin implements IPlugin {
  name = 'LayerResizerPlugin';
  namespace = 'LayerResizer';
  forbiddenLayers: Array<Layer> = [];
  canvas!: Canvas;
  dispose: any;
  draggingHandles: boolean = false;
  targetLayer: Layer | undefined;
  selectedLayers: Array<Layer> = [];
  boundingBox: any;
  boundingBoxWRTWorld: any;
  selectedLayerDims: Map<Layer, any> = new Map<Layer, any>();
  startValues: any;
  updatedStartValues: any;
  snapPoints: any;
  boundQueueUpdates: Map<Layer, any> = new Map();
  rerenderHandles: boolean = true;

  isAltPressed: boolean = false;
  isShiftPressed: boolean = false;
  layoutElementArray: Array<any> = [];
  shiftDirection: string = '';

  startX = 0;
  startY = 0;
  startAbsoluteX = 0;
  startAbsoluteY = 0;

  totalDx = 0;
  totalDy = 0;

  dragEventData: any;

  getHandlesForFill = (layer: Layer) => {
    let handlesForFill: Array<string> = [];
    const isFillWidth = layer.isFillWidth();
    const isFillHeight = layer.isFillHeight();
    let flexSiblings: Array<Layer> = [];
    if (layer.type !== 'root') {
      flexSiblings = layer.getSiblingsWithFlex();
    }
    if (flexSiblings.length === 0) {
      handlesForFill = this.getHandlesForFlexLayer(isFillHeight, isFillWidth);
    } else if (layer.parent) {
      const layerParent = layer.parent as Layer;

      const parentFlexDirection = layerParent.layout.get('flexDirection');
      handlesForFill = this.getHandlesForFlexLayerWithSiblings(
        layer,
        parentFlexDirection,
        isFillHeight,
        isFillWidth
      );
    }
    return handlesForFill;
  };
  getHandlesForAutoOrAbsoluteLayer = (
    isAutoHeight: boolean,
    isAutoWidth: boolean
  ) => {
    if (isAutoHeight && isAutoWidth) {
      return ['ROTATE'];
    } else if (isAutoHeight) {
      return ['ROTATE', 'L', 'R'];
    } else if (isAutoWidth) {
      return ['ROTATE', 'T', 'B'];
    } else {
      return ['ROTATE', 'T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL'];
    }
  };
  getHandlesForFlexLayer = (isFillHeight: boolean, isFillWidth: boolean) => {
    if (isFillHeight && isFillWidth) {
      return [];
    } else if (isFillHeight) {
      return ['L', 'R'];
    } else if (isFillWidth) {
      return ['T', 'B'];
    } else {
      return ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
    }
  };
  getHandlesForFlexLayerWithSiblings = (
    layer: Layer,
    parentFlexDirection: string,
    isFillHeight: boolean,
    isFillWidth: boolean
  ) => {
    if (parentFlexDirection === 'row') {
      if (isFillHeight) {
        // if fill Width and fill Height or only fill Height
        return this.getResizerHandlesLayout(layer, parentFlexDirection);
      } else if (isFillWidth) {
        let resizer = this.getResizerHandlesLayout(layer, parentFlexDirection);
        return resizer.concat(['T', 'B']);
      } else {
        return ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
      }
    } else {
      // column
      if (isFillWidth) {
        // if fill Width and fill Height or only fill Height
        return this.getResizerHandlesLayout(layer, parentFlexDirection);
      } else if (isFillHeight) {
        let resizer = this.getResizerHandlesLayout(layer, parentFlexDirection);
        return resizer.concat(['L', 'R']);
      } else {
        return ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
      }
    }
  };
  getResizerHandlesLayout = (layer: Layer, parentFlexDirection: string) => {
    let parentLayer = layer.parent as Layer;
    let handles: any = [];
    const childrenLength = parentLayer.children.length;
    parentLayer.children.forEach((childLayer: Layer, index: number) => {
      if (childLayer === layer) {
        // first
        if (index === 0) {
          if (parentFlexDirection === 'row') {
            handles.push('R');
          } else {
            handles.push('B');
          }
        } else if (index === childrenLength - 1) {
          // last
          if (parentFlexDirection === 'row') {
            handles.push('L');
          } else {
            handles.push('T');
          }
        } else {
          // middle
          if (parentFlexDirection === 'row') {
            handles.push('R');
            handles.push('L');
          } else {
            handles.push('B');
            handles.push('T');
          }
        }
      }
    });
    return handles;
  };
  // getResizerHandlesForLayers = (layers: any) => {
  //   // add all handles except ROTATE handle when icon is selected

  //   let handles: any = ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
  //   if (layers.length === 0) {
  //     return [];
  //   }
  //   layers.forEach((layer: Layer) => {
  //     if (layer.type === 'icon' || layer.type === 'switch') {
  //       handles = intersection(handles, ['ROTATE']);
  //     }
  //     const isLayerAbsolute = !isNil(layer.layout.get('position'));
  //     const isAutoHeight = layer.isAutoHeight();
  //     const isAutoWidth = layer.isAutoWidth();
  //     let handlesForAutoOrAbs = this.getHandlesForAutoOrAbsoluteLayer(
  //       isAutoHeight,
  //       isAutoWidth
  //     );

  //     if (isLayerAbsolute) {
  //       handles = intersection(handles, handlesForAutoOrAbs);
  //     } else {
  //       const handlesForFill = this.getHandlesForFill(layer);
  //       let handlesHere = intersection(handlesForAutoOrAbs, handlesForFill);
  //       handles = intersection(handles, handlesHere);
  //     }
  //   });

  //   return handles;
  // };

  getHandles = (layers: Array<Layer>) => {
    return this.canvas.executeMiddleware(
      'LayerResizer.getHandles',
      layers,
      (layers: Array<Layer>) => {
        let handles = ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
        if (
          layers.length === 0 ||
          (layers.length === 1 && layers[0].type === 'canvas')
        ) {
          return [];
        }

        layers.forEach((layer: Layer) => {
          const isLayerAbsolute = !isNil(layer.layout.get('position'));
          const isAutoHeight = layer.isAutoHeight();
          const isAutoWidth = layer.isAutoWidth();
          if (
            layer.type === 'icon' ||
            layer.type === 'switch' ||
            layer.type === 'activityIndicator' ||
            (isAutoHeight && isAutoWidth)
          ) {
            handles = intersection(handles, ['ROTATE']);
          }
          let handlesForAutoOrAbs = this.getHandlesForAutoOrAbsoluteLayer(
            isAutoHeight,
            isAutoWidth
          );

          if (isLayerAbsolute) {
            handles = intersection(handles, handlesForAutoOrAbs);
          } else {
            const handlesForFill = this.getHandlesForFill(layer);
            let handlesHere = intersection(handlesForAutoOrAbs, handlesForFill);
            handles = intersection(handles, handlesHere);
          }
        });

        return handles;
      }
    );
  };
  layersQueuedForUpdate: any = [];

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
    this.selectedLayers = [];
  }
  hideLayerResizer(context: any) {
    if (!this.forbiddenLayers.includes(context.layer)) {
      this.forbiddenLayers.push(context.layer);
      this.setSelectedLayers();
      this.rerender();
    }
  }
  showLayerResizer(context: any) {
    if (this.forbiddenLayers.includes(context.layer)) {
      this.forbiddenLayers.splice(
        this.forbiddenLayers.indexOf(context.layer),
        1
      );
      this.setSelectedLayers();
      this.rerender();
    }
  }
  rerender() {
    let dims = this.canvas.getBoundingBox(this.selectedLayers);
    if (this.rerenderHandles) {
      this.canvas.executeFunction('ResizerRenderer.hideAllHandles');
      this.getHandles(this.canvas.selectedLayers).map((handle: string) => {
        this.canvas.executeFunction('ResizerRenderer.showHandle', handle);
      });
    }
    this.canvas.executeFunction('ResizerRenderer.setOrientation', dims);
  }
  onUpdate = () => {
    this.rerender();
  };

  queueUpdate = (layer: Layer) => {
    this.layersQueuedForUpdate.push(layer);

    // render when all the layers have finised emiiting layout updates
    if (this.layersQueuedForUpdate.length === this.selectedLayers.length) {
      this.rerender();
      this.layersQueuedForUpdate = [];
    }
  };
  setSelectedLayers() {
    this.layersQueuedForUpdate = [];
    this.boundQueueUpdates.forEach((cb: any, layer: Layer) => {
      layer.off('yoga', cb);
    });
    this.boundQueueUpdates.clear();
    this.selectedLayers = this.canvas.selectedLayers.filter(
      (layer: Layer) => !this.forbiddenLayers.includes(layer)
    );

    this.selectedLayers.forEach((layer: Layer) => {
      const cb = this.queueUpdate.bind(this, layer);
      layer.on('yoga', cb);
      this.boundQueueUpdates.set(layer, cb);
    });
  }
  getParentRelativeDims(dims: IDimensionWithRotation, layer: Layer) {
    let parentDims = layer.parent
      ? layer.parent.getBoundingClientRect()
      : { rootX: 0, rootY: 0 };
    return {
      ...dims,
      x: dims.x - parentDims.rootX,
      y: dims.y - parentDims.rootY,
    };
  }
  applyDimensions(eventType: string) {
    const data: ResizerRendererDragEvent = this.dragEventData;
    const deltaX = data.session.current.x - data.session.start.x;
    const deltaY = data.session.current.y - data.session.start.y;

    if (!this.isShiftPressed) {
      this.shiftDirection = '';
    }
    if (data.targetHandle.indexOf('C') > -1 && this.isShiftPressed) {
      if (!this.shiftDirection) {
        if (
          Math.abs(data.session.current.deltaX) >
          Math.abs(data.session.current.deltaY)
        ) {
          this.shiftDirection = 'horizontal';
        } else {
          this.shiftDirection = 'vertical';
        }
      }

      if (this.shiftDirection === 'horizontal') {
        this.totalDx = deltaX;
        // this.totalDy = 0;
      } else {
        this.totalDy = deltaY;
        // this.totalDx = 0;
      }
      // if (this.shiftDirection === "horizontal") {}
    } else {
      this.totalDy = deltaY;
      this.totalDx = deltaX;
    }

    // console.log(this.totalDx, this.totalDy, 'drag data here');

    if (
      !this.selectedLayers.length ||
      (this.selectedLayers.length && !this.selectedLayers[0].parent)
    ) {
      return;
    }
    const parentAbsoluteRotation =
      this.selectedLayers.length && this.selectedLayers[0].parent
        ? this.selectedLayers[0].parent.getAbsoluteRotation()
        : 0;
    let rotateDelta = ModelUtils.rotatePoint(
      [this.totalDx, this.totalDy],
      360 - parentAbsoluteRotation,
      [0, 0]
    );

    const snappedData = this.canvas.executeFunction(
      'snapToPoints',
      this.snapPoints,
      [this.startX, this.startY],
      [rotateDelta[0], rotateDelta[1]],
      {
        ...this.boundingBox,
      },
      [this.totalDx, this.totalDy],
      [this.startAbsoluteX, this.startAbsoluteY],
      parentAbsoluteRotation,
      this.boundingBoxWRTWorld
    );
    this.boundingBox = {
      ...this.boundingBox,
      x: this.boundingBox?.x + snappedData.delta.x,
      y: this.boundingBox?.y + snappedData.delta.y,
    };

    let delta = snappedData.delta;

    this.setNewLayout(
      this.selectedLayers,
      this.boundingBox,
      data.session,
      data.targetHandle,
      eventType,
      data,
      delta,
      snappedData.meta
      // { x: rotateDelta[0], y: rotateDelta[1] }
    );
  }

  normalizeChildrenFlex() {
    const parent = this.selectedLayers.length
      ? this.selectedLayers[0].parent
      : undefined;

    if (parent) {
      const totalChildrenFlex = parent.getTotalFlexOfFlexChildren();
      parent.children.forEach((child: Layer, index: number) => {
        const layerFlex = child.getFlexValue();
        if (!isNil(layerFlex) && !isNil(totalChildrenFlex)) {
          // this.commandManager.execute(
          //   new RemoveStyleAttribute(this.session, child, 'flexGrow')
          // );

          child.removeLayout('flexGrow');
          const newFlex = layerFlex * (1 / totalChildrenFlex);
          // this.commandManager.execute(
          //   new SetStyleAttribute(this.session, child, 'flex', newFlex)
          // );
          child.setLayout('flex', newFlex);
        }
      });
    }
  }
  setLayoutChildren() {
    let normalizeForFlex = false;

    normalizeForFlex = this.selectedLayers.some((layer: Layer) => {
      return (
        (layer.isFillHeight() || layer.isFillWidth()) && layer.type !== 'root'
      );
    });

    if (normalizeForFlex) {
      this.normalizeChildrenFlex();
    }

    this.layoutElementArray.length = 0;

    const selectedLayers = this.selectedLayers;
    if (selectedLayers.length === 0) {
      return;
    }
    const parentLayer = this.selectedLayers[0].parent;
    if (parentLayer) {
      parentLayer.children.forEach((child: any, index: number) => {
        const layoutChild = {
          layer: child,
          startFlex: child.layout.get('flex') ? child.layout.get('flex') : 0,
        };
        this.layoutElementArray.push(layoutChild);
      });
    }
  }

  // flex drag here
  private setLayoutChildSize(layer: Layer, deltaFactor: any) {
    const parentFlexDirection = layer.getParentLayerFlexDirection();
    const startDims = layer.executeFunction('getData', 'initialDims');
    let width = startDims.width;
    let height = startDims.height;
    if (startDims) {
      width = startDims.width + startDims.width * deltaFactor;
      height = startDims.height + startDims.height * deltaFactor;
    }
    if (parentFlexDirection === 'row') {
      layer.setLayout('width', width);
      // this.commandManager.execute(
      //   new SetStyleAttribute(this.session, layer, 'width', width)
      // );
    } else {
      layer.setLayout('height', height);

      // this.commandManager.execute(
      //   new SetStyleAttribute(this.session, layer, 'height', height)
      // );
    }
  }
  private getDeltaFactor(layer: Layer, delta: any, handle: any): any {
    const parent = layer.parent;

    if (!parent) {
      return 0;
    }
    const flexDirection = layer.getParentLayerFlexDirection();

    const width = parent.getBoundingClientRect().width;
    const height = parent.getBoundingClientRect().height;
    let deltaX = delta.x / width;
    let deltaY = delta.y / height;
    let deltaFactor = 0;
    if (handle.indexOf('C') === -1) {
      if (flexDirection === 'row') {
        if (handle.indexOf('L') > -1) {
          deltaX = -deltaX;
        }
        deltaFactor = deltaX;
      } else {
        if (handle.indexOf('T') > -1) {
          deltaY = -deltaY;
        }
        deltaFactor = deltaY;
      }
    }
    return +deltaFactor.toFixed(2);
  }

  dragLayout(layer: Layer, delta: IPosition, handle: any, eventName: string) {
    let deltaX: number = delta.x;
    let deltaY: number = delta.y;

    const startDims = layer.executeFunction('getData', 'initialDims');
    const originalDelta: IPosition = { x: deltaX, y: deltaY };

    let totalFlex = 0;

    let deltaFactor = this.getDeltaFactor(layer, originalDelta, handle);

    const selectedLayerFlex = startDims.flex;

    if (!selectedLayerFlex) {
      this.setLayoutChildSize(layer, deltaFactor);
    } else {
      const childrenLength = this.layoutElementArray.length;
      this.layoutElementArray.forEach((child: any, index: number) => {
        let expectedFlex;
        if (child.layer === layer) {
          expectedFlex = selectedLayerFlex + deltaFactor;
        } else {
          expectedFlex = child.startFlex - deltaFactor / (childrenLength - 1);
        }
        expectedFlex = +expectedFlex;

        if (!isNil(expectedFlex)) {
          child.layer.dispatchEvent(
            `${this.namespace}.${eventName}`,
            new LayerResizerEvent(
              `${this.namespace}.${eventName}`,
              child.layer,
              { flex: expectedFlex },
              null
            )
          );

          totalFlex = expectedFlex + totalFlex;
        }
      });
    }
  }

  private dragLayoutIsDoable(handle: string, layer: Layer) {
    const position = layer.layout.get('position');
    // TODO: implement this condition after symbols are done
    // workspace.persist.domain.isSymbolRootLayer(this.selectedLayers[0]
    return (
      ((handle.indexOf('B') > -1 || handle.indexOf('T') > -1) &&
        position !== 'absolute' &&
        layer.isFillHeight()) ||
      ((handle.indexOf('L') > -1 || handle.indexOf('R') > -1) &&
        position !== 'absolute' &&
        layer.isFillWidth())
    );
  }

  applyDimensionsForHandle(eventType: string) {
    const data = this.dragEventData;
    this.totalDx = data.session.current.x - data.session.start.x;
    this.totalDy = data.session.current.y - data.session.start.y;

    if (
      !this.selectedLayers.length ||
      (this.selectedLayers.length && !this.selectedLayers[0].parent)
    ) {
      return;
    }
    const parentAbsoluteRotation =
      this.selectedLayers.length && this.selectedLayers[0].parent
        ? this.selectedLayers[0].parent.getAbsoluteRotation()
        : 0;
    let rotateDelta = ModelUtils.rotatePoint(
      [this.totalDx, this.totalDy],
      360 - parentAbsoluteRotation,
      [0, 0]
    );

    if (this.dragLayoutIsDoable(data.targetHandle, this.selectedLayers[0])) {
      this.dragLayout(
        this.selectedLayers[0],
        { x: rotateDelta[0], y: rotateDelta[1] },
        data.targetHandle,
        eventType + 'Layout'
      );
      return;
    }

    const snappedData = this.canvas.executeFunction(
      'snapToPoints',
      this.snapPoints,
      [this.startX, this.startY],
      [rotateDelta[0], rotateDelta[1]],
      {
        ...this.boundingBox,
      },
      [this.totalDx, this.totalDy],
      [this.startAbsoluteX, this.startAbsoluteY],
      parentAbsoluteRotation,
      this.boundingBoxWRTWorld,
      data.targetHandle
    );

    let delta = snappedData.delta;

    let boundingBox = this.boundingBox;
    if (this.selectedLayers.length === 1) {
      let rotateDelta1 = ModelUtils.rotatePoint(
        [snappedData.delta.x, snappedData.delta.y],
        360 - this.selectedLayers[0].getBoundingClientRect().rotation,
        [0, 0]
      );
      delta = {
        x: rotateDelta1[0],
        y: rotateDelta1[1],
      };
    } else {
      boundingBox = this.boundingBoxWRTWorld;
      boundingBox = {
        ...this.boundingBoxWRTWorld,
        x: this.boundingBoxWRTWorld.x,
        y: this.boundingBoxWRTWorld.y,
      };
    }

    this.setNewLayout(
      this.selectedLayers,
      boundingBox,
      data.session,
      data.targetHandle,
      eventType,
      data,
      delta,
      snappedData.meta
    );
  }

  activate() {
    this.setSelectedLayers();

    this.startX = 0;
    this.startY = 0;
    this.startAbsoluteX = 0;
    this.startAbsoluteY = 0;

    this.totalDx = 0;
    this.totalDy = 0;

    this.canvas.on('selectedLayers', (event: any) => {
      this.setSelectedLayers();
      if (isEmpty(this.canvas.selectedLayers)) {
        this.canvas.executeFunction('ResizerRenderer.hide');
      } else {
        this.canvas.executeFunction('ResizerRenderer.show');
        this.rerender();
      }
    });
    this.canvas.addFunction(`${this.namespace}.stopHandleRerender`, () => {
      this.rerenderHandles = false;
    });
    this.canvas.addFunction(`${this.namespace}.startHandleRerender`, () => {
      this.rerenderHandles = true;
      this.rerender();
    });
    this.canvas.addEventListener(
      'ResizerRenderer.dragStart',
      (data: ResizerRendererDragEvent) => {
        this.canvas.executeMiddleware(
          'LayerResizer.dragStart',
          { data, selectedLayers: this.selectedLayers },
          (event: any) => {
            const data: ResizerRendererDragEvent = event.data;
            this.boundingBox = this.canvas.getContainerBoundingBoxWRTParent(
              this.selectedLayers
            );
            this.boundingBoxWRTWorld = this.canvas.getContainerBoundingBox(
              this.selectedLayers
            );

            forEach(this.selectedLayers, selectedLayer => {
              selectedLayer.setDragging(true);
            });

            const parentLayer = this.selectedLayers.length
              ? this.selectedLayers[0].parent
              : undefined;

            if (!parentLayer) {
              return;
            }
            this.startValues = this.getParentRelativeDims(
              data.startValues,
              this.selectedLayers[0]
            );

            this.startX = this.boundingBox.x;
            this.startY = this.boundingBox.y;

            this.startAbsoluteX =
              this.boundingBoxWRTWorld.x -
              this.canvas.getContainerBoundingBox([parentLayer]).x;

            this.startAbsoluteY =
              this.boundingBoxWRTWorld.y -
              this.canvas.getContainerBoundingBox([parentLayer]).y;

            this.snapPoints = this.canvas.executeFunction(
              'getSnapPoints',
              this.selectedLayers,
              parentLayer
            );

            this.selectedLayers.forEach((selectedLayer: any) => {
              let initialDims = selectedLayer.getBoundingClientRect();

              // initialDims.x = initialDims.x - parentLayer.getBorderLeft();
              // initialDims.y = initialDims.y - parentLayer.getBorderTop();
              initialDims.rootX =
                initialDims.rootX - parentLayer.getBorderLeft();
              initialDims.rootY =
                initialDims.rootY - parentLayer.getBorderTop();

              selectedLayer.executeFunction('setData', 'initialDims', {
                ...initialDims,
                absoluteRotation: selectedLayer.getAbsoluteRotation(),
                flex: selectedLayer.layout.get('flex'),
              });
              selectedLayer.dispatchEvent(
                `${this.namespace}.dragStart`,
                new LayerResizerEvent(
                  `${this.namespace}.dragStart`,
                  selectedLayer,
                  initialDims,
                  data,
                  undefined,
                  undefined,
                  this.snapPoints
                )
              );
              this.selectedLayerDims.set(selectedLayer, initialDims);
            });

            // this.snapPoints.gridSnap = this.config.gridSnap;
          }
        );
      }
    );
    this.canvas.addEventListener(
      'ResizerRenderer.drag',
      (data: ResizerRendererDragEvent) => {
        this.canvas.executeMiddleware(
          'LayerResizer.drag',
          { data, selectedLayers: this.selectedLayers },
          (event: any) => {
            this.dragEventData = event.data;
            this.applyDimensions('drag');
          }
        );
      }
    );
    this.canvas.addEventListener(
      'ResizerRenderer.dragEnd',
      (data: ResizerRendererDragEvent) => {
        this.canvas.executeMiddleware(
          'LayerResizer.dragEnd',
          { data, selectedLayers: this.selectedLayers },
          (event: any) => {
            forEach(this.selectedLayers, selectedLayer => {
              selectedLayer.setDragging(false);
            });
            this.dragEventData = event.data;
            this.applyDimensions('dragEnd');

            this.startValues = undefined;
            this.selectedLayerDims = new Map();
            this.shiftDirection = '';
            this.dragEventData = undefined;
            this.totalDx = 0;
            this.totalDy = 0;
          }
        );
      }
    );
    this.canvas.addEventListener(
      'ResizerRenderer.handleDragStart',
      (data: ResizerRendererDragEvent) =>
        this.canvas.executeMiddleware(
          'LayerResizer.handleDragStart',
          { data, selectedLayer: this.selectedLayers[0] },
          (event: any) => {
            const data: ResizerRendererDragEvent = event.data;
            this.boundingBox = this.canvas.getContainerBoundingBoxWRTParent(
              this.selectedLayers
            );
            this.boundingBoxWRTWorld = this.canvas.getContainerBoundingBox(
              this.selectedLayers
            );

            forEach(this.selectedLayers, selectedLayer => {
              selectedLayer.setDragging(true);
            });

            this.startValues = this.getParentRelativeDims(
              data.startValues,
              this.selectedLayers[0]
            );

            const parentLayer = this.selectedLayers.length
              ? this.selectedLayers[0].parent
              : undefined;

            if (!parentLayer) {
              return;
            }
            this.snapPoints = this.canvas.executeFunction(
              'getSnapPoints',
              this.selectedLayers,
              parentLayer,
              data.targetHandle
            );

            // console.log(this.snapPoints, 'snap points here');
            this.setLayoutChildren();

            this.selectedLayers.forEach((selectedLayer: any) => {
              const initialDims = selectedLayer.getBoundingClientRect();
              initialDims.rootX =
                initialDims.rootX - parentLayer.getBorderLeft();
              initialDims.rootY =
                initialDims.rootY - parentLayer.getBorderTop();
              selectedLayer.executeFunction('setData', 'initialDims', {
                ...initialDims,
                absoluteRotation: selectedLayer.getAbsoluteRotation(),
                flex: selectedLayer.layout.get('flex'),
              });
              selectedLayer.dispatchEvent(
                `${this.namespace}.handleDragStart`,
                new LayerResizerEvent(
                  `${this.namespace}.handleDragStart`,
                  selectedLayer,
                  initialDims,
                  data,
                  undefined,
                  undefined,
                  this.snapPoints
                )
              );
              this.selectedLayerDims.set(selectedLayer, initialDims);
            });

            this.startX = this.boundingBox.x;
            this.startY = this.boundingBox.y;

            this.startAbsoluteX =
              this.boundingBoxWRTWorld.x -
              this.canvas.getContainerBoundingBox([parentLayer]).x;

            this.startAbsoluteY =
              this.boundingBoxWRTWorld.y -
              this.canvas.getContainerBoundingBox([parentLayer]).y;
          }
        )
    );

    this.canvas.addEventListener(
      'ResizerRenderer.handleDrag',
      (data: ResizerRendererDragEvent) =>
        this.canvas.executeMiddleware(
          'LayerResizer.handleDrag',
          { data, selectedLayer: this.selectedLayers[0] },
          (event: any) => {
            this.dragEventData = event.data;
            this.applyDimensionsForHandle('handleDrag');
          }
        )
    );
    this.canvas.addEventListener(
      'ResizerRenderer.handleDragEnd',
      (data: ResizerRendererDragEvent) =>
        this.canvas.executeMiddleware(
          'LayerResizer.handleDragEnd',
          { data, selectedLayer: this.selectedLayers[0] },
          (event: any) => {
            forEach(this.selectedLayers, selectedLayer => {
              selectedLayer.setDragging(false);
            });
            this.dragEventData = event.data;
            this.applyDimensionsForHandle('handleDragEnd');
            this.startValues = undefined;
            this.dragEventData = undefined;
            this.totalDx = 0;
            this.totalDy = 0;
            this.selectedLayerDims = new Map();
          }
        )
    );
    this.canvas.addFunction(
      'LayerResizer.hideLayer',
      this.hideLayerResizer.bind(this)
    );
    this.canvas.addFunction(
      'LayerResizer.showLayer',
      this.showLayerResizer.bind(this)
    );

    this.canvas.addEventListener('blur', () => {
      if (this.isAltPressed) this.isAltPressed = false;
      if (this.isShiftPressed) this.isShiftPressed = false;
    });
    this.canvas.addEventListener('keyUp', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = false;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = false;
      }

      if (this.dragEventData) {
        if (this.dragEventData.targetHandle.indexOf('C') === -1) {
          this.applyDimensionsForHandle('handleDrag');
        } else {
          this.applyDimensions('drag');
        }
      }
    });
    this.canvas.addEventListener('keyDown', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = true;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = true;
      }
      if (this.dragEventData) {
        if (this.dragEventData.targetHandle.indexOf('C') === -1) {
          this.applyDimensionsForHandle('handleDrag');
        } else {
          this.applyDimensions('drag');
        }
      }
    });

    this.canvas.addEventListener('resetResizer', (e: ResetResizerEvent) => {
      const event = e.originalEvent;
      // const newLayer = e.newTarget;

      this.startValues = undefined;
      this.selectedLayerDims = new Map();

      this.boundingBox = this.canvas.getContainerBoundingBox(
        this.selectedLayers
      );
      this.startValues = this.getParentRelativeDims(
        event.startValues,
        this.selectedLayers[0]
      );
      const parentLayer = this.selectedLayers.length
        ? this.selectedLayers[0].parent
        : undefined;
      this.startX = this.boundingBox.x - (parentLayer ? parentLayer.rootX : 0);
      this.startY = this.boundingBox.y - (parentLayer ? parentLayer.rootY : 0);
      this.selectedLayers.forEach((selectedLayer: Layer) => {
        const initialDims = selectedLayer.getBoundingClientRect();
        selectedLayer.executeFunction('setData', 'initialDims', initialDims);
        this.selectedLayerDims.set(selectedLayer, initialDims);
      });

      this.snapPoints = this.canvas.executeFunction(
        'getSnapPoints',
        this.selectedLayers,
        parentLayer
      );
    });
  }
  setNewLayout(
    layers: Array<Layer>,
    boundingBox: IDimensionWithRotation,
    session: any,
    handle: string,
    eventName: string,
    originalEvent: any,
    delta?: any,
    meta?: any
  ) {
    let deltaInput = {
      x: session.current.x - session.start.x,
      y: session.current.y - session.start.y,
    };

    if (delta) {
      deltaInput = delta;
    }

    layers.forEach((selectedLayer: Layer, index: number) => {
      const initialDims = selectedLayer.executeFunction(
        'getData',
        'initialDims'
      );
      if (!initialDims) {
        return;
      }
      let initialDimensions = {
        x: initialDims.rootX,
        y: initialDims.rootY,
        height: initialDims.height,
        width: initialDims.width,
        rotation: initialDims.rotation,
      };
      let dimensions;
      if (handle.indexOf('ROTATE') !== -1) {
        let rotationDelta = {
          x: session.current.x - session.start.x,
          y: session.current.y - session.start.y,
        };

        if (layers.length === 1) {
          boundingBox = {
            ...boundingBox,
            x: selectedLayer.getBoundingClientRect().x,
            y: selectedLayer.getBoundingClientRect().y,
          };
          const rotateDelta = ModelUtils.rotatePoint(
            [rotationDelta.x, rotationDelta.y],
            360 - initialDims.absoluteRotation,
            [0, 0]
          );

          rotationDelta = {
            x: rotateDelta[0],
            y: rotateDelta[1],
          };
        }

        let rotation = calculateRotation(
          { ...boundingBox },
          session,
          initialDims.rotation,
          rotationDelta,
          handle
        );

        if (this.isShiftPressed) {
          const ROTATION_FACTOR = 15;
          const multiplicationFactor = Math.round(rotation / ROTATION_FACTOR);
          rotation = ROTATION_FACTOR * multiplicationFactor;
        }

        dimensions = { ...initialDimensions, rotation };
      } else {
        let isFlexChildren = false;
        let maintainAspectRatio = false;
        layers.forEach((layer: Layer) => {
          if (layer.isFlexChild()) isFlexChildren = true;
          if (layer.aspectRatio) maintainAspectRatio = true;
        });
        if (layers.length === 1) {
          dimensions = computeOrientation(
            initialDimensions,
            deltaInput,
            handle,
            this.isShiftPressed || maintainAspectRatio,
            this.isAltPressed && !isFlexChildren
          );
        } else {
          dimensions = computeOrientationMultiple(
            initialDimensions,
            deltaInput,
            handle,
            boundingBox,
            ModelUtils.getPointsFromBoundingBox(
              initialDimensions,
              initialDims.rotation
            ),
            this.isShiftPressed || maintainAspectRatio,
            this.isAltPressed && !isFlexChildren
          );
        }
      }
      let parentRootDims = { rootX: 0, rootY: 0 };
      if (selectedLayer.parent) {
        parentRootDims = selectedLayer.parent.getBoundingClientRect();
      }
      let newDimension = {
        ...dimensions,
      };
      // if (handle.indexOf('C') !== -1 || handle.indexOf('ROTATE') !== -1) {
      newDimension = {
        ...dimensions,
        x: dimensions.x - parentRootDims.rootX,
        y: dimensions.y - parentRootDims.rootY,
      };
      // }

      selectedLayer.dispatchEvent(
        `${this.namespace}.${eventName}`,
        new LayerResizerEvent(
          `${this.namespace}.${eventName}`,
          selectedLayer,
          newDimension,
          originalEvent,
          undefined,
          meta
        )
      );
      this.selectedLayerDims.set(selectedLayer, newDimension);
    });
  }
  deactivate() {
    this.dispose();
  }
}
