import * as React from 'react';
import { Layer, Gradient, Canvas } from '../../';
import { StopPoints } from '../../models/Gradient';
import LineSegmentSliderInput, { Stop } from 'line-segment-slider-input';
import {
  IDimensionWithRoot,
  IDimensionWithRotation,
  IPosition,
} from 'interfaces';

type propType = {
  layer: Layer;
  dims: IDimensionWithRoot & IDimensionWithRotation;
  gradient: Gradient;
  canvas: Canvas;
  zoom: number;
  scroll: IPosition;
  onChange: (
    gradient: LineSegmentGradient,
    type: '' | 'Skip' | 'Forget'
  ) => void;
  onClickOutside: (e: any) => void;
  onWrapperMouseDown: (e: any) => void;
  onWrapperContexMenu: (e: any) => void;
  onWrapperDoubleClick: (e: any) => void;
  onWrapperMouseOut: (e: any) => void;
  onWrapperDrop: (e: any) => void;
  onDragOver: (e: any) => void;
  onWrapperMouseMove: (e: any) => void;
  onWrapperMouseUp: (e: any) => void;
};

export type LineSegmentGradient = {
  from: number[];
  to: number[];
  stops: Stop[];
  activeIndex: number;
  gradientType: 'LinearGradient' | 'RadialGradient' | 'AngularGradient';
};

export const getLineSegmentObject = (gradient: Gradient) => {
  let stops: Array<Stop> = [];
  gradient.stops.map(stop => {
    stops.push({
      position: stop.offset,
      color: stop.stopColor,
    });
  });
  return {
    from: [gradient.from.x, gradient.from.y],
    to: [gradient.to.x, gradient.to.y],
    stops,
    activeIndex: gradient.activeIndex,
    gradientType: gradient.gradientType,
  };
};

export const getGradientFromLineSegmentObject = (
  gradient: LineSegmentGradient
) => {
  let stops: Array<StopPoints> = [];
  gradient.stops.map((stop: Stop) => {
    stops.push({
      offset: stop.position,
      stopColor: stop.color,
    });
  });
  return {
    from: { x: gradient.from[0], y: gradient.from[1] },
    to: { x: gradient.to[0], y: gradient.to[1] },
    stops,
    activeIndex: gradient.activeIndex,
    gradientType: gradient.gradientType,
  };
};

export default class GradientEditorRenderer extends React.Component<
  propType,
  any
> {
  constructor(props: propType) {
    super(props);
    this.state = {
      activeIndex: props.gradient.activeIndex,
      ...getLineSegmentObject(props.gradient),
    };
  }
  componentDidMount() {
    const { canvas } = this.props;
    canvas.addFunction(
      'GradientEditor.changeStopColor',
      (context: any, payload: any) => {
        let newStops: Stop[] = this.state.stops;
        newStops[this.state.activeIndex].color = payload.color;
        this.setState({ stops: newStops });
        this.props.onChange({ ...this.state } as LineSegmentGradient, 'Skip');
      }
    );
    canvas.addFunction(
      'GradientEditor.change',
      (context: any, gradient: Gradient) => {
        if (gradient) {
          const lineSegmentObj = getLineSegmentObject(gradient);
          this.setState({
            ...lineSegmentObj,
          });
        }
      }
    );
  }
  handleMove = (type: string, handle: number[] | Stop[]) => {
    if (type === 'from') {
      this.props.onChange(
        {
          ...this.state,
          from: handle,
        } as LineSegmentGradient,
        'Skip'
      );
      this.setState({ from: handle });
    } else if (type === 'to') {
      this.props.onChange(
        {
          ...this.state,
          to: handle,
        } as LineSegmentGradient,
        'Skip'
      );
      this.setState({ to: handle });
    } else {
      this.props.onChange(
        {
          ...this.state,
          stops: handle,
        } as LineSegmentGradient,
        'Skip'
      );
      this.setState({ stops: handle });
    }
    return null;
  };
  removeHandle = () => {
    if (this.state.activeIndex) {
      let stops = this.state.stops;
      if (
        !(
          this.state.activeIndex === 0 ||
          this.state.activeIndex === stops.length - 1
        )
      ) {
        stops.splice(this.state.activeIndex, 1);
      }

      this.setState({ stops });
      this.props.onChange({ ...this.state } as LineSegmentGradient, '');
    }
  };

  changeIndex = (activeIndex: number) => {
    this.setState({ activeIndex });
    this.props.onChange(
      { ...this.state, activeIndex } as LineSegmentGradient,
      'Skip'
    );
  };
  handleDragStart = () => {
    this.props.onChange({ ...this.state } as LineSegmentGradient, 'Forget');
  };
  handleDragEnd = () => {
    this.props.onChange({ ...this.state } as LineSegmentGradient, '');
  };
  render() {
    const { props, state } = this;
    const { dims, zoom, scroll } = props;
    return (
      <LineSegmentSliderInput
        //@ts-ignore
        x={dims.rootX}
        y={dims.rootY}
        width={dims.width}
        height={dims.height}
        from={state.from}
        to={state.to}
        stops={state.stops}
        index={state.activeIndex}
        zoom={zoom}
        rotation={dims.rotation}
        scroll={scroll}
        changeIndex={this.changeIndex}
        handleMove={this.handleMove}
        removeHandle={this.removeHandle}
        onClickOutside={this.props.onClickOutside}
        onWrapperMouseDown={this.props.onWrapperMouseDown}
        onWrapperContexMenu={this.props.onWrapperContexMenu}
        onWrapperDoubleClick={this.props.onWrapperDoubleClick}
        onWrapperMouseOut={this.props.onWrapperMouseOut}
        onWrapperDrop={this.props.onWrapperDrop}
        onDragOver={this.props.onDragOver}
        onWrapperMouseMove={this.props.onWrapperMouseMove}
        onWrapperMouseUp={this.props.onWrapperMouseUp}
        onDragStart={this.handleDragStart}
        onDragEnd={this.handleDragEnd}
      />
    );
  }
}
