import { get } from 'lodash';
import * as React from 'react';
import AndroidStatusBar from './StatusBars/AndroidStatusBar';
import IPhoneXStatusBar from './StatusBars/IPhoneXStatusBar';
import IOSStatusBar from './StatusBars/IOSStatusBar';
import { Layer } from '../../..';

export default class StatusBarComponent extends React.Component<
  {
    layer: Layer;
    width: number;
    height: number;
    deviceName: string;
    deviceOS: string;
    deviceOrientation: string;
    backgroundColor: string;
    barStyle: string;
  },
  any
> {
  constructor(props: any) {
    super(props);
  }
  onUpdate = () => {
    // this.forceUpdate();
  };
  attachStatusBarListeners = () => {
    // const statusBarRenderLayer = this.props.layer;
    // const statusBarLayer = statusBarRenderLayer.domainCompiledRef.domainRef;
    // if (statusBarLayer) {
    //   statusBarLayer.props.on('hidden', this.onUpdate);
    //   statusBarLayer.props.on('backgroundColor', this.onUpdate);
    //   statusBarLayer.props.on('barStyle', this.onUpdate);
    //   statusBarLayer.props.on('animated', this.onUpdate);
    // }
    // if (this.props.layer.getFile()) {
    //   this.props.layer.getFile().on('height', this.onUpdate);
    //   this.props.layer.getFile().on('width', this.onUpdate);
    // }
  };
  removeStatusBarListeners = () => {
    // const statusBarRenderLayer = this.props.layer;
    // const statusBarLayer = statusBarRenderLayer.domainCompiledRef.domainRef;
    // if (statusBarLayer) {
    //   statusBarLayer.props.off('hidden', this.onUpdate);
    //   statusBarLayer.props.off('backgroundColor', this.onUpdate);
    //   statusBarLayer.props.off('barStyle', this.onUpdate);
    //   statusBarLayer.props.off('animated', this.onUpdate);
    // }
    // if (this.props.layer.getFile()) {
    //   this.props.layer.getFile().off('height', this.onUpdate);
    //   this.props.layer.getFile().off('width', this.onUpdate);
    // }
    // // if (this.props.layer.parent) {
    // //   this.props.layer.parent.off("height", this.onUpdate);
    // //   this.props.layer.parent.off("width", this.onUpdate);
    // // }
  };
  componentDidMount() {
    this.props.layer.on(['layout', 'updateDom'], this.onUpdate);
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(['layout', 'updateDom'], this.onUpdate);
    this.props.layer.on(['layout', 'updateDom'], () => {
      this.onUpdate();
    });
  }
  componentWillUnmount() {
    this.props.layer.off(['layout', 'updateDom'], this.onUpdate);
  }
  getElement() {
    if (!this.props.layer) {
      return null;
    }

    const deviceName = this.props.deviceName;
    const deviceOS = this.props.deviceOS;
    const deviceOrientation = this.props.deviceOrientation;

    let barColor: string = '#000';

    let deviceWidth = this.props.width;
    let deviceHeight = this.props.height;
    // if (deviceOrientation === 'portrait') {
    //   deviceWidth = device.resolution.width / device.pixelRatio;
    //   deviceHeight = device.resolution.height / device.pixelRatio;
    // } else {
    //   deviceWidth = device.resolution.height / device.pixelRatio;
    //   deviceHeight = device.resolution.width / device.pixelRatio;
    // }

    const style = {
      // display: statusBarRenderLayer.props.style.display,
    };
    // if (statusBarRenderLayer.props.hidden) {
    //   return null;
    // }
    // const height = renderFile ? renderFile.height : 0;
    // const width = renderFile ? renderFile.width : 0;
    let IPHONEX_OR_IPHONEXS: boolean = false;
    let IPHONEXR_OR_IPHONEXSMAX: boolean = false;

    if (deviceOS && deviceName) {
      IPHONEX_OR_IPHONEXS =
        deviceOS.toLowerCase() === 'ios' &&
        (deviceName.toLowerCase() === 'iphone x' ||
          deviceName.toLowerCase() === 'iphone xs' ||
          deviceName.toLowerCase() === 'iphone 11 pro');

      IPHONEXR_OR_IPHONEXSMAX =
        deviceOS.toLowerCase() === 'ios' &&
        (deviceName.toLowerCase() === 'iphone xr' ||
          deviceName.toLowerCase() === 'iphone xs max' ||
          deviceName.toLowerCase() === 'iphone 11' ||
          deviceName.toLowerCase() === 'iphone 11 pro max');
    }

    //    &&
    // ((deviceWidth === height && deviceHeight === width) ||
    //   (deviceWidth === width && deviceHeight === height));

    const IOS: boolean = deviceOS === 'iOS';
    const WEB: boolean = deviceOS === 'Web';
    if (WEB) {
      return null;
    }

    if (IOS && !IPHONEX_OR_IPHONEXS && !IPHONEXR_OR_IPHONEXSMAX) {
      if (this.props.barStyle === 'light-content') {
        barColor = '#ffffff';
      } else if (this.props.barStyle === 'dark-content') {
        barColor = '#000000';
      }
      return (
        <div style={{ position: 'absolute', top: 0, left: 0 }}>
          <IOSStatusBar width={deviceWidth} color={barColor} style={style} />
        </div>
      );
    } else if (IPHONEX_OR_IPHONEXS || IPHONEXR_OR_IPHONEXSMAX) {
      if (this.props.barStyle === 'light-content') {
        barColor = '#ffffff';
      } else if (this.props.barStyle === 'dark-content') {
        barColor = '#000000';
      }
      return (
        <div style={{ position: 'absolute', top: 0, left: 0 }}>
          <IPhoneXStatusBar
            width={deviceWidth}
            deviceOrientation={deviceOrientation}
            backgroundColor="rgba(255,255,255,0.2)"
            color={barColor}
            style={style}
          />
        </div>
      );
    } else {
      if (this.props.barStyle === 'light-content') {
        barColor = '#ffffff';
      } else if (this.props.barStyle === 'dark-content') {
        barColor = '#404040';
      }
      return (
        <div style={{ position: 'absolute', top: 0, left: 0 }}>
          <AndroidStatusBar
            width={deviceWidth}
            height={deviceHeight}
            backgroundColor={this.props.backgroundColor || '#999999'}
            color={barColor}
            style={style}
          />
        </div>
      );
    }
  }
  render() {
    return this.getElement();
  }
}
