import { Canvas, IPlugin, Layer } from '../..';

export default class HoverPlugin implements IPlugin {
  name = 'HoverPlugin';
  canvas!: Canvas;
  dispose: any;
  forbiddenLayers: Array<Layer> = [];
  hoverLayer: any;
  hoverLayers: Array<any> = [];
  hoverWrapperLayer: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }
  hideLayerHover(context: any) {
    if (!this.forbiddenLayers.includes(context.layer)) {
      this.forbiddenLayers.push(context.layer);
    }
  }
  showLayerHover(context: any) {
    if (this.forbiddenLayers.includes(context.layer)) {
      this.forbiddenLayers.splice(
        this.forbiddenLayers.indexOf(context.layer),
        1
      );
    }
  }

  hideAllHoverBoxes(context: any) {
    this.hoverWrapperLayer.style.display = 'none';
  }
  showAllHoverBoxes(context: any) {
    this.hoverWrapperLayer.style.display = 'block';
  }
  showHover(layer: Layer) {
    return (
      !this.forbiddenLayers.includes(layer) &&
      layer.type !== 'canvas' &&
      !this.canvas.selectedLayers.includes(layer)
    );
  }
  renderBoxes() {
    for (let index = 0; index < this.canvas.hoveredLayers.length; index++) {
      const layer = this.canvas.hoveredLayers[index];
      if (this.showHover(layer)) {
        let hoverBox;
        if (this.hoverLayers[index]) {
          hoverBox = this.hoverLayers[index];
        } else {
          hoverBox = document.createElement('div');
          this.hoverWrapperLayer.appendChild(hoverBox);
        }
        hoverBox.style.position = 'absolute';
        hoverBox.style.pointerEvents = 'none';
        let dims = layer.getBoundingClientRect();

        const box = this.canvas.getBoundingBox([layer]);
        hoverBox.style.top = box.y + 'px';
        hoverBox.style.left = box.x + 'px';
        hoverBox.style.height = dims.height + 'px';
        hoverBox.style.width = dims.width + 'px';
        // hoverBox.style.width = box.rotation + 'px';
        hoverBox.style.transform = box.rotation
          ? `rotate(${box.rotation}deg)`
          : '';
        hoverBox.id = 'hover' + index;
        hoverBox.style.border = `${1 / this.canvas.getZoom()}px solid #25B9D1`;
        this.hoverLayers[index] = hoverBox;
      } else {
        if (this.hoverLayers[index] && this.hoverLayers[index].remove) {
          this.hoverLayers[index].remove();
          this.hoverLayers.splice(index, 1);
          index--;
        }
      }
    }
    let breakIndex = this.canvas.hoveredLayers.length;
    if (breakIndex < this.hoverLayers.length) {
      for (let i = breakIndex; i < this.hoverLayers.length; i++) {
        if (this.hoverLayers[i] && this.hoverLayers[i].remove) {
          this.hoverLayers[i].remove();
          this.hoverLayers.splice(i, 1);
          i--;
        }
      }
    }
  }
  activate() {
    this.hoverWrapperLayer = document.createElement('div');
    this.dispose = this.canvas.addDomToWorld(this.hoverWrapperLayer, true, 102);
    this.hoverWrapperLayer.style.pointerEvents = 'none';
    this.canvas.on('hoveredLayers', (e: any) => {
      this.renderBoxes();
    });
    this.canvas.on('selectedLayers', (e: any) => {
      this.renderBoxes();
    });
    this.canvas.on('zoom', (e: any) => {
      this.renderBoxes();
    });
    this.canvas.addFunction('Hover.hideLayer', this.hideLayerHover.bind(this));
    this.canvas.addFunction('Hover.showLayer', this.showLayerHover.bind(this));
    this.canvas.addFunction(
      'Hover.hideAllHoverBoxes',
      this.hideAllHoverBoxes.bind(this)
    );
    this.canvas.addFunction(
      'Hover.showAllHoverBoxes',
      this.showAllHoverBoxes.bind(this)
    );
  }

  deactivate() {
    this.dispose();
  }
}
