import * as React from 'react';
import * as ReactDOM from 'react-dom';

import Scrollbars from 'react-custom-scrollbars';
import { Canvas } from '../models/Canvas';
import SetScrollEvent from '../events/SetScrollEvent';

type Props = {
  canvas: Canvas;
  style?: any;
  stage?: string;
  displayMode?: string;
};
export default class ScrollContainer extends React.PureComponent<Props, any> {
  scrollDom: any;
  scrollContainerRef: Scrollbars | null | undefined;
  center: any;
  zoom: any;
  scroll: any;
  baseState: any;
  viewportState: any;

  constructor(props: any) {
    super(props);
    this.zoom = props.canvas.getZoom();
    this.scroll = props.canvas.getScroll();

    const scrollFactor: any = { x: 0.5, y: 0.5 };
    const scrollXFactor: number = scrollFactor.x;
    const scrollYFactor: number = scrollFactor.y;

    this.state = {
      scroll: { x: scrollXFactor, y: scrollYFactor },
    };
  }

  onUpdate = () => {
    const viewport = this.props.canvas.getViewport();
    this.viewportState = {
      height: viewport.height,
      width: viewport.width,
    };

    this.baseState = {
      height: this.props.canvas.layout.get('height'),
      width: this.props.canvas.layout.get('width'),
    };
    this.forceUpdate();
    this.centerScrollTo();
  };

  componentDidMount() {
    if (this.scrollContainerRef) {
      this.scrollDom = ReactDOM.findDOMNode(this.scrollContainerRef);
    }
    document.addEventListener('keydown', function(e: any) {
      if (e.keyCode === 32 && e.target === document.body) {
        e.preventDefault();
      }
    });

    this.setCenterPosition();
    this.getCenterPointInCurrentViewport();
    this.centerScrollTo();
    this.props.canvas.addEventListener('setScroll', this.setScroll);
    this.props.canvas.on('zoom', (e: any) => {
      if (this.zoom !== this.props.canvas.getZoom()) {
        this.zoom = this.props.canvas.getZoom();
        this.centerScrollTo();
      }
    });

    this.props.canvas.on(
      ['viewport', 'layout', 'mouseCursorType'],
      this.onUpdate
    );
    this.props.canvas.on(['moveWithGrabHandle'], this.onUpdate);
  }

  componentWillUnmount() {
    this.props.canvas.off(
      ['viewport', 'layout', 'mouseCursorType'],
      this.onUpdate
    );
    this.props.canvas.off(['moveWithGrabHandle'], this.onUpdate);
    this.props.canvas.removeEventListener('setScroll', this.setScroll);
  }
  setScroll = (scroll: SetScrollEvent) => {
    this.state.scroll.x = scroll.scrollX;
    this.state.scroll.y = scroll.scrollY;

    const scrollX: number =
      this.state.scroll.x *
      (this.baseState.width * this.zoom - this.viewportState.width);

    const scrollY: number =
      this.state.scroll.y *
      (this.baseState.height * this.zoom - this.viewportState.height);

    const centerX =
      ((scrollX as number) + this.viewportState.width / 2) /
      (this.baseState.width * this.zoom);

    const centerY =
      ((scrollY as number) + this.viewportState.height / 2) /
      (this.baseState.height * this.zoom);

    this.center = { x: centerX, y: centerY };

    this.centerScrollTo();
  };

  getScrollPositon(inputScroll?: any): any {
    const props = this.props;
    let scroll = inputScroll ? inputScroll : this.scroll;
    const scrollX: number =
      scroll.x * (this.baseState.width * this.zoom - this.viewportState.width);

    const scrollY: number =
      scroll.y *
      (this.baseState.height * this.zoom - this.viewportState.height);

    return { x: scrollX, y: scrollY };
  }
  getCenterPointInCurrentViewport() {
    const props = this.props;
    const scrollPostion = this.getScrollPositon();
    let centerX =
      (scrollPostion.x + this.viewportState.width / 2) /
      (this.baseState.width * this.zoom);

    let centerY =
      (scrollPostion.y + this.viewportState.height / 2) /
      (this.baseState.height * this.zoom);
    this.center = { x: centerX, y: centerY };
  }
  setCenterPosition() {
    this.center = {
      x:
        (this.state.scroll.x +
          this.viewportState.width / (this.baseState.width * this.zoom)) /
        2,
      y:
        (this.state.scroll.y +
          this.viewportState.height / (this.baseState.height * this.zoom)) /
        2,
    };
  }

  centerScrollTo = (emit: boolean = true) => {
    const centerX: number = this.center.x * (this.baseState.width * this.zoom);
    const centerY: number = this.center.y * (this.baseState.height * this.zoom);

    let scrollY = centerY - this.viewportState.height / 2;
    let scrollX = centerX - this.viewportState.width / 2;

    if (this.scrollContainerRef) {
      this.scrollContainerRef.scrollLeft(scrollX);
      this.scrollContainerRef.scrollTop(scrollY);
    }
  };

  onScrollFrame = (values: any) => {
    if (this.scrollContainerRef) {
      if (this.viewportState.height > 0 && this.viewportState.width > 0) {
        const x =
          (values.scrollLeft as number) /
          (this.baseState.width * this.zoom - this.viewportState.width);
        const y =
          (values.scrollTop as number) /
          (this.baseState.height * this.zoom - this.viewportState.height);

        const centerX =
          ((values.scrollLeft as number) + this.viewportState.width / 2) /
          (this.baseState.width * this.zoom);

        const centerY =
          ((values.scrollTop as number) + this.viewportState.height / 2) /
          (this.baseState.height * this.zoom);

        this.center = {
          x: centerX,
          y: centerY,
        };

        this.state.scroll.x = isNaN(x) ? 0 : x;
        this.state.scroll.y = isNaN(y) ? 0 : y;
        this.centerScrollTo();
        this.props.canvas.setScroll(this.state.scroll);
      }
    }
  };

  moveWithGrabHandle = (event: React.MouseEvent<Scrollbars>) => {
    if (this.props.canvas.isMouseDown && this.props.canvas.moveWithGrabHandle) {
      // window.requestAnimationFrame(() => {
      if (this.scrollContainerRef) {
        const deltaX = event.nativeEvent.movementX,
          deltaY = event.nativeEvent.movementY,
          currentScrollX = this.scrollContainerRef.getScrollLeft(),
          currentScrollY = this.scrollContainerRef.getScrollTop();
        this.scrollContainerRef.scrollLeft(currentScrollX - deltaX);
        this.scrollContainerRef.scrollTop(currentScrollY - deltaY);
        event.stopPropagation();
      }
      // });
    }
  };

  render() {
    const spaceKey = this.props.canvas.moveWithGrabHandle;

    this.baseState = {
      height: this.props.canvas.layout.get('height'),
      width: this.props.canvas.layout.get('width'),
    };
    const viewport = this.props.canvas.getViewport();
    this.viewportState = {
      height: viewport.height,
      width: viewport.width,
    };
    return (
      <Scrollbars
        ref={c => {
          this.scrollContainerRef = c;
        }}
        autoHide
        onScrollFrame={this.onScrollFrame}
        style={{
          ...this.props.style,
          width: this.viewportState.width,
          height: this.viewportState.height,
          cursor: this.props.canvas.mouseCursorType
            ? this.props.canvas.mouseCursorType
            : 'initial',
          overflow: 'scroll',
        }}
        onMouseDown={e => {
          // @ts-ignore
          if (e.button !== 2 && e.which !== 3) {
            // this.mouseDown = true;
            this.props.canvas.setMouseDown(true);
          }
        }}
        onMouseUp={() => {
          // this.mouseDown = false;
          this.props.canvas.setMouseDown(false);
        }}
        onMouseLeave={() => {
          // this.mouseDown = false;
          this.props.canvas.setMouseDown(false);
        }}
        onMouseMove={this.moveWithGrabHandle}
        className="scrollcontainer"
      >
        {React.Children.map(this.props.children, (child: any) => {
          if (child) {
            return React.cloneElement(child, {
              ...child.props,
              style: {
                ...child.props.style,
                ...(spaceKey && this.props.canvas.isMouseDown
                  ? { pointerEvents: 'none' }
                  : {}),
              },
            });
          }
          return null;
        })}
      </Scrollbars>
    );
  }
}
