import { Canvas, IPlugin, IContext } from '../..';

export default class LayerMouseCursorPlugin implements IPlugin {
  name = 'LayerMouseCursorPlugin';
  canvas!: Canvas;
  dispose: any;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  activate() {
    this.canvas.addFunction(
      'setMouseCursor',
      (context: IContext, cursor: string) => {
        context.layer.addEventListener('mouseEnter', (data: any) => {
          this.canvas.setMouseCursor(cursor);
        });
        context.layer.addEventListener('mouseLeave', (data: any) => {
          this.canvas.setMouseCursor('default');
        });
      }
    );
  }

  deactivate() {
    this.canvas.removeFunction('setMouseCursor');
    this.dispose();
  }
}
