import * as React from 'react';
import { TextInput } from 'react-native-web';
import { Layer } from 'models/Layer';
import { pick } from 'lodash';

const rerenderProps = ['layout', 'children', 'props', 'style', 'visible'];
export default class TextInputRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;

  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  getStyles = (layer: Layer) => {
    const appliedStyles = { ...layer.style.getAll(), ...layer.layout.getAll() };
    delete appliedStyles['fontPostscriptName'];
    delete appliedStyles['wordBreak'];
    return appliedStyles;
  };

  render() {
    const layer: Layer = this.props.layer;
    const layerProps = { ...layer.props.getAll() };
    const filteredProps = pick(layerProps, [
      'defaultValue',
      'numberOfLines',
      'multiline',
      'maxLength',
      'placeholder',
      'placeholderTextColor',
      'value',
    ]);
    const layerId = layerProps['id'];
    delete layerProps['id'];
    return (
      <TextInput
        ref={(c: any) => {
          this._ref = c;
        }}
        {...filteredProps}
        style={this.getStyles(layer)}
        nativeID={layerId}
      />
    );
  }
}
