export default class MiddlewareRegistry {
  _middlewares: Map<string, Array<any>>;
  constructor() {
    this._middlewares = new Map();
  }

  addMiddleware = (type: string, callback: any) => {
    let middlewares = this._middlewares.get(type);
    if (middlewares) {
      middlewares.push(callback);
    } else {
      this._middlewares.set(type, [callback]);
    }
  };

  execute(middlewareCallbacks: Array<any>, cb: any, data: any) {
    const self = this;
    if (middlewareCallbacks.length === 0) {
      return cb(data);
    } else {
      const mw = middlewareCallbacks[0];
      const remainingCbs = [...middlewareCallbacks];
      remainingCbs.splice(0, 1);

      const next = (_data: any) => {
        return self.execute(remainingCbs, cb, _data);
      };
      return mw(next, data);
    }
  }
  executeMiddleware(type: string, data: any, callback: any) {
    const middlewares = this._middlewares.get(type);
    if (middlewares) {
      // @ts-ignore
      if (window['debug'] && window['debugLog']) {
        // @ts-ignore
        window['debugLog']('Middleware: ' + type);
      }
      return this.execute(middlewares, callback, data);
    } else {
      return callback(data);
    }
  }
}
