import { Canvas, IPlugin, Layer, IContext } from '../..';
export default class FloatingButton implements IPlugin {
  name = 'FloatingButton';
  canvas!: Canvas;

  dom: any;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction(
      'addFloatingButton',
      this.addFloatingButton.bind(this)
    );
    this.canvas.addFunction(
      'removeFloatingButton',
      this.removeFloatingButton.bind(this)
    );
  }

  removeFloatingButton() {
    if (this.dom) this.canvas.removeDomFromViewport(this.dom);
  }

  addFloatingButton(context: any, caption: any, callback: any) {
    var button = document.createElement('button');

    this.dom = button;

    button.innerHTML = caption;
    button.addEventListener('click', callback);

    let styles = `text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
    user-select: none;
    -webkit-font-smoothing: antialiased;
    outline: none;
    font-size: 11px;
    align-items: center;
    display: inline-flex;
    position: relative;
    border: none;
    transition: all 0.3s ease 0s;
    cursor: pointer;
    border-right: none;
    border-left: none;
    padding: 3px 6px;
    justify-content: center;
    margin-right: 0px;
    margin-left: 0px;
    min-width: 0px;
    -webkit-app-region: no-drag;
    border-radius: 2px;
    background: rgb(37, 41, 49);
    width: auto;
    color: rgb(251, 251, 251);
    height: 30px;
    padding-left: 6px;
    padding-right: 6px;
    margin-top: 30px;
    margin-left: 30px;

    text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
    vertical-align: baseline;
    border-width: 0px;
    border-style: initial;
    border-color: initial;
    border-image: initial;
    font: inherit;
    box-sizing: border-box;
    user-select: none;
    -webkit-font-smoothing: antialiased;
    font-family: Roboto, -apple-system, BlinkMacSystemFont, sans-serif;
    font-weight: 400;
    align-self: center;
    text-overflow: ellipsis;
    letter-spacing: -0.27px;
    font-size: 14px;
    color: rgb(241, 242, 244, 0.6);

    `;

    button.setAttribute('style', styles);

    this.canvas.addDomToViewport(button);
  }

  deactivate() {
    this.canvas.removeFunction('addFloatingButton');
    this.canvas.removeFunction('removeFloatingButton');
  }
}
