import { Event, Layer } from '../../..';

export default class StageDesignerDrawEvent extends Event {
  dimensions: { x: number; y: number; height?: number; width?: number };
  worldX: number;
  worldY: number;
  meta?: any;
  snapPoints?: any;
  constructor(
    name: string,
    target: Layer,
    payload: {
      x: number;
      y: number;
      height?: number;
      width?: number;
      worldX: number;
      worldY: number;
    },
    currentTarget?: Layer,
    meta?: any,
    snapPoints?: any
  ) {
    super(name, target, currentTarget);
    this.dimensions = {
      x: payload.x,
      y: payload.y,
      height: payload.height,
      width: payload.width,
    };
    this.worldX = payload.worldX;
    this.worldY = payload.worldY;
    this.meta = meta;
    this.snapPoints = snapPoints;
  }
}
