import * as React from 'react';
import { Image } from 'react-native-web';
import { get } from 'lodash';
import Svg, { Path, Defs, Mask, Image as SVGImage } from 'svgs';
import { Layer } from 'models/Layer';
import { generateSVGPath } from '@builderx-core/conversions';
import { generateRandomString } from '../../models/utils';
const rerenderProps = ['layout', 'children', 'props', 'style', 'visible'];
export default class ImageRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    if (this._ref) {
      this.props.layer.setDomRef(this._ref);
    } else {
      this.props.layer.setDomRef(this);
    }
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    if (this._ref) {
      this.props.layer.setDomRef(this._ref);
    } else {
      this.props.layer.setDomRef(this);
    }
  }
  get maskPath() {
    const layer: Layer = this.props.layer;
    let imageMask = layer.props.get('mask');
    if (!imageMask) {
      return '';
    }
    return generateSVGPath(
      imageMask || [],
      1,
      true,
      layer.props.get('viewBox')
    );
  }
  render() {
    const layer: Layer = this.props.layer;
    let layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    let transform = layer.rotation ? `rotate(${layer.rotation}deg)` : undefined;
    let imageStyle = {
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
      transform,
    };
    let sourceUri: string = get(layerProps, 'source.uri', undefined);
    const imageURI = layer.props.get('blobURL')
      ? layer.props.get('blobURL')
      : sourceUri
      ? sourceUri
      : undefined;
    if (this.maskPath) {
      let imageMaskId = generateRandomString(6);
      return (
        <Svg
          // @ts-ignore
          id={layerId}
          style={imageStyle}
          key={layer.id}
          viewBox={layerProps.viewBox}
          preserveAspectRatio="none"
          ref={(c: any) => {
            this._ref = c;
          }}
        >
          <Defs>
            <Mask id={imageMaskId} x="0" y="0">
              <Path
                fill="white" // DOC: hard-coded acc. to the masking behaviour in sketch
                d={this.maskPath}
              />
            </Mask>
          </Defs>
          <SVGImage
            x={layerProps.svgDims.x}
            y={layerProps.svgDims.y}
            width={layerProps.svgDims.width}
            height={layerProps.svgDims.height}
            href={imageURI}
            preserveAspectRatio="none"
            mask={`url(#${imageMaskId})`}
          />
        </Svg>
      );
    } else if (layerProps.resizeMode === 'center') {
      return (
        <img
          ref={(c: any) => {
            this._ref = c;
          }}
          src={imageURI}
          {...layerProps}
          style={{
            ...imageStyle,
            objectFit: 'none',
          }}
          id={layerId}
        />
      );
    }
    return (
      <Image
        {...layerProps}
        style={imageStyle}
        source={{
          uri: imageURI,
        }}
        resizeMode={
          layerProps['resizeMode'] ? layerProps['resizeMode'] : undefined
        }
        nativeID={layerId}
      />
    );
  }
}
