import { Canvas } from '../../';
import { IPlugin, Layer } from '../../';
import drawXLines from './components/XLines';
import drawYLines from './components/YLines';
import { get } from 'lodash';
import { Box } from './types';

export default class MeasurementLinesPlugin implements IPlugin {
  name = 'MeasurementLinesPlugin';
  namespace = 'MeasurementLines';
  canvas!: Canvas;
  dispose: any;
  show: boolean = false;
  lines: Array<any> = [];
  currentHoveredLayer: any;
  temporarilyHidden: boolean = false;
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }
  display() {
    if (this.show) {
      const hoveredLayers = this.canvas.hoveredLayers.filter(
        (layer: Layer) => layer.type !== 'canvas'
      );
      const emptyBox = { x: 0, y: 0, height: 0, width: 0 };
      let box: Box = emptyBox;
      let helperBox: Box = emptyBox;
      let hoveredDims;
      let parentHoveredLayer;
      if (hoveredLayers.length) {
        hoveredDims = this.canvas.getBoundingBox([hoveredLayers[0]]);
        this.currentHoveredLayer = hoveredLayers[0];
        parentHoveredLayer = get(hoveredLayers, '[0].parent');
      }
      if (this.canvas.selectedLayers.length && hoveredDims) {
        helperBox = this.canvas.getBoundingBox(this.canvas.selectedLayers);
        box = hoveredDims;
      } else if (
        parentHoveredLayer &&
        parentHoveredLayer.type !== 'canvas' &&
        hoveredDims
      ) {
        helperBox = hoveredDims;
        box = this.canvas.getBoundingBox([parentHoveredLayer]);
      } else if (this.canvas.selectedLayers.length) {
        helperBox = this.canvas.getBoundingBox(this.canvas.selectedLayers);
        let parent = get(this.canvas, 'selectedLayers[0].parent');
        box =
          parent && parent.type !== 'canvas'
            ? this.canvas.getBoundingBox([parent])
            : emptyBox;
      }
      const xLines = drawXLines({
        box,
        helperBox,
        canvas: this.canvas,
      });
      const yLines = drawYLines({
        box,
        helperBox,
        canvas: this.canvas,
      });
      if (this.lines && this.lines.length > 0) {
        this.lines.forEach(line => {
          line.parentNode.removeChild(line);
        });
        this.lines = [];
      }
      this.lines = [...xLines, ...yLines];
    }
  }
  showLines() {
    this.show = true;
    this.temporarilyHidden = false;
    this.display();
  }
  hideLines() {
    this.show = false;
    if (this.lines && this.lines.length > 0) {
      this.lines.forEach(line => {
        line.parentNode.removeChild(line);
      });
      this.lines = [];
    }
    this.currentHoveredLayer = undefined;
  }
  activate() {
    this.canvas.addFunction(
      `${this.namespace}.show`,
      this.showLines.bind(this)
    );
    this.canvas.addFunction(
      `${this.namespace}.hide`,
      this.hideLines.bind(this)
    );
    this.canvas.addEventListener('keyUp', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 18) {
        this.temporarilyHidden = false;
        this.hideLines();
      }
    });
    this.canvas.addEventListener('blur', (data: any) => {
      this.hideLines();
    });
    this.canvas.addEventListener('keyDown', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 18) {
        this.showLines();
      }
    });
    this.canvas.on('mouseEventsBlocked', () => {
      if (this.canvas.mouseEventsBlocked && this.show) {
        this.temporarilyHidden = true;
        this.hideLines();
      } else if (this.temporarilyHidden) {
        this.temporarilyHidden = false;
        this.showLines();
      }
    });
    this.canvas.on('hoveredLayers', () => {
      if (
        this.show &&
        this.canvas.hoveredLayers.length > 0 &&
        this.canvas.hoveredLayers[0].type !== 'canvas' &&
        this.canvas.hoveredLayers[0] !== this.currentHoveredLayer
      ) {
        this.display();
      }
    });
  }

  deactivate() {
    this.canvas.removeFunction(`${this.namespace}.show`);
    this.canvas.removeFunction(`${this.namespace}.hide`);
    this.dispose();
  }
}
