import { IPlugin, IContext, Canvas } from '../../';
import GridDimension from './interfaces/GridDimension';
import GridData from './interfaces/GridData';
import { Layer } from '../../models/Layer';
import SetLayoutEvent from '../../events/SetLayoutEvent';

export default class GridRenderer implements IPlugin {
  name = 'GridRenderer';
  canvas!: Canvas;
  dispose: any;
  gridLayers: Map<Layer, GridData> = new Map<Layer, GridData>();
  gridWrapper: any;
  gridLayersRef: Map<Layer, Map<string, any>> = new Map<
    Layer,
    Map<string, any>
  >();

  gridOffset: {
    x: number;
    y: number;
  } = {
    x: 0,
    y: 0,
  };
  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    let canvasDims = this.canvas.getBoundingClientRect();
    this.gridWrapper = document.createElement('div');
    this.dispose = this.canvas.addDomToWorld(this.gridWrapper, true);
    this.gridWrapper.style.pointerEvents = 'none';
    this.gridWrapper.style.position = 'absolute';
    this.gridWrapper.style.height = canvasDims.height + 'px';
    this.gridWrapper.style.width = canvasDims.width + 'px';

    this.canvas.addFunction('showGrid', this.showGrid);
    this.canvas.addFunction('hideGrid', this.hideGrid);
    this.canvas.addFunction('getGridSize', this.getGridSize);
    this.canvas.addFunction('setGridSize', this.setGridSize);
    this.canvas.addFunction('setGridOffset', this.setGridOffset);
    this.canvas.addFunction('getGridOffset', this.getGridOffset);

    this.canvas.addEventListener('setLayout', (data: SetLayoutEvent) => {
      if (data.target.type === 'canvas') {
        let newDims = data.target.getBoundingClientRect();
        this.gridWrapper.style.height = newDims.height;
        this.gridWrapper.style.width = newDims.width;
        this.gridLayers.forEach((gridData, layer) => {
          this.refreshGrid(layer);
        });
      }
      if (this.gridLayers.has(data.target)) {
        let gridData = this.gridLayers.get(data.target);
        if (gridData) {
          let dims = gridData.dimensions;
          data.target.executeFunction('showGrid', dims);
        }
      }
    });

    this.canvas.on('zoom', () => {
      this.adjustStroke();
    });
  }
  deactivate() {
    this.canvas.removeFunction('showGrid');
    this.canvas.removeFunction('hideGrid');
    this.canvas.removeFunction('getGridSize');
    this.canvas.removeFunction('setGridSize');
    this.canvas.removeFunction('setGridOffset');
    this.canvas.removeFunction('getGridOffset');
    this.dispose();
  }
  refreshGrid = (layer: Layer) => {
    let gridData = this.gridLayers.get(layer);
    if (layer.type !== 'canvas') {
      if (gridData) {
        this.createGrid(layer, gridData.dimensions);
      } else {
        this.createGrid(layer, {
          x: 0,
          y: 0,
        });
      }
    }
  };

  getGridSize = (context: IContext) => {
    let layer = context.layer;
    let data = this.gridLayers.get(layer);
    if (data) {
      return data.dimensions;
    }
    return null;
  };

  setGridSize = (context: IContext, gridSize: any) => {
    let layer = context.layer;
    if (this.gridLayers.has(layer)) {
      this.gridLayers.delete(layer);
    }

    this.gridLayers.set(layer, {
      dimensions: gridSize,
    });
  };

  hideGrid = (context: IContext) => {
    let layer = context.layer;
    if (this.gridLayers.has(layer)) {
      this.gridLayers.delete(layer);
      if (this.gridLayersRef.get(layer)) {
        let gridElementMap = this.gridLayersRef.get(layer);
        if (gridElementMap) {
          let wrapperElement = gridElementMap.get('wrapper');
          this.gridWrapper.removeChild(wrapperElement);
        }
        this.gridLayersRef.delete(layer);
      }
    }
    this.gridLayers.forEach((gridData, layer) => {
      this.refreshGrid(layer);
    });
  };

  showGrid = (context: IContext, dims: GridDimension) => {
    this.setGridSize(context, dims);
    this.gridLayers.forEach((gridData, layer) => {
      this.refreshGrid(layer);
    });
  };

  setGridOffset = (context: IContext, offset: any) => {
    this.gridOffset = offset;
  };

  getGridOffset = (context: IContext, dims: GridDimension) => {
    return this.gridOffset;
  };
  adjustStroke = () => {
    this.gridLayers.forEach((gridData, layer) => {
      let gridElementMap = this.gridLayersRef.get(layer);
      if (gridElementMap) {
        let pathElement = gridElementMap.get('path');
        if (pathElement) {
          pathElement.setAttributeNS(
            null,
            'stroke-width',
            `${1 / this.canvas.getZoom()}px`
          );
        }
      }
    });
  };
  createGrid = (layer: Layer, gridDims: GridDimension): void => {
    const dims = layer.getBoundingClientRect();
    let gridElementMap = this.gridLayersRef.get(layer);
    if (gridElementMap) {
      let wrapperElement = gridElementMap.get('wrapper');
      if (!wrapperElement) {
        wrapperElement = document.createElement('div');
        wrapperElement.style.pointerEvents = 'none';
        wrapperElement.style.position = 'absolute';
        this.gridWrapper.appendChild(wrapperElement);
        gridElementMap.set('wrapper', wrapperElement);
      }
      wrapperElement.style.left = dims.rootX + 'px';
      wrapperElement.style.top = dims.rootY + 'px';
      wrapperElement.style.height = dims.height + 'px';
      wrapperElement.style.width = dims.width + 'px';
      const svgns = 'http://www.w3.org/2000/svg';
      let svgElement = gridElementMap.get('svg');
      if (!svgElement) {
        svgElement = document.createElementNS(svgns, 'svg');
        svgElement.setAttributeNS(null, 'height', '100%');
        svgElement.setAttributeNS(null, 'width', '100%');
        wrapperElement.appendChild(svgElement);
        gridElementMap.set('svg', svgElement);
      }
      let defsElement = gridElementMap.get('defs');
      if (!defsElement) {
        defsElement = document.createElementNS(svgns, 'defs');
        svgElement.appendChild(defsElement);
        gridElementMap.set('defs', defsElement);
      }
      let patternElement = gridElementMap.get('pattern');
      if (!patternElement) {
        patternElement = document.createElementNS(svgns, 'pattern');
        defsElement.appendChild(patternElement);
        patternElement.setAttributeNS(null, 'id', 'grid-pattern');
        patternElement.setAttributeNS(null, 'patternUnits', 'userSpaceOnUse');
        gridElementMap.set('pattern', patternElement);
      }
      patternElement.setAttributeNS(null, 'width', gridDims.x + 'px');
      patternElement.setAttributeNS(null, 'height', gridDims.y + 'px');
      let pathElement = gridElementMap.get('path');
      if (!pathElement) {
        pathElement = document.createElementNS(svgns, 'path');
        patternElement.appendChild(pathElement);
        pathElement.setAttributeNS(null, 'fill', 'none');
        pathElement.setAttributeNS(null, 'stroke', '#DBDBDB');
        gridElementMap.set('path', pathElement);
      }
      pathElement.setAttributeNS(
        null,
        'd',
        `M ${gridDims.x} 0 L 0 0 0 ${gridDims.y}`
      );
      pathElement.setAttributeNS(
        null,
        'stroke-width',
        `${1 / this.canvas.getZoom()}px`
      );
      let rectElement = gridElementMap.get('rect');
      if (!rectElement) {
        rectElement = document.createElementNS(svgns, 'rect');
        svgElement.appendChild(rectElement);
        rectElement.setAttributeNS(null, 'height', '100%');
        rectElement.setAttributeNS(null, 'width', '100%');
        rectElement.setAttributeNS(null, 'fill', `url(#grid-pattern)`);
        gridElementMap.set('rect', rectElement);
      }
    } else {
      this.gridLayersRef.set(layer, new Map<string, any>());
      this.createGrid(layer, gridDims);
    }
  };
}
