import styled from 'styled-components';

interface ProgressBarPropTypes {
  value: number;
  minimumTrackTintColor: string | undefined;
  maximumTrackTintColor: string | undefined;
}
interface ButtonPropTypes {
  value: number;
  disabled?: boolean;
  thumbTintColor: string | undefined;
}

export const SliderWrapper = styled.div``;

export const AndroidSliderButton = styled.div<
  ButtonPropTypes & React.HTMLProps<HTMLDivElement>
>`
  -webkit-appearance: none;
  background-color: #666;
  width: 14px;
  height: 14px;
  // z-index:2;
  border-radius: 50%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: relative;
  top: -5.5px;
  background: ${(props: any) => {
    const { disabled, thumbTintColor } = props;
    return disabled ? '#666' : thumbTintColor ? thumbTintColor : '#00897b';
  }};
  left: ${(props: any) => {
    const { value } = props;
    return value ? `${value}px` : '0px';
  }};
  box-shadow: ${(props: any) =>
    props.disabled ? '0px 2px 4px transparent' : '0px 2px 4px rgba(0,0,0,.2)'};
`;

export const AndroidSliderProgressBar = styled.div<
  ProgressBarPropTypes & React.HTMLProps<HTMLDivElement>
>`
  height: 2px;
  border-radius: 2px;
  position: relative;
  -webkit-appearance: none;

  background-image: ${(props: any) => {
    const { value, minimumTrackTintColor, maximumTrackTintColor } = props;
    return value
      ? `-webkit-gradient(
              linear,
              left top,
              right top,
              color-stop(${value}, ${
          minimumTrackTintColor ? minimumTrackTintColor : '#00897b'
        }),
              color-stop(${value}, ${
          maximumTrackTintColor ? maximumTrackTintColor : '#b7b7b7'
        }))`
      : `-webkit-gradient(
              linear,
              left top,
              right top,
              color-stop(0, #00897b),
              color-stop(0, #b7b7b7))`;
  }};
`;

export const IOSSliderButton = styled.div<
  ButtonPropTypes & React.HTMLProps<HTMLDivElement>
>`
  webkit-appearance: none;
  background-color: #666;
  display: block;
  width: 28px;
  height: 28px;
  // z-index:2;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: relative;
  top: -12.5px;

  left: ${(props: any) => {
    const { value } = props;
    return value ? `${value}px` : '0px';
  }};
`;
export const IOSSliderProgressBar = styled.div<
  ProgressBarPropTypes & React.HTMLProps<HTMLDivElement>
>`
  height: 2px;
  border-radius: 2px;
  position: relative;
  top: -2px;
  -webkit-appearance: none;

  background-image: ${(props: any) => {
    const { value, minimumTrackTintColor, maximumTrackTintColor } = props;
    return value
      ? `-webkit-gradient(
          linear,
          left top,
          right top,
          color-stop(${value}, ${
          minimumTrackTintColor ? minimumTrackTintColor : '#007aff'
        }),
          color-stop(${value}, ${
          maximumTrackTintColor ? maximumTrackTintColor : '#b7b7b7'
        }))`
      : `-webkit-gradient(
          linear,
          left top,
          right top,
          color-stop(0, #007aff),
          color-stop(0, #b7b7b7))`;
  }};
`;
