import * as React from 'react';
import { isNil } from 'lodash';
import {
  SliderWrapper,
  AndroidSliderButton,
  AndroidSliderProgressBar,
  IOSSliderButton,
  IOSSliderProgressBar,
} from './SliderStyledComponents';

type propType = {
  value?: number;
  maximumValue?: number;
  minimumValue?: number;
  style?: any;
  minimumTrackTintColor?: string;
  maximumTrackTintColor?: string;
  thumbTintColor?: string;
  disabled?: boolean;
  platform: string;
  className?: string;
};

export default class Slider extends React.PureComponent<propType, any> {
  render() {
    const isAndroid = this.props.platform === 'Android';
    const SliderProgressBar = isAndroid
      ? AndroidSliderProgressBar
      : IOSSliderProgressBar;
    const SliderButton = isAndroid ? AndroidSliderButton : IOSSliderButton;

    const value = isNil(this.props.value) ? 0 : this.props.value;
    const maximumValue = isNil(this.props.maximumValue)
      ? 100
      : this.props.maximumValue;
    const minimumValue = this.props.minimumValue || 0;
    const sliderRange = maximumValue - minimumValue;
    const sliderValue = value - minimumValue >= 0 ? value - minimumValue : 0;
    const sliderButtonPosition =
      ((this.props.style.width || 0) - (isAndroid ? 12 : 28)) *
      (sliderValue / sliderRange);
    return (
      <SliderWrapper
        style={{
          ...this.props.style,
          paddingTop: this.props.style.height ? this.props.style.height / 2 : 0,
        }}
        className={this.props.className ? this.props.className : undefined}
      >
        <SliderProgressBar
          style={{
            width: '100%',
            height: 2,
            opacity: this.props.disabled ? (isAndroid ? 0.7 : 0.5) : 1,
          }}
          value={sliderValue / sliderRange}
          minimumTrackTintColor={this.props.minimumTrackTintColor}
          maximumTrackTintColor={this.props.maximumTrackTintColor}
        >
          <SliderButton
            value={sliderButtonPosition}
            thumbTintColor={isAndroid ? this.props.thumbTintColor : undefined}
          />
        </SliderProgressBar>
      </SliderWrapper>
    );
  }
}
