import { Layer } from '../models/Layer';
import { Event } from './index';
export default class TextEditorEvent extends Event {
  payload: any;
  isFinal: boolean;
  constructor(
    name: string,
    target: Layer,
    payload?: any,
    isFinal?: boolean,
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    this.payload = payload;
    this.isFinal = isFinal || false;
  }
}
