import * as React from 'react';
import { Layer } from 'models/Layer';
import Slider from './Slider';

const rerenderProps = ['layout', 'props', 'style', 'visible'];
export default class SliderRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;

  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  render() {
    const layer: Layer = this.props.layer;
    let sliderProps = { ...layer.props.getAll() };
    let sliderStyle = {
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
      width: layer.getBoundingClientRect().width,
      height: layer.getBoundingClientRect().height,
    };

    let platform = 'iOS';
    const deviceData = this.props.layer.executeFunction('getDeviceData');

    if (deviceData) {
      platform = deviceData.platform;
    }

    return (
      <Slider
        ref={(c: any) => {
          this._ref = c;
        }}
        {...sliderProps}
        platform={platform}
        style={sliderStyle}
      />
    );
  }
}
