import { Canvas, IPlugin, IContext, Layer, Event } from '../..';
import { indexOf, map, isEqual } from 'lodash';
import { iconLeft, iconRight } from './iconsBase64';

function getButtonCss(zoom: number) {
  return `
background: transparent;
width: auto;
color: rgb(251,251,251);
height: 80px;
padding-left: ${6 / zoom}px;
padding-right: ${6 / zoom}px;
margin-top: 0px;
border:none;
min-width: ${100 / zoom}px;
`;
}
function getImgCss(zoom: number) {
  return `
  border: none;
  margin-right: 0px;
  margin-left: 0px;
  opacity: 1;
  width: ${32 / zoom}px;
  height: ${32 / zoom}px;
  `;
}

const containerCss = `
position: absolute;
pointer-events: auto;
z-index: 4;
transform-origin: 0px 0px;
`;

export default class StageDesignerPreviewModePlugin implements IPlugin {
  name = 'StageDesignerPreviewMode';
  canvas!: Canvas;
  disposeNextBtn: any;
  disposePreviousBtn: any;
  nextBtnContainerNode: any;
  previousBtnContainerNode: any;
  artboards: Array<Layer> = [];
  prevBtnElement: any;
  nextBtnElement: any;
  prevImgElement: any;
  nextImgElement: any;
  currentMode!: string;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.disposeNextBtn = null;
    this.disposePreviousBtn = null;
    this.currentMode = 'stage';
  }

  activate() {
    this.canvas.addFunction('switchToPreviewMode', this.previewMode.bind(this));
    this.canvas.addFunction('switchToStageMode', this.stageMode.bind(this));
    this.disposeNextBtn = this.renderNextButton();
    this.disposePreviousBtn = this.renderPreviousButton();
    this.previousBtnContainerNode.style.display = 'none';
    this.nextBtnContainerNode.style.display = 'none';
    this.canvas.on('zoom', () => {
      if (this.currentMode === 'preview' && this.artboards.length > 1) {
        this.prevImgElement.style.cssText = getImgCss(this.canvas.getZoom());
        this.prevBtnElement.style.cssText = getButtonCss(this.canvas.getZoom());
        this.nextBtnElement.style.cssText = getButtonCss(this.canvas.getZoom());
        this.nextImgElement.style.cssText = getImgCss(this.canvas.getZoom());
        this.setPreviousButtonContainerLayout();
        this.setNextButtonContainerLayout();
        this.setDisplayForButtons();
      }
    });
  }

  previewMode(context: IContext) {
    this.currentMode = 'preview';
    const artboards = this.canvas.executeFunction('getAllArtboards');
    this.artboards = Array.from(artboards.values());
    this.canvas.executeFunction('hidePlusButton');

    if (this.artboards.length > 1) {
      this.handleSelectedArtboardChange();
    }
  }

  stageMode(context: IContext) {
    this.currentMode = 'stage';
    const artboards = this.canvas.executeFunction('getAllArtboards');
    this.artboards = Array.from(artboards.values());
    this.previousBtnContainerNode.style.display = 'none';
    this.nextBtnContainerNode.style.display = 'none';
    this.canvas.executeFunction('showPlusButton');
    map(this.artboards, (artboard: Layer) => {
      artboard.show();
      artboard.executeFunction('LayerLabel.show');
      artboard.executeFunction(
        'showGrid',
        this.canvas.executeFunction('getGridSize')
      );
    });
  }

  nextArtboard() {
    const selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    selectedArtboard.hide();
    let selectedArtboardIndex = 0;
    if (selectedArtboard)
      selectedArtboardIndex = indexOf(this.artboards, selectedArtboard);
    selectedArtboardIndex += 1;
    this.artboards[selectedArtboardIndex].show();
    this.canvas.dispatchEvent(
      'StageDesignerPreviewMode.selectArtboard',
      new Event(
        'StageDesignerPreviewMode.selectArtboard',
        this.artboards[selectedArtboardIndex]
      )
    );
    this.handleSelectedArtboardChange();
  }

  previousArtboard() {
    const selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    selectedArtboard.hide();
    let selectedArtboardIndex = 0;
    if (selectedArtboard)
      selectedArtboardIndex = indexOf(this.artboards, selectedArtboard);
    selectedArtboardIndex -= 1;
    this.artboards[selectedArtboardIndex].show();
    this.canvas.dispatchEvent(
      'StageDesignerPreviewMode.selectArtboard',
      new Event(
        'StageDesignerPreviewMode.selectArtboard',
        this.artboards[selectedArtboardIndex]
      )
    );
    this.handleSelectedArtboardChange();
  }
  setDisplayForButtons() {
    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (selectedArtboard) {
      let selectedArtboardIndex = indexOf(this.artboards, selectedArtboard);
      if (selectedArtboardIndex === 0) {
        this.previousBtnContainerNode.style.display = 'none';
        this.nextBtnContainerNode.style.display = '';
      } else if (selectedArtboardIndex === this.artboards.length - 1) {
        this.nextBtnContainerNode.style.display = 'none';
        this.previousBtnContainerNode.style.display = '';
      } else {
        this.previousBtnContainerNode.style.display = '';
        this.nextBtnContainerNode.style.display = '';
      }
    } else {
      this.nextBtnContainerNode.style.display = 'none';
      this.previousBtnContainerNode.style.display = 'none';
    }
  }
  handleSelectedArtboardChange() {
    this.setPreviousButtonContainerLayout();
    this.setNextButtonContainerLayout();
    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (selectedArtboard) {
      this.setDisplayForButtons();
    } else {
      selectedArtboard = this.artboards[0];
      if (selectedArtboard) {
        this.canvas.dispatchEvent(
          'StageDesignerPreviewMode.selectArtboard',
          new Event('StageDesignerPreviewMode.selectArtboard', selectedArtboard)
        );
      }
      this.previousBtnContainerNode.style.display = 'none';
      this.nextBtnContainerNode.style.display = '';
    }
    map(this.artboards, (artboard: Layer) => {
      if (!isEqual(artboard, selectedArtboard)) {
        artboard.hide();
        artboard.executeFunction('LayerLabel.hide');
        artboard.executeFunction('hideGrid');
      } else {
        artboard.show();
        artboard.executeFunction('LayerLabel.show');
        artboard.executeFunction(
          'showGrid',
          this.canvas.executeFunction('getGridSize')
        );

        if (artboard) {
          this.canvas.dispatchEvent(
            'StageDesignerPreviewMode.selectArtboard',
            new Event('StageDesignerPreviewMode.selectArtboard', artboard)
          );
        }
      }
    });
    selectedArtboard.executeFunction('focusArtboard');
  }

  renderNextButton() {
    this.nextBtnContainerNode = document.createElement('div');
    this.nextBtnElement = document.createElement('button');
    this.nextBtnElement.style.cssText = getButtonCss(this.canvas.getZoom());
    this.nextBtnElement.onclick = () => this.nextArtboard();
    this.nextImgElement = document.createElement('img');
    this.nextImgElement.src = iconRight;
    this.nextImgElement.style.cssText = getImgCss(this.canvas.getZoom());
    this.nextBtnElement.append(this.nextImgElement);
    this.nextBtnContainerNode.appendChild(this.nextBtnElement);
    return this.canvas.addDomToWorld(this.nextBtnContainerNode);
  }

  renderPreviousButton() {
    this.previousBtnContainerNode = document.createElement('div');
    this.prevBtnElement = document.createElement('button');
    this.prevBtnElement.style.cssText = getButtonCss(this.canvas.getZoom());
    this.prevBtnElement.onclick = () => this.previousArtboard();
    this.prevImgElement = document.createElement('img');
    this.prevImgElement.src = iconLeft;
    this.prevImgElement.style.cssText = getImgCss(this.canvas.getZoom());
    this.prevBtnElement.append(this.prevImgElement);
    this.previousBtnContainerNode.appendChild(this.prevBtnElement);
    return this.canvas.addDomToWorld(this.previousBtnContainerNode);
  }
  setPreviousButtonContainerLayout() {
    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (!selectedArtboard) selectedArtboard = this.artboards[0];
    const containerLayout = {
      left: selectedArtboard.layout.data.left - 150 / this.canvas.getZoom(),
      top:
        selectedArtboard.layout.data.height / 2 +
        selectedArtboard.layout.data.top -
        40 / this.canvas.getZoom(),
    };
    this.previousBtnContainerNode.style.cssText =
      containerCss +
      `left:${containerLayout.left}px;top:${containerLayout.top}px`;
  }

  setNextButtonContainerLayout() {
    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (!selectedArtboard) selectedArtboard = this.artboards[0];

    const containerLayout = {
      left:
        selectedArtboard.layout.data.left +
        selectedArtboard.layout.data.width +
        50 / this.canvas.getZoom(),
      top:
        selectedArtboard.layout.data.height / 2 +
        selectedArtboard.layout.data.top -
        40 / this.canvas.getZoom(),
    };
    this.nextBtnContainerNode.style.cssText =
      containerCss +
      `left:${containerLayout.left}px;top:${containerLayout.top}px`;
  }

  deactivate() {
    this.canvas.removeFunction('switchToPreviewMode');
    this.canvas.removeFunction('switchToStageMode');
    this.disposeNextBtn();
    this.disposePreviousBtn();
  }
}
