import { IPlugin } from 'interfaces/IPlugin';
import { Canvas } from 'models/Canvas';
import ISnapPoints from './interfaces/ISnapPoints';
// import getSnapPoints from './getSnapPoints';
import LayerDragEvent from './LayerDragEvent';
import ISnapXPoint from './interfaces/ISnapXPoint';
import ISnapYPoint from './interfaces/ISnapYPoint';
import Config from './interfaces/Config';
import { IDimensionWithRoot, IDimension, IContext } from 'interfaces';
import ModelUtils from '../../models/utils';

export default class LayerDrag implements IPlugin {
  name = 'LayerDrag';
  nameSpace: string = 'LayerDrag';
  canvas!: Canvas;
  dispose: any;
  targetLayer: any;
  dragStarted = false;
  boundingBox: any;
  boundingBoxWRTWorld: any;
  startX: any = 0;
  startY: any = 0;
  startLayoutX: any = 0;
  startLayoutY: any = 0;
  startAbsoluteX: any = 0;
  startAbsoluteY: any = 0;
  totalDx: any = 0;
  totalDy: any = 0;
  snapPoints: any;

  isAltPressed: boolean = false;
  isShiftPressed: boolean = false;
  shiftDirection: string = '';

  oldDelta: any;
  dragEventData: any;
  applyDimensions(eventType: string, showLog: boolean = false) {
    const data = this.dragEventData;
    // if (showLog)
    // console.log(this.totalDx, this.totalDy, 'database me chal raha hai');
    const worldDx = isNaN(data.worldDx) ? 0 : data.worldDx;
    const worldDy = isNaN(data.worldDy) ? 0 : data.worldDy;
    // this.totalDx += worldDx;
    // this.totalDy += worldDy;

    // const deltaX  = worldDx;
    // const deltaY += worldDy;

    this.totalDx += worldDx;
    this.totalDy += worldDy;

    let currentDeltaX = this.totalDx;
    let currentDeltaY = this.totalDy;

    if (!this.targetLayer.parent) {
      return;
    }

    const parentAbsoluteRotation = this.targetLayer
      ? this.targetLayer.parent.getAbsoluteRotation()
      : 0;

    let rotateDelta = ModelUtils.rotatePoint(
      [currentDeltaX, currentDeltaY],
      360 - parentAbsoluteRotation,
      [0, 0]
    );

    const snappedData = this.canvas.executeFunction(
      'snapToPoints',
      this.snapPoints,
      [this.startX, this.startY],
      [rotateDelta[0], rotateDelta[1]],
      {
        ...this.boundingBox,
      },
      [currentDeltaX, currentDeltaY],
      [this.startAbsoluteX, this.startAbsoluteY],
      parentAbsoluteRotation,
      this.boundingBoxWRTWorld
    );

    this.boundingBox = {
      ...this.boundingBox,
      x: this.boundingBox.x + snappedData.delta.x,
      y: snappedData.y + snappedData.delta.y,
    };

    let delta = snappedData.delta;

    if (!this.isShiftPressed) {
      this.shiftDirection = '';
    }

    let payload = {
      layerWorldX: this.startLayoutX + delta.x,
      layerWorldY: this.startLayoutY + delta.y,
      meta: snappedData.meta,
    };

    if (this.isShiftPressed) {
      if (!this.shiftDirection) {
        if (Math.abs(data.worldDx) !== Math.abs(data.worldDy)) {
          if (Math.abs(data.worldDx) > Math.abs(data.worldDy)) {
            this.shiftDirection = 'horizontal';
          } else {
            this.shiftDirection = 'vertical';
          }
        }
      }
      if (this.shiftDirection) {
        if (this.shiftDirection === 'horizontal') {
          payload.layerWorldY = this.targetLayer.getBoundingClientRect().y;
        } else {
          payload.layerWorldX = this.targetLayer.getBoundingClientRect().x;
        }
      }
    }

    // this.oldDelta = {
    //   x: delta.x,
    //   y: delta.y,
    // };

    let layerDragEvent = new LayerDragEvent(
      eventType,
      this.targetLayer,
      data.originalEvent,
      payload
    );
    this.targetLayer.dispatchEvent(eventType, layerDragEvent, data);

    // this.setNewLayout(
    //   this.selectedLayers,
    //   this.boundingBox,
    //   data.session,
    //   data.targetHandle,
    //   eventType,
    //   data,
    //   delta
    //   // { x: rotateDelta[0], y: rotateDelta[1] }
    // );
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  activate() {
    let snapPoints: ISnapPoints;

    let totalDx: number = 0;
    let totalDy: number = 0;

    let startX: number = 0;
    let startY: number = 0;

    let startRootX: number = 0;
    let startRootY: number = 0;

    this.canvas.addFunction(`${this.nameSpace}.isDragging`, () => {
      return this.dragStarted;
    });

    this.canvas.addEventListener('dragStart', (event: any) => {
      return this.canvas.executeMiddleware(
        'LayerDrag.dragStart',
        event,
        async (event: any) => {
          this.dragStarted = true;
          this.targetLayer = event.target;

          if (this.targetLayer) {
            //

            this.targetLayer.setDragging(true);
            this.boundingBox = this.canvas.getContainerBoundingBoxWRTParent([
              this.targetLayer,
            ]);

            this.boundingBoxWRTWorld = this.canvas.getContainerBoundingBox([
              this.targetLayer,
            ]);

            const parentLayer = this.targetLayer.parent;

            if (!parentLayer) {
              return;
            }

            this.startX = this.boundingBox.x;
            this.startY = this.boundingBox.y;

            this.startAbsoluteX =
              this.boundingBoxWRTWorld.x -
              this.canvas.getContainerBoundingBox([parentLayer]).x;

            this.startAbsoluteY =
              this.boundingBoxWRTWorld.y -
              this.canvas.getContainerBoundingBox([parentLayer]).y;

            this.snapPoints = this.canvas.executeFunction(
              'getSnapPoints',
              [this.targetLayer],
              parentLayer
            );

            //
            let layoutData: IDimensionWithRoot = this.targetLayer.getBoundingClientRect();
            this.startLayoutX = layoutData.x;
            this.startLayoutY = layoutData.y;

            let payload = {
              layerWorldX: layoutData.x,
              layerWorldY: layoutData.y,
              snapPoints: this.snapPoints,
            };

            let layerDragEvent = new LayerDragEvent(
              'layerDragStart',
              this.targetLayer,
              event.originalEvent,
              payload
            );

            // TODO: data to be removed later
            this.targetLayer.dispatchEvent(
              'layerDragStart',
              layerDragEvent,
              event
            );
          }
        }
      );
    });

    this.canvas.addEventListener('drag', (data: any) =>
      this.canvas.executeMiddleware('LayerDrag.drag', data, (data: any) => {
        if (this.dragStarted) {
          if (!this.targetLayer) {
            return;
          }
          this.dragEventData = data;
          this.applyDimensions('layerDrag');
          // let layoutData = this.targetLayer
          //   ? this.canvas.getBoundingBoxWRTParent([this.targetLayer], true)
          //   : null;

          // // let dims = this.canvas.getBoundingBoxWRTParent([data.target], true); //.getBoundingClientRect();

          // totalDx += data.worldDx;
          // totalDy += data.worldDy;

          // let left = startX + totalDx;
          // let top = startY + totalDy;

          // const absoluteRotation = this.targetLayer.parent.getAbsoluteRotation();
          // let rotateDelta = ModelUtils.rotatePoint(
          //   [totalDx, totalDy],
          //   360 - absoluteRotation,
          //   [0, 0]
          // );
          // const newDx = rotateDelta[0];
          // const newDy = rotateDelta[1];

          // let newSnapPoints = this.getSnapPointCoordinates(
          //   snapPoints,
          //   top,
          //   left,
          //   startX,
          //   startY,
          //   newDx,
          //   newDy,
          //   layoutData
          // );

          // left = newSnapPoints.left;
          // top = newSnapPoints.top;

          // let payload = { layerWorldX: left, layerWorldY: top };
          // let layerDragEvent = new LayerDragEvent(
          //   'layerDrag',
          //   this.targetLayer,
          //   data.originalEvent,
          //   payload
          // );
          // this.targetLayer.dispatchEvent('layerDrag', layerDragEvent, data);
        }
      })
    );

    this.canvas.addEventListener('dragEnd', (data: LayerDragEvent) =>
      this.canvas.executeMiddleware(
        'LayerDrag.dragEnd',
        data,
        (data: LayerDragEvent) => {
          if (this.dragStarted) {
            if (!this.targetLayer) {
              return;
            }
            this.targetLayer.setDragging(false);
            this.dragEventData = data;
            this.applyDimensions('layerDragEnd');
            this.shiftDirection = '';

            this.totalDx = 0;
            this.totalDy = 0;
            this.startX = 0;
            this.startY = 0;
            this.startAbsoluteX = 0;
            this.startAbsoluteY = 0;
            this.dragStarted = false;
            this.dragEventData = undefined;
          }
        }
      )
    );

    this.canvas.addEventListener('blur', () => {
      if (this.isAltPressed) this.isAltPressed = false;
      if (this.isShiftPressed) this.isShiftPressed = false;
    });
    this.canvas.addEventListener('keyUp', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = false;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = false;
      }

      if (this.dragEventData) {
        this.applyDimensions('layerDrag', true);
      }
    });
    this.canvas.addEventListener('keyDown', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = true;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = true;
      }

      if (this.dragEventData) {
        this.applyDimensions('layerDrag');
      }
    });
  }

  deactivate() {
    this.dispose();
  }
}
