import { PropTypes, Box } from '../types';
import { isContained, isAlignedonAxis, getDirection } from '../utils';

const getLeftEnd = (xContained: boolean, yContained: boolean, box: Box) => {
  if (xContained && yContained) {
    return box.x;
  }
  return box.x + box.width;
};
const getLeftWidth = (
  xContained: boolean,
  yContained: boolean,
  box: Box,
  helperBox: Box
) => {
  if (xContained && yContained) {
    return helperBox.x - box.x;
  }
  return helperBox.x - box.x - box.width;
};
const getTop = (helperBox: Box) => {
  return helperBox.y + Math.floor(helperBox.height / 2);
};

const getRightEnd = (helperBox: Box) => {
  return helperBox.x + helperBox.width;
};

const getRightWidth = (
  xContained: boolean,
  yContained: boolean,
  box: Box,
  helperBox: Box
) => {
  if (xContained && yContained) {
    return box.x + box.width - (helperBox.x + helperBox.width);
  }
  return box.x - helperBox.x - helperBox.width;
};

const drawXLines = (props: PropTypes) => {
  let { box, helperBox } = props;
  let xContained = isContained('x', box, helperBox);
  let yContained = isContained('y', box, helperBox);
  let xAligned = isAlignedonAxis('x', box, helperBox);
  let leftAligned = false;
  let rightAligned = false;
  let direction = getDirection('x', box, helperBox);
  if (xAligned) {
    if (direction === 'left') {
      leftAligned = true;
    } else if (direction === 'right') {
      rightAligned = true;
    }
  }
  let leftEnd = getLeftEnd(xContained, yContained, props.box);
  let leftWidth = getLeftWidth(
    xContained,
    yContained,
    props.box,
    props.helperBox
  );
  let top = getTop(props.helperBox);
  let rightEnd = getRightEnd(props.helperBox);
  let rightWidth = getRightWidth(
    xContained,
    yContained,
    props.box,
    props.helperBox
  );
  let leftDistance =
    xContained && yContained
      ? helperBox.x - box.x
      : helperBox.x - box.x - box.width;
  let rightDistance =
    xContained && yContained
      ? box.x + box.width - (helperBox.x + helperBox.width)
      : box.x - helperBox.x - helperBox.width;
  let leftShow =
    (leftAligned || (xContained && yContained)) && leftDistance > 0;
  let rightShow =
    (rightAligned || (xContained && yContained)) && rightDistance > 0;
  const lines = [];
  if (leftShow) {
    leftDistance = Math.round(leftDistance);
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        leftEnd,
        'measurementXLine1-edge',
        top - 2.5,
        top + 3,
        false
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        top,
        'measurementXLine1',
        leftEnd,
        leftEnd + leftWidth,
        false,
        leftDistance
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        leftEnd + leftWidth - 1,
        'measurementXLine1-edge',
        top - 2.5,
        top + 3,
        false
      )
    );
  }
  if (rightShow) {
    rightDistance = Math.round(rightDistance);
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        rightEnd,
        'measurementXLine1-edge',
        top - 2.5,
        top + 3,
        false
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        top,
        'measurementXLine2',
        rightEnd,
        rightWidth + rightEnd,
        false,
        rightDistance
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        rightWidth + rightEnd - 1,
        'measurementXLine1-edge',
        top - 2.5,
        top + 3,
        false
      )
    );
  }
  return lines;
};

export default drawXLines;
