import * as React from 'react';
import HorizontalRuler from './components/HorizontalRuler';
import VerticalRuler from './components/VerticalRuler';
import { Canvas } from '../..';
import { isEmpty } from 'lodash';

export default class RulerScaleWrapper extends React.PureComponent<any, any> {
  // TODO: move it to somewhere else
  getPixelRatio() {
    var context: any = document.createElement('canvas').getContext('2d');
    var backingStore = 1;

    if (context) {
      backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
    }
    return (window.devicePixelRatio || 1) / backingStore;
  }
  render() {
    const { horLineArr, verLineArr, shadow } = this.props;
    const canvas: Canvas = this.props.canvas;
    const { x, y, width, height } = shadow;
    const props = {
      perWidth: this.props.perWidth,
      scale: this.props.scale,
      zoom: this.props.zoom,
      fontScale: 0.83,
      ratio: 1,
      bgColor: this.props.bgColor,
      fgColor: this.props.fgColor,
      fontColor: this.props.fontColor,
      shadowColor: this.props.shadowColor,
    };
    return (
      <div>
        <div
          className="hor-ruler"
          onContextMenu={this.props.onContextMenu}
          style={{ cursor: 'row-resize', pointerEvents: 'auto' }}
          draggable
          onDragStart={this.props.onDragStartHorizontal}
          onDrag={this.props.onDragHorizontal}
          onDragEnd={this.props.onDragEndHorizontal}
        >
          <HorizontalRuler
            start={-this.props.initialPoint.x}
            lines={horLineArr}
            select={{ x, width }}
            height={20}
            width={canvas.getViewport().width}
            {...props}
          />
        </div>
        <div
          className="ver-ruler"
          style={{
            position: 'absolute',
            marginTop: -24,
            cursor: 'col-resize',
            pointerEvents: 'auto',
          }}
          draggable
          onDragStart={this.props.onDragStartVertical}
          onDrag={this.props.onDragVertical}
          onDragEnd={this.props.onDragEndVertical}
          onContextMenu={this.props.onContextMenu}
        >
          <VerticalRuler
            start={-this.props.initialPoint.y}
            lines={verLineArr}
            select={{ y, height }}
            height={canvas.getViewport().height}
            width={20}
            {...props}
          />
        </div>
      </div>
    );
  }
}
