import { Event, Layer } from '../../..';
import { IDimensionWithRotation } from 'interfaces';

export class LayerResizerEvent extends Event {
  payload: any;
  meta?: any;
  originalEvent: any;
  snapPoints?: any;
  constructor(
    name: string,
    target: Layer,
    payload: any,
    originalEvent: any,
    currentTarget?: Layer,
    meta?: any,
    snapPoints?: any
  ) {
    super(name, target, currentTarget);
    this.payload = payload;
    this.meta = meta;
    this.originalEvent = originalEvent;
    this.snapPoints = snapPoints;
  }
}
