import { Canvas } from '../..';
import { IPlugin } from '../..';
import TextInputRenderer from './TextInputRenderer';
import TextInputComponentDefinition from './TextInputComponentDefinition';

export default class TextInputRendererPlugin implements IPlugin {
  name = 'TextInputRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'textInput',
      TextInputRenderer,
      TextInputComponentDefinition
    );
  }

  deactivate() {}
}
