import { IPlugin } from 'interfaces/IPlugin';
import { Canvas } from 'models/Canvas';
import { IContext, IDimension } from 'interfaces';
import DrawEvent from './events/drawEvent';
import {
  // computeOrientationWithDeltaMultiple,
  computeOrientation,
  // calculateRotation,
} from './../../models/utils';
export default class DrawComponent implements IPlugin {
  name = 'DrawComponent';
  canvas!: Canvas;
  removeDom: any;
  mouseDown!: boolean;
  dragStarted!: boolean;
  dragStart: any = {
    x: 0,
    y: 0,
  };
  renderer: any;
  isShiftPressed: boolean = false;
  isAltPressed: boolean = false;
  overlayDispose: any;
  overlayLayer: any;
  dims: IDimension = { x: 0, y: 0, height: 0, width: 0 };
  dragSession = {
    start: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
    current: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
    end: { x: 0, y: 0, deltaX: 0, deltaY: 0 },
  };
  shapeType: any;
  ellipseRenderer: any;
  boundMouseMove: any;
  boundMouseUp: any;
  snapPoints: any;
  initialDelta: any;
  initialDimensions: any = {
    x: 0,
    y: 0,
    height: 0,
    width: 0,
    rotation: 0,
  };
  newDimensions: any = {
    x: 0,
    y: 0,
    height: 0,
    width: 0,
    rotation: 0,
  };
  getQuad = (
    start: { x: number; y: number },
    current: { x: number; y: number }
  ) => {
    if (current.x < start.x && current.y > start.y) return 2;
    else if (current.x < start.x && current.y <= start.y) return 3;
    else if (current.x >= start.x && current.y < start.y) return 4;
    else return 1;
    // DOC:  quad as  3|4
    //                2|1
  };

  onMouseMove = (shapeType: any, e: any) => {
    let startX = 0;
    let startY = 0;
    // let initialDimensions = {
    //   x: startX,
    //   y: startY,
    //   height: 0,
    //   width: 0,
    //   rotation: 0,
    // };
    if (this.mouseDown && !this.dragStarted) {
      if (e.pageX && e.pageY) {
        // let val = this.canvas.executeFunction('HtmlDragToWorld.drag', e);

        startX = this.dims.x;
        startY = this.dims.y;
        this.initialDimensions.x = startX;
        this.initialDimensions.y = startY;
        this.initialDimensions.width = 0;
        this.initialDimensions.height = 0;

        const snappedData = this.canvas.executeFunction(
          'snapToPoints',
          this.snapPoints,
          [this.initialDimensions.x, this.initialDimensions.y],
          [0, 0],
          {
            ...this.initialDimensions,
          },
          [0, 0],
          [this.initialDimensions.x, this.initialDimensions.y],
          0,
          this.initialDimensions
        );

        this.initialDelta = snappedData.delta;

        this.newDimensions = computeOrientation(
          this.initialDimensions,
          snappedData.delta,
          'C',
          false,
          false
        );

        this.dims.x = this.newDimensions.x;
        this.dims.y = this.newDimensions.y;

        this.initialDimensions.x = this.dims.x;
        this.initialDimensions.y = this.dims.y;

        this.dragStarted = true;

        const drawEvent = new DrawEvent(
          `${this.name}.draw`,
          this.canvas,
          {
            ...this.initialDimensions,
          },
          undefined,
          snappedData.meta
        );
        this.canvas.dispatchEvent(`${this.name}.draw`, drawEvent);
      }
    } else if (this.dragStarted) {
      let zoom = this.canvas.getZoom();
      let canvasDims = this.canvas.getBoundingClientRect();
      // console.log(this.dims, 'dims here @@@@');

      if (e.pageX && e.pageY) {
        let val = this.canvas.executeFunction('HtmlDragToWorld.drag', e);

        this.dragSession.current = val.session.current;
        // this.dims.x =
        //   this.dragSession.current.x <= this.dragSession.start.x
        //     ? this.dragSession.current.x
        //     : this.dragSession.start.x;
        // this.dims.y =
        //   this.dragSession.current.y <= this.dragSession.start.y
        //     ? this.dragSession.current.y
        //     : this.dragSession.start.y;
        // this.dims.height = Math.abs(
        //   this.dragSession.current.y - this.dragSession.start.y
        // );
        // this.dims.width = Math.abs(
        //   this.dragSession.current.x - this.dragSession.start.x
        // );
        let left: any = this.dims.x;
        let top: any = this.dims.y;
        // console.log(this.dims, 'dims here @@@@');

        let right;
        let bottom;

        const totalDx =
          val.session.current.x - val.session.start.x + this.initialDelta.x;
        const totalDy =
          val.session.current.y - val.session.start.y + this.initialDelta.y;

        const snappedData = this.canvas.executeFunction(
          'snapToPoints',
          this.snapPoints,
          [this.initialDimensions.x, this.initialDimensions.y],
          [totalDx, totalDy],
          {
            ...this.initialDimensions,
          },
          [totalDx, totalDy],
          [this.initialDimensions.x, this.initialDimensions.y],
          0,
          this.initialDimensions
        );

        const delta = snappedData.delta;
        let handle = '';

        if (delta.x >= 0) {
          handle += 'R';
        }
        if (delta.x < 0) {
          handle += 'L';
        }
        if (delta.y >= 0) {
          handle += 'B';
        }
        if (delta.y < 0) {
          handle += 'T';
        }
        this.newDimensions = computeOrientation(
          this.initialDimensions,
          delta,
          handle,
          this.isShiftPressed,
          this.isAltPressed
        );

        this.renderer.style.backgroundColor = 'transparent';
        if (shapeType === 'rect') {
          this.renderer.style.position = 'absolute';
          this.renderer.style.left = this.newDimensions.x
            ? this.newDimensions.x + 'px'
            : '';
          this.renderer.style.top = this.newDimensions.y
            ? this.newDimensions.y + 'px'
            : '';
          // this.renderer.style.right = right ? right + 'px' : '';
          // this.renderer.style.bottom = bottom ? bottom + 'px' : '';
          this.renderer.style.height = this.newDimensions.height + 'px';
          this.renderer.style.width = this.newDimensions.width + 'px';
          this.renderer.style.border = `${1 / zoom}px solid black`;
        } else if (shapeType === 'ellipse') {
          this.overlayLayer.style.display = 'block';
          this.renderer.style.position = 'absolute';
          this.renderer.style.left = this.newDimensions.x;
          this.renderer.style.top = this.newDimensions.y;
          this.renderer.setAttribute(
            'height',
            this.newDimensions.height + 'px'
          );
          this.renderer.setAttribute('width', this.newDimensions.width + 'px');
          this.ellipseRenderer.setAttribute(
            'cx',
            this.newDimensions.width / 2 + 'px'
          );
          this.ellipseRenderer.setAttribute(
            'cy',
            this.newDimensions.height / 2 + 'px'
          );
          let strokeZoomAdjustment = 1 / (2 * zoom);
          this.ellipseRenderer.setAttribute(
            'rx',
            this.newDimensions.width !== 0
              ? this.newDimensions.width / 2 - strokeZoomAdjustment + 'px'
              : 0
          );
          this.ellipseRenderer.setAttribute(
            'ry',
            this.newDimensions.height !== 0
              ? this.newDimensions.height / 2 - strokeZoomAdjustment + 'px'
              : 0
          );
          this.ellipseRenderer.style.strokeWidth = `${1 / zoom}px`;
        }

        this.overlayLayer.appendChild(this.renderer);
        // snap logic

        const drawEvent = new DrawEvent(
          `${this.name}.draw`,
          this.canvas,
          {
            ...this.newDimensions,
          },
          undefined,
          snappedData.meta
        );
        const viewportDims = this.canvas.convertPoint(
          this.newDimensions,
          'world',
          'viewport'
        );
        this.canvas.executeFunction(
          'Tooltip.show',
          {
            x: viewportDims.x + this.newDimensions.width * zoom,
            y: viewportDims.y + this.newDimensions.height * zoom,
          },
          `${Math.round(this.newDimensions.width)} ✕ ${Math.round(
            this.newDimensions.height
          )}`
        );
        this.canvas.dispatchEvent(`${this.name}.draw`, drawEvent);
      }
    } else {
    }
  };
  onMouseUp = (e: any) => {
    this.dragStarted = false;
    this.mouseDown = false;
    let drawEndEvent = new DrawEvent(`${this.name}.drawEnd`, this.canvas, {
      x: this.newDimensions.x,
      y: this.newDimensions.y,
      height: this.newDimensions.height,
      width: this.newDimensions.width,
    });
    this.canvas.executeFunction('Tooltip.hide');
    this.newDimensions.height = 0;
    this.newDimensions.width = 0;
    this.renderer.style.width = '0px';
    this.renderer.style.height = '0px';
    this.renderer.style.display = 'none';
    this.overlayDispose();
    this.canvas.setMouseCursor('default');
    this.canvas.dispatchEvent(`${this.name}.drawEnd`, drawEndEvent);

    if (this.overlayLayer) {
      this.overlayLayer.ownerDocument.removeEventListener(
        'mousemove',
        this.boundMouseMove
      );
      this.overlayLayer.ownerDocument.removeEventListener(
        'mouseup',
        this.boundMouseUp
      );
      this.overlayLayer.ownerDocument.removeEventListener(
        'contextmenu',
        this.boundMouseUp
      );
    }

    this.renderer = undefined;
    this.overlayLayer = undefined;
    this.shapeType = undefined;
  };
  drawShape(
    context: IContext,
    shapeType: string = 'rect',
    cursor: string = 'crosshair'
  ): void {
    if (this.shapeType && this.shapeType !== shapeType) {
      this.abortDraw();
    }

    if (!this.mouseDown && !this.overlayLayer) {
      let left = 0;
      let top = 0;

      const drawShape = new DrawEvent(
        `${this.name}.drawShape`,
        this.canvas,
        {
          x: left,
          y: top,
          height: 0,
          width: 0,
        },
        undefined,
        {}
      );
      this.canvas.dispatchEvent(`${this.name}.drawShape`, drawShape);

      this.canvas.setMouseCursor(cursor);
      this.shapeType = shapeType;
      if (shapeType === 'rect') {
        this.renderer = document.createElement('div');
      } else if (shapeType === 'ellipse') {
        let svgns = 'http://www.w3.org/2000/svg';
        this.renderer = document.createElementNS(
          'http://www.w3.org/2000/svg',
          'svg'
        );
        this.ellipseRenderer = document.createElementNS(svgns, 'ellipse');
        this.ellipseRenderer.style.fill = 'transparent';
        this.ellipseRenderer.style.stroke = 'black';
        this.renderer.appendChild(this.ellipseRenderer);
      }
      this.renderer.style.position = 'absolute';
      let canvasDims = this.canvas.getBoundingClientRect();

      this.overlayLayer = document.createElement('div');
      this.overlayLayer.style.background = 'transparent';
      this.overlayLayer.style.height = canvasDims.height + 'px';
      this.overlayLayer.style.width = canvasDims.width + 'px';
      this.overlayLayer.style.top = canvasDims.y + 'px';
      this.overlayLayer.style.left = canvasDims.x + 'px';
      this.overlayLayer.style.pointerEvents = 'none';
      this.overlayLayer.style.position = 'absolute';
      this.overlayLayer.style.zIndex = 1;

      this.overlayDispose = this.canvas.addDomToWorld(
        this.overlayLayer,
        true,
        105
      );

      this.overlayLayer.addEventListener(`mousemove`, (e: any) => {
        //
        if (!this.mouseDown) {
          let dragStartVal = this.canvas.executeFunction(
            'HtmlDragToWorld.dragStart',
            e,
            { x: 0, y: 0, height: 0, width: 0, rotation: 0 }
          );
          left = dragStartVal.session.start.x;
          top = dragStartVal.session.start.y;

          let dimension = {
            x: left,
            y: top,
            width: 0,
            height: 0,
          };

          const snappedData = this.canvas.executeFunction(
            'snapToPoints',
            this.snapPoints,
            [left, top],
            [0, 0],
            {
              ...dimension,
            },
            [0, 0],
            [left, top],
            0,
            this.initialDimensions
          );

          dimension = {
            ...dimension,
            x: dimension.x + snappedData.delta.x,
            y: dimension.y + snappedData.delta.y,
          };

          const drawEvent = new DrawEvent(
            `${this.name}.mousemove`,
            this.canvas,
            {
              ...dimension,
            },
            undefined,
            snappedData.meta
          );
          this.canvas.dispatchEvent(`${this.name}.mousemove`, drawEvent);
        }
        //   let dragStartVal = this.canvas.executeFunction(
        //     'HtmlDragToWorld.dragStart',
        //     e,
        //     { x: 0, y: 0, height: 0, width: 0, rotation: 0 }
        //   );
        //   left = dragStartVal.session.start.x;
        //   top = dragStartVal.session.start.y;
        // }

        // const snappedData = this.canvas.executeFunction(
        //   'snapToPoints',
        //   this.snapPoints,
        //   [left, top],
        //   [0, 0],
        //   {
        //     x: left,
        //     y: top,
        //     width: 0,
        //     height: 0,
        //     rotation: 0,
        //   },
        //   [0, 0],
        //   [top, left],
        //   0
        // );
      });
      this.overlayLayer.addEventListener(`mousedown`, (e: any) => {
        let dragStartVal = this.canvas.executeFunction(
          'HtmlDragToWorld.dragStart',
          e,
          { x: 0, y: 0, height: 0, width: 0, rotation: 0 }
        );
        left = dragStartVal.session.start.x;
        top = dragStartVal.session.start.y;
        this.dragSession.start = dragStartVal.session.start;
        this.mouseDown = true;
        this.dragStarted = false;
        this.dims = { x: left, y: top, height: 0, width: 0 };
        this.newDimensions = { x: left, y: top, height: 0, width: 0 };

        this.boundMouseMove = this.onMouseMove.bind(this, shapeType);
        this.boundMouseUp = this.onMouseUp;
        this.overlayLayer.ownerDocument.addEventListener(
          'mousemove',
          this.boundMouseMove
        );
        this.overlayLayer.ownerDocument.addEventListener(
          'mouseup',
          this.boundMouseUp
        );
        this.overlayLayer.ownerDocument.addEventListener(
          'contextmenu',
          this.boundMouseUp
        );

        const drawStartEvent = new DrawEvent(
          `${this.name}.drawStart`,
          this.canvas,
          {
            x: left,
            y: top,
            height: 0,
            width: 0,
          }
        );
        this.canvas.dispatchEvent(`${this.name}.drawStart`, drawStartEvent);
      });
      this.canvas.addEventListener('click', this.clickEvent);
    }
  }
  clickEvent = (data: any) => {
    let clickEvent = new DrawEvent(`${this.name}.click`, this.canvas, {
      x: data.worldX,
      y: data.worldY,
      height: undefined,
      width: undefined,
    });
    this.canvas.dispatchEvent(`${this.name}.click`, clickEvent);
  };
  abortDraw = () => {
    this.dragStarted = false;
    this.mouseDown = false;
    this.dims.height = 0;
    this.dims.width = 0;
    if (this.renderer && this.renderer.style) {
      this.renderer.style.width = '0px';
      this.renderer.style.height = '0px';
      this.renderer.style.display = 'none';
    }
    this.canvas.executeFunction('Tooltip.hide');
    if (this.overlayDispose) {
      this.overlayDispose();
    }
    this.canvas.setMouseCursor('default');
    this.canvas.removeEventListener('click', this.clickEvent);
    if (this.overlayLayer) {
      this.overlayLayer.ownerDocument.removeEventListener(
        'mousemove',
        this.boundMouseMove
      );
      this.overlayLayer.ownerDocument.removeEventListener(
        'mouseup',
        this.boundMouseUp
      );
      this.overlayLayer.ownerDocument.removeEventListener(
        'contextmenu',
        this.boundMouseUp
      );
      this.renderer = undefined;
      this.overlayLayer = undefined;
    }
    this.shapeType = undefined;
  };

  setSnapPoints = (context: IContext, snapPoints: any) => {
    this.snapPoints = snapPoints;
    if (this.snapPoints) {
      this.snapPoints.parentSnapPoints.x.forEach((point: any) => {
        point.value = point.rootValue;
      });

      this.snapPoints.parentSnapPoints.y.forEach((point: any) => {
        point.value = point.rootValue;
      });

      this.snapPoints.siblingSnapPoints.x.forEach((point: any) => {
        point.value = point.rootValue;
      });

      this.snapPoints.siblingSnapPoints.y.forEach((point: any) => {
        point.value = point.rootValue;
      });

      this.snapPoints.rulerSnapPoints.x.forEach((point: any) => {
        point.value = point.rootValue;
      });

      this.snapPoints.rulerSnapPoints.y.forEach((point: any) => {
        point.value = point.rootValue;
      });
    }
  };

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction('drawShape', this.drawShape.bind(this));
    this.canvas.addFunction('abortDraw', this.abortDraw.bind(this));
    this.canvas.addFunction(
      'DrawComponent.setSnapPoints',
      this.setSnapPoints.bind(this)
    );
    this.canvas.addEventListener('blur', () => {
      if (this.isAltPressed) this.isAltPressed = false;
      if (this.isShiftPressed) this.isShiftPressed = false;
    });
    this.canvas.addEventListener('keyUp', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = false;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = false;
      }
    });
    this.canvas.addEventListener('keyDown', (data: any) => {
      let event = data.originalEvent ? data.originalEvent : data;
      if (event.keyCode === 16) {
        this.isShiftPressed = true;
      }
      if (event.keyCode === 18) {
        this.isAltPressed = true;
      }
    });
  }
  deactivate() {
    this.canvas.removeFunction('drawShape');
    this.canvas.removeFunction('abortDraw');
    this.canvas.removeFunction('DrawComponent.setSnapPoints');
    this.overlayDispose();
  }
}
