import { Canvas, IPlugin, Layer, IContext } from '../..';
const path = require('path');
export default class StageDesignerDeviceData implements IPlugin {
  name = 'StageDesignerDeviceData';
  canvas!: Canvas;
  dataMap = new Map<Layer, any>();

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction('getDeviceData', this.getDeviceData);
  }

  getDeviceData = (context: IContext) => {
    const layer = context.layer;

    return this.getDeviceDataOnLayer(layer);
  };

  getDeviceDataOnLayer = (layer: Layer): any => {
    const deviceData = layer.executeFunction('getData', 'deviceData');
    if (deviceData) {
      return deviceData;
    } else if (layer.parent) {
      return this.getDeviceDataOnLayer(layer.parent);
    }

    return null;
  };

  deactivate() {
    this.canvas.removeFunction('getDeviceData');
  }
}
