import { Model } from './base-model';
import { types } from './serializable';
import { isNil, set, get } from 'lodash';
export class KeyValueContainer extends Model {
  private data: any = {};
  getFields(): {
    [key: string]: any;
  } {
    return {
      data: types.literal,
    };
  }
  isValidKey(key: string) {
    return true;
  }
  set(key: string | object, value?: any) {
    if (typeof key == 'object') {
      for (var prop in key) {
        if (this.isValidKey(prop)) {
          set(this.data, prop, (key as any)[prop]);
          // this.data[prop] = (key as any)[prop];
        } else {
          throw new Error(
            `Model KeyValueContainer Error: Invalid key '${prop}'`
          );
        }
      }
    } else {
      if (this.isValidKey(key)) {
        set(this.data, key, value);
        // this.data[key] = value;
      } else {
        throw new Error(`Model KeyValueContainer Error: Invalid key '${key}'`);
      }
    }
  }
  get(key: string): any {
    return get(this.data, key);
  }
  getAll() {
    return this.data;
  }
  remove(key: string | object) {
    if (typeof key === 'string' && !isNil(this.data[key])) {
      delete this.data[key];
    } else if (typeof key == 'object' && Array.isArray(key)) {
      for (let i = 0; i < key.length; i++) {
        if (key[i] && this.isValidKey(key[i])) {
          delete this.data[key[i]];
        } else {
          throw new Error(
            `Model KeyValueContainer Error: Invalid key '${key[i]}'`
          );
        }
      }
    }
  }
}
