import { Layer, MouseEvent } from '../../..';
import { IPosition, IDimensionWithRotation } from 'interfaces';

export class ResizerRendererDragEvent extends MouseEvent {
  targetHandle: string;
  startValues: IDimensionWithRotation;
  session: {
    start: IPosition;
    current: any;
    end: IPosition;
  };
  constructor(
    name: string,
    targetHandle: string,
    startValues: IDimensionWithRotation,
    session: { start: IPosition; current: IPosition; end: IPosition },
    originalEvent: any,
    target: Layer,
    currentTarget?: Layer
  ) {
    super(name, target, originalEvent, currentTarget);
    this.targetHandle = targetHandle;
    this.session = session;
    this.startValues = startValues;
  }
}
