import * as React from 'react';
import { Layer } from 'models/Layer';
import { ActivityIndicator } from 'react-native-web';
import RenderEvent from '../../../events/RenderEvent';
const rerenderProps = ['layout', 'props', 'style', 'visible'];
export default class AndroidActivityIndicatorRenderer extends React.PureComponent<
  any,
  any
> {
  _ref: any;

  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);

    this.props.layer.dispatchEvent(
      'domDidMount',
      new RenderEvent('domDidMount', this)
    );
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);

    this.props.layer.dispatchEvent(
      'domDidUpdate',
      new RenderEvent('domDidUpdate', this)
    );
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  render() {
    const layer: Layer = this.props.layer;
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    const activityIndicatorStyle = {
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
    };
    const height = layerProps.size === 'large' ? 40 : 22;
    const width = layerProps.size === 'large' ? 39 : 22;

    return (
      <ActivityIndicator
        ref={(c: any) => {
          this._ref = c;
        }}
        {...layerProps}
        style={{ ...activityIndicatorStyle, height, width }}
        nativeID={layerId}
      />
    );
  }
}
