import { Canvas, IPlugin, Layer } from '../..';

export default class ConstructionLinesPlugin implements IPlugin {
  name = 'ConstructionLinesPlugin';
  canvas!: Canvas;
  dispose: any;
  canvasDOMElement!: HTMLCanvasElement;
  constructionLinesWrapper!: HTMLDivElement;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  activate() {
    this.canvas.addFunction(
      'showConstructionLines',
      this.showConstructionLines.bind(this)
    );
    this.canvas.addFunction(
      'hideConstructionLines',
      this.hideConstructionLines.bind(this)
    );

    this.canvas.addEventListener('setLayout', this.setDimensions.bind(this));

    this.constructionLinesWrapper = document.createElement('div');
    this.constructionLinesWrapper.className = 'construction-line-wrapper';
    this.dispose = this.canvas.addDomToWorld(this.constructionLinesWrapper);
    this.canvasDOMElement = document.createElement('canvas');

    this.setDimensions();
    // this.canvasDOMElement.setAttribute(
    //   'height',
    //   this.canvas.getBoundingClientRect().height.toString() + 'px'
    // );
    // this.canvasDOMElement.setAttribute(
    //   'width',
    //   this.canvas.getBoundingClientRect().width.toString() + 'px'
    // );
    this.canvasDOMElement.style.pointerEvents = 'none';
    const context = this.canvasDOMElement.getContext('2d');
    this.constructionLinesWrapper.style.pointerEvents = 'none';
    this.constructionLinesWrapper.appendChild(this.canvasDOMElement);
    const collisionScope = this.canvas.collisionScope;
    this.attachListeners(this.canvas);
    if (context && collisionScope) {
      context.strokeStyle = '#f00';
      context.beginPath();
      collisionScope.drawBVH(context);
      context.stroke();
    }
  }

  attachListeners(layer: Layer) {
    layer.on('*', this.updateLines);

    layer.on(['children'], () => {
      layer.children.forEach((child: Layer) => {
        this.attachListeners(child);
      });
    });

    layer.children.forEach((child: Layer) => {
      this.attachListeners(child);
    });
  }

  updateLines = () => {
    const context = this.canvasDOMElement.getContext('2d');
    const collisionScope = this.canvas.collisionScope;
    if (context && collisionScope) {
      context.clearRect(
        0,
        0,
        this.canvasDOMElement.width,
        this.canvasDOMElement.height
      );
      context.strokeStyle = '#f00';
      context.beginPath();
      collisionScope.drawBVH(context);
      context.stroke();
    }
  };

  deactivate() {
    this.dispose();
    this.canvas.removeFunction('showConstructionLines');
    this.canvas.removeFunction('hideConstructionLines');
    this.canvas.removeEventListener('setLayout', this.setDimensions);
  }

  showConstructionLines = () => {
    this.constructionLinesWrapper.style.display = 'block';
  };

  setDimensions = () => {
    this.canvasDOMElement.setAttribute(
      'height',
      this.canvas.getBoundingClientRect().height.toString() + 'px'
    );
    this.canvasDOMElement.setAttribute(
      'width',
      this.canvas.getBoundingClientRect().width.toString() + 'px'
    );
  };
  hideConstructionLines = () => {
    this.constructionLinesWrapper.style.display = 'none';
  };
}
