import * as React from 'react';
import * as ReactDOM from 'react-dom';
import RootRenderer from './renderer/RootRenderer';
import ScrollContainer from './ScrollContainer';
import ZoomContainer from './ZoomContainer';
import { Canvas } from '../models/Canvas';
class CanvasRenderer extends React.PureComponent<{ canvas: Canvas }, any> {
  onUpdate = (keys: any) => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.canvas.on(['zoom', 'viewport', 'layout'], this.onUpdate);
  }
  componentWillUnmount() {
    this.props.canvas.off(['zoom', 'viewport', 'layout'], this.onUpdate);
  }

  render() {
    const baseHeight = this.props.canvas.layout.get('height');
    const baseWidth = this.props.canvas.layout.get('width');

    return (
      <div
        style={{
          position: 'relative',
          width: baseWidth,
          height: baseHeight,
          backgroundColor: this.props.canvas.style.get('backgroundColor'),
        }}
      >
        {this.props.canvas.children.map((child: any, index: any) => (
          <RootRenderer layer={child} key={index} canvas={this.props.canvas} />
        ))}
      </div>
    );
  }
}
export class DesignerReact extends React.PureComponent<
  { canvas: Canvas },
  any
> {
  renderCompiledDom: any;
  worldStackRef: any;
  viewportStackRef: any;
  thisNode: HTMLDivElement | null = null;

  onUpdate = (keys: any) => {
    this.forceUpdate();
  };
  showConstructionLines = () => {
    let canvasDOMElement = document.createElement('canvas');
    canvasDOMElement.setAttribute('height', '2000px');
    canvasDOMElement.setAttribute('width', '2000px');
    canvasDOMElement.setAttribute('class', 'canvas-element');
    canvasDOMElement.style.position = 'absolute';
    canvasDOMElement.style.top = '0px';
    canvasDOMElement.style.left = '0px';
    canvasDOMElement.style.opacity = '1';
    canvasDOMElement.style.pointerEvents = 'none';
    if (this.renderCompiledDom) {
      let pane = ReactDOM.findDOMNode(this.renderCompiledDom);
      const context = canvasDOMElement.getContext('2d');
      const collisionScope = this.props.canvas.collisionScope;
      if (pane && context && collisionScope) {
        context.strokeStyle = '#000';
        context.beginPath();
        collisionScope.drawBVH(context);
        context.stroke();
        pane.appendChild(canvasDOMElement);
      }
    }
  };
  renderDOMStack = () => {
    if (this.worldStackRef) {
      while (this.worldStackRef.firstChild) {
        this.worldStackRef.removeChild(this.worldStackRef.firstChild);
      }

      this.props.canvas.stackWorldDOM.forEach(element => {
        this.worldStackRef.appendChild(element);
      });
    }
    if (this.viewportStackRef) {
      while (this.viewportStackRef.firstChild) {
        this.viewportStackRef.removeChild(this.viewportStackRef.firstChild);
      }

      this.props.canvas.stackViewportDOM.forEach(element => {
        this.viewportStackRef.appendChild(element);
      });
    }
  };
  componentDidMount() {
    this.renderDOMStack();
    this.props.canvas.on(['zoom', 'viewport', 'layout'], this.onUpdate);

    this.props.canvas.on(
      ['stackWorldDOM', 'stackViewportDOM'],
      this.renderDOMStack
    );
  }

  componentWillUnmount() {
    this.props.canvas.off(['zoom', 'viewport', 'layout'], this.onUpdate);
    this.props.canvas.off(
      ['stackWorldDOM', 'stackViewportDOM'],
      this.renderDOMStack
    );
  }

  onMouseDown = (e: React.MouseEvent) => {
    if (this.thisNode && this.thisNode.ownerDocument) {
      this.thisNode.ownerDocument.addEventListener(
        'mousemove',
        this.onMouseMove
      );
      this.thisNode.ownerDocument.addEventListener('mouseup', this.onMouseUp);
      this.thisNode.ownerDocument.addEventListener(
        'contextmenu',
        this.onMouseUp
      );
    }

    this.props.canvas.handleNativeEvent(e);
  };

  onMouseMove = (e: MouseEvent) => {
    // if (!this.props.canvas.dragging) {
    this.props.canvas.handleNativeEvent(e);
    // }
  };

  onMouseUp = (e: MouseEvent) => {
    if (this.thisNode && this.thisNode.ownerDocument) {
      this.thisNode.ownerDocument.removeEventListener(
        'mousemove',
        this.onMouseMove
      );
      this.thisNode.ownerDocument.removeEventListener(
        'mouseup',
        this.onMouseUp
      );
      this.thisNode.ownerDocument.removeEventListener(
        'contextmenu',
        this.onMouseUp
      );
    }

    this.props.canvas.handleNativeEvent(e);
  };

  render() {
    const canvas = this.props.canvas;
    const zoom = canvas.getZoom();
    const baseHeight = canvas.layout.get('height');
    const baseWidth = canvas.layout.get('width');
    const viewport = canvas.getViewport();
    const viewportHeight = viewport.height;
    const viewportWidth = viewport.width;
    // const cursor = canvas.mouseCursorType;
    return (
      <div style={{ position: 'absolute' }} ref={r => (this.thisNode = r)}>
        <ScrollContainer
          canvas={canvas}
          ref={c => {
            this.props.canvas.setViewportRef(c);
          }}
        >
          <ZoomContainer
            canvas={canvas}
            style={{
              height: baseHeight * zoom,
              width: baseWidth * zoom,
            }}
          >
            <CanvasRenderer canvas={this.props.canvas} />
            <div
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
              }}
            >
              <div
                style={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                }}
                // onMouseMove={this.onMouseMove}
                onMouseDown={this.onMouseDown}
                onMouseEnter={e => canvas.handleNativeEvent(e)}
                onMouseMove={e => canvas.handleNativeEvent(e)}
                onClick={e => canvas.handleNativeEvent(e)}
                onContextMenu={e => canvas.handleNativeEvent(e)}
                onDoubleClick={e => canvas.handleNativeEvent(e)}
                onMouseOut={e => canvas.handleNativeEvent(e)}
                onDrop={e => canvas.handleNativeEvent(e)}
                onDragOver={e => e.preventDefault()}
              />
              <div
                ref={ref => {
                  if (this.worldStackRef && !ref) {
                    // THis means worldStackRef is being set null while it was set correctly earlier
                    // We are preventing that here. Don't know the root cause
                    return;
                  }
                  this.worldStackRef = ref;
                }}
                style={{
                  position: 'absolute',
                  pointerEvents: 'none',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                }}
              />
            </div>
          </ZoomContainer>
        </ScrollContainer>
        <div
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            height: viewportHeight,
            width: viewportWidth,
            pointerEvents: 'none',
            overflow: 'hidden',
          }}
          ref={ref => (this.viewportStackRef = ref)}
        />
      </div>
    );
  }
}
