import { Canvas, IPlugin, Layer } from '../..';
import RulerLinesAddEvent from '../../plugins/RulerLines/events/RulerLinesAddEvent';
import RulerLinesRemoveEvent from '../../plugins/RulerLines/events/RulerLinesRemoveEvent';
import RulerLinesSetPositionEvent from '../../plugins/RulerLines/events/RulerLinesSetPositionEvent';
import ArtboardEvent from 'events/ArtboardEvent';
import { IContext } from 'interfaces';
import { forEach } from 'lodash';

type RulerLineData = {
  axis: 'x' | 'y';
  axisCoordinate: number;
};

export default class StageDesignerRulerPlugin implements IPlugin {
  name = 'StageDesignerRuler';
  canvas!: Canvas;
  rulerVisibility: boolean = true;
  artboardLinesMap: Map<Layer, Array<RulerLineData>> = new Map();

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addEventListener('artboardSelected', this.artboardSelected);
    this.canvas.addEventListener('RulerLines.add', this.addLine);
    this.canvas.addEventListener('RulerLines.setPosition', this.setPosition);
    this.canvas.addEventListener('RulerLines.remove', this.removeLine);
    this.canvas.addFunction(`${this.name}.getAllLines`, this.getAllLines);
    this.canvas.addFunction(
      `${this.name}.getLineCoordinates`,
      this.getLineCoordinates
    );
    this.canvas.addFunction(`${this.name}.show`, this.showRuler);
    this.canvas.addFunction(`${this.name}.hide`, this.hideRuler);
    this.canvas.on('scroll', (e: any) => {
      const selectedArtboard: Layer = this.canvas.executeFunction(
        'getSelectedArtboard'
      );
      if (selectedArtboard) {
        const { x, y } = this.canvas.convertPoint(
          { x: selectedArtboard.rootX, y: selectedArtboard.rootY },
          'world',
          'viewport'
        );
        this.canvas.executeFunction('RulerScale.setInitialPoint', {
          x,
          y,
        });
      }
    });
  }

  showRuler = (context: IContext) => {
    this.rulerVisibility = true;
    this.canvas.executeFunction('RulerScale.show');
    this.canvas.executeFunction('RulerLines.show');
  };
  hideRuler = (context: IContext) => {
    this.rulerVisibility = false;
    this.canvas.executeFunction('RulerScale.hide');
    this.canvas.executeFunction('RulerLines.hide');
  };

  artboardSelected = (event: ArtboardEvent) => {
    const selectedArtboard = event.artboard;
    const artboardDims = selectedArtboard.getBoundingClientRect();
    if (selectedArtboard) {
      const { x, y } = this.canvas.convertPoint(
        { x: selectedArtboard.rootX, y: selectedArtboard.rootY },
        'world',
        'viewport'
      );
      this.canvas.executeFunction('RulerScale.setInitialPoint', {
        x,
        y,
      });
    }
    this.canvas.executeFunction('RulerLines.removeAll');
    let artboardLines: Array<RulerLineData> =
      this.artboardLinesMap.get(selectedArtboard) || [];

    if (artboardLines.length !== 0) {
      artboardLines = artboardLines.map((ruler: RulerLineData) => {
        if (ruler.axis === 'x') {
          return {
            axis: 'x',
            axisCoordinate: artboardDims.rootY + ruler.axisCoordinate,
          };
        } else {
          return {
            axis: 'y',
            axisCoordinate: artboardDims.rootX + ruler.axisCoordinate,
          };
        }
      });
    }

    this.canvas.executeFunction('RulerLines.addAll', artboardLines);

    if (!this.rulerVisibility) {
      this.canvas.executeFunction('RulerLines.hide', artboardLines);
    }
  };

  getAllLines = (context: IContext, selectedArtboard: Layer) => {
    if (!selectedArtboard) {
      selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    }
    return this.artboardLinesMap.get(selectedArtboard);
  };
  getLineCoordinates = () => {
    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');

    let coordinates: { x: Array<number>; y: Array<number> } = {
      x: [],
      y: [],
    };
    if (!selectedArtboard) {
      return coordinates;
    }
    const rulerLines = this.artboardLinesMap.get(selectedArtboard);

    if (!rulerLines) {
      return coordinates;
    }

    forEach(rulerLines, line => {
      if (line.axis === 'y') {
        coordinates.y.push(line.axisCoordinate);
      } else {
        coordinates.x.push(line.axisCoordinate);
      }
    });

    return coordinates;
  };

  addLine = (event: RulerLinesAddEvent) => {
    const selectedArtboard: Layer = this.canvas.executeFunction(
      'getSelectedArtboard'
    );
    if (!selectedArtboard) return;
    const artboardDims = selectedArtboard.getBoundingClientRect();
    let artboardLines = this.artboardLinesMap.get(selectedArtboard);
    if (!artboardLines) {
      artboardLines = [];
    }
    const artboardAxisCoordinate =
      event.axis === 'x' ? artboardDims.rootY : artboardDims.rootX;
    artboardLines.push({
      axis: event.axis,
      axisCoordinate: event.axisCoordinate - artboardAxisCoordinate,
    });
    this.artboardLinesMap.set(selectedArtboard, artboardLines);
  };

  setPosition = (event: RulerLinesSetPositionEvent) => {
    const selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (!selectedArtboard) return;
    const artboardDims = selectedArtboard.getBoundingClientRect();
    let artboardLines = this.artboardLinesMap.get(selectedArtboard);

    const artboardAxisCoordinate =
      event.axis === 'x' ? artboardDims.rootY : artboardDims.rootX;

    const line = {
      axis: event.axis,
      axisCoordinate: event.axisCoordinate - artboardAxisCoordinate,
    };
    if (artboardLines) {
      if (event.index || event.index === 0) {
        artboardLines[event.index] = line;
      } else {
        artboardLines[artboardLines.length - 1] = line;
      }
    } else {
      artboardLines = [];
      artboardLines.push(line);
    }
    this.artboardLinesMap.set(selectedArtboard, artboardLines);
  };

  removeLine = (event: RulerLinesRemoveEvent) => {
    const selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');
    if (!selectedArtboard) return;
    let artboardLines = this.artboardLinesMap.get(selectedArtboard);
    artboardLines?.splice(event.index, 1);
    this.artboardLinesMap.set(selectedArtboard, artboardLines || []);
  };

  deactivate() {
    this.canvas.removeEventListener('artboardSelected', this.artboardSelected);
    this.canvas.removeEventListener('RulerLines.add', this.addLine);
    this.canvas.removeEventListener('RulerLines.setPosition', this.setPosition);
    this.canvas.removeEventListener('RulerLines.remove', this.removeLine);
    this.canvas.removeFunction(`${this.name}.getAllLines`);
    this.canvas.removeFunction(`${this.name}.getLineCoordinates`);
    this.canvas.removeFunction(`${this.name}.show`);
    this.canvas.removeFunction(`${this.name}.hide`);
  }
}
