import { IContext } from 'interfaces';
import ISnapXPoint from './interfaces/ISnapXPoint';
import ISnapYPoint from './interfaces/ISnapYPoint';
import Config from './interfaces/Config';

function gridSnapPoint(
  delta: number,
  startPoint: number,
  gridSpace: number,
  stretch: number,
  handle?: any
): any {
  let snappedSide = {
    coordinate: NaN,
    start: false,
    center: false,
    end: false,
  };

  let point1 = startPoint + delta;
  point1 = Math.round(point1 / gridSpace) * gridSpace;
  const delta1 = point1 - startPoint;

  let point2 = startPoint + stretch + delta;
  point2 = Math.round(point2 / gridSpace) * gridSpace;
  const delta2 = point2 - stretch - startPoint;

  let outputDelta = delta1;

  snappedSide.coordinate = point1;
  snappedSide.start = true;
  outputDelta = delta1;

  if (handle) {
    if (handle.indexOf('L') === -1 && handle.indexOf('T') === -1) {
      snappedSide.coordinate = point2;
      snappedSide.end = true;
      outputDelta = delta2;
    }
  } else {
    if (
      Math.abs(delta + startPoint - point1) >
      Math.abs(delta + startPoint + stretch - point2)
    ) {
      snappedSide.coordinate = point2;
      snappedSide.end = true;
      outputDelta = delta2;
    }
  }

  return {
    value: snappedSide.coordinate,
    rootValue: snappedSide.coordinate,
    snappedSide: snappedSide,
    snapType: 'grid',
    delta: outputDelta,
  };
}

export default function snapToGrid(
  config: Config,
  context: IContext,
  startX: number,
  totalDeltaX: number,
  startY: number,
  totalDeltaY: number,
  stretchX: number,
  stretchY: number,
  handle?: any
): { x: any; y: any } | undefined {
  if (!config.snap || !config.gridSnap) {
    return;
  }

  const gridOffset = context.canvas.executeFunction('getGridOffset');

  let x: any;
  let y: any;

  let snapBounds = context.canvas.executeFunction('getGridSize');

  if (!snapBounds) {
    snapBounds = {
      x: 20,
      y: 20,
    };
  }

  if (!snapBounds) {
    return {
      x: {
        value: startX + totalDeltaX,
        rootValue: startX + totalDeltaX,
      },
      y: {
        value: startY + totalDeltaY,
        rootValue: startY + totalDeltaY,
      },
    };
  }

  let xSnapPoint = gridSnapPoint(
    totalDeltaX,
    startX - gridOffset.x,
    snapBounds.x,
    stretchX,
    handle
  );

  x = xSnapPoint;
  let ySnapPoints = gridSnapPoint(
    totalDeltaY,
    startY - gridOffset.y,
    snapBounds.y,
    stretchY,
    handle
  );
  // snappedSideY.coordinate = ySnapPoint;
  y = ySnapPoints;

  return {
    x,
    y,
  };
}
