import { Canvas, IPlugin } from '../..';
import ScrollViewRenderer from './ScrollViewRenderer';
import ScrollViewComponentDefinition from './ScrollViewComponentDefinition';

export default class ScrollViewRendererPlugin implements IPlugin {
  name = 'ScrollViewRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'scrollView',
      ScrollViewRenderer,
      ScrollViewComponentDefinition
    );
  }

  deactivate() {}
}
