import * as React from 'react';
export default class Ruler extends React.PureComponent<any, any> {
  domWidth: any;
  domHeight: any;
  height: any;
  width: any;
  ruler: any;
  ctx: any;
  lock: any;
  vertical: any;
  onLineChange: any;
  fontScale: any;
  bgColor: any;
  fgColor: any;
  fontColor: any;
  lineColor: any;
  shadowColor: any;
  horLineValue: any;
  verLineValue: any;
  startX: any;
  startY: any;
  perWidth: any;
  ratio: any;
  shadow: any;
  componentDidMount() {
    this.handleResize(true);
    window.addEventListener('resize', this.handleResize);
  }
  componentDidUpdate() {
    this.handleResize(true);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }
  handleResize = (render: any) => {
    const height = this.props.height;
    const width = this.props.width;
    this.domWidth = width;
    this.domHeight = height;
    this.width = width * this.props.ratio;
    this.height = height * this.props.ratio;
    this.ruler.width = this.width;
    this.ruler.height = this.height;

    const ctx = this.ruler.getContext('2d');
    ctx.font = `${12 *
      this.props.ratio}px Roboto, -apple-system, ".SFNSText-Regular",
      "SF UI Text", "Helvetica Neue", Arial, "PingFang SC",
      "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Zen Hei", sans-serif`;

    ctx.lineWidth = this.props.ratio;
    ctx.strokeStyle = '#6F727D';
    ctx.textBaseline = 'middle';
    this.ctx = ctx;

    const { start } = this.props;
    if (render) {
      this.drawRuler(start, ctx);
    }
  };
  drawRuler(start: any, select: any) {
    // console.log('*** drawRuler');
  }
  render() {
    return (
      <canvas
        style={{ pointerEvents: 'none' }}
        className="ruler"
        ref={ref => (this.ruler = ref)}
      />
    );
  }
}
