import { Layer } from '../models/Layer';
import { Event } from './index';
import { Canvas } from 'models/Canvas';
export default class SetScrollEvent extends Event {
  scrollX: number;
  scrollY: number;

  constructor(
    name: string,
    target: Layer,
    originalEvent: any,
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    this.scrollX = originalEvent.x;
    this.scrollY = originalEvent.y;
  }
}
