import * as React from 'react';
import { Layer } from 'models/Layer';
import get from 'lodash/get';
import Svg, { Ellipse } from 'svgs';
const rerenderProps = [
  'layout',
  'children',
  'props',
  'style',
  'visible',
  'gradient',
];

export default class EllipseRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this);
  }

  render() {
    const layer: Layer = this.props.layer;
    let ellipseProps = { ...layer.props.getAll() };
    const layerId = ellipseProps['id'];
    delete ellipseProps['id'];
    let transform = layer.rotation ? `rotate(${layer.rotation}deg)` : undefined;
    let ellipseStyle = {
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
      transform,
    };
    let strokeWidth = Number(get(ellipseProps, 'strokeWidth', 0));
    let appliedProps = {
      cx: ellipseProps.cx,
      cy: ellipseProps.cy,
      rx: ellipseProps.rx,
      ry: ellipseProps.ry,
      fill: get(ellipseProps, 'fill', 'transparent'),
      // fill: gradientType ? `url(#${gradientId})` : extractedProps.fill,
      fillOpacity: ellipseProps.fillOpacity,
      strokeOpacity: ellipseProps.strokeOpacity,
      stroke: ellipseProps.stroke,
      strokeWidth,
    };
    return (
      <Svg
        ref={(c: any) => {
          this._ref = c;
        }}
        // @ts-ignore
        getRef={(c: any) => {
          console.log(c, 'ref here');
        }}
        // @ts-ignore
        id={layerId}
        style={ellipseStyle}
        viewBox={ellipseProps.viewBox}
        preserveAspectRatio="none"
      >
        <Ellipse {...appliedProps} />
      </Svg>
    );
  }
}
