import { IPlugin } from 'interfaces/IPlugin';
import { Canvas } from 'models/Canvas';
import DrawEvent from '../../plugins/DrawComponent/events/drawEvent';
import StageDesignerDrawEvent from './events/stageDesignerDrawEvent';
import { Layer, Event, IContext } from '../..';
import { get } from 'lodash';
import Config from './interfaces/Config';

export default class StageDesignerDrawComponent implements IPlugin {
  name = 'StageDesignerDrawComponent';
  canvas!: Canvas;
  selectedArtboard!: Layer;
  firstTime: boolean = true;

  setSelectedArtboard = (data: DrawEvent) => {
    let { worldX, worldY } = data.dimensions;

    const artboard = this.canvas.executeFunction(
      'getHitArtboard',
      worldX,
      worldY
    );

    let selectedArtboard = this.canvas.executeFunction('getSelectedArtboard');

    if (artboard) {
      if (selectedArtboard !== artboard) {
        this.canvas.dispatchEvent(
          'selectArtboard',
          new Event('selectArtboard', artboard)
        );
      }
      this.selectedArtboard = artboard;
    } else if (selectedArtboard) {
      this.selectedArtboard = selectedArtboard;
    } else {
      throw new Error('No artboard is selected');
    }
  };

  drawShape = (data: any) => {
    let { worldX, worldY } = data.dimensions;
    let relativeDims = this.getDimsFromArtboard(data.dimensions);

    data.meta.parent = this.selectedArtboard.children[0];

    this.snapPoints = this.canvas.executeFunction(
      'getSnapPoints',
      [],
      this.selectedArtboard.children[0]
    );
    this.canvas.executeFunction('DrawComponent.setSnapPoints', this.snapPoints);

    let stageDesignerDrawEvent = new StageDesignerDrawEvent(
      `${this.name}.drawShape`,
      this.canvas,
      { ...relativeDims, worldX, worldY },
      undefined,
      data.meta,
      this.snapPoints
    );
    this.canvas.dispatchEvent(`${this.name}.drawShape`, stageDesignerDrawEvent);
  };
  addListeners = () => {
    this.canvas.addEventListener(
      'DrawComponent.drawShape',
      (data: DrawEvent) => {
        if (this.selectedArtboard) {
          data.meta.parent = this.selectedArtboard.children[0];
          this.drawShape(data);
        }
      }
    );
    this.canvas.addEventListener(
      'DrawComponent.mousemove',
      (data: DrawEvent) => {
        //
        let { worldX, worldY } = data.dimensions;
        const oldSelectedArtboard = this.selectedArtboard;
        this.setSelectedArtboard(data);
        let relativeDims = this.getDimsFromArtboard(data.dimensions);
        data.meta.parent = this.selectedArtboard.children[0];

        if (oldSelectedArtboard !== this.selectedArtboard) {
          this.drawShape(data);
        }

        let stageDesignerDrawEvent = new StageDesignerDrawEvent(
          `${this.name}.mousemove`,
          this.canvas,
          { ...relativeDims, worldX, worldY },
          undefined,
          data.meta
        );
        this.canvas.dispatchEvent(
          `${this.name}.mousemove`,
          stageDesignerDrawEvent
        );
      }
    );
    this.canvas.addEventListener(
      'DrawComponent.drawStart',
      (data: DrawEvent) => {
        this.setSelectedArtboard(data);
        let { worldX, worldY } = data.dimensions;

        let relativeDims = this.getDimsFromArtboard(data.dimensions);

        const parent = this.selectedArtboard.children[0];

        let stageDesignerDrawStartEvent = new StageDesignerDrawEvent(
          `${this.name}.drawStart`,
          this.canvas,
          { ...relativeDims, worldX, worldY },
          undefined,
          { parent },
          this.snapPoints
        );

        this.canvas.dispatchEvent(
          `${this.name}.drawStart`,
          stageDesignerDrawStartEvent
        );

        this.canvas.executeFunction(
          'DrawComponent.setSnapPoints',
          this.snapPoints
        );
      }
    );

    this.canvas.addEventListener('DrawComponent.draw', (data: DrawEvent) => {
      let { worldX, worldY } = data.dimensions;

      // data.target.setParent(this.selectedArtboard.children[0]);
      // (data.target as any).parent = this.selectedArtboard.children[0];

      data.meta.parent = this.selectedArtboard.children[0];
      let relativeDims = this.getDimsFromArtboard(data.dimensions);
      let stageDesignerDrawEvent = new StageDesignerDrawEvent(
        `${this.name}.draw`,
        this.canvas,
        {
          ...relativeDims,
          worldX: data.dimensions.worldX,
          worldY: data.dimensions.worldY,
        },
        undefined,
        data.meta
      );

      this.canvas.dispatchEvent(`${this.name}.draw`, stageDesignerDrawEvent);
    });

    this.canvas.addEventListener('DrawComponent.drawEnd', (data: DrawEvent) => {
      let { worldX, worldY } = data.dimensions;
      let relativeDims = this.getDimsFromArtboard(data.dimensions);
      let stageDesignerDrawEndEvent = new StageDesignerDrawEvent(
        `${this.name}.drawEnd`,
        this.canvas,
        { ...relativeDims, worldX, worldY }
      );

      this.canvas.dispatchEvent(
        `${this.name}.drawEnd`,
        stageDesignerDrawEndEvent
      );
    });

    this.canvas.addEventListener('DrawComponent.click', (data: DrawEvent) => {
      let { worldX, worldY } = data.dimensions;
      let relativeDims = this.getDimsFromArtboard(data.dimensions);

      let stageDesignerClickEvent = new StageDesignerDrawEvent(
        `${this.name}.click`,
        this.canvas,
        { ...relativeDims, worldX, worldY, height: undefined, width: undefined }
      );

      this.canvas.dispatchEvent(`${this.name}.click`, stageDesignerClickEvent);
    });
  };

  getDimsFromArtboard = (
    layerPoints:
      | { worldX: number; worldY: number; height: number; width: number }
      | {}
  ) => {
    let relativeX = 0;
    let relativeY = 0;
    let layerX = get(layerPoints, 'worldX', undefined);
    let layerY = get(layerPoints, 'worldY', undefined);
    let height = get(layerPoints, 'height', undefined);
    let width = get(layerPoints, 'width', undefined);
    if (layerX && this.selectedArtboard) {
      relativeX = layerX - this.selectedArtboard.rootX;
    }

    if (layerY && this.selectedArtboard) {
      relativeY = layerY - this.selectedArtboard.rootY;
    }

    return {
      x: relativeX,
      y: relativeY,
      height,
      width,
    };
  };
  snapPoints: any;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.addListeners();
  }
  deactivate() {}
}
