import * as React from 'react';
import { Layer } from 'models/Layer';
import { Icons } from './IconMap';
import RenderEvent from '../../events/RenderEvent';

const rerenderProps = [
  'layout',
  'children',
  'props',
  'style',
  'innerText',
  'visible',
];
export default class IconRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
    this.props.layer.dispatchEvent(
      'domDidMount',
      new RenderEvent('domDidMount', this)
    );
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentWillUpdate() {
    this.props.layer.dispatchEvent(
      'domWillUpdate',
      new RenderEvent('domWillUpdate', this)
    );
  }
  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);

    this.props.layer.dispatchEvent(
      'domDidUpdate',
      new RenderEvent('domDidUpdate', this)
    );
  }

  render() {
    const layer: Layer = this.props.layer;
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    let iconStyle = { ...layer.style.getAll(), ...layer.layout.getAll() };
    iconStyle = {
      ...iconStyle,
      overflow: iconStyle.overflow ? iconStyle.overflow : 'hidden',
      height: 'fit-content',
      width: 'fit-content',
    };
    delete iconStyle['fontPostscriptName'];
    const iconType = layerProps.type ? layerProps.type : 'Entypo';
    const Icon = Icons[`${iconType}Icon`] || Icons[`EntypoIcon`];
    return (
      <Icon
        ref={(c: any) => {
          this._ref = c;
        }}
        {...layerProps}
        style={iconStyle}
        nativeID={layerId}
      />
    );
  }
}
