import { IContext, IDimension } from 'interfaces';
import DeviceFrameEvent from './events/DeviceFrameEvent';
import RemoveChildEvent from 'events/RemoveChildEvent';
import ArtboardEvent from 'events/ArtboardEvent';
import {
  Canvas,
  IPlugin,
  MouseEvent as DesignerMouseEvent,
  Layer,
} from '../..';
import * as React from 'react';
import ReactDOM from 'react-dom';
import StatusBarComponent from './StatusBarComponent';

export default class StatusBar implements IPlugin {
  name = 'StatusBar';
  canvas!: Canvas;
  labeltext: any;
  wrapperDiv: any;
  deviceName: any;
  deviceOrientation: any;
  dispose: any;
  removeDom: any;
  statusBarMap: Map<any, any> = new Map([]);
  lastPageX = 0;
  lastPageY = 0;
  dragStart: any = {
    x: 0,
    y: 0,
  };
  dims: IDimension = { x: 0, y: 0, height: 0, width: 0 };
  ghostImageDOM: any;

  // updateFrameLayout(layer: any) {
  //   this.render();
  // }

  selectedArtboardChanged = (event: ArtboardEvent) => {
    // const selectedArtboard = event.artboard;
    // if (selectedArtboard) {
    //   this.statusBarMap.forEach((label: any, artboard: any) => {
    //     if (artboard === selectedArtboard) {
    //       let layerTextNode = label.labeltextNode;
    //       layerTextNode.parentElement.style.color = 'rgb(26, 170, 198)';
    //     } else {
    //       label.labeltextNode.parentElement.style.color = 'rgb(186, 189, 198)';
    //     }
    //   });
    // }
  };

  removeStatusBar(context: IContext): void {
    const deviceFrame = this.statusBarMap.get(context.layer);

    if (deviceFrame) {
      deviceFrame.removeCallback();
    }
  }

  render = (layer: Layer) => {
    const deviceFrame = this.statusBarMap.get(layer);
    if (!deviceFrame) {
      return;
    }
    deviceFrame.frameNode.style.top =
      layer.getBoundingClientRect().rootY + 'px';
    deviceFrame.frameNode.style.left =
      layer.getBoundingClientRect().rootX + 'px';

    // const statusBarConfig = layer.executeFunction('getData', 'statusBarConfig');

    ReactDOM.render(
      <StatusBarComponent
        layer={layer}
        width={layer.getBoundingClientRect().width}
        height={layer.getBoundingClientRect().height}
        deviceName={deviceFrame.deviceName}
        deviceOS={deviceFrame.deviceOS}
        deviceOrientation={deviceFrame.deviceOrientation}
        barStyle={deviceFrame.barStyle}
        backgroundColor={deviceFrame.backgroundColor}
      />,
      deviceFrame.frameNode
    );
  };

  addStatusBar(context: IContext, config: any): void {
    let layer = context.layer;

    const deviceFrame = this.statusBarMap.get(layer);

    if (deviceFrame) {
      deviceFrame.removeCallback();
    }
    const wrapperDiv = document.createElement('div');
    wrapperDiv.className = 'status-bar-wrappers';
    wrapperDiv.style.position = 'absolute';

    let dispose = this.canvas.addDomToWorld(wrapperDiv, true, 100);
    wrapperDiv.style.pointerEvents = 'none';

    this.statusBarMap.set(layer, {
      removeCallback: dispose,
      frameNode: wrapperDiv,
      deviceName: config.deviceName,
      deviceOS: config.deviceOS,
      deviceOrientation: config.deviceOrientation,
      barStyle: config.barStyle,
      backgroundColor: config.backgroundColor,
    });

    this.render(layer);

    if (layer.parent) {
      layer.parent.addEventListener(
        'removeChild',
        (event: RemoveChildEvent) => {
          if (layer === event.layer) layer.executeFunction('StatusBar.remove');
        }
      );
    }

    context.layer.on('layout', () => {
      this.render(context.layer);
    });
    context.layer.on('updateDom', () => {
      this.render(context.layer);
    });
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction(`${this.name}.add`, this.addStatusBar.bind(this));
    this.canvas.addFunction(
      `${this.name}.remove`,
      this.removeStatusBar.bind(this)
    );
    // this.canvas.addEventListener(
    //   'artboardSelected',
    //   this.selectedArtboardChanged
    // );
  }
  deactivate() {}
}
