import { Canvas, Layer } from '../../';
import { IPlugin } from '../../';
import MouseRectEvent from './events';
import { IDimension } from 'interfaces';

export default class MouseRectPlugin implements IPlugin {
  name = 'MouseRectPlugin';
  namespace = 'MouseRect';
  canvas!: Canvas;
  dispose: any;
  dragStarted: boolean = false;
  dragStart: any = {
    x: 0,
    y: 0,
  };
  dims: IDimension = { x: 0, y: 0, height: 0, width: 0 };
  wrapperLayer: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }
  getLayers = () => {
    const hitRectResult = this.canvas.getHitRectResult(
      this.dims.x,
      this.dims.y,
      this.dims.width,
      this.dims.height
    );
    return this.canvas.executeMiddleware(
      'MouseRect.getHitRectResult',
      hitRectResult,
      (hitRectResult: Array<Layer>) => {
        return hitRectResult;
      }
    );
  };
  activate() {
    let zoom = this.canvas.getZoom();

    this.wrapperLayer = document.createElement('div');

    this.dispose = this.canvas.addDomToWorld(this.wrapperLayer, true, 105);

    this.wrapperLayer.style.position = 'absolute';
    this.wrapperLayer.style.border = `${1 /
      this.canvas.getZoom()}px solid #25B9D1`;
    this.wrapperLayer.style.top = '0px';
    this.wrapperLayer.style.left = '0px';
    this.wrapperLayer.style.height = '0px';
    this.wrapperLayer.style.width = '0px';
    this.wrapperLayer.style.display = 'none';
    this.wrapperLayer.style.backgroundColor = 'rgba(121,121,121,0.09)';
    this.wrapperLayer.style.pointerEvents = 'none';

    this.canvas.addEventListener('dragStart', (event: any) => {
      return this.canvas.executeMiddleware(
        'MouseRect.dragStart',
        event,
        (event: any) => {
          this.dragStarted = true;
          this.dragStart = {
            x: event.worldX,
            y: event.worldY,
          };

          this.wrapperLayer.style.display = '';
          this.wrapperLayer.style.left = this.dragStart.x + 'px';
          this.wrapperLayer.style.top = this.dragStart.y + 'px';
        }
      );
    });
    this.canvas.addEventListener('drag', (event: any) => {
      if (this.dragStarted) {
        this.dims.x =
          event.worldX < this.dragStart.x ? event.worldX : this.dragStart.x;
        this.dims.y =
          event.worldY < this.dragStart.y ? event.worldY : this.dragStart.y;
        this.dims.height = Math.abs(event.worldY - this.dragStart.y);
        this.dims.width = Math.abs(event.worldX - this.dragStart.x);
        this.wrapperLayer.style.left = this.dims.x + 'px';
        this.wrapperLayer.style.top = this.dims.y + 'px';
        this.wrapperLayer.style.height = this.dims.height + 'px';
        this.wrapperLayer.style.width = this.dims.width + 'px';

        const drawEvent = new MouseRectEvent(
          `${this.namespace}.draw`,
          this.canvas,
          this.getLayers(),
          this.canvas
        );
        this.canvas.dispatchEvent(`${this.namespace}.draw`, drawEvent);
      }
    });

    this.canvas.addEventListener('dragEnd', (event: any) => {
      if (this.dragStarted) {
        this.wrapperLayer.style.top = '0px';
        this.wrapperLayer.style.left = '0px';
        this.wrapperLayer.style.height = '0px';
        this.wrapperLayer.style.width = '0px';
        this.wrapperLayer.style.display = 'none';
        const drawEndEvent = new MouseRectEvent(
          `${this.namespace}.drawEnd`,
          this.canvas,
          this.getLayers(),
          this.canvas
        );
        this.canvas.dispatchEvent(`${this.namespace}.drawEnd`, drawEndEvent);
        this.dragStarted = false;
      }
    });
    this.canvas.on('zoom', (e: any) => {
      let zoom = this.canvas.getZoom();

      this.wrapperLayer.style.border = `${1 / zoom}px solid #25B9D1`;
    });
  }

  deactivate() {
    this.dispose();
  }
}
