import { Canvas, Layer, IPlugin } from '../..';
import { forEach, indexOf, sortBy, cloneDeep, isNil, get } from 'lodash';
import { IDimensionWithRotation } from 'interfaces';
import DesignerMouseEvent from '../../events/MouseEvent';
import { Event } from '../../events';
import CommandEvent from '../../events/CommandEvent';

const transitionStyle = document.createElement('style');
transitionStyle.innerHTML = `
 #transition {
   transition: top 0.2s ease-in 0s, left 0.2s ease-in 0s, right 0.2s ease-in 0s,
     bottom 0.2s ease-in 0s;
 }`;

export default class FlexDragPlugin implements IPlugin {
  name = 'FlexDragPlugin';
  namespace = 'FlexDrag';
  canvas!: Canvas;
  disabled: boolean = false;
  layerOpacities: Map<Layer, number> = new Map<Layer, number>();
  previousTriggerPoint: number | null = null;
  nextTriggerPoint: number | null = null;
  targetInitialDims!: IDimensionWithRotation;
  target: Array<Layer> | Layer = [];
  flexDirection!: string;
  flexLayers: Array<Layer | undefined> = [];
  totalDx: number = 0;
  totalDy: number = 0;
  newTargetIndex: number | undefined;
  initialSnapshotData: Map<Layer, any> = new Map();
  beforeDragSnapshotData: Map<Layer, any> = new Map();
  currentSnapshotData: Map<Layer, any> = new Map();
  ghostLayer!: Layer;
  targetGhostLayer!: Layer;
  isFlex: boolean = false;
  layersHeightWidth: Map<Layer, { height: any; width: any }> = new Map();
  initialParentData: { layer: Layer; dims: any } | undefined;
  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addMiddleware(`LayerDrag.dragStart`, this.layerDragStart);
    this.canvas.addMiddleware(`LayerDrag.drag`, this.layerDrag);
    this.canvas.addMiddleware(`LayerDrag.dragEnd`, this.layerDragEnd);

    this.canvas.addMiddleware(`LayerResizer.dragStart`, this.layerDragStart);
    this.canvas.addMiddleware(`LayerResizer.drag`, this.layerResizerDrag);
    this.canvas.addMiddleware(`LayerResizer.dragEnd`, this.layerResizerDragEnd);
    this.canvas.addFunction(`${this.namespace}.disable`, () => {
      this.disabled = true;
    });
  }
  saveLayersHeightWidth = (layer: Layer) => {
    if (!this.layersHeightWidth.get(layer)) {
      this.layersHeightWidth.set(layer, {
        height: layer.layout.get('height'),
        width: layer.layout.get('width'),
      });
    }
  };
  clearFlexLayersHeightWidth = () => {
    this.layersHeightWidth.clear();
  };
  saveSiblingOpacity = (parentLayer: Layer) => {
    forEach(parentLayer.children, (layer: Layer) => {
      if (layer.isFlexChild()) {
        let opacity = !isNil(layer.style.get('opacity'))
          ? layer.style.get('opacity')
          : 1;
        this.layerOpacities.set(layer, opacity);
        layer.setStyle('opacity', 0.3);
      }
    });
  };
  revertSiblingOpacity = () => {
    this.layerOpacities.forEach((opacity: any, layer: Layer) => {
      layer.setStyle('opacity', this.layerOpacities.get(layer));
    });
    this.layerOpacities.clear();
  };
  layerResizerDrag = (next: any, event: any) => {
    if (this.disabled) return;
    if (event.selectedLayers.length === 0) {
      return;
    }
    if (!this.isFlex) {
      return next(event);
    } else {
    }
  };
  layerResizerDragEnd = (next: any, event: any) => {
    if (this.disabled) return;
    if (event.selectedLayers.length === 0) {
      return;
    }
    this.revertParentDims();
    this.canvas.dispatchEvent(
      `${this.namespace}.dragEnd`,
      new Event(`${this.namespace}.dragEnd`, this.canvas)
    );
    if (!this.isFlex) {
      return next(event);
    }
  };
  revertParentDims = () => {
    if (this.initialParentData) {
      const layer = this.initialParentData.layer;
      const dims = this.initialParentData.dims;
      if (layer.isAutoHeight() || layer.isAutoWidth()) {
        layer.removeLayout(['position', 'top', 'left', 'right', 'bottom']);
        if (!dims.hasOwnProperty('height')) {
          layer.removeLayout('height');
        }
        if (!dims.hasOwnProperty('width')) {
          layer.removeLayout('width');
        }
        layer.setLayout(dims);
        this.initialParentData = undefined;
      }
    }
  };
  changeParentDims = (layer: Layer) => {
    if (
      !this.initialParentData &&
      (layer.isAutoHeight() || layer.isAutoWidth())
    ) {
      this.initialParentData = { layer, dims: layer.layout.getAll() };
      const dims = this.getMarginAdjustedBounds(layer);
      layer.setLayout({
        position: 'absolute',
        left: dims.x,
        top: dims.y,
        width: dims.width,
        height: dims.height,
      });
    }
  };
  /*
    ---> This is used to differentiate between both in the code wherever required:
    - Event from layerResizer is of type {data:Event,selectedLayers:current selected layers}
    - Event from layer drag is directly Event
    ---> On each drag start a ghost layer is created and everything else is handeled on that
         ghost layer only.
  */

  setFlexLayers = () => {
    let parent;
    if (this.target instanceof Layer) {
      parent = this.target.parent;
    } else {
      parent = this.target[0].parent;
    }
    if (parent) {
      this.flexLayers = [];
      forEach(parent.children, (layer: Layer) => {
        document.body.appendChild(transitionStyle);
        if (layer.isFlexChild()) {
          layer.setProp('id', 'transition');
          this.saveLayersHeightWidth(layer);
          this.flexLayers.push(layer);
        } else {
          this.flexLayers.push(undefined);
        }
      });
    }
  };

  createGhostLayer = (layout: any, parent: Layer) => {
    const dims = {
      width: layout.width,
      height: layout.height,
      left: layout.x,
      top: layout.y,
      position: 'absolute',
    };
    if (this.target instanceof Layer) {
      this.ghostLayer = this.canvas.createLayer();
      parent.addChild(this.ghostLayer);
      this.ghostLayer.setLayout(dims);
      this.ghostLayer.executeFunction('Hover.hideLayer');
      this.target.setStyle('opacity', this.layerOpacities.get(this.target));
      const layerStyles = { ...this.target.style.getAll() };
      this.ghostLayer.setStyle({ ...layerStyles, opacity: 0 });
    } else {
      const wrapperLayer = this.canvas.createLayer();
      parent.addChild(wrapperLayer);
      wrapperLayer.setLayout(dims);
      wrapperLayer.executeFunction('Hover.hideLayer');
      this.target.forEach((layer: Layer) => {
        const copyLayer = this.canvas.createLayer();
        wrapperLayer.addChild(copyLayer);
        const layerDims = this.getMarginAdjustedBounds(layer);
        const layerStyles = { ...layer.style.getAll() };
        layer.setStyle('opacity', this.layerOpacities.get(layer));
        const wrapperLayerDims = this.getMarginAdjustedBounds(wrapperLayer);
        copyLayer.setLayout({
          left: layerDims.x - wrapperLayerDims.x,
          top: layerDims.y - wrapperLayerDims.y,
          width: layerDims.width,
          height: layerDims.height,
          position: 'absolute',
        });
        copyLayer.setStyle({ ...layerStyles, opacity: 0 });
      });
      this.ghostLayer = wrapperLayer;
    }
  };

  startGhostDragging = (event: any) => {
    document.addEventListener('mousemove', this.ghostMove);
    document.addEventListener('mouseup', this.ghostMouseUp);
    document.addEventListener('contextmenu', this.ghostMouseUp);
    this.canvas.setDragging(true);
    const mouseDownEvent = new DesignerMouseEvent(
      'mousedown',
      this.canvas,
      event.originalEvent,
      this.canvas
    );
    this.canvas.dispatchEventOnHitLayer('mousedown', mouseDownEvent);
  };
  ghostMove = (e: any) => {
    this.canvas.handleNativeEvent(e);
  };
  ghostMouseUp = (e: any) => {
    document.removeEventListener('mousemove', this.ghostMove);
    document.removeEventListener('mouseup', this.ghostMouseUp);
    document.removeEventListener('contextmenu', this.ghostMouseUp);
    this.canvas.handleNativeEvent(e);
    this.canvas.setDragging(false);
  };
  getMarginAdjustedBounds = (layer: Layer) => {
    let initialBounds = layer.getBoundingClientRect();
    return {
      ...initialBounds,
      x: initialBounds.x - layer.getMarginLeft(),
      y: initialBounds.y - layer.getMarginTop(),
    };
  };
  endFlexLayerDragging = (event: any) => {
    const mouseUpEvent = new DesignerMouseEvent(
      'mouseup',
      this.canvas,
      event.originalEvent,
      this.canvas
    );
    this.canvas.dispatchEventOnHitLayer('mouseup', mouseUpEvent);
  };

  recordCurrentSnapshot = (parent: Layer) => {
    this.currentSnapshotData.clear();
    parent.children.forEach((child: Layer, index: number) => {
      if (child !== this.ghostLayer)
        this.currentSnapshotData.set(child, {
          index,
          dims: cloneDeep(this.getMarginAdjustedBounds(child)),
        });
    });
  };

  changeToAbsolute = (parent: Layer) => {
    parent.children.forEach((sibling: Layer) => {
      if (sibling !== this.ghostLayer) {
        const siblingDims = this.getMarginAdjustedBounds(sibling);
        sibling.setLayout({
          position: 'absolute',
          left: siblingDims.x,
          top: siblingDims.y,
          width: siblingDims.width,
          height: siblingDims.height,
        });
      }
    });
  };

  revertBackToSnapshot = (parent: Layer, snapshotData: Map<Layer, any>) => {
    snapshotData.forEach((value: any, layer: Layer) => {
      if (layer.getIndexInParent() !== value.index)
        layer.move(parent, value.index);
      if (this.flexLayers.includes(layer)) {
        layer.removeLayout(['position', 'top', 'left', 'right', 'bottom']);
      }
      const originalHW = this.layersHeightWidth.get(layer);
      if (this.flexLayers.includes(layer) && originalHW) {
        if (!originalHW.height && !isNil(layer.layout.get('height')))
          layer.removeLayout('height');
        if (!originalHW.width && !isNil(layer.layout.get('width')))
          layer.removeLayout('width');
      }
    });
  };

  findAbsolutePreviousLayer = (index: number) => {
    let previousLayer = undefined;
    let prevLayerIndex = index - 1;
    while (isNil(previousLayer) && prevLayerIndex >= 0) {
      if (this.flexLayers[prevLayerIndex]) {
        previousLayer = this.flexLayers[prevLayerIndex];
      }
      prevLayerIndex--;
    }
    return previousLayer;
  };
  findPreviousLayer = (index: number) => {
    if (this.flexDirection && this.flexDirection.includes('reverse')) {
      return this.findAbsoluteNextLayer(index);
    }
    return this.findAbsolutePreviousLayer(index);
  };
  findAbsoluteNextLayer = (index: any) => {
    let nextLayer = undefined;
    let nextLayerIndex = index + 1;
    while (isNil(nextLayer) && nextLayerIndex < this.flexLayers.length) {
      if (this.flexLayers[nextLayerIndex]) {
        nextLayer = this.flexLayers[nextLayerIndex];
      }
      nextLayerIndex++;
    }
    return nextLayer;
  };
  findNextLayer = (index: number) => {
    if (this.flexDirection && this.flexDirection.includes('reverse')) {
      return this.findAbsolutePreviousLayer(index);
    }
    return this.findAbsoluteNextLayer(index);
  };

  setTriggerPoints = () => {
    this.previousTriggerPoint = null;
    this.nextTriggerPoint = null;
    if (this.target instanceof Layer) {
      const targetIndexInParent = indexOf(this.flexLayers, this.target);
      if (targetIndexInParent !== -1) {
        let previousLayer = this.findPreviousLayer(targetIndexInParent);
        let nextLayer = this.findNextLayer(targetIndexInParent);
        const currentTargetDims = this.getMarginAdjustedBounds(this.target);
        if (previousLayer) {
          const previousLayerDims = this.getMarginAdjustedBounds(previousLayer);
          this.previousTriggerPoint = this.calculateTriggerPoint(
            previousLayerDims,
            currentTargetDims
          );
        }

        if (nextLayer) {
          const nextLayerDims = this.getMarginAdjustedBounds(nextLayer);
          this.nextTriggerPoint = this.calculateTriggerPoint(
            currentTargetDims,
            nextLayerDims
          );
        }
      }
    } else {
      const sortedSelectedLayers = sortBy(this.target, (layer: Layer) =>
        layer.getIndexInParent()
      );

      const firstSelectedLayerIndex = indexOf(
        this.flexLayers,
        sortedSelectedLayers[0]
      );
      const lastSelectedLayerIndex = indexOf(
        this.flexLayers,
        sortedSelectedLayers[sortedSelectedLayers.length - 1]
      );

      if (firstSelectedLayerIndex !== -1 && lastSelectedLayerIndex !== -1) {
        const previousLayer = this.findPreviousLayer(firstSelectedLayerIndex);
        const nextLayer = this.findNextLayer(lastSelectedLayerIndex);

        const boundingBoxDims = this.canvas.getBoundingBox(
          sortedSelectedLayers
        );
        const parentDims =
          sortedSelectedLayers.length && sortedSelectedLayers[0].parent
            ? this.getMarginAdjustedBounds(sortedSelectedLayers[0].parent)
            : undefined;

        if (parentDims) {
          const boundingBoxDimsWRTParent = {
            ...boundingBoxDims,
            x: boundingBoxDims.x - parentDims.rootX,
            y: boundingBoxDims.y - parentDims.rootY,
          };
          if (previousLayer) {
            const previousLayerDims = this.getMarginAdjustedBounds(
              previousLayer
            );
            this.previousTriggerPoint = this.calculateTriggerPoint(
              previousLayerDims,
              boundingBoxDimsWRTParent
            );
          }

          if (nextLayer) {
            const nextLayerDims = this.getMarginAdjustedBounds(nextLayer);
            this.nextTriggerPoint = this.calculateTriggerPoint(
              boundingBoxDimsWRTParent,
              nextLayerDims
            );
          }
        }
      }
    }
  };

  calculateTriggerPoint = (
    previousLayerDims: IDimensionWithRotation,
    nextLayerDims: IDimensionWithRotation
  ) => {
    if (this.flexDirection === 'row' || this.flexDirection === 'row-reverse') {
      return (previousLayerDims.x + nextLayerDims.x + nextLayerDims.width) / 2;
    } else {
      return (nextLayerDims.y + nextLayerDims.height + previousLayerDims.y) / 2;
    }
  };

  handleSwapWRTNewPosition = (oldCenter: number, newCenter: number) => {
    const crossedPreviousTriggerPoint =
      this.previousTriggerPoint &&
      oldCenter > this.previousTriggerPoint &&
      newCenter < this.previousTriggerPoint;

    const crossedNextTriggerPoint =
      this.nextTriggerPoint &&
      oldCenter < this.nextTriggerPoint &&
      newCenter > this.nextTriggerPoint;
    let targetLayerIndex;
    let newIndex;
    let toMoveLayer;
    if (this.target instanceof Layer) {
      targetLayerIndex = indexOf(this.flexLayers, this.target);
      newIndex = targetLayerIndex;
      toMoveLayer = crossedPreviousTriggerPoint
        ? this.findPreviousLayer(targetLayerIndex)
        : crossedNextTriggerPoint
        ? this.findNextLayer(targetLayerIndex)
        : null;
      if (toMoveLayer)
        this.newTargetIndex = indexOf(this.flexLayers, toMoveLayer);
    } else {
      const sortedSelectedLayers = sortBy(this.target, (layer: Layer) =>
        layer.getIndexInParent()
      );

      const firstSelectedLayerIndex = indexOf(
        this.flexLayers,
        sortedSelectedLayers[0]
      );
      const lastSelectedLayerIndex = indexOf(
        this.flexLayers,
        sortedSelectedLayers[sortedSelectedLayers.length - 1]
      );
      if (crossedPreviousTriggerPoint) {
        newIndex = lastSelectedLayerIndex;
        toMoveLayer = this.findPreviousLayer(firstSelectedLayerIndex);
        if (toMoveLayer)
          this.newTargetIndex = indexOf(this.flexLayers, toMoveLayer);
      } else if (crossedNextTriggerPoint) {
        newIndex = firstSelectedLayerIndex;
        toMoveLayer = this.findNextLayer(lastSelectedLayerIndex);
        if (toMoveLayer)
          this.newTargetIndex = indexOf(this.flexLayers, toMoveLayer);
      }
    }
    if (!isNil(newIndex) && newIndex >= 0 && toMoveLayer) {
      this.internalMove(toMoveLayer, newIndex);
    }
  };

  internalMove = (targetLayer: Layer, newIndex: number) => {
    let parent = targetLayer.parent;
    if (parent) {
      this.revertBackToSnapshot(parent, this.currentSnapshotData);
      targetLayer.move(parent, newIndex);
      const newPositionData: Map<Layer, any> = new Map();
      parent.children.forEach((child: Layer) => {
        if (child !== this.ghostLayer) {
          newPositionData.set(child, this.getMarginAdjustedBounds(child));
        }
      });
      this.recordCurrentSnapshot(parent);
      this.setFlexLayers();
      this.setTriggerPoints();
      this.revertBackToSnapshot(parent, this.beforeDragSnapshotData);
      this.changeToAbsolute(parent);
      parent.children.forEach((child: Layer) => {
        const newChildLayout = newPositionData.get(child);
        if (child !== this.ghostLayer && newChildLayout) {
          child.setLayout({
            left: newChildLayout.x,
            top: newChildLayout.y,
          });
        }
      });
    }
  };
  layerDragStart = (next: any, event: any) => {
    if (this.disabled) return;
    this.totalDx = 0;
    this.totalDy = 0;
    this.canvas.dispatchEvent(
      `${this.namespace}.dragStart`,
      new Event(`${this.namespace}.dragStart`, this.canvas)
    );
    if (event.selectedLayers) {
      const isFlex = event.selectedLayers.every((layer: Layer) =>
        layer.isFlexChild()
      );
      this.isFlex = isFlex;
      if (!isFlex) {
        return next(event);
      }
      this.target = event.selectedLayers;

      const parent: Layer = get(event.selectedLayers, '[0].parent');
      const boundingBoxDims = this.canvas.getBoundingBox(event.selectedLayers);
      if (parent) {
        const parentDims = this.getMarginAdjustedBounds(parent);
        this.saveSiblingOpacity(parent);
        const boundingBoxDimsWRTParent = {
          ...boundingBoxDims,
          x: boundingBoxDims.x - parentDims.rootX,
          y: boundingBoxDims.y - parentDims.rootY,
        };
        this.targetInitialDims = boundingBoxDimsWRTParent;
        this.flexDirection = parent.layout.get('flexDirection');
        this.setFlexLayers();
        this.recordCurrentSnapshot(parent);
        this.initialSnapshotData = new Map(this.currentSnapshotData);
        this.beforeDragSnapshotData = new Map(this.currentSnapshotData);
        this.endFlexLayerDragging(event.data);
        this.createGhostLayer(this.targetInitialDims, parent);
        this.startGhostDragging(event.data);
        this.changeParentDims(parent);
        this.changeToAbsolute(parent);
        this.setTriggerPoints();
      }
    } else {
      if (!event || !(event && event.target)) {
        return next(event);
      }
      const isFlex = event.target ? event.target.isFlexChild() : false;
      this.isFlex = isFlex;
      if (event.target === this.ghostLayer) {
        this.targetGhostLayer = event.target;
      }
      if (!isFlex) {
        return next(event);
      }
      this.target = event.target;
      this.targetInitialDims = this.getMarginAdjustedBounds(event.target);
      const parent: Layer = event.target.parent;
      if (parent) {
        this.saveSiblingOpacity(parent);
        this.flexDirection = parent.layout.get('flexDirection');
        this.setFlexLayers();
        this.recordCurrentSnapshot(parent);
        this.initialSnapshotData = new Map(this.currentSnapshotData);
        this.beforeDragSnapshotData = new Map(this.currentSnapshotData);
        this.endFlexLayerDragging(event);
        this.createGhostLayer(this.targetInitialDims, parent);
        this.startGhostDragging(event);
        this.changeParentDims(parent);
        this.changeToAbsolute(parent);
        this.setTriggerPoints();
      }
    }
  };

  layerDrag = (next: any, event: any) => {
    if (this.disabled) return;
    // if (!this.isFlex) {
    //   return next(event);
    // }
    this.canvas.dispatchEvent(
      `${this.namespace}.drag`,
      new Event(`${this.namespace}.drag`, this.canvas)
    );
    if (this.targetGhostLayer) {
      this.canvas.selectLayers([]);
      this.totalDx += event.worldDx;
      this.totalDy += event.worldDy;

      let targetNewDims;
      if (this.target instanceof Layer) {
        targetNewDims = this.getMarginAdjustedBounds(this.target);
      } else {
        const boundingBoxDims = this.canvas.getBoundingBox(this.target);
        const parentDims = this.target[0].parent
          ? this.getMarginAdjustedBounds(this.target[0].parent)
          : undefined;
        if (parentDims) {
          targetNewDims = {
            ...boundingBoxDims,
            x: boundingBoxDims.x - parentDims.rootX,
            y: boundingBoxDims.y - parentDims.rootY,
          };
        } else {
          targetNewDims = boundingBoxDims;
        }
      }

      const newLeft = this.targetInitialDims.x + this.totalDx;
      const newTop = this.targetInitialDims.y + this.totalDy;
      this.ghostLayer.setLayout({ left: newLeft, top: newTop });

      let oldCenterTargetLayer = null;
      let newCenterTargetLayer = null;
      if (
        this.flexDirection === 'row' ||
        this.flexDirection === 'row-reverse'
      ) {
        oldCenterTargetLayer = targetNewDims.x + targetNewDims.width / 2;
        newCenterTargetLayer = newLeft + this.targetInitialDims.width / 2;
      } else {
        oldCenterTargetLayer = targetNewDims.y + targetNewDims.height / 2;
        newCenterTargetLayer = newTop + this.targetInitialDims.height / 2;
      }
      this.handleSwapWRTNewPosition(oldCenterTargetLayer, newCenterTargetLayer);
    } else {
      next(event);
    }
  };
  layerDragEnd = (next: any, event: any) => {
    if (this.disabled) return;
    if (this.targetGhostLayer) {
      this.isFlex = false;
      const parent = this.targetGhostLayer.parent;
      this.revertSiblingOpacity();
      if (parent) {
        while (this.ghostLayer.children.length !== 0) {
          this.ghostLayer.removeChild(this.ghostLayer.children[0]);
        }
        parent.removeChild(this.ghostLayer);
        delete this.ghostLayer;
        this.revertBackToSnapshot(parent, this.beforeDragSnapshotData);
      }
      document.body.removeChild(transitionStyle);
      delete this.targetGhostLayer;
      if (this.target instanceof Layer) {
        if (
          !isNil(this.newTargetIndex) &&
          this.target.getIndexInParent() !== this.newTargetIndex
        ) {
          this.canvas.dispatchEvent(
            'moveLayer',
            new CommandEvent('moveLayer', this.canvas, {
              fromParent: this.target.parent,
              toParent: this.target.parent,
              fromIndex: this.target.getIndexInParent(),
              toIndex: this.newTargetIndex,
            })
          );
        }
        this.target.parent?.children.forEach((sibling: Layer) => {
          sibling.setProp('id', undefined);
        });
      } else {
        let sortedSelectedLayers = sortBy(this.target, (layer: Layer) =>
          layer.getIndexInParent()
        );

        if (
          !isNil(this.newTargetIndex) &&
          sortedSelectedLayers[0].getIndexInParent() > this.newTargetIndex
        ) {
          sortedSelectedLayers = sortedSelectedLayers.reverse();
        }
        let newToSelectLayers: Array<Layer> = [];
        if (
          !isNil(this.newTargetIndex) &&
          sortedSelectedLayers[0].getIndexInParent() !== this.newTargetIndex
        ) {
          sortedSelectedLayers.forEach((layer: Layer) => {
            const parent = layer.parent;
            this.canvas.dispatchEvent(
              'moveLayer',
              new CommandEvent('moveLayer', this.canvas, {
                fromParent: parent,
                toParent: parent,
                fromIndex: layer.getIndexInParent(),
                toIndex: this.newTargetIndex,
              })
            );
            if (parent && !isNil(this.newTargetIndex))
              newToSelectLayers.push(parent.children[this.newTargetIndex]);
          });
        }

        if (newToSelectLayers.length)
          this.canvas.selectLayers(newToSelectLayers);
        else this.canvas.selectLayers(this.target);

        this.target[0].parent?.children.forEach((sibling: Layer) => {
          sibling.setProp('id', undefined);
        });
      }
      this.clearFlexLayersHeightWidth();
      this.revertParentDims();
      delete this.newTargetIndex;
      this.canvas.dispatchEvent(
        `${this.namespace}.dragEnd`,
        new Event(`${this.namespace}.dragEnd`, this.canvas)
      );
    } else {
      return next(event);
    }
  };

  deactivate() {}
}
