import { Canvas, IPlugin } from '../../';

export default class ScrollMousePositionPlugin implements IPlugin {
  name = 'ScrollMousePositionPlugin';
  canvas!: Canvas;
  dispose: any;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  activate() {
    const wrapperLayer = document.createElement('div');
    this.dispose = this.canvas.addDomToViewport(wrapperLayer);

    const positionElement = document.createElement('div');
    wrapperLayer.appendChild(positionElement);
    positionElement.style.position = 'absolute';
    positionElement.style.top = '10px';
    positionElement.style.right = '10px';
    positionElement.style.height = '95px';
    positionElement.style.width = '245px';
    positionElement.style.pointerEvents = 'none';
    positionElement.style.backgroundColor = 'skyblue';
    positionElement.style.zIndex = '10';
    let mousePosWorldNode = document.createTextNode(
      ` Mouse World: {x:0, y:0} `
    );
    positionElement.appendChild(mousePosWorldNode);
    positionElement.appendChild(document.createElement('br'));
    let mousePosViewportNode = document.createTextNode(
      `Mouse Viewport: {x: 0, y:0} `
    );
    positionElement.appendChild(mousePosViewportNode);

    positionElement.appendChild(document.createElement('br'));
    let pagePosPageNode = document.createTextNode(`Mouse Page: {x: 0, y:0} `);
    positionElement.appendChild(pagePosPageNode);
    positionElement.appendChild(document.createElement('br'));

    let scroll = this.canvas.getScroll();
    let scrollPosNode = document.createTextNode(
      `Scroll: {x:${scroll.x}, y:${scroll.y}}`
    );
    positionElement.appendChild(scrollPosNode);
    positionElement.appendChild(document.createElement('br'));

    let zoom = this.canvas.getZoom();
    let zoomNode = document.createTextNode(`Zoom: ${Number(zoom.toFixed(2))}`);
    positionElement.appendChild(zoomNode);

    this.canvas.on('scroll', () => {
      scroll = this.canvas.getScroll();
      scrollPosNode.nodeValue = `Scroll: {x:${Number(
        scroll.x.toFixed(2)
      )}, y:${Number(scroll.y.toFixed(2))}}`;
    });
    this.canvas.on('zoom', (e: any) => {
      zoom = this.canvas.getZoom();
      zoomNode.nodeValue = `Zoom: ${Number(zoom.toFixed(2))}`;
    });
    this.canvas.addEventListener('mousemove', (e: any) => {
      mousePosWorldNode.nodeValue = `Mouse W: {x:${Number(
        e.worldX.toFixed(2)
      )}, y:${Number(e.worldY.toFixed(2))}}`;
      mousePosViewportNode.nodeValue = `Mouse VP: {x:${Number(
        e.viewportX.toFixed(2)
      )}, y:${Number(e.viewportY.toFixed(2))}}`;

      pagePosPageNode.nodeValue = `Mouse P: {x:${Number(
        e.pageX.toFixed(2)
      )}, y:${Number(e.pageY.toFixed(2))}}`;
    });
  }

  deactivate() {
    this.dispose();
  }
}
