import { Canvas, Layer } from '../../';
import { IPlugin } from '../../';
import DragEvent from './DragEvent';

export default class Drag implements IPlugin {
  name = 'Drag';
  canvas!: Canvas;
  dispose: any;
  mouseDown: boolean = false;
  dragStarted: boolean = false;
  target: Layer | undefined;
  offsetPageX: number = 0;
  offsetPageY: number = 0;
  lastPageX: number = 0;
  lastPageY: number = 0;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
    this.offsetPageX = 0;
    this.offsetPageY = 0;
  }

  activate() {
    let relPos = {
      x: 0,
      y: 0,
    };
    this.canvas.addEventListener('mousedown', (event: any) => {
      if (event.originalEvent.button !== 2 && event.originalEvent.which !== 3) {
        // Ignore right click mousedown
        this.mouseDown = true;
        this.dragStarted = false;
        this.lastPageX = event.worldX;
        this.lastPageY = event.worldY;
      }
    });

    this.canvas.addEventListener('mousemove', (event: any) => {
      relPos = {
        x: event.worldX - this.lastPageX,
        y: event.worldY - this.lastPageY,
      };

      if (this.mouseDown && !this.dragStarted) {
        this.dragStarted = true;
        let payload = {
          worldDx: relPos.x,
          worldDy: relPos.y,
          worldX: this.lastPageX,
          worldY: this.lastPageY,
        };
        const dragStartEvent = new DragEvent(
          'dragStart',
          this.canvas,
          event.originalEvent,
          payload
        );
        this.canvas.dispatchEventOnHitLayer('dragStart', dragStartEvent, event);
      } else if (this.dragStarted) {
        let payload = {
          worldDx: relPos.x,
          worldDy: relPos.y,
          worldX: event.worldX,
          worldY: event.worldY,
        };
        const dragEvent = new DragEvent(
          'drag',
          this.canvas,
          event.originalEvent,
          payload
        );
        this.canvas.dispatchEventOnHitLayer('drag', dragEvent, event);
        this.lastPageX = event.worldX;
        this.lastPageY = event.worldY;
      }
    });

    this.canvas.addEventListener('mouseup', (event: any) => {
      relPos = {
        x: event.worldX - this.lastPageX,
        y: event.worldY - this.lastPageY,
      };
      let payload = {
        worldDx: 0,
        worldDy: 0,
        worldX: event.worldX,
        worldY: event.worldY,
      };
      if (this.dragStarted) {
        const dragEndEvent = new DragEvent(
          'dragEnd',
          this.canvas,
          event.originalEvent,
          payload
        );
        this.canvas.dispatchEventOnHitLayer('dragEnd', dragEndEvent, event);
      }
      this.lastPageX = 0;
      this.lastPageY = 0;
      this.mouseDown = false;
      this.dragStarted = false;
    });
  }

  deactivate() {
    this.dispose();
  }
}
