import * as React from 'react';
import AndroidRenderer from './android/AndroidSwitchRenderer';
import IOSRenderer from './iOS/IOSSwitchRenderer';
import { Layer } from 'models/Layer';
import RenderEvent from '../../events/RenderEvent';

const rerenderProps = ['layout', 'props', 'style', 'visible'];
export default class SwitchRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
  }

  render() {
    let platform = 'iOS';
    const deviceData = this.props.layer.executeFunction('getDeviceData');

    if (deviceData) {
      platform = deviceData.platform;
    }

    return platform === 'Android' ? (
      <AndroidRenderer {...this.props} />
    ) : (
      <IOSRenderer {...this.props} />
    );
  }
}
