import { forEach, isFunction } from 'lodash';
interface Points {
  x: number;
  y: number;
}

export interface StopPoints {
  stopColor: string;
  stopOpacity?: string;
  offset: number;
}

export class Gradient {
  gradientType: 'LinearGradient' | 'RadialGradient' | 'AngularGradient';
  elipseLength?: number;
  from: Points;
  to: Points;
  stops: Array<StopPoints>;
  activeIndex?: number = 0;
  constructor(
    gradientType: 'LinearGradient' | 'RadialGradient' | 'AngularGradient',
    from: Points,
    to: Points,
    stops: Array<StopPoints>,
    activeIndex?: number,
    elipseLength?: number
  ) {
    this.gradientType = gradientType;
    this.from = from;
    this.to = to;
    this.stops = stops;
    this.activeIndex = activeIndex;
    this.elipseLength = elipseLength;
  }

  toJS() {
    const gradientJSON: any = {};

    forEach(this, (value: any, key: any) => {
      // if (![""].includes(key)) {
      if (!isFunction(value)) {
        gradientJSON[key] = value && value.toJS ? value.toJS() : value;
      }
      // }
      // }
    });
    return gradientJSON;
  }

  rotate(cx: number, cy: number, x: number, y: number, angle: number) {
    let radians = (Math.PI / 180) * angle;
    let cos = Math.cos(radians);
    let sin = Math.sin(radians);
    let nx = cos * (x - cx) + sin * (y - cy) + cx;
    let ny = cos * (y - cy) - sin * (x - cx) + cy;
    return { x: Math.round(nx), y: Math.round(ny) };
  }

  rotateGradient(angle: number) {
    const cx = (this.from.x + this.to.x) / 2;
    const cy = (this.from.y + this.to.y) / 2;

    const nFrom = this.rotate(cx, cy, this.from.x, this.from.y, angle);
    const nTo = this.rotate(cx, cy, this.to.x, this.to.y, angle);

    const rotatedFrom = { x: nFrom.x, y: nFrom.y };
    const rotatedTo = { x: nTo.x, y: nTo.y };

    this.from = rotatedFrom;
    this.to = rotatedTo;
  }

  getGradientString = (palette?: any) => {
    let colors: any = [];

    if (palette) {
      forEach(palette, (data: any, index: number) => {
        colors.push(data.color);
      });
    } else {
      forEach(this.stops, (data: any, index: number) => {
        colors.push(data.stopColor);
      });
    }
    let vector;
    if (this.to && this.from) {
      vector = {
        x: (this.to.x as number) - (this.from.x as number),
        y: (this.to.y as number) - (this.from.y as number),
      };
    } else {
      vector = {
        x: 0,
        y: 2,
      };
    }
    const angleRad = Math.atan2(vector.y, vector.x);
    const angleDeg = (angleRad * 180) / Math.PI + 90;

    let realLocations: any = [];
    if (palette) {
      forEach(palette, (data: any, index: number) => {
        realLocations.push(data.pos);
      });
    } else {
      forEach(this.stops, (data: any, index: number) => {
        realLocations.push(data.offset);
      });
    }

    const colorStrings = colors
      .map(
        (color: any, i: number) =>
          `${color} ${Math.round(realLocations[i] * 100)}%`
      )
      .join(', ');

    if (this.gradientType && this.gradientType === 'LinearGradient') {
      return `linear-gradient(${angleDeg}deg, ${colorStrings})`;
    } else if (this.gradientType && this.gradientType === 'RadialGradient') {
      return `radial-gradient(${colorStrings})`;
    } else if (this.gradientType && this.gradientType === 'AngularGradient') {
      return `conic-gradient(${colorStrings})`;
    } else {
      return `linear-gradient(${angleDeg}deg, ${colorStrings})`;
    }
  };
}
