import { Layer, Event } from '../../..';

export default class GradientEditorEvent extends Event {
  task?: 'open' | 'close';
  constructor(
    name: string,
    target: Layer,
    task?: 'open' | 'close',
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    this.task = task;
  }
}
