export function getResizerQuad(rotation: number): string {
  rotation = rotation % 360;
  if (rotation < 0) {
    rotation += 360;
  }
  if (!rotation) {
    return '4';
  }
  if (
    (rotation >= 22.5 && rotation < 67.5) ||
    (rotation >= 202.5 && rotation < 247.5)
  ) {
    return '1';
  }

  if (
    (rotation >= 67.5 && rotation < 112.5) ||
    (rotation >= 247.5 && rotation < 292.5)
  ) {
    return '2';
  }
  if (
    (rotation >= 112.5 && rotation < 157.5) ||
    (rotation >= 292.5 && rotation < 337.5)
  ) {
    return '3';
  }

  if (
    (rotation >= 157.5 && rotation < 202.5) ||
    (rotation >= 337.5 && rotation < 22.5)
  ) {
    return '4';
  }
  return '4';
}

export function getCursor(handleName: string, rotation: any) {
  let resizerQuad = getResizerQuad(rotation);
  let cursor = 'default';
  if (handleName === 'T' || handleName === 'B') {
    if (resizerQuad === '1') {
      cursor = 'nesw-resize';
    } else if (resizerQuad === '2') {
      cursor = 'ew-resize';
    } else if (resizerQuad === '3') {
      cursor = 'nwse-resize';
    } else if (resizerQuad === '4') {
      cursor = 'ns-resize';
    } else {
      cursor = 'ns-resize';
    }
  } else if (handleName === 'L' || handleName === 'R') {
    if (resizerQuad === '1') {
      cursor = 'nwse-resize';
    } else if (resizerQuad === '2') {
      cursor = 'ns-resize';
    } else if (resizerQuad === '3') {
      cursor = 'nesw-resize';
    } else if (resizerQuad === '4') {
      cursor = 'ew-resize';
    } else {
      cursor = 'ew-resize';
    }
  } else if (handleName === 'TL' || handleName === 'BR') {
    if (resizerQuad === '1') {
      cursor = 'ns-resize';
    } else if (resizerQuad === '2') {
      cursor = 'nesw-resize';
    } else if (resizerQuad === '3') {
      cursor = 'ew-resize';
    } else if (resizerQuad === '4') {
      cursor = 'nwse-resize';
    } else {
      cursor = 'nwse-resize';
    }
  } else if (handleName === 'TR' || handleName === 'BL') {
    if (resizerQuad === '1') {
      cursor = 'ew-resize';
    } else if (resizerQuad === '2') {
      cursor = 'nwse-resize';
    } else if (resizerQuad === '3') {
      cursor = 'ns-resize';
    } else if (resizerQuad === '4') {
      cursor = 'nesw-resize';
    } else {
      cursor = 'nesw-resize';
    }
  }
  return cursor;
}

export function getRotationQuad(rotation: number) {
  rotation = rotation % 360;
  if (rotation < 0) {
    rotation = -rotation;
  }
  if (rotation >= 0 && rotation < 45) {
    return 1;
  }
  if (rotation >= 45 && rotation < 90) {
    return 2;
  }
  if (rotation >= 90 && rotation < 135) {
    return 3;
  }
  if (rotation >= 135 && rotation < 180) {
    return 4;
  }
  if (rotation >= 180 && rotation < 225) {
    return 5;
  }
  if (rotation >= 225 && rotation < 270) {
    return 6;
  }
  if (rotation >= 270 && rotation < 315) {
    return 7;
  }
  if (rotation >= 315 && rotation < 360) {
    return 8;
  }
  return 1;
}

export function getInitialRotation(handleName: string) {
  switch (handleName) {
    case 'TL':
      return 0;
    case 'T':
      return 45;
    case 'TR':
      return 90;
    case 'R':
      return 135;
    case 'BR':
      return 180;
    case 'B':
      return 225;
    case 'BL':
      return 270;
    case 'L':
      return 315;
    default:
      return 0;
  }
}

