import { Canvas, IPlugin } from '../../';
import { IDimensionWithRotation, IPosition } from 'interfaces';
import ModelUtils from './../../models/utils';
import NativeHtmlDragEvent from './NativeHtmlDragEvent';
export default class NativeHtmlDrag implements IPlugin {
  name = 'NativeHtmlDrag';
  namespace = 'NativeHtmlDrag';
  canvas!: Canvas;
  mouseDown: boolean = false;
  dragStarted: boolean = false;
  dragDom: any;
  boundMouseMove: any;
  boundMouseUp: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  onMouseMove = (dragDom: any, e: any) => {
    if (this.mouseDown && !this.dragStarted) {
      this.dragStarted = true;
      var event = new MouseEvent('htmlDragStart', e);
      dragDom.dispatchEvent(event);
    } else if (this.dragStarted) {
      var event = new MouseEvent('htmlDrag', e);
      dragDom.dispatchEvent(event);
    }
  };

  disableSelect = (event: Event) => {
    event.preventDefault();
  };
  onMouseUp = (dragDom: any, e: any) => {
    e.preventDefault();
    if (this.dragStarted) {
      var event = new MouseEvent('htmlDragEnd', e);
      dragDom.dispatchEvent(event);
    }

    this.mouseDown = false;
    this.dragStarted = false;
    dragDom.ownerDocument.removeEventListener('mousemove', this.boundMouseMove);
    dragDom.ownerDocument.removeEventListener('mouseup', this.boundMouseUp);
    dragDom.ownerDocument.removeEventListener('contextmenu', this.boundMouseUp);
    window.removeEventListener('selectstart', this.disableSelect);
  };
  handleHtmlDrag(context: any, dragDom: any) {
    this.dragDom = dragDom;
    dragDom.addEventListener('mousedown', (event: any) => {
      window.addEventListener('selectstart', this.disableSelect);

      // event.preventDefault();
      if (event.button !== 2 && event.which !== 3) {
        // Ignore right click mousedown
        this.mouseDown = true;
        this.dragStarted = false;

        if (dragDom && dragDom.ownerDocument) {
          this.boundMouseMove = this.onMouseMove.bind(this, dragDom);
          this.boundMouseUp = this.onMouseUp.bind(this, dragDom);
          dragDom.ownerDocument.addEventListener(
            'mousemove',
            this.boundMouseMove
          );
          dragDom.ownerDocument.addEventListener('mouseup', this.boundMouseUp);
          dragDom.ownerDocument.addEventListener(
            'contextmenu',
            this.boundMouseUp
          );
        }
      }
    });

    // dragDom.addEventListener('mousemove', (e: any) => {
    //   if (this.mouseDown && !this.dragStarted) {
    //     this.dragStarted = true;
    //     var event = new MouseEvent('htmlDragStart', e);
    //     dragDom.dispatchEvent(event);
    //   } else if (this.dragStarted) {
    //     var event = new MouseEvent('htmlDrag', e);
    //     dragDom.dispatchEvent(event);
    //   }
    // });

    // dragDom.addEventListener('mouseup', (e: any) => {
    //   e.preventDefault();
    //   if (this.dragStarted) {
    //     var event = new MouseEvent('htmlDragEnd', e);
    //     dragDom.dispatchEvent(event);
    //   }

    //   this.mouseDown = false;
    //   this.dragStarted = false;
    // });
  }

  activate() {
    this.canvas.addFunction(`handleHtmlDrag`, this.handleHtmlDrag.bind(this));
  }

  deactivate() {
    //
    this.canvas.removeFunction(`handleHtmlDrag`);
  }
}
