import { Layer } from '../models/Layer';
import { Event } from './index';
import { Canvas } from 'models/Canvas';
export default class SetZoomEvent extends Event {
  zoom: number;

  constructor(
    name: string,
    target: Layer,
    zoom: number,
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    this.zoom = zoom;
  }
}
