import { Layer } from '../models/Layer';
import { last } from 'lodash';

class GlobalLayerUpdateController {
  layerMap = new Map<Layer, any>();
  get updateLayout(): boolean {
    return this.updateLayoutCount === 0 ? true : false;
  }
  get updateChildrenLayout(): boolean {
    return this.updateChildrenLayoutCount === 0 ? true : false;
  }
  get rerender(): boolean {
    return this.rerenderCount === 0 ? true : false;
  }

  shouldBatch: Array<boolean> = [];
  updateLayoutCount: number = 0;
  updateChildrenLayoutCount: number = 0;
  rerenderCount: number = 0;

  shouldAddToBatch = () => {
    return last(this.shouldBatch);
  };
  stopChildrenLayoutUpdates = () => {
    this.updateChildrenLayoutCount++;
  };
  startChildrenLayoutUpdates = () => {
    this.updateChildrenLayoutCount--;
  };
  stopRerender = () => {
    this.rerenderCount++;
  };
  startRerender = () => {
    this.rerenderCount--;
  };

  stopAndBatchLayoutUpdates = () => {
    if (this.shouldAddToBatch() === false) {
      this.shouldBatch.push(false);
    } else {
      this.shouldBatch.push(true);
    }
    this.updateLayoutCount++;
  };

  stopAndForgetLayoutUpdates = () => {
    this.shouldBatch.push(false);
    this.updateLayoutCount++;
  };

  resumeLayoutUpdates = () => {
    this.updateLayoutCount--;
    if (this.updateLayoutCount === 0) {
      this.layerMap.forEach((value, layer) => {
        layer.setYogaAfterBatch();
        layer.onUpdateDom();
      });
      this.shouldBatch = [];
      this.layerMap.clear();
    }
  };
}

export const globalLayerUpdateController = new GlobalLayerUpdateController();

export const stopChildrenLayoutUpdates =
  globalLayerUpdateController.stopChildrenLayoutUpdates;
export const startChildrenLayoutUpdates =
  globalLayerUpdateController.startChildrenLayoutUpdates;
export const stopRerender = globalLayerUpdateController.stopRerender;
export const startRerender = globalLayerUpdateController.startRerender;
export const shouldAddToBatch = globalLayerUpdateController.shouldAddToBatch;
export const stopAndBatchLayoutUpdates =
  globalLayerUpdateController.stopAndBatchLayoutUpdates;
export const stopAndForgetLayoutUpdates =
  globalLayerUpdateController.stopAndForgetLayoutUpdates;
export const resumeLayoutUpdates =
  globalLayerUpdateController.resumeLayoutUpdates;
