import { Canvas, IPlugin, Layer, IContext } from '../..';
import DesignerMouseEvent from '../../events/MouseEvent';
export default class PlusButton implements IPlugin {
  name = 'PlusButton';
  canvas!: Canvas;
  dispose: any;
  dom: any;
  passedEvent = ['mouseup', 'mousemove', 'drop'];

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }
  activate() {
    this.canvas.addFunction('addPlusButton', this.addPlusButton.bind(this));
    this.canvas.addFunction('showPlusButton', this.showPlusButton.bind(this));
    this.canvas.addFunction('hidePlusButton', this.hidePlusButton.bind(this));
    this.canvas.addFunction(
      'removePlusButton',
      this.removePlusButton.bind(this)
    );

    this.canvas.on('zoom', this.handleZoom.bind(this));
    this.canvas.addEventListener(
      'artboardLayoutChanges',
      this.positionPlusButton.bind(this)
    );
  }

  passEvent(e: Event) {
    e.preventDefault();
    this.canvas.dispatchEventOnHitLayer(
      e.type,
      new DesignerMouseEvent(e.type, this.canvas, e, this.canvas),
      e
    );
  }

  handleLayerListeners(handle: any) {
    this.passedEvent.map(event => {
      handle.addEventListener(event, (e1: any) => {
        this.passEvent(e1);
      });
    });
  }

  positionPlusButton() {
    if (!this.dom) return;
    var zoom = 1;
    var artboards = this.canvas.executeFunction('getAllArtboards');

    let left = Number.MIN_VALUE;
    let artLeft = Number.MIN_VALUE;
    let artTop = Number.MIN_VALUE;
    let top = Number.MIN_VALUE;

    if (artboards.size === 0) {
      this.dom.style.display = 'none';
    }
    // else {
    //   this.dom.style.display = 'inline-block';
    // }

    if (artboards.size === 0) {
      position = { left: 300, top: 50, artTop: 50, artLeft: 300 };
    }
    artboards.forEach((artboard: Layer) => {
      const artboardClientRect = artboard.getBoundingClientRect();
      const artMaxX = artboardClientRect.x + artboardClientRect.width;
      const artMidY = artboardClientRect.y + artboardClientRect.height / 2;
      if (left < artMaxX) {
        left = artMaxX;
        top = artMidY;
        artTop = artboardClientRect.y;
      }
    });
    artLeft = left + 50;
    left = left * zoom + 50;
    top = top * zoom - 40;
    var position = { left, top, artTop, artLeft };

    this.dom.style.top = position.top + 'px';
    this.dom.style.left = position.left + 'px';
  }

  handleZoom() {
    if (this.dom) {
      let zoom = this.canvas.getZoom();
      if (zoom > 0.5) {
        this.dom.style.transform = 'scale(' + 1 / zoom + ')';
      }
    }
  }

  removePlusButton() {
    if (this.dom) this.canvas.removeDomFromWorld(this.dom);
  }

  hidePlusButton() {
    if (this.dom) this.dom.style.display = 'none';
  }
  showPlusButton() {
    if (this.dom) {
      this.dom.style.display = '';
    }
  }
  addPlusButton(context: any, caption: any, callback: any) {
    let button = document.createElement('button');
    this.dom = button;
    button.innerHTML = '<span style="font-size: 36px">+</span>';
    button.addEventListener('click', (event: any) => {
      if (!this.canvas.mouseEventsBlocked && event.sourceCapabilities) {
        callback();
      }
    });

    this.handleLayerListeners(button);

    let styles = `text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
    user-select: none;
    -webkit-font-smoothing: antialiased;
    outline: none;
    font-size: 11px;
    align-items: center;
    display: inline-flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 80px;
    border: none;
    cursor: inherit;
    border-right: none;
    border-left: none;
    padding: 3px 6px;
    justify-content: center;
    margin-right: 0px;
    margin-left: 0px;
    min-width: 0px;
    -webkit-app-region: no-drag;
    border-radius: 2px;
    background: rgb(37, 41, 49);
    color: rgb(251, 251, 251);
    padding-left: 6px;
    padding-right: 6px;
    margin-top: 2px;
    margin-left: 2px;

    text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    vertical-align: baseline;
    border-width: 0px;
    border-style: initial;
    border-color: initial;
    border-image: initial;
    font: inherit;
    box-sizing: border-box;
    user-select: none;
    -webkit-font-smoothing: antialiased;
    font-family: Roboto, -apple-system, BlinkMacSystemFont, sans-serif;
    font-weight: 400;
    align-self: center;
    text-overflow: ellipsis;
    letter-spacing: -0.27px;
    font-size: 14px;
    color: rgb(241, 242, 244, 0.6);
    transform-origin: 0 0;
    `;

    button.setAttribute('style', styles);
    button.setAttribute('name', 'add-artboard');
    button.addEventListener('mouseenter', (event: any) => {
      if (!this.canvas.mouseEventsBlocked) {
        this.canvas.setMouseCursor('pointer');
      }
    });
    button.addEventListener('mouseleave', (event: any) => {
      if (!this.canvas.mouseEventsBlocked) {
        this.canvas.setMouseCursor('default');
      }
    });
    this.dispose = this.canvas.addDomToWorld(button);
    this.positionPlusButton();
  }

  deactivate() {
    this.canvas.removeFunction('addPlusButton');
    this.canvas.removeFunction('removePlusButton');
    this.dispose();
  }
}
