import * as React from 'react';
import { View } from 'react-native-web';
import { Layer } from 'models/Layer';
const rerenderProps = [
  'layout',
  'children',
  'props',
  'style',
  'visible',
  'gradient',
];
export default class TouchableOpacityRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  _ref: any;
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this._ref);
  }

  getStyles = (layer: Layer) => {
    let styles = { ...layer.style.getAll() };
    delete styles['gradient'];
    const returnStyles = {
      ...styles,
      ...layer.layout.getAll(),
    };
    if (returnStyles.overflow === 'scroll') {
      returnStyles.overflow = 'hidden';
    } else {
      returnStyles.overflow = returnStyles.overflow || 'hidden';
    }
    return returnStyles;
  };

  render() {
    const layer: Layer = this.props.layer;
    const { RootRenderer }: any = this.props;
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    return (
      <View
        ref={(c: any) => {
          this._ref = c;
        }}
        style={this.getStyles(layer)}
        {...layerProps}
        nativeID={layerId}
      >
        {layer.children.map((child: any, index: any) => (
          <RootRenderer {...this.props} key={child.id} layer={child} />
        ))}
      </View>
    );
  }
}
