import { Canvas } from '../..';
import { IPlugin } from '../..';
import EllipseRenderer from './EllipseRenderer';
import EllipseComponentDefinition from './EllipseComponentDefinition';

export default class EllipseRendererPlugin implements IPlugin {
  name = 'EllipseRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;

    // canvas.registerDataStore("MyPlugin")
  }

  activate() {
    this.canvas.addComponent(
      'ellipse',
      EllipseRenderer,
      EllipseComponentDefinition
    );
    // this.canvas.addEventListener('click', e => {
    //   console.log(e);
    // });
  }

  deactivate() {}
}
