import { Layer } from '../../models/Layer';
import DesignerMouseEvent from '../../events/MouseEvent';
export default class LayerDragEvent extends DesignerMouseEvent {
  layout: any;
  meta: any;
  snapPoints: any;
  constructor(
    name: string,
    target: Layer,
    originalEvent: any,
    payload: {
      layerWorldX: number;
      layerWorldY: number;
      meta?: any;
      snapPoints?: any;
    },
    currentTarget?: Layer
  ) {
    super(name, target, originalEvent, currentTarget);
    let dims = target.getBoundingClientRect();
    let newDims = target.convertLayerDimensions({
      x: payload.layerWorldX,
      y: payload.layerWorldY,
      width: dims.width,
      height: dims.height,
      rotation: dims.rotation,
    });
    this.layout = newDims;
    this.meta = payload.meta;
    this.snapPoints = payload.snapPoints;
  }
}
