import * as React from 'react';
import { Text } from 'react-native-web';
import includes from 'lodash/includes';
import replace from 'lodash/replace';
import { Layer } from 'models/Layer';
import RenderEvent from '../../events/RenderEvent';

const rerenderProps = [
  'layout',
  'children',
  'props',
  'innerText',
  'style',
  'visible',
];
export default class TextRenderer extends React.PureComponent<
  { layer: Layer },
  any
> {
  textRef: any;
  onUpdate = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this.textRef);

    this.whiteSpaceTo('noWrap');
    this.props.layer.dispatchEvent(
      'domDidMount',
      new RenderEvent('domDidMount', this)
    );
  }
  componentWillUnmount() {
    this.props.layer.off(rerenderProps, this.onUpdate);
  }

  componentDidUpdate(prevProps: any) {
    prevProps.layer.off(rerenderProps, this.onUpdate);
    this.props.layer.on(rerenderProps, this.onUpdate);
    this.props.layer.setDomRef(this.textRef);

    this.props.layer.dispatchEvent(
      'domDidUpdate',
      new RenderEvent('domDidUpdate', this)
    );
  }

  whiteSpaceTo(value: any) {
    // Redacted: animation related code
    this.textRef.setNativeProps({
      whiteSpace: value,
    });
  }

  getStyles = (layer: Layer) => {
    // let maxWidth = get(layer, 'parent.width', undefined);
    let updatedStyle = {
      color: 'black',
      ...layer.style.getAll(),
      ...layer.layout.getAll(),
      WebkitFontSmoothing: 'antialiased',
      wordBreak: 'break-all',
    };
    delete updatedStyle['fontPostscriptName'];
    delete updatedStyle['wordBreak'];
    // TODO: heavy jugaad
    // if (layer.parent && maxWidth) {
    //   updatedStyle.maxWidth = Math.max(maxWidth, updatedStyle.width);
    // }
    if (updatedStyle.textDecoration) {
      updatedStyle.textDecorationLine = updatedStyle.textDecoration;
    }
    return updatedStyle;
  };

  render() {
    const layer: Layer = this.props.layer;
    let { innerText } = layer;
    if (innerText && includes(innerText, "{'\n'}")) {
      innerText = replace(innerText, new RegExp("{'\n'}", 'g'), '\n');
    }
    if (innerText && includes(innerText, "{'\t'}")) {
      innerText = replace(innerText, new RegExp("{'\t'}", 'g'), '\t');
    }
    const layerProps = { ...layer.props.getAll() };
    const layerId = layerProps['id'];
    delete layerProps['id'];
    const style = this.getStyles(layer);

    let whiteSpace = undefined;
    let height = undefined;
    let width = undefined;

    if (layer.isAutoWidth()) {
      whiteSpace = 'pre';
    }

    if (layer.isFlexChild()) {
      height = 'fit-content';
      width = 'fit-content';
    } else {
      if (!layer.isFillHeight()) {
        height = 'fit-content';
      }
      if (!layer.isFillWidth()) {
        width = 'fit-content';
      }
    }

    return (
      <Text
        webkit="true"
        {...layerProps}
        style={{ fontSize: 14, height, width, ...style, whiteSpace }}
        nativeID={layerId}
        ref={(c: any) => {
          this.textRef = c;
        }}
      >
        {innerText}
      </Text>
    );
  }
}
