import { Canvas, IPlugin, Layer } from '../..';
import { map } from 'lodash';
import DesignerDropEvent from '../../events/DropEvent';
import StageDesignerDropEvent from './StageDesignerDropEvent';

export default class StageDesignerDropPlugin implements IPlugin {
  name = 'StageDesignerDrop';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addEventListener('drop', (e: DesignerDropEvent) => {
      const draggedLayerStyles = JSON.parse(
        e.originalEvent.dataTransfer.getData('text')
      );
      const hitResult = this.canvas.getHitRectResult(
        e.worldX - draggedLayerStyles.offsetX,
        e.worldY - draggedLayerStyles.offsetY,
        parseInt(draggedLayerStyles.width.replace('px', '')),
        parseInt(draggedLayerStyles.height.replace('px', ''))
      );
      const canvas = hitResult.pop();
      let artboard = null;

      map(hitResult, (hitLayer: Layer) => {
        if (canvas.children.indexOf(hitLayer) >= 0) {
          artboard = hitLayer;
        }
      });
      if (!artboard) {
        artboard = this.canvas.executeFunction('getSelectedArtboard');
      }

      this.canvas.dispatchEvent(
        'StageDesigner.drop',
        new StageDesignerDropEvent(
          'StageDesigner.drop',
          this.canvas,
          e.originalEvent,
          artboard,
          this.canvas
        )
      );
    });
  }

  deactivate() {
    // TODO: call remove event listener with callback
    // this.canvas.removeEventListener('drop');
  }
}
