import * as React from 'react';
import {
  Editor,
  EditorState,
  ContentState,
  getDefaultKeyBinding,
  Modifier,
} from 'draft-js';
import { Layer, TextEditorEvent } from '../../';
import './Draft.css';
import { IDimensionWithRoot } from 'interfaces';
import { isNil } from 'lodash';

type propType = {
  layer: Layer;
  onEvent: (event: TextEditorEvent) => void;
};

export default class TextEditor extends React.Component<propType, any> {
  isBlurred: boolean = false;
  text: string = '';
  dims: IDimensionWithRoot = this.props.layer.getBoundingClientRect();
  editorRef: any;
  isText: boolean = this.props.layer.type === 'text';
  isAuto: boolean =
    this.props.layer &&
    this.props.layer.layout &&
    isNil(this.props.layer.layout.get('width'));
  constructor(props: any) {
    super(props);
    this.text = this.isText
      ? this.props.layer.innerText
      : this.props.layer.props
      ? this.props.layer.props.get('placeholder')
      : ' ';
    this.state = {
      editorState: EditorState.moveFocusToEnd(
        EditorState.createWithContent(
          ContentState.createFromText(this.text || '')
        )
      ),
    };
  }
  componentDidMount() {
    this.onFocus();
    document.addEventListener('keydown', this.handleNumpadEnter);
    document.execCommand('selectAll', false, undefined);
  }
  isNumpadEnter(e: any) {
    return e.key === 'Enter' && e.location === 3 && e.which === 13;
  }
  onBlur = () => {
    this.isBlurred = true;
    const event = new TextEditorEvent(
      'onEditorBlur',
      this.props.layer,
      this.state.editorState.getCurrentContent().getPlainText(),
      this.isBlurred
    );
    this.props.onEvent(event);
  };

  onFocus = () => {
    this.isBlurred = false;
    const event = new TextEditorEvent(
      'onEditorFocus',
      this.props.layer,
      this.state.editorState.getCurrentContent().getPlainText()
    );
    this.props.onEvent(event);
  };
  handleNumpadEnter = (e: any) => {
    if (this.isNumpadEnter(e)) {
      this.onBlur();
    }
  };
  removePositionalAttribute = (style: any) => {
    let newStyle = { ...style };
    delete newStyle['left'];
    delete newStyle['top'];
    delete newStyle['width'];
    delete newStyle['height'];
    delete newStyle['flex'];
    delete newStyle['margin'];
    delete newStyle['marginTop'];
    delete newStyle['marginRight'];
    delete newStyle['marginBottom'];
    delete newStyle['marginLeft'];
    if (!newStyle['fontFamily'] || newStyle['fontFamily'] === 'System') {
      newStyle['fontFamily'] = 'system-ui';
    }
    return newStyle;
  };
  onChange = (editorState: any) => {
    if (this.isBlurred) {
      return;
    }
    if (editorState.getCurrentContent().getPlainText() !== this.text) {
      const event = new TextEditorEvent(
        'onEditorChange',
        this.props.layer,
        this.state.editorState.getCurrentContent().getPlainText(),
        this.isBlurred
      );
      this.props.onEvent(event);
    }
    this.setState({ editorState });
  };
  myBlockStyleFn = (contentBlock: any) => {
    const type = contentBlock.getType();
    if (type === 'unstyled') {
      return 'defaultTextStyle';
    }
    return '';
  };
  getStringStyle = (propertyName: string) => {
    let property = this.props.layer.layout.isValidKey(propertyName)
      ? this.props.layer.layout.get(propertyName)
      : this.props.layer.style.get(propertyName);
    return typeof property === 'number' ? property.toString() + 'px' : property;
  };
  handleTab = (event: any) => {
    event.preventDefault();
    if (this.isBlurred) {
      return;
    }
    let newContentState = Modifier.replaceText(
      this.state.editorState.getCurrentContent(),
      this.state.editorState.getSelection(),
      '\t'
    );
    const editorState = EditorState.push(
      this.state.editorState,
      newContentState,
      'insert-characters'
    );
    if (editorState.getCurrentContent().getPlainText() !== this.text) {
      const event = new TextEditorEvent(
        'onEditorChange',
        this.props.layer,
        this.state.editorState.getCurrentContent().getPlainText(),
        this.isBlurred
      );
      this.props.onEvent(event);
    }
    this.setState({ editorState });
  };
  handleKeyBinding = (event: any) => {
    if (event.keyCode === 27) {
      this.onBlur();
    }
    return getDefaultKeyBinding(event);
  };
  render() {
    let style = this.removePositionalAttribute({
      ...this.props.layer.style.getAll(),
      ...this.props.layer.layout.getAll(),
    });
    return (
      <div
        className="editorWrapper"
        style={{
          ...style,
          display: '',
          visibility: 'visible',
          position: 'absolute',
          transform: this.props.layer.rotation
            ? `rotate(${this.props.layer.rotation}deg)`
            : undefined,
          top:
            this.dims.rootY +
            (!this.isText
              ? this.dims.height / 2 - 7.6 // No clue how, but it works
              : 0),
          left: this.dims.rootX,
          height: this.dims.height,
          width: this.isAuto ? undefined : this.dims.width,
          caretColor: this.isText ? style.color || 'black' : 'grey',
          color: this.isText ? style.color || 'black' : '#757575',
          fontSize: style.fontSize ? parseInt(style.fontSize, 10) : 14,
          lineHeight: this.getStringStyle('lineHeight') || 'normal',
          letterSpacing: this.getStringStyle('letterSpacing'),
          WebkitFontSmoothing: 'antialiased',
        }}
      >
        <Editor
          ref={(c: any) => {
            this.editorRef = c;
          }}
          editorState={this.state.editorState}
          onChange={this.onChange}
          keyBindingFn={this.handleKeyBinding}
          blockStyleFn={this.myBlockStyleFn}
          handleReturn={(e: any, editorState: EditorState) =>
            this.isNumpadEnter(e) ? 'handled' : 'not-handled'
          }
          onBlur={this.onBlur}
          onFocus={this.onFocus}
          onTab={this.handleTab}
        />
      </div>
    );
  }
}
