import { Canvas } from '../../models/Canvas';
import { Layer } from '../../models/Layer';
import ISnapPoints from './interfaces/ISnapPoints';
import ModelUtils from './../../models/utils';
import _, { forEach } from 'lodash';
import { IContext } from 'interfaces';
import Config from './interfaces/Config';

function filterSnapPointX(snapPoint: any, handle: any) {
  //
  snapPoint.snapTo.center = false;
  snapPoint.snapTo.start = true;
  snapPoint.snapTo.end = true;

  if (handle.indexOf('R') !== -1) {
    snapPoint.snapTo.start = false;
  }
  if (handle.indexOf('L') !== -1) {
    snapPoint.snapTo.end = false;
  }
}
function filterSnapPointY(snapPoint: any, handle: any) {
  snapPoint.snapTo.center = false;
  snapPoint.snapTo.start = true;
  snapPoint.snapTo.end = true;
  if (handle.indexOf('B') !== -1) {
    snapPoint.snapTo.start = false;
  }
  if (handle.indexOf('T') !== -1) {
    snapPoint.snapTo.end = false;
  }
}

export default function getSnapPoints(
  config: Config,
  context: IContext,
  selectedLayers: Array<Layer>,
  parent: any = undefined,
  handle?: any
) {
  let snapPoints: ISnapPoints = {
    x: [],
    y: [],
  };

  if (!parent || !config.snap) {
    return {
      parentSnapPoints: snapPoints,
      siblingSnapPoints: snapPoints,
      gutterSnapPoints: snapPoints,
      rulerSnapPoints: snapPoints,
      gridSnap: undefined,
    };
  }
  let layers: any = [];

  if (parent) {
    layers = parent.children;
  }
  // TODO: Suraj review
  if (layers.length === 0 && !parent) {
    return {
      parentSnapPoints: snapPoints,
      siblingSnapPoints: snapPoints,
      gutterSnapPoints: snapPoints,
      rulerSnapPoints: snapPoints,
      gridSnap: config.gridSnap,
    };
  }

  let layerParent = parent;
  if (!layerParent) {
    layerParent = layers[0].parent;
  }

  // document bound snap points
  let documentBound: Layer = layerParent;
  let parentSnapPoints = snapPoints;
  if (documentBound && config.parentSnap) {
    // @ts-ignore
    parentSnapPoints = documentBound.executeFunction('getParentSnapPoints', [
      documentBound,
    ]);
  }

  snapPoints.x = snapPoints.x.concat(parentSnapPoints.x);
  snapPoints.y = snapPoints.y.concat(parentSnapPoints.y);

  // layers snap points
  let siblingSnapPoints: ISnapPoints = { x: [], y: [] };

  if (config.siblingSnap && layers[0]) {
    siblingSnapPoints = layers[0].executeFunction(
      'getSiblingSnapPoints',
      selectedLayers,
      parent
    );
  }

  let gutterSnapPoints: ISnapPoints = { x: [], y: [] };

  if (config.gutterSnap && parent && layers[0]) {
    // @ts-ignore
    gutterSnapPoints = layers[0].executeFunction(
      'getGutterSnapPoints',
      selectedLayers
    );
  }

  let rulerSnapPoints: { x: Array<any>; y: Array<any> } = {
    x: [],
    y: [],
  };

  if (config.rulerSnap) {
    rulerSnapPoints = context.canvas.executeFunction(
      'RulerLines.getLineCoordinates'
    );

    const boundingBox = context.canvas.getBoundingBox([parent], true);

    forEach(rulerSnapPoints.x, (coordinate, index) => {
      let point = coordinate - boundingBox.y;
      rulerSnapPoints.x[index] = { value: point, rootValue: coordinate };
    });
    forEach(rulerSnapPoints.y, (coordinate, index) => {
      const point = coordinate - boundingBox.x;
      rulerSnapPoints.y[index] = { value: point, rootValue: coordinate };
    });
  }
  // window['ModelUtils'] = ModelUtils;

  // filtered snap point based on handle

  if (handle) {
    parentSnapPoints.x.forEach((snapPoint: any) => {
      filterSnapPointX(snapPoint, handle);
    });

    parentSnapPoints.y.forEach((snapPoint: any) => {
      filterSnapPointY(snapPoint, handle);
    });

    siblingSnapPoints.x.forEach((snapPoint: any) => {
      // console.log(snapPoint, 'snap pont');
      // snapPoint.snapTo.center = false;

      filterSnapPointX(snapPoint, handle);
    });

    siblingSnapPoints.y.forEach((snapPoint: any) => {
      // snapPoint.snapTo.center = false;

      filterSnapPointY(snapPoint, handle);
    });
  }

  // console.log(paren)
  return {
    parentSnapPoints,
    siblingSnapPoints,
    gutterSnapPoints,
    rulerSnapPoints,
    gridSnap: config.gridSnap,
  };
}
