import { Canvas, IPlugin } from '../..';
import PathRenderer from './PathRenderer';
import PathComponentDefinition from './PathComponentDefinition';

export default class PathRendererPlugin implements IPlugin {
  name = 'PathRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent('path', PathRenderer, PathComponentDefinition);
  }

  deactivate() {}
}
