import ISnapPoints from './interfaces/ISnapPoints';
import ISnapXPoint from './interfaces/ISnapXPoint';
import { IDimensionWithRoot, IContext } from 'interfaces';
import { minBy } from 'lodash';
import ModelUtils from './../../models/utils';
import ISnapYPoint from './interfaces/ISnapYPoint';
export function snapToPoints(
  context: IContext,
  snapPoints: {
    parentSnapPoints: ISnapPoints;
    siblingSnapPoints: ISnapPoints;
    gutterSnapPoints: ISnapPoints;
    rulerSnapPoints: { x: Array<number>; y: Array<number> };
    gridSnap: boolean;
  },
  startPoint: any,
  totalDelta: any,
  dims: IDimensionWithRoot,
  absoluteDelta: any = [0, 0],
  startAbsolutePoint: any = [0, 0],
  parentAbsoluteRotation: number,
  boundingBoxWRTWorld: any,
  handle?: any
) {
  //

  let meta: any = {
    snappedSide: {
      x: undefined,
      y: undefined,
    },
  };

  let x = totalDelta[0];
  let y = totalDelta[1];

  if (snapPoints) {
    const snappedData = snapPoint(
      context,
      snapPoints,
      startPoint,
      totalDelta,
      dims,
      absoluteDelta,
      startAbsolutePoint,
      parentAbsoluteRotation,
      boundingBoxWRTWorld,
      handle
    );
    x = snappedData.delta.x;
    y = snappedData.delta.y;

    meta.snappedSide.x = snappedData.meta.x;
    meta.snappedSide.y = snappedData.meta.y;
  }
  let delta = {
    x: x,
    y: y,
  };

  return {
    x,
    y,
    delta,
    meta,
  };
}
export default function snapPoint(
  context: IContext,
  snapPoints: {
    parentSnapPoints: ISnapPoints;
    siblingSnapPoints: ISnapPoints;
    gutterSnapPoints: ISnapPoints;
    rulerSnapPoints: { x: Array<number>; y: Array<number> };
    gridSnap: boolean;
  },
  startPoint: any,
  totalDelta: any,
  dims: IDimensionWithRoot,
  absoluteDelta: any,
  startAbsolutePoint: any,
  parentAbsoluteRotation: number,
  boundingBoxWRTWorld: any,
  handle?: any
): {
  value: number;
  delta: any;
  meta: any;
} {
  let stretchX = dims.width;
  let stretchY = dims.height;

  let rulerCoordinatesX = snapPoints.rulerSnapPoints.y;
  let rulerCoordinatesY = snapPoints.rulerSnapPoints.x;

  let documentCoordinatesX = snapPoints.parentSnapPoints.x;
  let documentCoordinatesY = snapPoints.parentSnapPoints.y;

  let layerCoordinatesX = snapPoints.siblingSnapPoints.x;
  let layerCoordinatesY = snapPoints.siblingSnapPoints.y;

  // let gutterCoordinates =
  //   axis === 'x'
  //     ? snapPoints.gutterSnapPoints.x
  //     : snapPoints.gutterSnapPoints.y;

  let snapBounds =
    context.canvas.executeFunction('getSnapBound') / context.canvas.getZoom();
  let coordinateValueX = startPoint[0] + totalDelta[0];
  let coordinateValueY = startPoint[1] + totalDelta[1];

  let parentSnappedDataX = getPointForParentSnap(
    documentCoordinatesX,
    coordinateValueX,
    stretchX,
    snapBounds
  );

  let parentSnappedDataY = getPointForParentSnap(
    documentCoordinatesY,
    coordinateValueY,
    stretchY,
    snapBounds
  );

  parentSnappedDataX.snapType = 'documentBounds';
  parentSnappedDataY.snapType = 'documentBounds';
  parentSnappedDataX.delta = parentSnappedDataX.value - startPoint[0];
  parentSnappedDataY.delta = parentSnappedDataY.value - startPoint[1];

  const siblingDataX = getPointForSiblingSnap(
    layerCoordinatesX,
    coordinateValueX,
    stretchX,
    snapBounds,
    true
  );
  siblingDataX.snapType = 'sibling';

  const siblingDataY = getPointForSiblingSnap(
    layerCoordinatesY,
    coordinateValueY,
    stretchY,
    snapBounds
  );
  siblingDataY.snapType = 'sibling';

  siblingDataX.delta = siblingDataX.value - startPoint[0];
  siblingDataY.delta = siblingDataY.value - startPoint[1];

  let rulersDataX = getPointForRulerLines(
    rulerCoordinatesX,
    startAbsolutePoint[0] + absoluteDelta[0],
    stretchX,
    snapBounds
  );
  let rulersDataY = getPointForRulerLines(
    rulerCoordinatesY,
    startAbsolutePoint[1] + absoluteDelta[1],
    stretchY,
    snapBounds
  );

  rulersDataX.snapType = 'ruler';
  rulersDataY.snapType = 'ruler';
  rulersDataX.delta = rulersDataX.value - startAbsolutePoint[0];
  rulersDataY.delta = rulersDataY.value - startAbsolutePoint[1];

  const rotateDelta = ModelUtils.rotatePoint(
    [rulersDataX.delta, rulersDataY.delta],
    360 - parentAbsoluteRotation,
    [0, 0]
  );

  rulersDataX.delta = rotateDelta[0];
  rulersDataY.delta = rotateDelta[1];

  // grid snapping
  let gridDataX: any = {
    delta: 0,
    snappedSide: {
      coordinate: NaN,
      start: true,
      center: false,
      end: true,
    },
  };
  let gridDataY: any = {
    delta: 0,
    snappedSide: {
      coordinate: NaN,
      start: true,
      center: false,
      end: true,
    },
  };

  // console.log(
  //   gridOffset.x,
  //   gridOffset.y,
  //   startAbsolutePoint[1],
  //   startAbsolutePoint[0],
  //   'grid snap points here'
  // );
  let gridSnapPoints: {
    x: ISnapXPoint;
    y: ISnapYPoint;
  } = context.canvas.executeFunction(
    'snapToGrid',
    boundingBoxWRTWorld.x,
    absoluteDelta[0],
    boundingBoxWRTWorld.y,
    absoluteDelta[1],
    stretchX,
    stretchY,
    handle
  );

  if (gridSnapPoints) {
    gridDataX = gridSnapPoints.x;
    gridDataY = gridSnapPoints.y;
    const rotateDeltaGrid = ModelUtils.rotatePoint(
      [gridDataX.delta, gridDataY.delta],
      360 - parentAbsoluteRotation,
      [0, 0]
    );

    gridDataX.delta = rotateDeltaGrid[0];
    gridDataY.delta = rotateDeltaGrid[1];
  }

  // console.log(gridSnapPoints, 'grid snap points');

  let minSnappedDataX = minBy(
    [rulersDataX, gridDataX, siblingDataX, parentSnappedDataX],
    data => {
      let point = startPoint[0];

      if (data.snappedSide.end) {
        point = startPoint[0] + stretchX;
      }

      if (data.snappedSide.center) {
        point = startPoint[0] + stretchX / 2;
      }

      // console.log(
      //   point,
      //   data.delta,
      //   totalDelta[0],
      //   coordinateValueX,
      //   data.snapType,
      //   data.snappedSide.coordinate,
      //   Math.abs(data.snappedSide.coordinate - coordinateValueX),
      //   '&&&&&&&&'
      // );
      if (!isNaN(data.snappedSide.coordinate))
        return Math.abs(data.delta - totalDelta[0]);
      else return Number.MAX_VALUE;
    }
  );

  // console.log(minSnappedDataX, '&&&&&&&& ******');
  let minSnappedDataY = minBy(
    [rulersDataY, gridDataY, siblingDataY, parentSnappedDataY],
    data => {
      let point = startPoint[1];

      if (data.snappedSide.end) {
        point = startPoint[1] + stretchY;
      }

      if (data.snappedSide.center) {
        point = startPoint[1] + stretchY / 2;
      }

      if (!isNaN(data.snappedSide.coordinate))
        return Math.abs(data.delta - totalDelta[1]);
      else return Number.MAX_VALUE;
    }
  );

  return {
    value: minSnappedDataX.value,
    delta: {
      x: minSnappedDataX.delta,
      y: minSnappedDataY.delta,
    },
    meta: {
      x: {
        snappedSide: minSnappedDataX.snappedSide,
        snapType: minSnappedDataX.snapType,
      },
      y: {
        snappedSide: minSnappedDataY.snappedSide,
        snapType: minSnappedDataY.snapType,
      },
    },
  };
}

function getPointForParentSnap(
  documentCoordinates: Array<ISnapXPoint>,
  coordinateValue: number,
  stretch: number,
  snapBounds: number = 0
): any {
  // TODO: think of a better logic
  let distance = Number.MAX_VALUE;
  // let snappedSide;
  let coordinate;
  let snappedSide = {
    coordinate: NaN,
    start: false,
    center: false,
    end: false,
    snappedPoint: {
      value: NaN,
      rootValue: NaN,
    },
  };
  const originalCoordinateValue = coordinateValue;
  for (let coordinate of documentCoordinates) {
    let newData = calculateCoordinate(
      originalCoordinateValue,
      coordinateValue,
      coordinate,
      snapBounds,
      stretch,
      distance,
      snappedSide
    );
    coordinateValue = newData.coordinateValue;

    distance = newData.distance;
    snappedSide = newData.snapped;
    coordinate = coordinate;

    // snappedSide.snappedPoint = newData.snappedPoint;
  }
  return { value: coordinateValue, snappedSide };
}

function getPointForSiblingSnap(
  layerCoordinates: Array<ISnapXPoint>,
  coordinateValue: number,
  stretch: number,
  snapBounds: number = 0,
  showLog?: boolean
): any {
  // TODO: think of a better logic
  let distance = Number.MAX_VALUE;

  let snappedSide = {
    coordinate: NaN,
    start: false,
    center: false,
    end: false,
    snappedPoint: {
      value: NaN,
      rootValue: NaN,
    },
  };

  let originalCoordinateValue = coordinateValue;
  for (let coordinate of layerCoordinates) {
    let newData = calculateCoordinate(
      originalCoordinateValue,
      coordinateValue,
      coordinate,
      snapBounds,
      stretch,
      distance,
      snappedSide,
      showLog
    );
    coordinateValue = newData.coordinateValue;
    // coordinateValueFinal = newData.coordinateValue;
    distance = newData.distance;

    snappedSide = newData.snapped;

    // snappedSide = newData.snapped;
  }

  return { value: coordinateValue, snappedSide };
}

function getPointForRulerLines(
  rulerCoordinates: Array<any>,
  coordinateValue: number,
  stretch: number,
  snapBounds: number = 0
): any {
  let snappedSide = {
    coordinate: NaN,
    start: false,
    center: false,
    end: false,
    snappedPoint: {
      value: NaN,
      rootValue: NaN,
    },
  };

  let distance = Number.MAX_VALUE;

  let originalCoordinateValue = coordinateValue;
  for (let coordinate of rulerCoordinates) {
    let newData = calculateCoordinate(
      originalCoordinateValue,
      coordinateValue,
      coordinate,
      snapBounds,
      stretch,
      distance,
      snappedSide
    );

    // if (newData) {

    // }

    snappedSide = newData.snapped;
    coordinateValue = newData.coordinateValue;
    distance = newData.distance;
  }
  return { value: coordinateValue, snappedSide };
}

function getDistance(value1: number, value2: number): number {
  return Math.abs(value1 - value2);
}

