function calculateCoordinate(
  originalCoordinateValue: number,
  coordinateValue: number,
  coordinate: ISnapXPoint,
  snapMagnet: number,
  stretch: number,
  distance: number,
  snappedSide: any,
  showLog?: boolean
): {
  coordinateValue: number;
  distance: number;
  snapped: any;
  // snappedPoint: any;
} {
  let snapTo = {
    start: true,
    center: true,
    end: true,
  };

  let snapped = snappedSide;
  snapped.coordinateValue = coordinateValue;
  snapped.distance = distance;

  if (coordinate.snapTo) {
    snapTo = coordinate.snapTo;
  }

  if (
    Math.abs(originalCoordinateValue - coordinate.value) <= snapMagnet &&
    snapTo.start &&
    distance >= getDistance(originalCoordinateValue, coordinate.value)
  ) {
    // coordinateValue = coordinate.value;
    // distance = getDistance(coordinateValue, coordinate.value);
    snapped.start = true;
    snapped.coordinate = coordinate.value;
    snapped.snappedPoint = {
      value: coordinate.value,
      rootValue: coordinate.rootValue,
    };
    snapped.distance = getDistance(originalCoordinateValue, coordinate.value);
    snapped.coordinateValue = coordinate.value;
  }
  if (
    Math.abs(originalCoordinateValue + stretch / 2 - coordinate.value) <=
      snapMagnet &&
    snapTo.center &&
    distance >=
      getDistance(originalCoordinateValue + stretch / 2, coordinate.value)
  ) {
    // coordinateValue = coordinate.value - stretch / 2;
    // distance = getDistance(coordinateValue + stretch / 2, coordinate.value);
    // let snapped = snappedSide;
    snapped.distance = getDistance(
      originalCoordinateValue + stretch / 2,
      coordinate.value
    );
    snapped.coordinateValue = coordinate.value - stretch / 2;

    snapped.center = true;
    snapped.coordinate = coordinate.value;
    snapped.snappedPoint = {
      value: coordinate.value,
      rootValue: coordinate.rootValue,
    };
    // snappedArray.push(snapped);
  }
  if (
    Math.abs(originalCoordinateValue + stretch - coordinate.value) <=
      snapMagnet &&
    snapTo.end &&
    distance >= getDistance(originalCoordinateValue + stretch, coordinate.value)
  ) {
    // coordinateValue = coordinate.value - stretch;
    // distance = getDistance(coordinateValue + stretch, coordinate.value);
    // let snapped = snappedSide;

    snapped.distance = getDistance(
      originalCoordinateValue + stretch,
      coordinate.value
    );
    snapped.coordinateValue = coordinate.value - stretch;

    snapped.end = true;
    snapped.coordinate = coordinate.value;

    snapped.snappedPoint = {
      value: coordinate.value,
      rootValue: coordinate.rootValue,
    };
    // snappedArray.push(snapped);
  }

  return {
    coordinateValue: snapped.coordinateValue,
    distance: snapped.distance,
    snapped: snapped,
  };
}

function isAlignedToAxis(
  dims: IDimensionWithRoot,
  coordinates: any,
  axis: 'x' | 'y'
): boolean {
  if (axis === 'x') {
    if (
      dims.rootY > coordinates.minY + (coordinates.maxY - coordinates.minY) ||
      coordinates.minY > dims.rootY + dims.height
    ) {
      return false;
    }
    return true;
  } else {
    if (
      dims.rootX > coordinates.minX + (coordinates.maxX - coordinates.minX) ||
      coordinates.minX > dims.rootX + dims.width
    ) {
      return false;
    }
    return true;
  }
}
