import { Layer } from '../models/Layer';
import { Event } from './index';

type CommandParams = {
  [key: string]: any;
};

export default class CommandEvent extends Event {
  commandParams!: CommandParams;
  type: '' | 'Skip' | 'Forget';
  constructor(
    name: string,
    target: Layer,
    commandParams: CommandParams,
    currentTarget?: Layer,
    type?: '' | 'Skip' | 'Forget'
  ) {
    super(name, target, currentTarget);
    this.commandParams = commandParams;
    this.type = type || '';
  }
}
