import { IPlugin } from '../../interfaces/IPlugin';
import { Canvas } from '../../models/Canvas';
import LayerDragEvent from '../../plugins/LayerDrag/LayerDragEvent';
import ISnapPoints from './interfaces/ISnapPoints';
import drawLinesForSnap, { drawAllLinesForSnap } from './drawLinesForSnap';
import Config from './interfaces/Config';
import drawLinesForGutterSnap from './drawLinesForGutterSnap';
import { Layer } from '../../models/Layer';
import StageDesignerDrawEvent from 'plugins/StageDesignerDrawComponent/events/stageDesignerDrawEvent';

export default class SnapLines implements IPlugin {
  name = 'SnapLines';
  canvas!: Canvas;

  layer: Layer | undefined;
  layerParent: Layer | null | undefined;

  parentSnapPoints: ISnapPoints | undefined;
  siblingSnapPoints: ISnapPoints | undefined;
  gutterSnapPoints: ISnapPoints | undefined;

  config: Config | undefined;

  removeElementsWithClass(name: string) {
    let snapLinesX = document.querySelectorAll('.' + name + '-x');
    // TODO: remove using architecture instead of DOM
    // @ts-ignore
    for (let snapLine of snapLinesX) {
      snapLine.remove();
      this.canvas.removeDomFromWorld(snapLine);
    }

    let snapLinesY = document.querySelectorAll('.' + name + '-y');
    // TODO: remove using architecture instead of DOM
    // @ts-ignore
    for (let snapLine of snapLinesY) {
      snapLine.remove();
      this.canvas.removeDomFromWorld(snapLine);
    }
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  dragStartListener(data: LayerDragEvent, className: string = 'snap-lines') {
    this.removeElementsWithClass(className);
    let boundingBox = this.canvas.getContainerBoundingBoxWRTParent([
      data.target,
    ]);
    drawAllLinesForSnap(
      this.canvas,
      data.target,
      boundingBox,
      data.snapPoints,
      className,
      data.meta
    );
  }
  dragListener(data: LayerDragEvent, className: string = 'snap-lines') {
    // this.removeElementsWithClass(className);

    let boundingBox = this.canvas.getContainerBoundingBoxWRTParent([
      data.target,
    ]);
    if (data.target.parent) {
      boundingBox = {
        ...boundingBox,
        x: boundingBox.x + data.target.parent.rootX,
        y: boundingBox.y + data.target.parent.rootY,
      };
    }
    if (data.target) {
      drawLinesForSnap(
        this.canvas,
        data.target,
        boundingBox,
        data.meta,
        className
      );
    }
  }

  dragListenerDraw(
    data: StageDesignerDrawEvent,
    className: string = 'snap-lines'
  ) {
    // this.removeElementsWithClass(className);

    const boundingBox = {
      x: data.worldX,
      y: data.worldY,
      width: data.dimensions.width,
      height: data.dimensions.height,
    };
    if (data.target) {
      drawLinesForSnap(
        this.canvas,
        data.target,
        boundingBox,
        data.meta,
        className
      );
    }
  }

  dragListenerResizer(data: LayerDragEvent, className: string = 'snap-lines') {
    // this.removeElementsWithClass(className);
    let boundingBox = this.canvas.getContainerBoundingBoxWRTParent(
      this.canvas.selectedLayers
    );
    if (this.canvas.selectedLayers[0].parent) {
      boundingBox = {
        ...boundingBox,
        x: boundingBox.x + this.canvas.selectedLayers[0].parent.rootX,
        y: boundingBox.y + this.canvas.selectedLayers[0].parent.rootY,
      };
    }
    if (data.target) {
      drawLinesForSnap(
        this.canvas,
        data.target,
        boundingBox,
        data.meta,
        className
      );
    }
  }

  drawComponentMouseMoveListener(
    data: LayerDragEvent,
    className: string = 'snap-lines'
  ) {
    // this.removeElementsWithClass(className);

    const boundingBox = {
      x: data.worldX,
      y: data.worldY,
      width: 0,
      height: 0,
    };

    if (data.target) {
      drawLinesForSnap(
        this.canvas,
        data.target,
        boundingBox,
        data.meta,
        className
      );
    }
  }

  activate() {
    // layer drag listener

    this.canvas.addEventListener('layerDragStart', (data: LayerDragEvent) => {
      this.dragStartListener(data);
    });

    this.canvas.addEventListener('layerDrag', (data: LayerDragEvent) => {
      this.dragListener(data);
    });

    this.canvas.addEventListener('layerDragEnd', (data: LayerDragEvent) => {
      this.removeElementsWithClass('snap-lines');
    });

    // resizer listener
    this.canvas.addEventListener(
      'LayerResizer.dragStart',
      (data: LayerDragEvent) => {
        this.dragStartListener(data);
      }
    );

    this.canvas.addEventListener(
      'LayerResizer.drag',
      (data: LayerDragEvent) => {
        this.dragListenerResizer(data);
      }
    );

    this.canvas.addEventListener(
      'LayerResizer.dragEnd',
      (data: LayerDragEvent) => {
        this.removeElementsWithClass('snap-lines');
      }
    );

    // resizer handle drag listener
    this.canvas.addEventListener(
      'LayerResizer.handleDragStart',
      (data: LayerDragEvent) => {
        this.dragStartListener(data);
      }
    );
    this.canvas.addEventListener(
      'LayerResizer.handleDrag',
      (data: LayerDragEvent) => {
        this.dragListenerResizer(data);
      }
    );

    this.canvas.addEventListener(
      'LayerResizer.handleDragEnd',
      (data: LayerDragEvent) => {
        this.removeElementsWithClass('snap-lines');
      }
    );

    /// draw component listener
    this.canvas.addEventListener(
      'StageDesignerDrawComponent.drawShape',
      (data: LayerDragEvent) => {
        this.dragStartListener(data, 'snap-lines-draw');
      }
    );
    this.canvas.addEventListener(
      'StageDesignerDrawComponent.mousemove',
      (data: LayerDragEvent) => {
        this.drawComponentMouseMoveListener(data, 'snap-lines-draw');
      }
    );

    // resizer listener
    this.canvas.addEventListener(
      'StageDesignerDrawComponent.drawStart',
      (data: LayerDragEvent) => {
        this.dragStartListener(data);
      }
    );

    this.canvas.addEventListener(
      'StageDesignerDrawComponent.draw',
      (data: StageDesignerDrawEvent) => {
        this.dragListenerDraw(data);
      }
    );

    this.canvas.addEventListener(
      'StageDesignerDrawComponent.drawEnd',
      (data: LayerDragEvent) => {
        this.removeElementsWithClass('snap-lines');
        this.removeElementsWithClass('snap-lines-draw');
      }
    );
  }

  deactivate() {}
}
