import { Canvas, Layer, IPlugin } from '../..';
import { FontResizerEvent } from './FontResizerEvent';
import { IDimensionWithRoot } from 'interfaces';

export default class FontResizerPlugin implements IPlugin {
  name = 'FontResizerPlugin';
  namespace = 'FontResizer';
  canvas!: Canvas;
  disabled: boolean = false;
  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addMiddleware(
      `LayerResizer.handleDragStart`,
      this.handleDragStart
    );
    this.canvas.addMiddleware(`LayerResizer.handleDrag`, this.handleDrag);
    this.canvas.addMiddleware(`LayerResizer.handleDragEnd`, this.handleDragEnd);
    this.canvas.addMiddleware(`LayerResizer.getHandles`, this.getHandles);
    this.canvas.addFunction(
      `${this.namespace}.disable`,
      this.disableDrag.bind(this)
    );
  }
  disableDrag() {
    this.disabled = true;
  }
  isInsideCenterBox(
    layerDims: IDimensionWithRoot,
    parentDims: IDimensionWithRoot
  ): boolean {
    const parentBoundaries = {
      left: parentDims.rootX + parentDims.width / 5,
      right: parentDims.rootX + (parentDims.width * 4) / 5,
      top: parentDims.rootY + parentDims.height / 10,
      bottom: parentDims.rootY + (parentDims.height * 9) / 10,
    };
    const layerCenterY = layerDims.rootY + layerDims.height / 2;
    const layerCenterX = layerDims.rootX + layerDims.width / 2;
    return (
      layerCenterX > parentBoundaries.left &&
      layerCenterX < parentBoundaries.right &&
      layerCenterY > parentBoundaries.top &&
      layerCenterY < parentBoundaries.bottom
    );
  }
  getHandles = (next: any, layers: Array<Layer>) => {
    if (
      layers.length === 1 &&
      layers[0].type === 'text' &&
      !layers[0].layout.get('height') &&
      !layers[0].layout.get('width')
    ) {
      const textLayer = layers[0];
      const parentDims = textLayer.parent?.getBoundingClientRect();
      const textLayerDims = textLayer.getBoundingClientRect();
      let newHandle = '';

      if (parentDims) {
        let parentCenterY = parentDims.rootY + parentDims.height / 2;
        let parentCenterX = parentDims.rootX + parentDims.width / 2;

        let textLayerCenterY = textLayerDims.rootY + textLayerDims.height / 2;
        let textLayerCenterX = textLayerDims.rootX + textLayerDims.width / 2;
        if (textLayerCenterY < parentCenterY) {
          newHandle = 'B';
        } else if (textLayerCenterY > parentCenterY) {
          newHandle += 'T';
        } else {
          newHandle += '';
        }

        if (textLayerCenterX < parentCenterX) {
          newHandle += 'R';
        } else if (textLayerCenterX > parentCenterX) {
          newHandle += 'L';
        } else {
          newHandle += '';
        }

        if (
          textLayer.parent &&
          this.isInsideCenterBox(textLayerDims, parentDims)
        ) {
          newHandle = 'B';
        }
        return [newHandle, 'ROTATE'];
      }
    } else {
      return next(layers);
    }
  };
  handleDragStart = (next: any, data: any) => {
    const event = data.data;
    const selectedLayer = data.selectedLayer;
    if (
      (event.targetHandle && event.targetHandle.includes('ROTATE')) ||
      this.disabled
    ) {
      return next(data);
    }
    if (
      selectedLayer &&
      selectedLayer.type === 'text' &&
      !selectedLayer.layout.get('height') &&
      !selectedLayer.layout.get('width')
    ) {
      this.canvas.executeFunction('Tooltip.hide');
      selectedLayer.executeFunction(
        'setData',
        'fontSize',
        selectedLayer.layout.get('fontSize') || 14
      );
      selectedLayer.executeFunction(
        'setData',
        'initialDimensions',
        selectedLayer.getBoundingClientRect()
      );
      this.canvas.executeFunction('LayerResizer.stopHandleRerender');
      this.canvas.dispatchEvent(
        `${this.namespace}.dragStart`,
        new FontResizerEvent(`${this.namespace}.dragStart`, selectedLayer, {})
      );
    } else {
      return next(data);
    }
  };

  handleDrag = (next: any, data: any) => {
    const event = data.data;
    const selectedLayer = data.selectedLayer;

    if (
      (event.targetHandle && event.targetHandle.includes('ROTATE')) ||
      this.disabled
    ) {
      return next(data);
    }
    if (
      selectedLayer &&
      selectedLayer.type === 'text' &&
      !selectedLayer.layout.get('height') &&
      !selectedLayer.layout.get('width')
    ) {
      const { session, targetHandle } = event;

      let deltaInput = {
        x: session.current.x - session.start.x,
        y: session.current.y - session.start.y,
      };

      const currentFontSize = selectedLayer.executeFunction(
        'getData',
        'fontSize'
      );
      const initialDims = selectedLayer.executeFunction(
        'getData',
        'initialDimensions'
      );

      const ratioX = deltaInput.x / initialDims.width;
      const ratioY = deltaInput.y / initialDims.height;
      let newFontSize = currentFontSize;
      let newLeft = initialDims.x;
      let newTop = initialDims.y;

      if (targetHandle === 'B') {
        newFontSize += currentFontSize * ratioY;
      } else if (targetHandle === 'T') {
        newFontSize += -currentFontSize * ratioY;
      } else {
        if (targetHandle.indexOf('L') === -1) {
          newFontSize += currentFontSize * ratioX;
        } else {
          newFontSize += -currentFontSize * ratioX;
        }
      }
      if (newFontSize <= 0) {
        newFontSize *= -1;
      }
      this.canvas.dispatchEvent(
        `${this.namespace}.setFontSize`,
        new FontResizerEvent(`${this.namespace}.setFontSize`, selectedLayer, {
          fontSize: Math.round(newFontSize),
        })
      );
      const currentDims = selectedLayer.getBoundingClientRect();
      const widthChanged = currentDims.width - initialDims.width;
      const heightChanged = currentDims.height - initialDims.height;
      if (targetHandle === 'L' || targetHandle === 'R') {
        newTop = newTop - heightChanged / 2;
      } else if (!(targetHandle.indexOf('B') !== -1)) {
        newTop = newTop - heightChanged;
      }
      if (!(targetHandle.indexOf('L') === -1)) {
        newLeft = newLeft - widthChanged;
      } else if (targetHandle === 'T' || targetHandle === 'B') {
        newLeft = newLeft - widthChanged / 2;
      }
      const positionChanged =
        initialDims.y !== newTop || initialDims.x !== newLeft;
      if (positionChanged) {
        this.canvas.dispatchEvent(
          `${this.namespace}.setPosition`,
          new FontResizerEvent(`${this.namespace}.setPosition`, selectedLayer, {
            y: newTop,
            x: newLeft,
          })
        );
      }
      this.canvas.executeFunction(
        'Tooltip.show',
        {
          x: event.viewportX + 20,
          y: event.viewportY + 20,
        },
        `${Math.round(newFontSize)}`
      );
    } else {
      return next(data);
    }
  };

  handleDragEnd = (next: any, data: any) => {
    const event = data.data;
    const selectedLayer = data.selectedLayer;
    if (
      (event.targetHandle && event.targetHandle.includes('ROTATE')) ||
      this.disabled
    ) {
      return next(data);
    }
    if (
      selectedLayer &&
      selectedLayer.type === 'text' &&
      !selectedLayer.layout.get('height') &&
      !selectedLayer.layout.get('width')
    ) {
      this.canvas.executeFunction('Tooltip.hide');
      selectedLayer.executeFunction(
        'setData',
        'fontSize',
        selectedLayer.layout.get('fontSize') || 14
      );
      selectedLayer.executeFunction(
        'setData',
        'initialDimensions',
        selectedLayer.getBoundingClientRect()
      );
      this.canvas.executeFunction('LayerResizer.startHandleRerender');
      this.canvas.dispatchEvent(
        `${this.namespace}.dragEnd`,
        new FontResizerEvent(`${this.namespace}.dragEnd`, selectedLayer, {})
      );
    } else {
      return next(data);
    }
  };

  deactivate() {}
}
