import { forEach } from 'lodash';
import { ResizerRendererDragEvent } from './ResizerRendererDragEvent';
import { Canvas, IPlugin, MouseEvent as DesignerMouseEvent } from '../../';
import { IContext, IDimensionWithRotation } from '../../interfaces';
import ResetResizerEvent from 'events/ResetResizerEvent';
import {
  getInitialRotation,
  getHorizontalResizerIcon,
  getRotationResizerIcon,
} from './utils';
export default class ResizerRendererPlugin implements IPlugin {
  name = 'ResizerRenderer';
  namespace = 'ResizerRenderer';
  canvas!: Canvas;
  dispose: any;
  allowClickEvents: boolean = false;
  wrapperLayer: any;
  resizerHandleLayersRef!: Map<string, any>;
  resizerData!: { dims: IDimensionWithRotation; handles: Array<string> };
  passedEvent = [
    'mousedown',
    'mouseup',
    'mousemove',
    'dblclick',
    'contextmenu',
    'click',
    'drop',
  ];
  show: boolean = true;
  mouseDown: boolean = false;
  dragStarted: boolean = false;
  handleDragStarted: boolean = false;
  cursor: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
    this.resizerData = {
      dims: { x: 0, y: 0, height: 0, width: 0, rotation: 0 },
      handles: [],
    };
    this.resizerHandleLayersRef = new Map<string, any>();
  }

  setHandleStyle(handleInstance: any, handleName: string) {
    let zoom = this.canvas.getZoom();
    const rotationHandleSize = 20;
    const handleSize = 10;
    if (handleName === 'ROTATE') {
      return;
    }
    let propsWithoutPx: any = {
      position: 'absolute',
      display: '',
      'background-color': 'rgba(255, 255, 255, 1)',
      border: `${1 / zoom}px solid #1FB2CC`,
      'pointer-events': 'auto',
      'z-index': '1',
    };
    let fixedLayoutProps: any = {
      height: handleSize / zoom,
      width: handleSize / zoom,
      'margin-left': -1 / zoom - handleSize / (2 * zoom),
      'margin-top': -1 / zoom - handleSize / (2 * zoom),
      'margin-right': -1 / zoom - handleSize / (2 * zoom),
      'margin-bottom': -1 / zoom - handleSize / (2 * zoom),
      'border-radius': 20 / zoom,
    };
    let pos: any = {};
    let rotatorPos = {};
    let dims: any = this.resizerData.dims;
    switch (handleName) {
      case 'T':
        // pos = {
        //   left: handleSize + 2,
        //   top: 0,
        //   width: dims.width - handleSize - 2,
        // };
        pos = { left: dims.width / 2, top: 0 };
        rotatorPos = {
          left: dims.width / 2 - rotationHandleSize / (2 * zoom),
          top: -rotationHandleSize / zoom,
        };
        break;
      case 'L':
        // pos = {
        //   left: 0,
        //   top: handleSize + 2,
        //   height: dims.height - handleSize - 2,
        // };
        pos = { left: 0, top: dims.height / 2 };
        rotatorPos = {
          left: -rotationHandleSize / zoom,
          top: dims.height / 2 - rotationHandleSize / (2 * zoom),
        };
        break;
      case 'R':
        // pos = {
        //   left: dims.width,
        //   top: handleSize + 2,
        //   height: dims.height - handleSize - 2,
        // };
        pos = { left: dims.width, top: dims.height / 2 };
        rotatorPos = {
          left: dims.width,
          top: dims.height / 2 - rotationHandleSize / (2 * zoom),
        };
        break;
      case 'B':
        // pos = {
        //   left: handleSize + 2,
        //   top: dims.height,
        //   width: dims.width - handleSize - 2,
        // };
        pos = { left: dims.width / 2, top: dims.height };
        rotatorPos = {
          left: dims.width / 2 - rotationHandleSize / (2 * zoom),
          top: dims.height,
        };
        break;
      case 'TR':
        rotatorPos = {
          left: dims.width,
          top: -rotationHandleSize / zoom,
        };
        pos = { left: dims.width, top: 0 };
        break;
      case 'BL':
        rotatorPos = {
          left: -rotationHandleSize / zoom,
          top: dims.height,
        };
        pos = { left: 0, top: dims.height };
        break;
      case 'BR':
        rotatorPos = {
          left: dims.width,
          top: dims.height,
        };
        pos = { left: dims.width, top: dims.height };
        break;
      default:
        rotatorPos = {
          left: -rotationHandleSize / zoom,
          top: -rotationHandleSize / zoom,
        };
        pos = { left: 0, top: 0 };
        break;
    }
    forEach(propsWithoutPx, (val: string, key: string) => {
      handleInstance.handleLayer.style[key] = val;
    });
    forEach({ ...fixedLayoutProps, ...pos }, (val: string, key: string) => {
      handleInstance.handleLayer.style[key] = val.toString() + 'px';
    });
    if (handleName !== 'ROTATE') {
      let rotatorPropsWithoutPx = {
        position: 'absolute',
        display: '',
        'pointer-events': 'auto',
      };
      fixedLayoutProps = {
        height: rotationHandleSize / zoom,
        width: rotationHandleSize / zoom,
      };
      pos = rotatorPos;
      forEach(rotatorPropsWithoutPx, (val: string, key: string) => {
        handleInstance.handleRotator.style[key] = val;
      });
      forEach({ ...fixedLayoutProps, ...pos }, (val: string, key: string) => {
        handleInstance.handleRotator.style[key] = val.toString() + 'px';
      });
    }
  }
  addEventListeners(
    handleLayer: any,
    handleName: string,
    rotationType?: string
  ) {
    let dragData: any;
    if (rotationType) {
      this.resizerData.dims = {
        ...this.resizerData.dims,
        rotation:
          this.resizerData.dims.rotation + getInitialRotation(rotationType),
      };
    }
    this.canvas.executeFunction('handleHtmlDrag', handleLayer);
    handleLayer.addEventListener('htmlDragStart', (e: any) => {
      if (
        (rotationType && !this.resizerData.handles.includes('ROTATE')) ||
        this.canvas.moveWithGrabHandle
      ) {
        return;
      }
      this.canvas.blockMouseEvents();
      let dragVal = this.canvas.executeFunction(
        'HtmlDragToWorld.dragStart',
        e,
        this.resizerData.dims
      );
      this.handleDragStarted = true;
      if (rotationType) {
        this.canvas.setMouseCursor(
          `url("data:image/svg+xml,${escape(
            getRotationResizerIcon(
              getInitialRotation(rotationType) + this.resizerData.dims.rotation
            )
          )}") 10 10, auto`
        );
      }
      dragData = dragVal;
      this.canvas.dispatchEvent(
        `${this.namespace}.handleDragStart`,
        new ResizerRendererDragEvent(
          `${this.namespace}.handleDragStart`,
          handleName + (rotationType ? rotationType : ''),
          dragVal.startValues,
          dragVal.session,
          dragVal.event,
          this.canvas
        )
      );
    });
    handleLayer.addEventListener('htmlDrag', (e: any) => {
      if (
        (rotationType && !this.resizerData.handles.includes('ROTATE')) ||
        this.canvas.moveWithGrabHandle
      ) {
        return;
      }
      if (e.pageX && e.pageY) {
        let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
        if (
          dragVal.session.current.deltaX !== 0 ||
          dragVal.session.current.deltaY !== 0
        ) {
          dragData = dragVal;
          if (rotationType) {
            this.canvas.setMouseCursor(
              `url("data:image/svg+xml,${escape(
                getRotationResizerIcon(
                  getInitialRotation(rotationType) +
                    this.resizerData.dims.rotation
                )
              )}") 10 10, auto`
            );
          }
          this.canvas.dispatchEvent(
            `${this.namespace}.handleDrag`,
            new ResizerRendererDragEvent(
              `${this.namespace}.handleDrag`,
              handleName + (rotationType ? rotationType : ''),
              dragVal.startValues,
              dragVal.session,
              dragVal.event,
              this.canvas
            )
          );
        }
      }
    });
    handleLayer.addEventListener('htmlDragEnd', (e: any) => {
      e.preventDefault();
      this.canvas.setMouseCursor('default');
      this.canvas.unblockMouseEvents();
      if (
        (rotationType && !this.resizerData.handles.includes('ROTATE')) ||
        this.canvas.moveWithGrabHandle
      ) {
        return;
      }
      this.handleDragStarted = false;
      this.canvas.dispatchEvent(
        `${this.namespace}.handleDragEnd`,
        new ResizerRendererDragEvent(
          `${this.namespace}.handleDragEnd`,
          handleName + (rotationType ? rotationType : ''),
          dragData.startValues,
          dragData.session,
          dragData.event,
          this.canvas
        )
      );
    });
  }
  renderCursor(handleInstance: any, handleName: string) {
    handleInstance.handleLayer.addEventListener('mousemove', (event: any) => {
      if (!this.handleDragStarted && !this.canvas.mouseEventsBlocked) {
        this.canvas.setMouseCursor(
          `url("data:image/svg+xml,${escape(
            getHorizontalResizerIcon(
              getInitialRotation(handleName) + this.resizerData.dims.rotation
            )
          )}") 10 10, auto`
        );
      }
    });
    handleInstance.handleLayer.addEventListener('mouseenter', (event: any) => {
      if (!this.handleDragStarted && !this.canvas.mouseEventsBlocked) {
        this.canvas.setMouseCursor(
          `url("data:image/svg+xml,${escape(
            getHorizontalResizerIcon(
              getInitialRotation(handleName) + this.resizerData.dims.rotation
            )
          )}") 10 10, auto`
        );
      }
    });
    handleInstance.handleLayer.addEventListener('mouseleave', () => {
      if (
        !this.handleDragStarted &&
        !this.canvas.mouseEventsBlocked &&
        this.canvas.mouseCursorType &&
        this.canvas.mouseCursorType.includes('url')
      ) {
        this.canvas.setMouseCursor('default');
      }
    });
    handleInstance.handleLayer.addEventListener('contextmenu', (e: any) => {
      e.preventDefault();
    });
    handleInstance.handleRotator.addEventListener('contextmenu', (e: any) => {
      e.preventDefault();
    });
    handleInstance.handleRotator.addEventListener(
      'mouseenter',
      (event: any) => {
        if (
          this.resizerData.handles.includes('ROTATE') &&
          !this.handleDragStarted &&
          !this.canvas.mouseEventsBlocked
        ) {
          this.canvas.setMouseCursor(
            `url("data:image/svg+xml,${escape(
              getRotationResizerIcon(
                getInitialRotation(handleName) + this.resizerData.dims.rotation
              )
            )}") 10 10, auto`
          );
        }
      }
    );
    handleInstance.handleRotator.addEventListener('mouseleave', () => {
      if (
        !this.handleDragStarted &&
        this.canvas.mouseCursorType &&
        this.canvas.mouseCursorType.includes('url') &&
        !this.canvas.mouseEventsBlocked
      ) {
        this.canvas.setMouseCursor('default');
      }
    });
  }
  renderHandles() {
    let handles = this.resizerData.handles;
    return handles.map((handleName: any, i: number) => {
      let handle = this.resizerHandleLayersRef.get(handleName);
      if (!handle) {
        const handleLayer = document.createElement('div');
        const handleRotator = document.createElement('div');
        this.wrapperLayer.appendChild(handleLayer);
        this.wrapperLayer.appendChild(handleRotator);
        this.resizerHandleLayersRef.set(handleName, {
          handleLayer,
          handleRotator,
        });
        handle = { handleLayer, handleRotator };
        this.addEventListeners(handle.handleLayer, handleName);
        this.addEventListeners(handle.handleRotator, 'ROTATE', handleName);
        this.renderCursor(handle, handleName);
      }
      this.setHandleStyle(handle, handleName);
    });
  }
  isVisible() {
    return (
      this.show &&
      this.resizerData.dims.x &&
      this.resizerData.dims.y &&
      this.resizerData.dims.width &&
      this.resizerData.dims.height
    );
  }
  renderResizer() {
    if (this.isVisible()) {
      let zoom = this.canvas.getZoom();
      this.wrapperLayer.style.border = `${1 / zoom}px solid #1FB2CC`;
      this.wrapperLayer.style.top = this.resizerData.dims.y + 'px';
      this.wrapperLayer.style.left = this.resizerData.dims.x + 'px';
      this.wrapperLayer.style.height = this.resizerData.dims.height + 'px';
      this.wrapperLayer.style.width = this.resizerData.dims.width + 'px';
      if (this.resizerData.dims.rotation) {
        this.wrapperLayer.style.transform = `rotate(${this.resizerData.dims.rotation}deg`;
      } else {
        this.wrapperLayer.style.transform = ``;
      }
      this.wrapperLayer.style.display = '';
      this.renderHandles();
    } else {
      this.wrapperLayer.style.display = 'none';
    }
  }
  toggleResizer(show: boolean) {
    this.show = show;
    this.renderResizer();
  }
  showHandle(context: IContext, name: string) {
    if (!this.resizerData.handles.includes(name.toUpperCase())) {
      this.resizerData.handles.push(name.toUpperCase());
      this.renderHandles();
    }
  }
  hideHandle(context: IContext, name: string) {
    if (this.resizerData.handles.includes(name.toUpperCase())) {
      this.resizerData.handles.splice(
        this.resizerData.handles.indexOf(name.toUpperCase()),
        1
      );
      let handle = this.resizerHandleLayersRef.get(name);
      if (handle) {
        const { handleLayer, handleRotator } = handle;
        handleLayer.style.display = 'none';
        handleRotator.style.display = 'none';
      }
    }
  }
  isValidKey(key: string) {
    return (
      key === 'x' ||
      key === 'y' ||
      key === 'height' ||
      key === 'width' ||
      key === 'rotation'
    );
  }
  setResizerOrientation(
    context: any,
    key: 'x' | 'y' | 'height' | 'width' | 'rotation' | object,
    value?: any
  ) {
    if (typeof key == 'object') {
      for (var prop in key) {
        if (this.isValidKey(prop)) {
          // @ts-ignore
          this.resizerData.dims[prop] = (key as any)[prop];
        } else {
          throw new Error(`Resizer Orientation Error: Invalid key '${prop}'`);
        }
      }
    } else {
      if (this.isValidKey(key)) {
        this.resizerData.dims[key] = value;
      } else {
        throw new Error(`Resizer Orientation Error: Invalid key '${key}'`);
      }
    }
    this.renderResizer();
  }

  passEvent = (e: MouseEvent) => {
    // FIXME: If we include this, drag events get fired and mouse rect starts showing but we need this in case sibling overlaps and the bottom one is already selected and we are trying to drag the top one.
    if (e.type !== 'mousedown') {
      if (e.type === 'mousemove') {
        if (this.mouseDown) {
          this.dragStarted = true;
        }
      }

      if (this.dragStarted && this.mouseDown) {
      } else {
        this.canvas.dispatchEventOnHitLayer(
          e.type,
          new DesignerMouseEvent(e.type, this.canvas, e, this.canvas),
          e
        );
      }

      if (e.type === 'mouseup') {
        this.dragStarted = false;
        this.mouseDown = false;

        this.canvas.dispatchEventOnHitLayer(
          e.type,
          new DesignerMouseEvent(e.type, this.canvas, e, this.canvas),
          e
        );
      }
    } else {
      if (e.button !== 2 && e.which !== 3) {
        this.mouseDown = true;
      }
    }
  };
  handleLayerListeners(handle: any) {
    this.passedEvent.map(event => {
      handle.addEventListener(event, (e1: any) => {
        this.passEvent(e1);
      });
    });
  }
  hideAllHandles(context: IContext) {
    this.resizerData.handles.forEach((handleName: string) => {
      let handle = this.resizerHandleLayersRef.get(handleName);
      if (handle) {
        const { handleLayer, handleRotator } = handle;
        handleLayer.style.display = 'none';
        handleRotator.style.display = 'none';
      }
    });
    this.resizerData.handles = [];
  }
  activate() {
    this.canvas.addFunction(
      `${this.namespace}.show`,
      this.toggleResizer.bind(this, true)
    );
    this.canvas.addFunction(
      `${this.namespace}.hide`,
      this.toggleResizer.bind(this, false)
    );
    this.canvas.addFunction(
      `${this.namespace}.showHandle`,
      this.showHandle.bind(this)
    );
    this.canvas.addFunction(
      `${this.namespace}.hideHandle`,
      this.hideHandle.bind(this)
    );
    this.canvas.addFunction(
      `${this.namespace}.setOrientation`,
      this.setResizerOrientation.bind(this)
    );
    this.canvas.addFunction(
      `${this.namespace}.hideAllHandles`,
      this.hideAllHandles.bind(this)
    );
    this.canvas.addEventListener('resetResizer', (e: ResetResizerEvent) => {
      this.canvas.executeFunction('HtmlDragToWorld.dragEnd', e.originalEvent);
      this.canvas.executeFunction(
        'HtmlDragToWorld.dragStart',
        e.originalEvent,
        this.resizerData.dims
      );
    });

    this.wrapperLayer = document.createElement('div');
    this.wrapperLayer.style.pointerEvents = 'none';
    this.wrapperLayer.style.position = 'absolute';
    this.wrapperLayer.style.zIndex = 200;
    this.renderDragLayer();
    this.dispose = this.canvas.addDomToWorld(this.wrapperLayer, true, 101);
    this.renderResizer();
    ['zoom', 'scroll'].map((event: string) => {
      this.canvas.on(event, (e: any) => {
        this.renderResizer();
      });
    });
  }

  renderDragLayer = () => {
    let dragLayer = document.createElement('div');
    dragLayer.style.pointerEvents = 'auto';
    dragLayer.style.position = 'absolute';
    dragLayer.style.top = '0';
    dragLayer.style.left = '0';
    dragLayer.style.right = '0';
    dragLayer.style.bottom = '0';
    dragLayer.setAttribute('class', 'resizer move ' + dragLayer.className);
    this.wrapperLayer.appendChild(dragLayer);
    this.handleLayerListeners(dragLayer);
    this.canvas.executeFunction('handleHtmlDrag', dragLayer);
    dragLayer.addEventListener('htmlDragStart', e => {
      let dragVal = this.canvas.executeFunction(
        'HtmlDragToWorld.dragStart',
        e,
        this.resizerData.dims
      );
      this.canvas.blockMouseEvents();
      this.canvas.dispatchEvent(
        `${this.namespace}.dragStart`,
        new ResizerRendererDragEvent(
          `${this.namespace}.handleDragStart`,
          'C',
          dragVal.startValues,
          dragVal.session,
          dragVal.event,
          this.canvas
        )
      );
      return false;
    });

    dragLayer.addEventListener('htmlDrag', (e: any) => {
      if (e.pageX && e.pageY) {
        let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
        if (
          dragVal.session.current.deltaX !== 0 ||
          dragVal.session.current.deltaY !== 0
        ) {
          this.canvas.dispatchEvent(
            `${this.namespace}.drag`,
            new ResizerRendererDragEvent(
              `${this.namespace}.drag`,
              'C',
              dragVal.startValues,
              dragVal.session,
              dragVal.event,
              this.canvas
            )
          );
        }
      }
    });
    dragLayer.addEventListener('htmlDragEnd', (e: any) => {
      let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
      this.canvas.unblockMouseEvents();
      this.canvas.dispatchEvent(
        `${this.namespace}.dragEnd`,
        new ResizerRendererDragEvent(
          `${this.namespace}.dragEnd`,
          'C',
          dragVal.startValues,
          dragVal.session,
          dragVal.event,
          this.canvas
        )
      );
    });
  };
  deactivate() {
    this.canvas.removeFunction(`${this.namespace}.show`);
    this.canvas.removeFunction(`${this.namespace}.hide`);
    this.canvas.removeFunction(`${this.namespace}.showHandle`);
    this.canvas.removeFunction(`${this.namespace}.hideHandle`);
    this.canvas.removeFunction(`${this.namespace}.setOrientation`);
    this.canvas.removeFunction(`${this.namespace}.hideAllHandles`);
    this.dispose();
  }
}
