// import { IPlugin } from '../../interfaces/IPlugin';
// import { Canvas } from '../../models/Canvas';
import { IContext, IDimension } from 'interfaces';
import DeviceFrameEvent from './events/DeviceFrameEvent';
import RemoveChildEvent from 'events/RemoveChildEvent';
import ArtboardEvent from 'events/ArtboardEvent';
import {
  Canvas,
  IPlugin,
  MouseEvent as DesignerMouseEvent,
  Layer,
} from '../..';
import IPhoneXFrame from './Frames/iPhoneX';
import * as React from 'react';
import IPhoneXsMaxFrame from './Frames/iPhoneXsMax';
import ReactDOM from 'react-dom';

export default class DeviceFrame implements IPlugin {
  name = 'DeviceFrame';
  canvas!: Canvas;
  labeltext: any;
  wrapperDiv: any;
  deviceName: any;
  deviceOrientation: any;
  dispose: any;
  removeDom: any;
  deviceFrameMap: Map<any, any> = new Map([]);
  lastPageX = 0;
  lastPageY = 0;
  dragStart: any = {
    x: 0,
    y: 0,
  };
  dims: IDimension = { x: 0, y: 0, height: 0, width: 0 };
  DeviceFrameMap: Map<any, any> = new Map([]);
  ghostImageDOM: any;

  // updateFrameLayout(layer: any) {
  //   this.render();
  // }

  selectedArtboardChanged = (event: ArtboardEvent) => {
    const selectedArtboard = event.artboard;
    if (selectedArtboard) {
      this.DeviceFrameMap.forEach((label: any, artboard: any) => {
        if (artboard === selectedArtboard) {
          let layerTextNode = label.labeltextNode;
          layerTextNode.parentElement.style.color = 'rgb(26, 170, 198)';
        } else {
          label.labeltextNode.parentElement.style.color = 'rgb(186, 189, 198)';
        }
      });
    }
  };

  removeFrame(context: IContext): void {
    const deviceFrame = this.deviceFrameMap.get(context.layer);

    if (deviceFrame) {
      deviceFrame.removeCallback();
    }
  }

  render = (layer: Layer) => {
    let FrameComponent;

    const deviceFrame = this.deviceFrameMap.get(layer);

    if (!deviceFrame) {
      return;
    }

    deviceFrame.frameNode.style.top =
      layer.getBoundingClientRect().rootY + 'px';
    deviceFrame.frameNode.style.left =
      layer.getBoundingClientRect().rootX + 'px';

    if (deviceFrame.deviceName) {
      switch (deviceFrame.deviceName.toLowerCase()) {
        case 'iphone x':
        case 'iphone xs':
        case 'iphone 11 pro':
          FrameComponent = (
            <IPhoneXFrame
              deviceOrientation={deviceFrame.deviceOrientation}
              left={0}
              top={0}
            />
          );
          break;
        case 'iphone xs max':
        case 'iphone xr':
        case 'iphone 11 pro max':
        case 'iphone 11':
          FrameComponent = (
            <IPhoneXsMaxFrame
              deviceOrientation={deviceFrame.deviceOrientation}
              left={0}
              top={0}
            />
          );
          break;
        default:
      }
    }

    if (!FrameComponent) {
      return;
    }

    ReactDOM.render(FrameComponent, deviceFrame.frameNode);
  };

  addFrame(
    context: IContext,
    deviceName: string,
    deviceOrientation: string
  ): void {
    let layer = context.layer;

    const deviceFrame = this.deviceFrameMap.get(layer);

    if (deviceFrame) {
      deviceFrame.removeCallback();
    }
    const wrapperDiv = document.createElement('div');
    wrapperDiv.className = 'frame-wrappers';
    wrapperDiv.style.pointerEvents = 'none';
    wrapperDiv.style.position = 'absolute';

    let dispose = this.canvas.addDomToWorld(wrapperDiv, true, 100);

    this.deviceFrameMap.set(layer, {
      removeCallback: dispose,
      frameNode: wrapperDiv,
      deviceName: deviceName,
      deviceOrientation: deviceOrientation,
    });

    this.render(layer);

    if (layer.parent) {
      layer.parent.addEventListener(
        'removeChild',
        (event: RemoveChildEvent) => {
          if (layer === event.layer)
            layer.executeFunction('DeviceFrame.remove');
        }
      );
    }

    context.layer.on('layout', () => {
      this.render(context.layer);
    });
    context.layer.on('updateDom', () => {
      this.render(context.layer);
    });
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction(`${this.name}.add`, this.addFrame.bind(this));
    this.canvas.addFunction(`${this.name}.remove`, this.removeFrame.bind(this));
    this.canvas.addEventListener(
      'artboardSelected',
      this.selectedArtboardChanged
    );
  }
  deactivate() {}
}
