import { IDimensionWithRotation, IPosition, Layer } from './../';
import { isNil, map, reverse, forEach } from 'lodash';
import { Gradient } from './Gradient';
export default class ModelUtils {
  static pointsOverlap(point1: number, point2: number) {
    return Math.abs(point1 - point2) <= 1;
  }

  static getDimensionFromPolygon(points: any) {
    let height = points[3][1] - points[0][1];
    let width = points[1][0] - points[0][0];
    let y = points[0][1];
    let x = points[0][0];

    return { x, y, width, height };
  }
  static rotatePolygon(poly: any, angle: number, centroid?: any) {
    var centroid = centroid ? centroid : ModelUtils.getCentroid(poly);
    poly.map((point: any, key: number) => {
      point = ModelUtils.rotatePoint(point, angle, centroid);
      poly[key] = point;
    });

    return poly;
  }
  static findAngle(A: any, B: any, C: any, range: 180 | 360) {
    let AB = Math.sqrt(Math.pow(B[0] - A[0], 2) + Math.pow(B[1] - A[1], 2));
    let BC = Math.sqrt(Math.pow(B[0] - C[0], 2) + Math.pow(B[1] - C[1], 2));
    let AC = Math.sqrt(Math.pow(C[0] - A[0], 2) + Math.pow(C[1] - A[1], 2));

    let value = (BC * BC + AB * AB - AC * AC) / (2 * BC * AB);
    if (value > 1) {
      value = 1;
    }
    if (value < -1) {
      value = -1;
    }
    let angle = Math.acos(value);
    angle = angle * (180 / Math.PI);
    if (range === 360) {
      if (C[0] < A[0]) {
        angle = 360 - angle;
      }
    }
    return angle;
  }

  static getAbsoluteRotation(poly: Array<any>) {
    const centroid = ModelUtils.getCentroid(poly);
    // get three points of a triangle to get angle between two lines
    const point1 = [centroid[0], centroid[1] - 2];
    const point3 = ModelUtils.middlePoint(poly[0], poly[1]);
    // get angle created by two lines
    const angle = ModelUtils.findAngle(point1, centroid, point3, 360);
    return angle;
  }
  static middlePoint(point1: any, point2: any) {
    let newX = (point1[0] + point2[0]) / 2;
    let newY = (point1[1] + point2[1]) / 2;
    return [newX, newY];
  }
  static getCentroid(coord: Array<any>) {
    var center: any = coord.reduce(
      function(point1: any, point2: any) {
        return [
          point1[0] + point2[0] / coord.length,
          point1[1] + point2[1] / coord.length,
        ];
      },
      [0, 0]
    );
    return center;
  }
  static distance(point1: any, point2: any) {
    var a = point1[0] - point2[0];
    var b = point1[1] - point2[1];
    var c = Math.sqrt(a * a + b * b);
    return c;
  }
  static rotatePoint(point: any, angle: number, centroid: any) {
    angle = (angle * Math.PI) / 180.0;
    let newX =
      (point[0] - centroid[0]) * Math.cos(angle) -
      (point[1] - centroid[1]) * Math.sin(angle) +
      centroid[0];
    let newY =
      (point[1] - centroid[1]) * Math.cos(angle) +
      (point[0] - centroid[0]) * Math.sin(angle) +
      centroid[1];
    return [newX, newY];
  }
  static getPointsFromBoundingBox(box: any, rotation: number = 0): Array<any> {
    let poly = [
      [box.x, box.y],
      [box.x + box.width, box.y],
      [box.x + box.width, box.y + box.height],
      [box.x, box.y + box.height],
    ];

    // var centroid = getCentroid(poly);
    var rotatedPoly = ModelUtils.rotatePolygon(poly, rotation);

    return rotatedPoly;
  }
  static getPointsFromDims(dims: any) {
    let newX = dims.x;
    let newY = dims.y;
    let width = dims.width;
    let height = dims.height;

    let poly = [
      [newX, newY],
      [newX + width, newY],
      [newX + width, newY + height],
      [newX, newY + height],
    ];

    let transform = reverse([...dims.transform]);

    transform.map((obj: any, index: number) => {
      if (obj.rotate && obj.rotate.rotation && obj.rotate.centroid) {
        poly = ModelUtils.rotatePolygon(
          poly,
          obj.rotate.rotation,
          obj.rotate.centroid
        );
      }
    });

    return poly;
  }
  static depthComparator(a: string | null, b: string | null): -1 | 0 | 1 {
    let activeA: number | null = ModelUtils.getActiveDigit(a);
    let activeB: number | null = ModelUtils.getActiveDigit(b);

    if (a === b) {
      return 0;
    }

    if (activeA !== null && activeB !== null && activeA !== activeB) {
      if (activeA < activeB) {
        return -1;
      } else {
        return 1;
      }
    } else if (activeA !== null && activeB !== null && activeA === activeB) {
      let nextA = ModelUtils.getNextCharacter(a);
      let nextB = ModelUtils.getNextCharacter(b);

      return ModelUtils.depthComparator(nextA, nextB);
    } else if (activeA !== null) {
      return 1;
    } else {
      return -1;
    }
  }
  private static getNextCharacter(a: string | null): string | null {
    if (a === null) {
      return '';
    }
    let aIndex = a.indexOf('.');
    if (aIndex === -1) {
      return '';
    }

    return a.substring(aIndex + 1, a.length);
  }
  private static getActiveDigit(a: string | null): number | null {
    if (a === null || a.length === 0) {
      return null;
    }

    if (!a.includes('.')) {
      return parseInt(a, 10);
    }
    return parseInt(a.substring(0, a.indexOf('.')), 10);
  }
}

export function getCorrection(
  orientation: IDimensionWithRotation,
  finalOrientation: IDimensionWithRotation,
  handle: any
) {
  let rotation = orientation.rotation;
  let angle = (rotation * Math.PI) / 180.0;

  let x1 = -orientation.width / 2;
  let y1 = orientation.height / 2;

  // tslint:disable:variable-name
  let new_x1 = y1 * Math.sin(angle) + x1 * Math.cos(angle);
  let new_y1 = y1 * Math.cos(angle) - x1 * Math.sin(angle);

  let diff1 = { x: new_x1 - x1, y: new_y1 - y1 };

  // only width
  let y2 = orientation.height / 2;
  let x2 = -finalOrientation.width / 2;
  let new_x2 = y2 * Math.sin(angle) + x2 * Math.cos(angle);
  let new_y2 = y2 * Math.cos(angle) - x2 * Math.sin(angle);
  let diff2 = { x: new_x2 - x2, y: new_y2 - y2 };

  let offsetWidth = { x: diff2.x - diff1.x, y: diff2.y - diff1.y };

  // only height

  // tslint:disable:no-duplicate-variable
  y2 = finalOrientation.height / 2;
  x2 = -orientation.width / 2;

  new_x2 = y2 * Math.sin(angle) + x2 * Math.cos(angle);
  new_y2 = y2 * Math.cos(angle) - x2 * Math.sin(angle);

  diff2 = { x: new_x2 - x2, y: new_y2 - y2 };

  let offsetHeight = { x: diff2.x - diff1.x, y: diff2.y - diff1.y };

  let newX = finalOrientation.x;
  let newY = finalOrientation.y;

  if (handle.indexOf('T') !== -1 || handle.indexOf('L') !== -1) {
    newX = finalOrientation.x + (offsetWidth.x + offsetHeight.x);
    newY = finalOrientation.y - (offsetWidth.y + offsetHeight.y);
  }

  if (handle.indexOf('B') !== -1 || handle.indexOf('R') !== -1) {
    newX = finalOrientation.x - (offsetWidth.x + offsetHeight.x);
    newY = finalOrientation.y + (offsetWidth.y + offsetHeight.y);
  }

  if (handle.indexOf('B') !== -1 && handle.indexOf('L') !== -1) {
    newX = finalOrientation.x + offsetWidth.x - offsetHeight.x;
    newY = finalOrientation.y - offsetWidth.y + offsetHeight.y;
  }

  if (handle.indexOf('T') !== -1 && handle.indexOf('R') !== -1) {
    newX = finalOrientation.x - offsetWidth.x + offsetHeight.x;
    newY = finalOrientation.y + offsetWidth.y - offsetHeight.y;
  }

  if (handle.indexOf('C') !== -1) {
    newX = finalOrientation.x;
    newY = finalOrientation.y;
  }

  return {
    x: newX,
    y: newY,
    height: finalOrientation.height,
    width: finalOrientation.width,
    rotation,
  };
}

export function getResizerQuad(rotation: number): string {
  rotation = rotation % 360;

  if (rotation < 0) {
    rotation += 360;
  }
  if (!rotation) {
    return '4';
  }
  if (rotation < 0) {
    //
  }
  if (
    (rotation >= 22.5 && rotation < 67.5) ||
    (rotation >= 202.5 && rotation < 247.5)
  ) {
    return '1';
  }

  if (
    (rotation >= 67.5 && rotation < 112.5) ||
    (rotation >= 247.5 && rotation < 292.5)
  ) {
    return '2';
  }
  if (
    (rotation >= 112.5 && rotation < 157.5) ||
    (rotation >= 292.5 && rotation < 337.5)
  ) {
    return '3';
  }

  if (
    (rotation >= 157.5 && rotation < 202.5) ||
    (rotation >= 337.5 && rotation < 22.5)
  ) {
    return '4';
  }
  return '4';
}

export function computeOrientation(
  layout: IDimensionWithRotation,
  delta: IPosition,
  handle: string,
  shift: boolean,
  alt: boolean
) {
  if (shift && !alt) {
    return computeOrientationWithDeltaAR(layout, delta, handle);
  } else if (!shift && alt) {
    return computeOrientationWithDeltaAlt(layout, delta, handle);
  } else if (shift && alt) {
    return computeOrientationWithDeltaShiftAlt(layout, delta, handle);
  } else {
    return computeOrientationWithDelta(layout, delta, handle);
  }
}

export function computeOrientationMultiple(
  orientation: IDimensionWithRotation,
  delta: IPosition,
  handle: any,
  boundaryBox: any,
  points: Array<any>,
  shift?: boolean,
  alt?: boolean
) {
  if (shift && !alt) {
    return computeOrientationWithDeltaARMultiple(
      orientation,
      delta,
      handle,
      boundaryBox,
      points
    );
  } else if (!shift && alt) {
    return computeOrientationWithDeltaAltMultiple(
      orientation,
      delta,
      handle,
      boundaryBox,
      points
    );
  } else if (shift && alt) {
    return computeOrientationWithDeltaShiftAltMultiple(
      orientation,
      delta,
      handle,
      boundaryBox,
      points
    );
  } else {
    return computeOrientationWithDeltaMultiple(
      orientation,
      delta,
      handle,
      boundaryBox,
      points
    );
  }
}

export function computeOrientationWithDelta(
  layout: IDimensionWithRotation,
  delta: IPosition,
  handle: string
): IDimensionWithRotation {
  let x = layout.x;
  let y = layout.y;
  let width = Math.max(layout.width, 0);
  let height = Math.max(layout.height, 0);
  let deltaX = !isNil(delta.x) ? delta.x : 0;
  let deltaY = !isNil(delta.y) ? delta.y : 0;
  let rotation = layout.rotation;

  if (handle.indexOf('C') !== -1) {
    x += deltaX;
    y += deltaY;
  }

  if (handle.indexOf('B') !== -1) {
    height += deltaY;
  }
  if (handle.indexOf('R') !== -1) {
    width += deltaX;
  }
  if (handle.indexOf('T') !== -1) {
    height -= deltaY;
    y += deltaY;
  }

  if (handle.indexOf('L') !== -1) {
    width -= deltaX;
    x += deltaX;
  }

  let finalOrientation = {
    x,
    y,
    width,
    height,
    rotation,
  };

  finalOrientation = getCorrection(layout, finalOrientation, handle);
  return finalOrientation;
}

export function computeOrientationWithDeltaMultiple(
  orientation: IDimensionWithRotation,
  delta: IPosition,
  handle: any,
  boundaryBox: any,
  points: Array<any>
): IDimensionWithRotation {
  let x = orientation.x;
  let y = orientation.y;
  let width = orientation.width;
  let height = orientation.height;
  let deltaX = !isNil(delta.x) ? delta.x : 0;
  let deltaY = !isNil(delta.y) ? delta.y : 0;
  let rotation = orientation.rotation;
  if (handle.indexOf('C') !== -1) {
    x += deltaX;
    y += deltaY;
  } else {
    let newPoints: any = [...points];
    map(newPoints, (point: any, index: number) => {
      let pointXDiff = 0;
      let pointYDiff = 0;
      if (handle.indexOf('B') !== -1) {
        pointYDiff =
          1 -
          (boundaryBox.height - (point[1] - boundaryBox.y)) /
            boundaryBox.height;
      }
      if (handle.indexOf('R') !== -1) {
        pointXDiff =
          1 -
          (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;
      }
      if (handle.indexOf('T') !== -1) {
        pointYDiff =
          (boundaryBox.height - (point[1] - boundaryBox.y)) /
          boundaryBox.height;
      }
      if (handle.indexOf('L') !== -1) {
        pointXDiff =
          (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;
      }
      let pointY = point[1] + pointYDiff * deltaY;
      let pointX = point[0] + pointXDiff * deltaX;
      let pointNew = [pointX, pointY];
      newPoints[index] = pointNew;
    });

    newPoints = ModelUtils.rotatePolygon(newPoints, 360 - rotation);
    let box = ModelUtils.getDimensionFromPolygon(newPoints);
    height = box.height;
    width = box.width;
    y = box.y;
    x = box.x;
  }

  return {
    width,
    height,
    x,
    y,
    rotation,
  };
}

export function computeOrientationWithDeltaARMultiple(
  orientation: IDimensionWithRotation,
  deltaInp: IPosition,
  handle: any,
  boundaryBox: any,
  points: Array<any>
): IDimensionWithRotation {
  let x = orientation.x;
  let y = orientation.y;
  let width = orientation.width;
  let height = orientation.height;
  let rotation = orientation.rotation;
  let deltaX = !isNil(deltaInp.x) ? deltaInp.x : 0;
  let deltaY = !isNil(deltaInp.y) ? deltaInp.y : 0;

  let aspectRatio = boundaryBox.width / boundaryBox.height;

  if (handle.indexOf('C') !== -1) {
    x += deltaX;
    y += deltaY;
  } else {
    let newPoints: any = [...points];
    let initialDims = ModelUtils.getDimensionFromPolygon(newPoints);
    let widthFactor =
      (boundaryBox.width - initialDims.width) / boundaryBox.width;

    map(newPoints, (point: any, index: number) => {
      let pointXDiff = 0,
        pointYDiff = 0,
        pointX = 0,
        pointY = 0,
        delta = 0;
      // for buttom and right
      let pointYDiffDirect =
        1 -
        (boundaryBox.height - (point[1] - boundaryBox.y)) / boundaryBox.height;
      let pointXDiffDirect =
        1 -
        (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;
      // for top and right
      let pointYDiffOpposite =
        (boundaryBox.height - (point[1] - boundaryBox.y)) / boundaryBox.height;
      let pointXDiffOpposite =
        (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;

      if (handle.length > 1) {
        if (handle.indexOf('B') !== -1 && handle.indexOf('L') !== -1) {
          delta = deltaX > -deltaY ? deltaX : deltaY;
          pointYDiff = pointYDiffDirect;
          pointXDiff = pointXDiffOpposite;

          if (deltaX > -deltaY) {
            pointY = point[1] - pointYDiff * delta * (1 / aspectRatio);
            pointX = point[0] + delta * pointXDiff;
          } else {
            pointY = point[1] + pointYDiff * delta;
            pointX = point[0] - delta * pointXDiff * aspectRatio;
          }
        }
        if (handle.indexOf('B') !== -1 && handle.indexOf('R') !== -1) {
          delta = deltaX < deltaY ? deltaX : deltaY;
          pointYDiff = pointYDiffDirect;
          pointXDiff = pointXDiffDirect;

          if (deltaX > deltaY) {
            pointY = point[1] + pointYDiff * delta;
            pointX = point[0] + delta * pointXDiff * aspectRatio;
          } else {
            pointY = point[1] + pointYDiff * delta * (1 / aspectRatio);
            pointX = point[0] + delta * pointXDiff;
          }
        }
        if (handle.indexOf('T') !== -1 && handle.indexOf('R') !== -1) {
          delta = deltaX < -deltaY ? deltaX : deltaY;

          pointYDiff = pointYDiffOpposite;
          pointXDiff = pointXDiffDirect;

          if (deltaX > -deltaY) {
            pointY = point[1] + pointYDiff * delta;
            pointX = point[0] - delta * pointXDiff * aspectRatio;
          } else {
            pointY = point[1] - pointYDiff * delta * (1 / aspectRatio);
            pointX = point[0] + delta * pointXDiff;
          }
        }
        if (handle.indexOf('T') !== -1 && handle.indexOf('L') !== -1) {
          delta = deltaX > deltaY ? deltaX : deltaY;
          pointYDiff = pointYDiffOpposite;
          pointXDiff = pointXDiffOpposite;

          if (deltaX > deltaY) {
            pointY = point[1] + pointYDiff * delta * (1 / aspectRatio);
            pointX = point[0] + delta * pointXDiff;
          } else {
            pointY = point[1] + pointYDiff * delta;
            pointX = point[0] + delta * pointXDiff * aspectRatio;
          }
        }
      } else {
        if (handle.indexOf('B') !== -1) {
          pointYDiff = pointYDiffDirect;
          pointXDiff = pointXDiffDirect;
          pointY = point[1] + pointYDiff * deltaY;
          pointX =
            point[0] +
            deltaY * aspectRatio * pointXDiff -
            (deltaY * aspectRatio) / 2;
        }

        if (handle.indexOf('T') !== -1) {
          pointYDiff = pointYDiffOpposite;
          pointXDiff = pointXDiffOpposite;

          pointY = point[1] + pointYDiff * deltaY;
          pointX =
            point[0] +
            deltaY * pointXDiff * aspectRatio -
            (deltaY * aspectRatio) / 2;
        }

        if (handle.indexOf('R') !== -1) {
          pointYDiff = pointYDiffDirect;
          pointXDiff = pointXDiffDirect;

          pointX = point[0] + pointXDiff * deltaX;
          pointY =
            point[1] +
            deltaX * pointYDiff * (1 / aspectRatio) -
            (deltaX * (1 / aspectRatio)) / 2;
        }

        if (handle.indexOf('L') !== -1) {
          pointYDiff = pointYDiffOpposite;
          pointXDiff = pointXDiffOpposite;

          pointX = point[0] + pointXDiff * deltaX;
          pointY =
            point[1] +
            deltaX * pointYDiff * (1 / aspectRatio) -
            (deltaX * (1 / aspectRatio)) / 2;
        }
      }

      let pointNew = [pointX, pointY];
      newPoints[index] = pointNew;
    });
    newPoints = ModelUtils.rotatePolygon(newPoints, 360 - rotation);

    let box = ModelUtils.getDimensionFromPolygon(newPoints);
    height = box.height;
    width = box.width;
    y = box.y;
    x = box.x;
  }

  return {
    width,
    height,
    x,
    y,
    rotation,
  };
}

export function computeOrientationWithDeltaAltMultiple(
  orientation: IDimensionWithRotation,
  delta: IPosition,
  handle: any,
  boundaryBox: any,
  points: Array<any>
): IDimensionWithRotation {
  let x = orientation.x;
  let y = orientation.y;
  let width = orientation.width;
  let height = orientation.height;
  let rotation = orientation.rotation;
  let deltaX = !isNil(delta.x) ? delta.x : 0;
  let deltaY = !isNil(delta.y) ? delta.y : 0;

  if (handle.indexOf('C') !== -1) {
    x += deltaX;
    y += deltaY;
  } else {
    let newPoints: any = [...points];
    let initialDims = ModelUtils.getDimensionFromPolygon(newPoints);
    let widthFactor =
      (boundaryBox.width - initialDims.width) / boundaryBox.width;

    map(newPoints, (point: any, index: number) => {
      let pointXDiff = 0,
        pointYDiff = 0,
        pointX = 0,
        pointY = 0;
      // delta = 0;
      // for buttom and right
      let pointYDiffDirect =
        1 -
        (boundaryBox.height - (point[1] - boundaryBox.y)) / boundaryBox.height;
      let pointXDiffDirect =
        1 -
        (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;

      // for top and right
      let pointYDiffOpposite =
        (boundaryBox.height - (point[1] - boundaryBox.y)) / boundaryBox.height;
      let pointXDiffOpposite =
        (boundaryBox.width - (point[0] - boundaryBox.x)) / boundaryBox.width;

      if (handle.indexOf('B') !== -1) {
        pointYDiff = pointYDiffDirect;
        pointXDiff = pointXDiffDirect;

        pointY = point[1] + pointYDiff * deltaY - deltaY / 2;
        if (handle.indexOf('L') === -1 && handle.indexOf('R') === -1) {
          pointX = point[0] + pointXDiff;
        } else {
          pointX = point[0] + pointXDiff * deltaX - deltaX / 2;
        }
      }

      if (handle.indexOf('T') !== -1) {
        pointYDiff = pointYDiffOpposite;
        pointXDiff = pointXDiffDirect;
        pointY = point[1] - pointYDiff * deltaY - deltaY / 2;

        if (handle.indexOf('L') === -1 && handle.indexOf('R') === -1) {
          pointX = point[0] + pointXDiff;
        } else {
          pointX = point[0] + pointXDiff * deltaX - deltaX / 2;
        }
      }

      if (handle.indexOf('R') !== -1) {
        pointXDiff = pointXDiffDirect;
        pointYDiff = pointYDiffDirect;

        pointX = point[0] + pointXDiff * deltaX - deltaX / 2;
        if (handle.indexOf('T') === -1 && handle.indexOf('B') === -1) {
          pointY = point[1] + pointYDiff;
        } else {
          if (handle.indexOf('B') !== -1) {
            pointY = point[1] + pointYDiff * deltaY - deltaY / 2;
          } else {
            pointY = point[1] - pointYDiff * deltaY + deltaY / 2;
          }
        }
      }

      if (handle.indexOf('L') !== -1) {
        pointXDiff = pointXDiffOpposite;
        pointYDiff = pointYDiffDirect;
        pointX = point[0] + pointXDiff * deltaX - deltaX / 2;
        if (handle.indexOf('T') === -1 && handle.indexOf('B') === -1) {
          pointY = point[1] + pointYDiff;
        } else {
          if (handle.indexOf('B') !== -1) {
            pointY = point[1] + pointYDiff * deltaY - deltaY / 2;
          } else {
            pointY = point[1] - pointYDiff * deltaY + deltaY / 2;
          }
        }
      }

      let pointNew = [pointX, pointY];
      newPoints[index] = pointNew;
    });
    newPoints = ModelUtils.rotatePolygon(newPoints, 360 - rotation);

    let box = ModelUtils.getDimensionFromPolygon(newPoints);
    height = box.height;
    width = box.width;
    y = box.y;
    x = box.x;
  }

  return {
    width,
    height,
    x,
    y,
    rotation,
  };
}

