export function getBoundingBoxFromOrientation({
  x,
  y,
  width,
  height,
}: any): any {
  return {
    minX: x,
    minY: y,
    maxX: x + width,
    maxY: y + height,
  };
}

export function computeDelta(
  oldOrientation: IDimensionWithRotation,
  newOrientation: IDimensionWithRotation
): IPosition {
  let [deltaX, deltaY] = [0, 0];

  const oldBB = getBoundingBoxFromOrientation(oldOrientation);
  const newBB = getBoundingBoxFromOrientation(newOrientation);

  if (oldBB.minX !== newBB.minX) {
    deltaX = newBB.minX - oldBB.minX;
  }
  if (oldBB.minY !== newBB.minY) {
    deltaY = newBB.minY - oldBB.minY;
  }

  if (oldBB.maxX !== newBB.maxX) {
    deltaX = newBB.maxX - oldBB.maxX;
  }
  if (oldBB.maxY !== newBB.maxY) {
    deltaY = newBB.maxY - oldBB.maxY;
  }

  return {
    x: deltaX,
    y: deltaY,
  };
}

export function getGradientString(gradient: Gradient, palette?: any) {
  let colors: any = [];

  if (palette) {
    forEach(palette, (data: any, index: number) => {
      colors.push(data.color);
    });
  } else {
    forEach(gradient.stops, (data: any, index: number) => {
      colors.push(data.stopColor);
    });
  }
  let vector;
  if (gradient.to && gradient.from) {
    vector = {
      x: (gradient.to.x as number) - (gradient.from.x as number),
      y: (gradient.to.y as number) - (gradient.from.y as number),
    };
  } else {
    vector = {
      x: 0,
      y: 2,
    };
  }
  const angleRad = Math.atan2(vector.y, vector.x);
  const angleDeg = (angleRad * 180) / Math.PI + 90;

  let realLocations: any = [];
  if (palette) {
    forEach(palette, (data: any, index: number) => {
      realLocations.push(data.pos);
    });
  } else {
    forEach(gradient.stops, (data: any, index: number) => {
      realLocations.push(data.offset);
    });
  }

  const colorStrings = colors
    .map(
      (color: any, i: number) =>
        `${color} ${Math.round(realLocations[i] * 100)}%`
    )
    .join(', ');

  if (gradient.gradientType && gradient.gradientType === 'LinearGradient') {
    return `linear-gradient(${angleDeg}deg, ${colorStrings})`;
  } else if (
    gradient.gradientType &&
    gradient.gradientType === 'RadialGradient'
  ) {
    const dist = Math.sqrt(
      (gradient.to.x - gradient.from.x) * (gradient.to.x - gradient.from.x) +
        (gradient.to.y - gradient.from.y) * (gradient.to.y - gradient.from.y)
    );
    const maxDist = Math.sqrt(
      (1 - gradient.from.x) * (1 - gradient.from.x) +
        (1 - gradient.from.y) * (1 - gradient.from.y)
    );
    const radialColorStrings = colors
      .map(
        (color: any, i: number) =>
          `${color} ${Math.round(realLocations[i] * (dist / maxDist) * 100)}%`
      )
      .join(', ');
    return `radial-gradient(circle at ${gradient.from.x * 100}% ${gradient.from
      .y * 100}%,${radialColorStrings})`;
  } else if (
    gradient.gradientType &&
    gradient.gradientType === 'AngularGradient'
  ) {
    return `conic-gradient(${colorStrings})`;
  } else {
    return `linear-gradient(${angleDeg}deg, ${colorStrings})`;
  }
}

export function getHandles(layers: Array<Layer>) {
  let handles = ['T', 'B', 'R', 'L', 'BL', 'BR', 'TR', 'TL', 'ROTATE'];
  if (
    layers.length === 0 ||
    (layers.length === 1 && layers[0].type === 'canvas')
  ) {
    return [];
  }

  if (
    layers.length === 1 &&
    layers[0].type === 'text' &&
    !layers[0].style.get('height') &&
    !layers[0].style.get('width')
  ) {
    const textLayer = layers[0];
    const parentDims = textLayer.parent?.getBoundingClientRect();
    const textLayerDims = textLayer.getBoundingClientRect();
    let newHandle = '';

    if (parentDims) {
      let parentCenterY = parentDims.rootY + parentDims.height / 2;
      let parentCenterX = parentDims.rootX + parentDims.width / 2;

      let textLayerCenterY = textLayerDims.rootY + textLayerDims.height / 2;
      let textLayerCenterX = textLayerDims.rootX + textLayerDims.width / 2;
      if (textLayerCenterY < parentCenterY) {
        newHandle = 'B';
      } else if (textLayerCenterY > parentCenterY) {
        newHandle += 'T';
      } else {
        newHandle += '';
      }

      if (textLayerCenterX < parentCenterX) {
        newHandle += 'R';
      } else if (textLayerCenterX > parentCenterX) {
        newHandle += 'L';
      } else {
        newHandle += '';
      }

      if (
        textLayerCenterX === parentCenterX &&
        textLayerCenterY === parentCenterY
      ) {
        newHandle = 'B';
      }
      return [newHandle, 'ROTATE'];
    }
  }
  // layers.forEach((layer: Layer) => {
  // if (layer.type === "icon" || layer.type === "switch") {
  //   handles = intersection(handles, ["ROTATE"]);
  // }
  // const isLayerAbsolute = !isNil(
  //   layer.style.get("position")
  // );
  // const isAutoHeight = layer.isHeightAuto;
  // const isAutoWidth = layer.isWidthAuto;
  // let handlesForAutoOrAbs = this.getHandlesForAutoOrAbsoluteLayer(
  //   isAutoHeight,
  //   isAutoWidth
  // );

  // if (isLayerAbsolute) {
  //   handles = intersection(handles, handlesForAutoOrAbs);
  // } else {
  //   const handlesForFill = this.getHandlesForFill(layer);
  //   let handlesHere = intersection(handlesForAutoOrAbs, handlesForFill);
  //   handles = intersection(handles, handlesHere);
  // }
  // });
  return handles;
}
function getHandlePoint(box: any, handle?: string) {
  if (handle) {
    let point = [box.x + box.width / 2, box.y];
    switch (handle) {
      case 'ROTATETL':
        point = [box.x, box.y];
        break;
      case 'ROTATETR':
        point = [box.x + box.width, box.y];
        break;
      case 'ROTATEL':
        point = [box.x, box.y + box.height / 2];
        break;
      case 'ROTATER':
        point = [box.x + box.width, box.y + box.height / 2];
        break;
      case 'ROTATEBL':
        point = [box.x, box.y + box.height];
        break;
      case 'ROTATEB':
        point = [box.x + box.width / 2, box.y + box.height];
        break;
      case 'ROTATEBR':
        point = [box.x + box.width, box.y + box.height];
        break;
      default:
        break;
    }
    return point;
  } else return [box.x + box.width / 2, box.y - 28];
}

function getQuotient(a: any, b: any, c: any) {
  return (b[0] - a[0]) * (c[1] - a[1]) - (b[1] - a[1]) * (c[0] - a[0]) > 0
    ? -1
    : 1;
}
export function calculateRotation(
  startValues: any,
  session: any,
  oldRotation?: number,
  delta?: any,
  handle?: string
) {
  let deltaInput = {
    x: session.current.x - session.start.x,
    y: session.current.y - session.start.y,
  };

  if (delta) {
    deltaInput = delta;
  }
  const rotation = oldRotation ? oldRotation : startValues.rotation;
  const point1 = getHandlePoint(startValues, handle);
  const polygon = ModelUtils.getPointsFromBoundingBox(startValues);
  const centroid = ModelUtils.getCentroid(polygon);
  const point3 = [point1[0] + deltaInput.x, point1[1] + deltaInput.y];
  let angle = ModelUtils.findAngle(point1, centroid, point3, 180);
  angle = rotation + angle * getQuotient(point1, centroid, point3);
  return angle;
}

export function getNewLayout(startValues: any, session: any, handle: any) {
  let newLayout: any;
  let deltaX = session.current.deltaX;
  let deltaY = session.current.deltaY;
  if (handle.indexOf('ROTATE') !== -1) {
    let rotation = calculateRotation(
      startValues,
      session,
      undefined,
      undefined,
      handle
    );
    newLayout = { ...startValues, rotation };
  } else {
    if (handle === 'C') {
      let rotateDelta = ModelUtils.rotatePoint(
        [deltaX, deltaY],
        startValues.rotation,
        [0, 0]
      );
      deltaX = rotateDelta[0];
      deltaY = rotateDelta[1];
      newLayout = computeOrientationWithDelta(
        startValues,
        { x: deltaX, y: deltaY },
        handle
      );
    } else {
      newLayout = computeOrientationWithDelta(
        startValues,
        { x: deltaX, y: deltaY },
        handle
      );
    }
  }
  if (newLayout.height === 0) {
    newLayout.height = 0;
  }
  if (newLayout.width === 0) {
    newLayout.width = 0;
  }
  return newLayout;
}

export function generateRandomString(length: number) {
  let result = '';
  let characters = 'ABCDEFGHIJKLMNPQRSTUVWXYZabcdefghijklmnpqrstuvwxyz';
  for (let i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * characters.length));
  }
  return result;
}
