import { Canvas, IPlugin, IContext } from '../..';
import { LineDrawerDragEvent } from '../LineDrawer/events';
import { isNil, findIndex } from 'lodash';
import RulerLinesAddEvent from './events/RulerLinesAddEvent';
import RulerLinesSetPositionEvent from './events/RulerLinesSetPositionEvent';
import RulerLinesRemoveEvent from './events/RulerLinesRemoveEvent';
import IRulerLine from './interfaces/IRulerLine';

export default class RulerLinesPlugin implements IPlugin {
  name = 'RulerLinesPlugin';
  canvas!: Canvas;
  lines: Array<IRulerLine> = [];
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.lines = [];
  }

  addLine = (
    context: IContext,
    axis: 'x' | 'y',
    axisCoordinate: number,
    noEmit: boolean = false
  ) => {
    const lineWrapper = this.canvas.executeFunction(
      'drawLine',
      axis,
      axisCoordinate,
      'rulerHorizontalLine',
      0,
      null,
      true
    );
    this.lines.push({ axis, axisCoordinate, dom: lineWrapper });
    if (!noEmit)
      this.canvas.dispatchEvent(
        'RulerLines.add',
        new RulerLinesAddEvent(
          'RulerLines.add',
          lineWrapper,
          axis,
          axisCoordinate
        )
      );
    this.canvas.addEventListener(
      'LineDrawer.dragStart',
      (data: LineDrawerDragEvent) => {
        const deltaInput = {
          x: data.session.current.x,
          y: data.session.current.y,
        };
        const target = data.originalEvent.originalEvent.target;
        const index = findIndex(this.lines, function(o) {
          return o.dom == target;
        });
        this.setPosition(
          { layer: target, canvas: this.canvas },
          data.axis == 'y' ? deltaInput.x : deltaInput.y,
          index
        );
      }
    );
    this.canvas.addEventListener(
      'LineDrawer.onDrag',
      (data: LineDrawerDragEvent) => {
        const deltaInput = {
          x: data.session.current.x,
          y: data.session.current.y,
        };
        const target = data.originalEvent.originalEvent.target;
        const index = findIndex(this.lines, function(o) {
          return o.dom == target;
        });
        this.setPosition(
          { layer: target, canvas: this.canvas },
          data.axis == 'y' ? deltaInput.x : deltaInput.y,
          index
        );
      }
    );
    this.canvas.addEventListener(
      'LineDrawer.dragEnd',
      (data: LineDrawerDragEvent) => {
        const deltaInput = {
          x: data.session.current.x,
          y: data.session.current.y,
        };
        const target = data.originalEvent.originalEvent.target;
        const index = findIndex(this.lines, function(o) {
          return o.dom == target;
        });
        this.setPosition(
          { layer: target, canvas: this.canvas },
          data.axis == 'y' ? deltaInput.x : deltaInput.y,
          index,
          true
        );
      }
    );
    return this.lines.length - 1;
  };

  removeLine = (context: IContext, index: number) => {
    const { dom } = this.lines[index];
    if (dom && dom.parentNode) {
      dom.parentNode.removeChild(dom);
      this.canvas.removeDomFromWorld(dom);
      this.lines.splice(index, 1);
      this.canvas.dispatchEvent(
        'RulerLines.remove',
        new RulerLinesRemoveEvent('RulerLines.remove', this.canvas, index)
      );
    }
  };

  setPosition = (
    context: IContext,
    axisCoordinate: number,
    index?: number,
    dragEnd?: boolean
  ) => {
    let indexOfLayer;
    if (isNil(index)) {
      indexOfLayer = this.lines.length - 1;
    } else {
      indexOfLayer = index;
    }
    const line = this.lines[indexOfLayer];
    if (line) {
      const { dom, axis } = line;
      if (axis == 'y') {
        dom.style.left = axisCoordinate + 'px';
      } else {
        dom.style.top = axisCoordinate + 'px';
      }
      this.lines[indexOfLayer] = { axis, axisCoordinate, dom };
      this.canvas.dispatchEvent(
        'RulerLines.setPosition',
        new RulerLinesSetPositionEvent(
          'RulerLines.setPosition',
          this.canvas,
          axis,
          axisCoordinate,
          indexOfLayer
        )
      );

      if (dragEnd) {
        let axisPoint = {
          x: axis == 'y' ? axisCoordinate : 0,
          y: axis == 'x' ? axisCoordinate : 0,
        };
        let viewportPoint = this.canvas.convertPoint(
          axisPoint,
          'world',
          'viewport'
        );
        let viewportAxisCoordinate =
          axis === 'y' ? viewportPoint.x : viewportPoint.y;
        if (viewportAxisCoordinate < 18) {
          this.removeLine(
            { layer: this.canvas, canvas: this.canvas },
            indexOfLayer
          );
        }
      }
    }
  };

  removeAll = (context: IContext) => {
    this.lines.forEach((line: any) => {
      const dom = line.dom;
      if (dom) {
        dom.remove();
        this.canvas.removeDomFromWorld(dom);
      }
    });
    this.lines = [];
  };

  addAll = (
    context: IContext,
    data: [{ axis: 'x' | 'y'; axisCoordinate: number }],
    emit: boolean = true
  ) => {
    data.forEach((line: { axis: 'x' | 'y'; axisCoordinate: number }) => {
      this.addLine(
        { layer: this.canvas, canvas: this.canvas },
        line.axis,
        line.axisCoordinate,
        emit
      );
    });
  };

  show = (context: IContext) => {
    this.lines.forEach((line: any) => {
      const dom = line.dom;
      if (dom) {
        dom.style.display = '';
      }
    });
  };

  hide = (context: IContext) => {
    this.lines.forEach((line: any) => {
      const dom = line.dom;
      if (dom) {
        dom.style.display = 'none';
      }
    });
  };

  getLineCoordinates = (context: IContext) => {
    let coordinates: { x: Array<number>; y: Array<number> } = {
      x: [],
      y: [],
    };

    if (this.lines && this.lines.length > 0) {
      if (this.lines[0].dom.style.display !== 'none') {
        for (let line of this.lines) {
          if (line.axis === 'x') {
            coordinates.x.push(line.axisCoordinate);
          } else {
            coordinates.y.push(line.axisCoordinate);
          }
        }
      }
    }

    return coordinates;
  };

  activate() {
    this.canvas.addFunction('RulerLines.addLine', this.addLine);
    this.canvas.addFunction('RulerLines.removeLine', this.removeLine);
    this.canvas.addFunction('RulerLines.setPosition', this.setPosition);
    this.canvas.addFunction('RulerLines.removeAll', this.removeAll);
    this.canvas.addFunction('RulerLines.addAll', this.addAll);
    this.canvas.addFunction('RulerLines.show', this.show);
    this.canvas.addFunction('RulerLines.hide', this.hide);
    this.canvas.addFunction(
      'RulerLines.getLineCoordinates',
      this.getLineCoordinates
    );
  }

  deactivate() {
    this.canvas.removeFunction('RulerLines.addLine');
    this.canvas.removeFunction('RulerLines.removeLine');
    this.canvas.removeFunction('RulerLines.setPosition');
    this.canvas.removeFunction('RulerLines.removeAll');
    this.canvas.removeFunction('RulerLines.addAll');
    this.canvas.removeFunction('RulerLines.show');
    this.canvas.removeFunction('RulerLines.hide');
    this.canvas.removeFunction('RulerLines.getLineCoordinates');
  }
}
