import * as React from 'react';
import ReactDOM from 'react-dom';
import { Canvas, IPlugin, PathEvent } from '../..';
import { IDimensionWithRoot } from 'interfaces';
import PathEditorRenderer from './PathEditorRenderer';

export default class PathEditorPlugin implements IPlugin {
  name = 'PathEditorPlugin';
  nameSpace: string = 'PathEditor';
  canvas!: Canvas;
  dispose: any;
  wrapperLayer: any;
  selectedTool: string = 'pen';
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  handlePathEditorEvents = (event: any) => {
    const { payload, target, name } = event;
    switch (name) {
      case 'pathEditorOnReady':
        const {
          deleteSelectedPoint,
          disconnectSelectedPoint,
          straightenSelectedPoint,
          finishEditing,
          closePath,
        } = payload.actions;
        this.canvas.addFunction(
          `${this.nameSpace}.deleteSelectedPoint`,
          deleteSelectedPoint
        );
        this.canvas.addFunction(
          `${this.nameSpace}.disconnectSelectedPoint`,
          disconnectSelectedPoint
        );
        this.canvas.addFunction(
          `${this.nameSpace}.straightenSelectedPoint`,
          straightenSelectedPoint
        );
        this.canvas.addFunction(
          `${this.nameSpace}.finishEditing`,
          finishEditing
        );
        this.canvas.addFunction(`${this.nameSpace}.closePath`, closePath);
        break;

      case 'pathEditorOnChange':
        const { pathProps, bounds } = payload;
        let viewBox = `0 0 ${bounds.width} ${bounds.height}`;
        let style = {
          left: bounds.x,
          top: bounds.y,
          width: bounds.width,
          height: bounds.height,
          position: 'absolute',
        };
        if (this.selectedTool === 'path' && target) {
          target.dispatchEvent(
            `${this.nameSpace}.onEdit`,
            new PathEvent(`${this.nameSpace}.onEdit`, target, {
              viewBox,
              points: pathProps.points,
              style,
              isClosed: pathProps.closed,
            })
          );
        } else if (this.selectedTool === 'pen') {
          this.canvas.dispatchEvent(
            `${this.nameSpace}.addNew`,
            new PathEvent(`${this.nameSpace}.addNew`, this.canvas, {
              viewBox,
              points: pathProps.points,
              style,
              isClosed: pathProps.closed,
            })
          );
          this.hide();
        }
        break;
      case 'pathEditorOnEnd':
        this.hide();
        target.setStyle('display', '');
        this.canvas.dispatchEvent(
          `${this.nameSpace}.isClosed`,
          new PathEvent(`${this.nameSpace}.isClosed`, this.canvas)
        );
        break;
      case 'pathEditor.selectParent':
        this.canvas.dispatchEvent(`${this.nameSpace}.selectParent`, event);
        break;
      default:
        break;
    }
  };

  hide = () => {
    ReactDOM.unmountComponentAtNode(this.wrapperLayer);
    this.wrapperLayer.style.display = 'none';
    this.canvas.removeFunction(`${this.nameSpace}.deleteSelectedPoint`);
    this.canvas.removeFunction(`${this.nameSpace}.disconnectSelectedPoint`);
    this.canvas.removeFunction(`${this.nameSpace}.straightenSelectedPoint`);
    this.canvas.removeFunction(`${this.nameSpace}.finishEditing`);
    this.canvas.removeFunction(`${this.nameSpace}.closePath`);
  };
  getScrollShift = () => {
    const canvasScroll = this.canvas.getScroll();
    const zoom = this.canvas.getZoom();
    const viewPortLeftPos: number =
      canvasScroll.x *
      (this.canvas.layout.get('width') * zoom -
        this.canvas.getViewport().width);
    const viewPortTopPos: number =
      canvasScroll.y *
      (this.canvas.layout.get('height') * zoom -
        this.canvas.getViewport().height);
    return { x: viewPortLeftPos, y: viewPortTopPos };
  };
  handleEvent = (data: PathEvent) => {
    this.selectedTool = data.payload.selectedTool;
    let dims: IDimensionWithRoot;
    const viewPort = this.canvas.getViewport();
    const { x, y } = this.getScrollShift();
    const zoom = this.canvas.getZoom();
    // let bounds = {
    //   x: x / zoom,
    //   y: y / zoom,
    //   rootX: x / zoom,
    //   rootY: y / zoom,
    //   height: viewPort.height / zoom,
    //   width: viewPort.width / zoom,
    // };
    let bounds = this.canvas.getBoundingClientRect();
    if (this.selectedTool === 'path') {
      data.target.setStyle('display', 'none');
      dims = data.target.getBoundingClientRect();
    } else {
      dims = bounds;
    }
    this.wrapperLayer.style.display = '';
    // this.wrapperLayer.style.top = dims.rootY + 'px';
    // this.wrapperLayer.style.left = dims.rootX + 'px';
    this.wrapperLayer.style.height = dims.height + 'px';
    this.wrapperLayer.style.width = dims.width + 'px';
    // this.wrapperLayer.style.border = '2px solid green';
    ReactDOM.render(
      <PathEditorRenderer
        onEvent={this.handlePathEditorEvents}
        layer={this.selectedTool === 'pen' ? this.canvas : data.target}
        newPath={this.selectedTool === 'pen'}
        bounds={{
          left: bounds.x,
          top: bounds.y,
          width: bounds.width / this.canvas.getZoom(),
          height: bounds.height / this.canvas.getZoom(),
        }}
        canvas={this.canvas}
        dimensions={dims}
      />,
      this.wrapperLayer
    );
  };
  activate() {
    this.wrapperLayer = document.createElement('div');
    this.dispose = this.canvas.addDomToWorld(this.wrapperLayer);
    this.wrapperLayer.style.position = 'absolute';
    this.wrapperLayer.style.top = '0';
    this.wrapperLayer.style.left = '0';
    this.wrapperLayer.style.height = '0';
    this.wrapperLayer.style.width = '0';
    this.wrapperLayer.style.display = 'none';
    this.canvas.addFunction(
      `${this.nameSpace}.open`,
      (context: any, data: PathEvent) => {
        this.handleEvent(data);
      }
    );
    this.canvas.addFunction(
      `${this.nameSpace}.mount`,
      this.mountEditor.bind(this)
    );
    this.canvas.addFunction(`${this.nameSpace}.unmount`, this.hide.bind(this));
  }
  mountEditor(context: any, targetLayer: any) {
    this.handleEvent(
      new PathEvent('PathEditor.open', targetLayer, { selectedTool: 'path' })
    );
    this.canvas.dispatchEvent(
      `${this.nameSpace}.isOpened`,
      new PathEvent(`${this.nameSpace}.isOpened`, this.canvas)
    );
  }
  deactivate() {
    this.canvas.removeFunction(`${this.nameSpace}.open`);
    this.canvas.removeFunction(`${this.nameSpace}.mount`);
    this.canvas.removeFunction(`${this.nameSpace}.unmount`);
    this.dispose();
  }
}
