import { Canvas, IPlugin } from '../..';
import IconRenderer from './IconRenderer';
import IconComponentDefinition from './IconComponentDefinition';

export default class IconRendererPlugin implements IPlugin {
  name = 'IconRendererPlugin';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent('icon', IconRenderer, IconComponentDefinition);
  }

  deactivate() {}
}
