import { Layer } from '../models/Layer';
import { Event } from './index';
import { Canvas } from 'models/Canvas';
export default class DesignerMouseEvent extends Event {
  originalEvent: any;
  worldX: number;
  worldY: number;
  viewportX: number;
  viewportY: number;
  pageX: number;
  pageY: number;
  constructor(
    name: string,
    target: Layer,
    originalEvent: any,
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    const canvas: Canvas = target.getCanvas();
    const { pageX, pageY } = originalEvent;
    this.originalEvent = originalEvent;
    this.pageX = pageX;
    this.pageY = pageY;

    let worldPoints = canvas.convertPoint(
      { x: pageX, y: pageY },
      'page',
      'world'
    );

    this.worldX = worldPoints.x;
    this.worldY = worldPoints.y;

    let viewportPoints = canvas.convertPoint(
      { x: pageX, y: pageY },
      'page',
      'viewport'
    );

    this.viewportX = viewportPoints.x;
    this.viewportY = viewportPoints.y;
  }

  preventDefault() {
    super.preventDefault();
    if (this.originalEvent && this.originalEvent.preventDefault) {
      this.originalEvent.preventDefault();
    }
  }
}
