import { Layer } from '../models/Layer';
import { Event } from './index';

export default class RemoveChildEvent extends Event {
  payload: any;
  layer: Layer | null;
  constructor(
    name: string,
    layer: Layer | null,
    payload?: any,
    currentTarget?: Layer
  ) {
    super(name, payload, currentTarget);
    this.payload = payload;
    this.layer = layer;
  }
}
