import { Canvas } from '../..';
import { IPlugin } from '../..';
import SwitchRenderer from './SwitchRenderer';
import SwitchComponentDefinition from './SwitchComponentDefinition';

export default class IOSSwitchRendererPlugin implements IPlugin {
  name = 'IOSSwitchRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'switch',
      SwitchRenderer,
      SwitchComponentDefinition
    );
  }

  deactivate() {}
}
