import { Box } from '../types';
import { round } from 'lodash';
export function isContained(axis: string, box: Box, helperBox: Box): boolean {
  if (axis === 'x') {
    const leftContained = box.x <= helperBox.x;
    const rightContained =
      round(box.x + box.width, 2) >= round(helperBox.x + helperBox.width, 2);
    if (leftContained && rightContained) {
      return true;
    }
  }

  if (axis === 'y') {
    const topContained = box.y <= helperBox.y;
    const bottomContained =
      round(box.y + box.height, 2) >= round(helperBox.y + helperBox.height, 2);
    if (topContained && bottomContained) {
      return true;
    }
  }

  return false;
}

export function isAlignedonAxis(axis: string, box: any, helperBox: any) {
  if (axis === 'x') {
    if (
      helperBox.y > box.y + box.height ||
      box.y > helperBox.y + helperBox.height
    ) {
      return false;
    }
    return true;
  } else if (axis === 'y') {
    if (
      helperBox.x > box.x + box.width ||
      box.x > helperBox.x + helperBox.width
    ) {
      return false;
    }
    return true;
  }
  return false;
}

export function getDirection(axis: string, box: Box, helperBox: Box): string {
  if (axis === 'x') {
    if (helperBox.x > box.x) {
      return 'left';
    }
    return 'right';
  } else if (axis === 'y') {
    if (helperBox.y > box.y) {
      return 'top';
    }
    return 'bottom';
  }
  return 'No Axis';
}
