import * as _ from 'lodash';
import ISnapPoints from './interfaces/ISnapPoints';
import ISnapXPoint from './interfaces/ISnapXPoint';
import ISnapYPoint from './interfaces/ISnapYPoint';
import { Layer } from '../../models/Layer';
import { IDimensionWithRoot } from '../../interfaces/IDimensionWithRoot';
import { IContext } from 'interfaces';
import Config from './interfaces/Config';

// The box closer to the start of the axis should be sent as box1 and other one as box2
function getDistance(
  axis: String,
  box1: IDimensionWithRoot,
  box2: IDimensionWithRoot
): number {
  if (axis === 'x') {
    return box2.x - (box1.x + box1.width);
  } else {
    return box2.y - (box1.y + box1.height);
  }
}

function getDistanceBetweenLayers(
  axis: String,
  layer1: Layer,
  layer2: Layer
): number {
  let distance = 0;

  let box1 = layer1.getBoundingClientRect();
  let box2 = layer2.getBoundingClientRect();

  if (axis === 'x') {
    // In case of overlap
    if (box1.x < box2.x && box1.x + box1.width > box2.x) {
      return -1;
    }

    // In case of overlap
    if (box2.x < box1.x && box2.x + box2.width > box1.x) {
      return -1;
    }

    if (box1.x < box2.x) {
      distance = getDistance('x', box1, box2);
    } else {
      distance = getDistance('x', box2, box1);
    }
  } else {
    if (box1.y < box2.y && box1.y + box1.height > box2.y) {
      return -1;
    }

    // In case of overlap
    if (box2.y < box1.y && box2.y + box2.height > box1.y) {
      return -1;
    }

    if (box1.y < box2.y) {
      distance = getDistance('y', box1, box2);
    } else {
      distance = getDistance('y', box2, box1);
    }
  }

  return distance;
}

function isAlignedOnAxis(
  axis: string,
  layerDims: IDimensionWithRoot,
  otherLayerDims: IDimensionWithRoot
) {
  if (axis === 'x') {
    if (
      otherLayerDims.rootY > layerDims.rootY + layerDims.height ||
      layerDims.rootY > otherLayerDims.rootY + otherLayerDims.height
    ) {
      return false;
    }
    return true;
  } else if (axis === 'y') {
    if (
      otherLayerDims.rootX > layerDims.rootX + layerDims.width ||
      layerDims.rootX > otherLayerDims.rootX + otherLayerDims.width
    ) {
      return false;
    }
    return true;
  }
  return false;
}

function getOverlapBetweenLayers(
  axis: 'x' | 'y',
  layerDims: IDimensionWithRoot,
  otherLayerDims: IDimensionWithRoot
): { max: number; min: number } {
  let extremities = {
    min: 0,
    max: 0,
  };
  if (axis === 'y') {
    extremities.min =
      layerDims.rootX > otherLayerDims.rootX
        ? layerDims.rootX
        : otherLayerDims.rootX;
    extremities.max =
      layerDims.rootX + layerDims.width >
      otherLayerDims.rootX + otherLayerDims.width
        ? otherLayerDims.rootX + otherLayerDims.width
        : layerDims.rootX + layerDims.width;
  } else {
    extremities.min =
      layerDims.rootY > otherLayerDims.rootY
        ? layerDims.rootY
        : otherLayerDims.rootY;
    extremities.max =
      layerDims.rootY + layerDims.height >
      otherLayerDims.rootY + otherLayerDims.height
        ? otherLayerDims.rootY + otherLayerDims.height
        : layerDims.rootY + layerDims.height;
  }

  return extremities;
}

function getDisconnectedGutterPointsForX(
  snapXPoints: Array<ISnapXPoint>,
  restrictedLayers: Array<Layer>,
  originalAllLayers: Array<Layer>,
  distance: number
): Array<ISnapXPoint> {
  let points: Array<ISnapXPoint> = [];
  let axis = 'x';
  let layer1 = restrictedLayers[0];
  let dims1 = layer1.getBoundingClientRect();
  let layer2 = restrictedLayers[1];
  let dims2 = layer2.getBoundingClientRect();

  let allLayers: Array<Layer> = originalAllLayers.filter((layer: Layer) => {
    return !restrictedLayers.includes(layer);
  });

  allLayers.forEach((layer: Layer) => {
    let dims = layer.getBoundingClientRect();
    if (
      isAlignedOnAxis(axis, dims1, dims) &&
      isAlignedOnAxis(axis, dims2, dims)
    ) {
      let leftMostPoint = dims.x - distance;
      let leftMostRootPoint = dims.rootX - distance;
      let rightMostPoint = dims.x + dims.width + distance;
      let rightMostRootPoint = dims.rootX + dims.width + distance;
      let minY = dims.rootY;
      let maxY = dims.rootY + dims.height;

      addToSnapXPoint(
        snapXPoints,
        rightMostPoint,
        rightMostRootPoint,
        minY,
        maxY,
        distance
      );
      addToSnapXPoint(
        snapXPoints,
        leftMostPoint,
        leftMostRootPoint,
        minY,
        maxY,
        distance
      );
    }
  });
  return points;
}

function getDisconnectedGutterPointsForY(
  snapYPoints: Array<ISnapXPoint>,
  restrictedLayers: Array<Layer>,
  originalAllLayers: Array<Layer>,
  distance: number
): Array<ISnapYPoint> {
  let points: Array<ISnapYPoint> = [];
  let axis = 'y';
  let layer1 = restrictedLayers[0];
  let dims1 = layer1.getBoundingClientRect();
  let layer2 = restrictedLayers[1];
  let dims2 = layer2.getBoundingClientRect();

  let allLayers: Array<Layer> = originalAllLayers.filter((layer: Layer) => {
    return !restrictedLayers.includes(layer);
  });

  allLayers.forEach((layer: Layer) => {
    let dims = layer.getBoundingClientRect();
    if (
      isAlignedOnAxis(axis, dims1, dims) &&
      isAlignedOnAxis(axis, dims2, dims)
    ) {
      let topMostPoint = dims.y - distance;
      let topMostRootPoint = dims.rootY - distance;
      let bottomMostPoint = dims.y + dims.height + distance;
      let bottomMostRootPoint = dims.rootY + dims.height + distance;
      let minX = dims.rootX;
      let maxX = dims.rootX + dims.width;

      addToSnapYPoint(
        snapYPoints,
        bottomMostPoint,
        bottomMostRootPoint,
        minX,
        maxX,
        distance
      );
      addToSnapYPoint(
        snapYPoints,
        topMostPoint,
        topMostRootPoint,
        minX,
        maxX,
        distance
      );
    }
  });
  return points;
}

function addToSnapXPoint(
  snapXPoints: Array<ISnapXPoint>,
  value: number,
  rootValue: number,
  min: number,
  max: number,
  distance: number
) {
  if (
    !_.find(snapXPoints, (val: any) => {
      return (
        val.value === value &&
        val.rootValue === rootValue &&
        val.minY === min &&
        val.maxY === max
      );
    })
  ) {
    snapXPoints.push({
      value: value,
      rootValue: rootValue,
      minY: min,
      maxY: max,
      snapLines: {
        distance,
      },
    });
  }
}

function addToSnapYPoint(
  snapYPoints: Array<ISnapYPoint>,
  value: number,
  rootValue: number,
  min: number,
  max: number,
  distance: number
) {
  if (
    !_.find(snapYPoints, (val: any) => {
      return (
        val.value === value &&
        val.rootValue === rootValue &&
        val.minY === min &&
        val.maxY === max
      );
    })
  ) {
    snapYPoints.push({
      value: value,
      rootValue: rootValue,
      minX: min,
      maxX: max,
      snapLines: {
        distance,
      },
    });
  }
}

export default function getGutterSnapPoints(
  config: Config,
  context: IContext,
  layers: Array<Layer>
): ISnapPoints {
  if (!layers || layers.length === 0 || !config.snap || !config.gutterSnap) {
    return {
      x: [],
      y: [],
    };
  }
  const layer = layers[0];
  let snapXPoints: Array<ISnapXPoint> = [];
  let snapYPoints: Array<ISnapYPoint> = [];

  if (!layer.parent) {
    return {
      x: [],
      y: [],
    };
  }
  let parent = layer.parent as Layer;
  parent.children.forEach((layer1: Layer) => {
    // Don't need the distance with this layer as it is being moved
    if (layers.includes(layer1)) {
      return;
    }

    parent.children.forEach((layer2: Layer) => {
      // No need of comparison between the same layers
      if (layer1 === layer2) return;

      // No need to do comparison as this layer is being moved
      if (layers.includes(layer2)) return;

      if (
        isAlignedOnAxis(
          'x',
          layer1.getBoundingClientRect(),
          layer2.getBoundingClientRect()
        )
      ) {
        let distance = getDistanceBetweenLayers('x', layer1, layer2);
        if (distance > 0) {
          let allGutterPoints = getDisconnectedGutterPointsForX(
            snapXPoints,
            [layer1, layer2, ...layers],
            parent.children,
            distance
          );
          snapXPoints.push(...allGutterPoints);
          let overlapX: {
            min: number;
            max: number;
          } = getOverlapBetweenLayers(
            'x',
            layer1.getBoundingClientRect(),
            layer2.getBoundingClientRect()
          );

          let box1 = layer1.getBoundingClientRect();
          let box2 = layer2.getBoundingClientRect();

          let rightMostPoint = 0;
          let rightMostRootPoint = 0;
          let leftMostPoint = 0;
          let leftMostRootPoint = 0;

          let right: IDimensionWithRoot;
          let left: IDimensionWithRoot;
          if (box1.x + box1.width > box2.x + box2.width) {
            right = box1;
            left = box2;
            rightMostPoint = box1.x + box1.width;
            rightMostRootPoint = box1.rootX + box1.width;
            leftMostPoint = box2.x - distance;
            leftMostRootPoint = box2.rootX - distance;
          } else {
            right = box2;
            left = box1;
            rightMostPoint = box2.x + box2.width;
            rightMostRootPoint = box2.rootX + box2.width;
            leftMostPoint = box1.x - distance;
            leftMostRootPoint = box1.rootX - distance;
          }

          rightMostPoint = rightMostPoint + distance;
          rightMostRootPoint = rightMostRootPoint + distance;

          addToSnapXPoint(
            snapXPoints,
            rightMostPoint,
            rightMostRootPoint,
            overlapX.min,
            overlapX.max,
            distance
          );
          addToSnapXPoint(
            snapXPoints,
            leftMostPoint,
            leftMostRootPoint,
            overlapX.min,
            overlapX.max,
            distance
          );
        }
      }

      if (
        isAlignedOnAxis(
          'y',
          layer1.getBoundingClientRect(),
          layer2.getBoundingClientRect()
        )
      ) {
        let distance = getDistanceBetweenLayers('y', layer1, layer2);
        if (distance > 0) {
          let allGutterPoints = getDisconnectedGutterPointsForY(
            snapYPoints,
            [layer1, layer2, ...layers],
            parent.children,
            distance
          );
          snapYPoints.push(...allGutterPoints);
          let overlapY: {
            min: number;
            max: number;
          } = getOverlapBetweenLayers(
            'y',
            layer1.getBoundingClientRect(),
            layer2.getBoundingClientRect()
          );

          let box1 = layer1.getBoundingClientRect();
          let box2 = layer2.getBoundingClientRect();

          let bottomMostPoint = 0;
          let bottomMostRootPoint = 0;
          let topMostPoint = 0;
          let topMostRootPoint = 0;

          let top: IDimensionWithRoot;
          let bottom: IDimensionWithRoot;
          if (box1.y + box1.height > box2.y + box2.height) {
            bottom = box1;
            top = box2;
            bottomMostPoint = box1.y + box1.height;
            bottomMostRootPoint = box1.rootY + box1.height;
            topMostPoint = box2.y - distance;
            topMostRootPoint = box2.rootY - distance;
          } else {
            top = box1;
            bottom = box2;
            bottomMostPoint = box2.y + box2.height;
            bottomMostRootPoint = box2.rootY + box2.height;
            topMostPoint = box1.y - distance;
            topMostRootPoint = box1.rootY - distance;
          }

          bottomMostPoint = bottomMostPoint + distance;
          bottomMostRootPoint = bottomMostRootPoint + distance;

          addToSnapYPoint(
            snapYPoints,
            bottomMostPoint,
            bottomMostRootPoint,
            overlapY.min,
            overlapY.max,
            distance
          );

          addToSnapYPoint(
            snapYPoints,
            topMostPoint,
            topMostRootPoint,
            overlapY.min,
            overlapY.max,
            distance
          );
        }
      }
    });
  });
  return { x: snapXPoints, y: snapYPoints };
}
