import ISnapPoints from './interfaces/ISnapPoints';
import ISnapXPoint from './interfaces/ISnapXPoint';
import ISnapYPoint from './interfaces/ISnapYPoint';
import * as _ from 'lodash';
import { Layer } from 'models/Layer';
import { IContext } from 'interfaces';
import Config from './interfaces/Config';

export default function getAllSiblingLayerPoints(
  config: Config,
  context: IContext,
  layers: Array<Layer>,
  parent: any
): ISnapPoints {
  let snapPoints: ISnapPoints = {
    x: [],
    y: [],
  };

  // if (!layers || layers.length === 0) {
  //   return snapPoints;
  // }

  // let selectedLayer = layers[0];
  if (!parent || !config.snap || !config.siblingSnap) {
    return {
      x: [],
      y: [],
    };
  }

  if (parent) {
    _.forEach(parent.children, async layer => {
      if (layers.includes(layer)) {
        return;
      }
      // TODO: Figure out what this was for
      // if (
      //   layers.indexOf(layer) > -1 ||
      //   layer.domainCompiledRef.type === 'fileHeader'
      // ) {
      //   return;
      // }
      let layerPoints: ISnapPoints = getLayerPoints(context, layer);

      snapPoints.x = snapPoints.x.concat(layerPoints.x);
      snapPoints.y = snapPoints.y.concat(layerPoints.y);
    });
  }
  return snapPoints;
}

function getLayerPoints(context: IContext, layer: Layer): ISnapPoints {
  let x: Array<ISnapXPoint> = [];
  let y: Array<ISnapYPoint> = [];

  let minX = undefined;
  let maxX = undefined;

  let minY = undefined;
  let maxY = undefined;

  let layerBox = context.canvas.getBoundingBoxWRTParent([layer], true);

  let xSnapPoint1: ISnapXPoint = {
    value: layerBox.x,
    rootValue: layerBox.rootX,
    minY: minY,
    maxY: maxY,
    snapTo: { center: true, start: true, end: true },
  };
  let xSnapPoint2: ISnapXPoint = {
    value: layerBox.x + layerBox.width,
    rootValue: layerBox.rootX + layerBox.width,
    minY: minY,
    maxY: maxY,
    snapTo: { center: true, start: true, end: true },
  };
  let xSnapPoint3: ISnapXPoint = {
    value: layerBox.x + layerBox.width / 2,
    rootValue: layerBox.rootX + layerBox.width / 2,

    minY: minY,
    maxY: maxY,
    snapTo: { center: true, start: true, end: true },
  };

  let ySnapPoint1: ISnapYPoint = {
    value: layerBox.y,
    rootValue: layerBox.rootY,
    minX: minX,
    maxX: maxX,
    snapTo: { center: true, start: true, end: true },
  };
  let ySnapPoint2: ISnapYPoint = {
    value: layerBox.y + layerBox.height,
    rootValue: layerBox.rootY + layerBox.height,
    minX: minX,
    maxX: maxX,
    snapTo: { center: true, start: true, end: true },
  };
  let ySnapPoint3: ISnapYPoint = {
    value: layerBox.y + layerBox.height / 2,
    rootValue: layerBox.rootY + layerBox.height / 2,
    minX: minX,
    maxX: maxX,
    snapTo: { center: true, start: true, end: true },
  };

  x.push(xSnapPoint1);
  x.push(xSnapPoint2);
  x.push(xSnapPoint3);
  y.push(ySnapPoint1);
  y.push(ySnapPoint2);
  y.push(ySnapPoint3);

  return {
    x,
    y,
  };
}
