import { Canvas, IPlugin } from '../..';
import TouchableOpacityRenderer from './TouchableOpacityRenderer';
import TouchableOpacityComponentDefinition from './TouchableOpacityComponentDefinition';

export default class TouchableOpacityRendererPlugin implements IPlugin {
  name = 'TouchableOpacityRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'touchableOpacity',
      TouchableOpacityRenderer,
      TouchableOpacityComponentDefinition
    );
  }

  deactivate() {}
}
