import { Canvas } from '../..';
import { IPlugin } from '../..';
import SliderRenderer from './SliderRenderer';
import SliderComponentDefinition from './SliderComponentDefinition';

export default class SliderRendererPlugin implements IPlugin {
  name = 'SliderRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;

    // canvas.registerDataStore("MyPlugin")
  }

  activate() {
    this.canvas.addComponent(
      'slider',
      SliderRenderer,
      SliderComponentDefinition
    ); // this.canvas.addEventListener('click', e => {
    //   console.log(e);
    // });
  }

  deactivate() {}
}
