import ISnapPoints from './interfaces/ISnapPoints';
import ISnapXPoint from './interfaces/ISnapXPoint';
import ISnapYPoint from './interfaces/ISnapYPoint';
import { IContext } from 'interfaces';
import { Layer } from '../../models/Layer';
import Config from './interfaces/Config';

// TODO: Add typing to it
// the arguments need to be the highest parent or whichever is the artboard
export default function getParentSnapPoints(
  config: Config,
  context: IContext,
  layers: Array<Layer>
): ISnapPoints {
  if (!layers || layers.length === 0 || !config.snap || !config.parentSnap) {
    return {
      x: [],
      y: [],
    };
  }
  const layer = layers[0];
  let x: Array<ISnapXPoint> = [];
  let y: Array<ISnapXPoint> = [];

  let minX = undefined;
  let maxX = undefined;

  let minY = undefined;
  let maxY = undefined;

  let layerDimensions = layer.getBoundingClientRect();

  let xSnapPoint1: ISnapXPoint = {
    value: 0,
    rootValue: layerDimensions.rootX,
    minY: minY,
    maxY: maxY,
    snapTo: {
      center: false,
      start: true,
      end: false,
    },
  };
  let xSnapPoint2: ISnapXPoint = {
    value: layerDimensions.width,
    rootValue: layerDimensions.rootX + layerDimensions.width,
    minY: minY,
    maxY: maxY,
    snapTo: { center: false, start: false, end: true },
  };
  let xSnapPoint3: ISnapXPoint = {
    value: layerDimensions.width / 2,
    rootValue: layerDimensions.rootX + layerDimensions.width / 2,
    minY: minY,
    maxY: maxY,
    snapTo: { center: true, start: true, end: true },
  };

  let ySnapPoint1: ISnapYPoint = {
    value: 0,
    rootValue: layerDimensions.rootY,
    minX: minX,
    maxX: maxX,
    snapTo: { center: false, start: true, end: false },
  };
  let ySnapPoint2: ISnapYPoint = {
    value: layerDimensions.height,
    rootValue: layerDimensions.rootY + layerDimensions.height,
    minX: minX,
    maxX: maxX,
    snapTo: { center: false, start: false, end: true },
  };
  let ySnapPoint3: ISnapYPoint = {
    value: layerDimensions.height / 2,
    rootValue: layerDimensions.rootY + layerDimensions.height / 2,
    minX: minX,
    maxX: maxX,
    snapTo: { center: true, start: true, end: true },
  };

  x.push(xSnapPoint1);
  x.push(xSnapPoint2);
  x.push(xSnapPoint3);
  y.push(ySnapPoint1);
  y.push(ySnapPoint2);
  y.push(ySnapPoint3);

  return {
    x,
    y,
  };
}
