// import { IPlugin } from '../../interfaces/IPlugin';
// import { Canvas } from '../../models/Canvas';
import { IContext, IDimension } from 'interfaces';
import LayerLabelEvent from './events/layerLabelEvent';
import RemoveChildEvent from 'events/RemoveChildEvent';
import ArtboardEvent from 'events/ArtboardEvent';
import { Canvas, IPlugin, MouseEvent as DesignerMouseEvent } from '../../';

export default class LayerLabel implements IPlugin {
  name = 'LayerLabel';
  canvas!: Canvas;
  labeltext: any;
  removeDom: any;
  lastPageX = 0;
  lastPageY = 0;
  dragStart: any = {
    x: 0,
    y: 0,
  };
  dims: IDimension = { x: 0, y: 0, height: 0, width: 0 };
  layerLabelMap: Map<any, any> = new Map([]);
  ghostImageDOM: any;

  handleGhostImage(e: any) {
    var crt = e.target.cloneNode(true);
    crt.style.backgroundColor = 'red';
    crt.style.opacity = 0;
    crt.style.pointerEvents = 'none';
    document.body.appendChild(crt);
    this.ghostImageDOM = crt;
    e.dataTransfer.setDragImage(crt, 0, 0);
  }

  updateLabelLayout(label: any, layer: any) {
    label.style.top = layer.rootY - 20 / this.canvas.getZoom() + 'px';
    label.style.left = layer.rootX + 'px';
    label.style.height = `${20 / this.canvas.getZoom()}px`;
    label.style.fontSize = `${12 / this.canvas.getZoom()}px`;
  }

  setLabel(context: IContext, layerLabel: string): void {
    let layer = this.layerLabelMap.get(context.layer);
    let layertextNode = layer.labeltextNode;
    layertextNode.nodeValue = layerLabel;
  }

  selectedArtboardChanged = (event: ArtboardEvent) => {
    const selectedArtboard = event.artboard;
    if (selectedArtboard) {
      this.layerLabelMap.forEach((label: any, artboard: any) => {
        if (label.labeltextNode.parentElement) {
          if (artboard === selectedArtboard) {
            label.labeltextNode.parentElement.style.color = 'rgb(26, 170, 198)';
          } else {
            label.labeltextNode.parentElement.style.color =
              'rgb(186, 189, 198)';
          }
        }
      });
    }
  };

  removeLabel(context: IContext): void {
    let layerCallback = this.layerLabelMap.get(context.layer).removeCallback;
    layerCallback();
  }
  hideLabel(context: IContext): void {
    let label = this.layerLabelMap.get(context.layer);
    if (label && label.labelNode) {
      label.labelNode.style.display = 'none';
    }
  }
  showLabel(context: IContext): void {
    let label = this.layerLabelMap.get(context.layer);
    if (label && label.labelNode) {
      label.labelNode.style.display = '';
    }
  }
  addLabel(context: IContext, layerLabel: string): void {
    let layer = context.layer;
    let label = document.createElement('span');
    let labeltext = document.createTextNode(layerLabel);
    label.appendChild(labeltext);

    label.style.position = 'absolute';
    label.style.top = layer.rootY - 20 / this.canvas.getZoom() + 'px';
    label.style.left = layer.rootX + 'px';
    // label.style.width = layer.getBoundingClientRect().width + 'px';

    // label.style.letterSpacing = '1px';
    label.style.height = `${20 / this.canvas.getZoom()}px`;
    label.style.fontSize = `${12 / this.canvas.getZoom()}px`;

    //label.style.lineHeight = '0.7px';
    label.style.fontFamily =
      'Roboto, -apple-system, BlinkMacSystemFont, sans-serif';
    label.style.textOverflow = 'ellipsis';
    label.style.color = 'rgb(186, 189, 198)';
    label.style.overflow = 'hidden';
    label.style.fontWeight = '300px';
    label.style.letterSpacing = '0.6px';

    this.canvas.on('zoom', () => {
      this.updateLabelLayout(label, context.layer);
    });
    if (layer.parent) {
      layer.parent.addEventListener(
        'removeChild',
        (event: RemoveChildEvent) => {
          if (layer === event.layer) layer.executeFunction('LayerLabel.remove');
        }
      );
    }
    let dispose = this.canvas.addDomToWorld(label, true, 101);
    label.setAttribute('class', 'file-header ' + label.className);
    label.setAttribute('data-cypress-id', 'file-header');
    this.layerLabelMap.set(layer, {
      removeCallback: dispose,
      labelNode: label,
      labeltextNode: labeltext,
    });
    label.style.pointerEvents = 'all';
    context.layer.on('layout', () => {
      this.updateLabelLayout(label, context.layer);
    });
    context.layer.on('updateDom', () => {
      this.updateLabelLayout(label, context.layer);
    });

    this.canvas.executeFunction('handleHtmlDrag', label);

    label.addEventListener('htmlDragStart', (e: Event) => {
      let dragVal = this.canvas.executeFunction(
        'HtmlDragToWorld.dragStart',
        e,
        {
          x: 0,
          y: 0,
          height: 0,
          width: 0,
          rotation: 0,
        }
      );

      this.lastPageX = dragVal.event.worldX;
      this.lastPageY = dragVal.event.worldY;

      let labelDragStartEvent = new LayerLabelEvent(
        `dragStart`,
        context.layer,
        e,
        { worldX: dragVal.event.worldX, worldY: dragVal.event.worldY },
        context.layer
      );

      layer.dispatchEvent(`dragStart`, labelDragStartEvent);
    });

    label.addEventListener('htmlDrag', (e: any) => {
      if (e.pageX && e.pageY) {
        let dragVal = this.canvas.executeFunction('HtmlDragToWorld.drag', e);
        if (dragVal.session.current.deltaX || dragVal.session.current.deltaY) {
          let relPos = {
            x: dragVal.event.worldX - this.lastPageX,
            y: dragVal.event.worldY - this.lastPageY,
          };

          let labelDragEvent = new LayerLabelEvent(`drag`, context.layer, e, {
            worldDx: relPos.x,
            worldDy: relPos.y,
          });

          layer.dispatchEvent(`drag`, labelDragEvent);
          this.lastPageX = dragVal.event.worldX;
          this.lastPageY = dragVal.event.worldY;
        }
      }
    });
    label.addEventListener('contextmenu', (e: any) => {
      this.canvas.dispatchEventOnHitLayer(
        e.type,
        new DesignerMouseEvent(e.type, this.canvas, e, this.canvas),
        e
      );
    });

    label.addEventListener('htmlDragEnd', (e: any) => {
      let dragVal = layer.executeFunction('HtmlDragToWorld.dragEnd', e);

      // let relPos = {
      //   x: dragVal.event.worldX - this.lastPageX,
      //   y: dragVal.event.worldY - this.lastPageY,
      // };

      let labelDragEndEvent = new LayerLabelEvent(
        `dragEnd`,
        context.layer,
        e,
        { worldX: dragVal.event.worldX, worldY: dragVal.event.worldY },
        context.layer
      );

      layer.dispatchEvent(`dragEnd`, labelDragEndEvent);

      this.lastPageX = 0;
      this.lastPageY = 0;
    });

    label.addEventListener('click', (e: any) => {
      let labelClickEvent = new LayerLabelEvent(
        `${this.name}.click`,
        layer,
        e,
        {}
      );
      layer.dispatchEvent(`${this.name}.click`, labelClickEvent);
    });

    label.addEventListener('dblclick', (e: any) => {
      let layerDoubleClickEvent = new LayerLabelEvent(
        `${this.name}.doubleClick`,
        layer,
        e,
        {}
      );
      layer.dispatchEvent(`${this.name}.doubleClick`, layerDoubleClickEvent);
    });
  }

  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  activate() {
    this.canvas.addFunction(`${this.name}.add`, this.addLabel.bind(this));
    this.canvas.addFunction(`${this.name}.set`, this.setLabel.bind(this));
    this.canvas.addFunction(`${this.name}.remove`, this.removeLabel.bind(this));
    this.canvas.addFunction(`${this.name}.show`, this.showLabel.bind(this));
    this.canvas.addFunction(`${this.name}.hide`, this.hideLabel.bind(this));
    this.canvas.addEventListener(
      'artboardSelected',
      this.selectedArtboardChanged
    );
  }
  deactivate() {}
}
