import { Canvas, IPlugin, Layer } from '../..';
import CommandEvent from '../../events/CommandEvent';
export default class OpacityShortcutPlugin implements IPlugin {
  name = 'OpacityShortcut';
  canvas!: Canvas;
  dispose: any;
  previousEvent: any;
  opacity: number = 100;

  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }

  activate() {
    this.canvas.addEventListener('keyDown', this.handleKeyPress);
  }

  handleKeyPress = (event: any) => {
    const originalEvent = event.originalEvent;
    const keycode = originalEvent.keyCode || originalEvent.which;
    let newOpacity = this.opacity;
    const isModifierKeyPressed =
      originalEvent.altKey ||
      originalEvent.ctrlKey ||
      originalEvent.metaKey ||
      originalEvent.shiftKey;
    if (
      !isModifierKeyPressed &&
      this.canvas.selectedLayers.length > 0 &&
      keycode >= 48 &&
      keycode <= 57
    ) {
      if (
        this.previousEvent &&
        originalEvent.timeStamp - this.previousEvent.timeStamp < 1000
      ) {
        newOpacity = this.opacity * 10 + parseInt(originalEvent.key);
        if (newOpacity > 100) {
          const opacityInString = newOpacity.toString();
          newOpacity = parseInt(
            opacityInString.slice(
              opacityInString.length - 2,
              opacityInString.length
            )
          );
        }
        this.opacity = newOpacity;
      } else {
        newOpacity = parseInt(originalEvent.key) * 10;
        this.opacity = parseInt(originalEvent.key);
      }
      this.previousEvent = originalEvent;

      this.canvas.selectedLayers.map((selectedLayer: Layer) => {
        const commandParams = {
          layer: selectedLayer,
          key: 'opacity',
          value: newOpacity / 100,
          stylePropName: 'style',
        };
        this.canvas.dispatchEvent(
          'setStyleAttribute',
          new CommandEvent('setStyleAttribute', this.canvas, commandParams)
        );
      });
    }
  };

  deactivate() {
    this.dispose();
  }
}
