import { Event, Layer } from '../..';

export class FontResizerEvent extends Event {
  fontSize!: number;
  y!: number;
  x!: number;
  constructor(
    name: string,
    target: Layer,
    payload: {
      fontSize?: number;
      x?: number;
      y?: number;
    },
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    const { x, y, fontSize } = payload;
    if (fontSize) this.fontSize = fontSize;
    if (y) this.y = y;
    if (x) this.x = x;
  }
}
