import { Canvas } from '../..';
import { IPlugin } from '../..';
import MapViewRenderer from './MapViewRenderer';
import MapViewComponentDefinition from './MapViewComponentDefinition';

export default class MapViewRendererPlugin implements IPlugin {
  name = 'MapViewRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent(
      'mapView',
      MapViewRenderer,
      MapViewComponentDefinition
    );
  }

  deactivate() {}
}
