import { Canvas, IPlugin } from '../..';
import ViewRenderer from './ViewRenderer';
import ViewComponentDefinition from './ViewComponentDefinition';

export default class ViewRendererPlugin implements IPlugin {
  name = 'ViewRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent('view', ViewRenderer, ViewComponentDefinition);
  }

  deactivate() {}
}
