import * as React from 'react';
import ReactDOM from 'react-dom';
import { Canvas, IPlugin, MouseEvent, IContext } from '../..';
import { contextMenu, MainMenu } from 'builderx-module-context-menu';
import { generateRandomString } from '../../models/utils';
import 'builderx-module-context-menu/src/lib/components/utils/ContextMenu.css';

export default class PopoverMenuPlugin implements IPlugin {
  name = 'PopoverMenu';
  canvas!: Canvas;
  dispose: any;
  containerElement: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
    this.dispose = null;
  }
  showMenu(context: IContext, json: any, event: MouseEvent) {
    this.dispose();
    event.originalEvent.preventDefault();
    this.containerElement = document.createElement('div');
    this.dispose = this.canvas.addDomToViewport(this.containerElement);
    const randomId = generateRandomString(4);
    const menuReactElement = (
      <MainMenu menuId={`menuId-${randomId}`} data={json} />
    );
    ReactDOM.render(menuReactElement, this.containerElement);
    contextMenu.show({
      id: `menuId-${randomId}`,
      event: event.originalEvent,
    });
  }
  activate() {
    this.containerElement = document.createElement('div');
    this.dispose = this.canvas.addDomToViewport(this.containerElement);
    this.canvas.addFunction('showPopoverMenu', this.showMenu.bind(this));
  }

  deactivate() {
    this.canvas.removeFunction('showPopoverMenu');
    this.dispose();
  }
}
