import { Canvas } from '../..';
import { IPlugin } from '../..';
import TextRenderer from './TextRenderer';
import TextComponentDefinition from './TextComponentDefinition';

export default class TextRendererPlugin implements IPlugin {
  name = 'TextRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent('text', TextRenderer, TextComponentDefinition);
  }

  deactivate() {}
}
