import { PropTypes, Box } from '../types';
import { isContained, isAlignedonAxis, getDirection } from '../utils';

const getTopEnd = (xContained: boolean, yContained: boolean, box: Box) => {
  if (xContained && yContained) {
    return box.y;
  }
  return box.y + box.height;
};

const getTopHeight = (
  xContained: boolean,
  yContained: boolean,
  box: Box,
  helperBox: Box
) => {
  if (xContained && yContained) {
    return helperBox.y - box.y;
  }
  return helperBox.y - box.y - box.height;
};

const getLeft = (helperBox: Box) => {
  return helperBox.x + Math.floor(helperBox.width / 2);
};

const getBottomEnd = (helperBox: Box) => {
  return helperBox.y + helperBox.height;
};

const getBottomHeight = (
  xContained: boolean,
  yContained: boolean,
  box: Box,
  helperBox: Box
) => {
  if (xContained && yContained) {
    return box.y + box.height - (helperBox.y + helperBox.height);
  }
  return box.y - helperBox.y - helperBox.height;
};

const drawYLines = (props: PropTypes) => {
  let { box, helperBox } = props;
  let xContained = isContained('x', box, helperBox);
  let yContained = isContained('y', box, helperBox);
  let yAligned = isAlignedonAxis('y', box, helperBox);
  let topAligned = false;
  let bottomAligned = false;
  let direction = getDirection('y', box, helperBox);
  if (yAligned) {
    if (direction === 'top') {
      topAligned = true;
    } else if (direction === 'bottom') {
      bottomAligned = true;
    }
  }
  let topEnd = getTopEnd(xContained, yContained, props.box);
  let topHeight = getTopHeight(
    xContained,
    yContained,
    props.box,
    props.helperBox
  );
  let left = getLeft(props.helperBox);
  let bottomEnd = getBottomEnd(props.helperBox);
  let bottomHeight = getBottomHeight(
    xContained,
    yContained,
    props.box,
    props.helperBox
  );

  let topDistance =
    xContained && yContained
      ? helperBox.y - box.y
      : helperBox.y - box.y - box.height;
  let topShow = (topAligned || (xContained && yContained)) && topDistance > 0;

  let bottomDistance =
    xContained && yContained
      ? box.y + box.height - (helperBox.y + helperBox.height)
      : box.y - helperBox.y - helperBox.height;

  let bottomShow =
    (bottomAligned || (xContained && yContained)) && bottomDistance > 0;
  const lines = [];
  if (topShow) {
    topDistance = Math.round(topDistance);
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        topEnd,
        'measurementXLine1-edge',
        left - 2.5,
        left + 3,
        false
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        left,
        'measurementYLine1',
        topEnd,
        topEnd + topHeight,
        false,
        topDistance
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        topEnd + topHeight - 1,
        'measurementXLine1-edge',
        left - 2.5,
        left + 3,
        false
      )
    );
  }
  if (bottomShow) {
    bottomDistance = Math.round(bottomDistance);
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        bottomEnd,
        'measurementXLine1-edge',
        left - 2.5,
        left + 3,
        false
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'y',
        left,
        'measurementYLine2',
        bottomEnd,
        bottomEnd + bottomHeight,
        false,
        bottomDistance
      )
    );
    lines.push(
      props.canvas.executeFunction(
        'drawLine',
        'x',
        bottomEnd + bottomHeight - 1,
        'measurementXLine1-edge',
        left - 2.5,
        left + 3,
        false
      )
    );
  }
  return lines;
};

export default drawYLines;
