import { Layer } from '../../../models/Layer';
import { Canvas } from 'models/Canvas';
import DesignerMouseEvent from '../../../events/MouseEvent';
export default class LayerLabelEvent extends DesignerMouseEvent {
  pageDx: number;
  pageDy: number;
  worldDx: number;
  worldDy: number;
  viewportDx: number;
  viewportDy: number;
  constructor(
    name: string,
    target: Layer,
    originalEvent: any,
    payload: any,
    currentTarget?: Layer
  ) {
    super(name, target, originalEvent, currentTarget);
    const canvas: Canvas = target.getCanvas();
    this.worldDx = payload.worldDx;
    this.worldDy = payload.worldDy;

    let pagePoints = canvas.convertPoint(
      { x: payload.worldDx, y: payload.worldDy },
      'world',
      'page'
    );
    this.pageDx = pagePoints.x;
    this.pageDy = pagePoints.y;

    let viewportPoints = canvas.convertPoint(
      { x: this.pageDx, y: this.pageDy },
      'page',
      'viewport'
    );

    this.viewportDx = viewportPoints.x;
    this.viewportDy = viewportPoints.y;
  }
}
