import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Collisions } from 'detect-collisions';
import { DesignerReact } from '../components/DesignerReact';
import { action } from './base-model';
import {
  IPosition,
  IComponentMap,
  IComponentDefinition,
  IContext,
} from './../interfaces';
import { isEmpty, maxBy, minBy } from 'lodash';
import { LayerInstantiable as Layer } from './Layer';
import { IPlugin, IDimensionWithRotation } from 'interfaces';
import MiddlewareRegistry from './MiddlewareRegistry';
import FunctionRegistry from './FunctionRegistry';
import DesignerMouseEvent from '../events/MouseEvent';
import ModelUtils from './../models/utils';
import DesignerDropEvent from '../events/DropEvent';
import HoverEvent from '../events/HoverEvent';
import { globalLayerUpdateController } from './GlobalLayerUpdateController';
import SetScrollEvent from '../events/SetScrollEvent';
import SetZoomEvent from '../events/SetZoomEvent';
import { ASSET_URL } from './../constants';

export class Canvas extends Layer {
  private plugins = new Map<string, IPlugin>();
  private dom: any;
  private zoom: number = 1;
  private _mouseCursorType: any;
  private scroll: IPosition = {
    x: 0,
    y: 0,
  };
  private viewport: {
    width: number;
    height: number;
  };
  private _viewportRef: any;
  private _hoveredLayers: Array<Layer>;
  private _selectedLayers: Array<Layer>;
  private _stackWorldDOM: Array<any>;
  private _stackViewportDOM: Array<any>;
  private _middlewareRegistry: MiddlewareRegistry = new MiddlewareRegistry();
  private _functionRegistry: FunctionRegistry = new FunctionRegistry();
  private _componentMap: IComponentMap = {};
  private _isMouseDown: boolean = false;
  private _assetURL: string = ASSET_URL;
  private _mouseEventsBlocked: boolean = false;
  moveWithGrabHandle: boolean = false;
  constructor(dom: any) {
    super();
    this.dom = dom;
    this.viewport = {
      width: 0,
      height: 0,
    };
    this.setType('canvas');
    this.setCollisionScope(new Collisions());
    this._hoveredLayers = [];
    this._selectedLayers = [];
    this._stackWorldDOM = [];
    this._stackViewportDOM = [];
    ReactDOM.render(<DesignerReact canvas={this} />, this.dom);
    this.addFunction('setScroll', this.setScrollCallback.bind(this));
    this.addFunction('setZoom', this.setZoomCallback.bind(this));
  }
  setMouseCursor(mouseCursorType: string) {
    this._mouseCursorType = mouseCursorType;
    action(() => {
      this.emit('mouseCursorType');
    });
  }
  setScrollCallback(context: IContext, scrollDims: any) {
    this.dispatchEvent(
      'setScroll',
      new SetScrollEvent('setScroll', this, scrollDims)
    );
  }
  setZoomCallback(context: IContext, zoom: number) {
    this.dispatchEvent('setZoom', new SetZoomEvent('setZoom', this, zoom));
  }
  get mouseEventsBlocked() {
    return this._mouseEventsBlocked;
  }
  blockMouseEvents() {
    this._mouseEventsBlocked = true;
    action(() => {
      this.emit('mouseEventsBlocked');
    });
  }
  unblockMouseEvents() {
    this._mouseEventsBlocked = false;
    action(() => {
      this.emit('mouseEventsBlocked');
    });
  }
  get functionRegistry() {
    return this._functionRegistry;
  }

  createLayer() {
    return new Layer(this);
  }

  addMiddleware(name: string, callback: (next: Function, data: any) => any) {
    this._middlewareRegistry.addMiddleware(name, callback);
  }
  executeMiddleware(name: string, data: any, callback: any) {
    return this._middlewareRegistry.executeMiddleware(name, data, callback);
  }
  // dispatchEvent(name: string, event: Event) {
  //   this._eventCallableRegistry.dispatchEvent(name, event);
  //   if (event.propagate)
  //     if (this._parent) this._parent.dispatchEvent(name, event);
  // }

  addFunction(
    name: string,
    callback: (context: IContext, ...args: any[]) => any
  ) {
    this._functionRegistry.addFunction(name, callback);
  }

  removeFunction(name: string) {
    this._functionRegistry.removeFunction(name);
  }

  setAssetUrl(url: string) {
    this._assetURL = url;
  }
  // DOC: canvas ops
  setViewport(dims: { width: number; height: number }) {
    this.viewport.width = dims.width;
    this.viewport.height = dims.height;
    action(() => {
      this.emit('viewport');
    });
  }

  setViewportRef(ref: any) {
    this._viewportRef = ref;
  }
  getViewport() {
    return this.viewport;
  }
  setZoom(zoom: number) {
    this.zoom = zoom;
    action(() => {
      this.emit('zoom');
    });
  }
  getZoom(): number {
    return this.zoom;
  }
  setScroll(scroll: IPosition) {
    this.scroll = scroll;
    action(() => {
      this.emit('scroll');
    });
  }
  getScroll(): IPosition {
    return this.scroll;
  }
  get assetURL() {
    return this._assetURL;
  }
  get hoveredLayers() {
    return this._hoveredLayers;
  }
  get mouseCursorType() {
    return this._mouseCursorType;
  }
  get selectedLayers() {
    return this._selectedLayers;
  }
  get stackWorldDOM(): Array<any> {
    return this._stackWorldDOM;
  }
  get stackViewportDOM(): Array<any> {
    return this._stackViewportDOM;
  }
  get componentMap(): IComponentMap {
    return this._componentMap;
  }

  get isMouseDown(): boolean {
    return this._isMouseDown;
  }
  setMouseDown(val: boolean) {
    this._isMouseDown = val;
    if (this.moveWithGrabHandle) {
      this.setMouseCursor(val ? '-webkit-grabbing' : '-webkit-grab');
    }
  }

  setHoverLayers(layers: Array<Layer> | Layer | undefined) {
    this._hoveredLayers = [];
    if (layers) {
      if (Array.isArray(layers)) {
        layers.forEach((layer: Layer) => {
          if (!layer.locked) {
            this._hoveredLayers.push(layer);
          }
        });
      } else if (!layers.locked) {
        this._hoveredLayers = [layers];
      }
    }
    action(() => {
      this.emit('hoveredLayers');
    });
  }
  selectLayers(layers: Array<Layer> | Layer | undefined) {
    this._selectedLayers = [];
    if (layers) {
      if (Array.isArray(layers)) {
        layers.forEach((layer: Layer) => {
          if (!layer.locked) {
            this._selectedLayers.push(layer);
          }
        });
      } else if (!layers.locked) {
        this._selectedLayers = [layers];
      }
    }
    if (globalLayerUpdateController.rerender) {
      action(() => {
        this.emit('selectedLayers');
      });
    }
  }

  private convertPointFromViewportToWorld(x: number, y: number) {
    let viewPortLeftPos =
      (this.layout.get('width') - this.viewport.width / this.zoom) *
      this.scroll.x;
    let viewPortTopPos =
      (this.layout.get('height') - this.viewport.height / this.zoom) *
      this.scroll.y;
    return {
      x: viewPortLeftPos + x / this.zoom,
      y: viewPortTopPos + y / this.zoom,
    };
  }
  private convertPointFromWorldToViewport(x: number, y: number) {
    let viewPortLeftPos =
      (this.layout.get('width') - this.viewport.width / this.zoom) *
      this.scroll.x;
    let viewPortTopPos =
      (this.layout.get('height') - this.viewport.height / this.zoom) *
      this.scroll.y;

    return {
      x: (x - viewPortLeftPos) * this.zoom,
      y: (y - viewPortTopPos) * this.zoom,
    };
  }
  private convertPointFromPageToViewport(x: number, y: number) {
    const pageScrollY = document.documentElement.scrollTop;
    const pageScrollX = document.documentElement.scrollLeft;
    const viewportDom = ReactDOM.findDOMNode(this._viewportRef);

    let offsetLeft = 0;
    let offsetTop = 0;
    if (viewportDom) {
      // @ts-ignore
      const clientRect = viewportDom.getBoundingClientRect();
      offsetLeft = clientRect.x;
      offsetTop = clientRect.y;
    }

    return {
      x: x - pageScrollX - offsetLeft,
      y: y - pageScrollY - offsetTop,
    };
  }
  private convertPointFromViewportToPage(x: number, y: number) {
    const pageScrollY = document.documentElement.scrollTop;
    const pageScrollX = document.documentElement.scrollLeft;
    const viewportDom = ReactDOM.findDOMNode(this._viewportRef);

    let offsetLeft = 0;
    let offsetTop = 0;
    if (viewportDom) {
      // @ts-ignore
      const clientRect = viewportDom.getBoundingClientRect();
      offsetLeft = clientRect.x;
      offsetTop = clientRect.y;
    }
    return {
      x: x + pageScrollX + offsetLeft,
      y: y + pageScrollY + offsetTop,
    };
  }

  convertPoint = (
    point: IPosition,
    from: 'viewport' | 'page' | 'world',
    to: 'viewport' | 'page' | 'world'
  ) => {
    let resultPoint: IPosition = { x: 0, y: 0 };
    if (from === 'viewport') {
      switch (to) {
        case 'page':
          resultPoint = this.convertPointFromViewportToPage(point.x, point.y);
          break;
        case 'world':
          resultPoint = this.convertPointFromViewportToWorld(point.x, point.y);
          break;
      }
    }
    if (from === 'page') {
      switch (to) {
        case 'viewport':
          resultPoint = this.convertPointFromPageToViewport(point.x, point.y);
          break;
        case 'world':
          const viewport = this.convertPointFromPageToViewport(
            point.x,
            point.y
          );
          resultPoint = this.convertPointFromViewportToWorld(
            viewport.x,
            viewport.y
          );
          break;
      }
    }
    if (from === 'world') {
      switch (to) {
        case 'viewport':
          resultPoint = this.convertPointFromWorldToViewport(point.x, point.y);
          break;
        case 'page':
          const world = this.convertPointFromWorldToViewport(point.x, point.y);
          resultPoint = this.convertPointFromViewportToPage(world.x, world.y);
          break;
      }
    }
    return resultPoint;
  };

  installPlugin(plugin: IPlugin) {
    this.plugins.set(plugin.name, plugin);
    plugin.init(this);
    plugin.activate();
  }
  dispatchEventOnHitLayer(
    name: string,
    event: DesignerMouseEvent,
    dispatchedData?: any
  ) {
    // @ts-ignore
    if (window['debug'] && window['debugLog']) {
      // @ts-ignore
      window['debugLog']('CanvasEvent:HitLayer ' + name);
    }

    let layer: any;

    if (name !== 'drag') {
      layer = this.getHitLayer(event.worldX, event.worldY);
    }
    if (layer) {
      event.target = layer;
      this._middlewareRegistry.executeMiddleware(
        'Canvas.dispatchEventOnHitLayer',
        event,
        (event: any) => {
          if (name === 'mousemove') {
            if (!this.dragging) {
              this.dispatchEvent(
                'Canvas.hoverLayers',
                new HoverEvent(
                  'Canvas.hoverLayers',
                  this,
                  event.target ? [event.target] : []
                )
              );
            }
          }
          layer.dispatchEvent(name, event, dispatchedData);
        }
      );
    } else {
      this.dispatchEvent(name, event, dispatchedData);
    }
  }
  addComponent(
    type: string,
    ReactComponent: React.ComponentClass<any, any>,
    componentDefinition: IComponentDefinition
  ) {
    this.componentMap[type] = {
      renderer: ReactComponent,
      definition: componentDefinition,
    };
  }

  getComponentDefinition(type: string) {
    try {
      return this.componentMap[type].definition;
    } catch (err) {
      throw new Error(`Component Definition for ${type} not found`);
    }
  }

  getRenderer(type: string): React.ComponentClass<any, any> {
    try {
      return this.componentMap[type].renderer;
    } catch (err) {
      throw new Error('Renderer not found');
    }
  }
  addDomToWorld(
    domElement: any,
    maintainScale: boolean = false,
    zIndex?: number
  ): () => void {
    let wrapperLayer = domElement;

    if (maintainScale) {
      let canvasDims = this.getBoundingClientRect();
      wrapperLayer = document.createElement('div');
      // wrapperLayer.style.background = 'rgba(255, 0,0,0.4)';
      wrapperLayer.style.height = canvasDims.height + 'px';
      wrapperLayer.style.width = canvasDims.width + 'px';
      wrapperLayer.style.top = canvasDims.y + 'px';
      wrapperLayer.style.left = canvasDims.x + 'px';
      wrapperLayer.style.pointerEvents = 'none';
      wrapperLayer.style.position = 'absolute';
      wrapperLayer.style.zIndex = zIndex ? zIndex : undefined;

      this.on('zoom', (e: any) => {
        wrapperLayer.style.transform = `scale(${1 / this.getZoom()})`;
        wrapperLayer.style.zoom = this.getZoom();
      });
      wrapperLayer.style.transform = `scale(${1 / this.getZoom()})`;
      wrapperLayer.style.transformOrigin = `0 0`;
      wrapperLayer.style.zoom = this.getZoom();

      wrapperLayer.appendChild(domElement);
    }
    this._stackWorldDOM.push(wrapperLayer);
    domElement.style.pointerEvents = 'all';
    action(() => {
      this.emit('stackWorldDOM');
    });
    return this.removeDomFromWorld.bind(this, wrapperLayer);
  }
  removeDomFromWorld(ele: any) {
    let index = this._stackWorldDOM.indexOf(ele);
    if (index !== -1) {
      this._stackWorldDOM.splice(index, 1);
      action(() => {
        this.emit('stackWorldDOM');
      });
    }
  }
  addDomToViewport(domElement: any) {
    this._stackViewportDOM.push(domElement);
    domElement.style.pointerEvents = 'all';
    action(() => {
      this.emit('stackViewportDOM');
    });
    return this.removeDomFromViewport.bind(this, domElement);
  }
  removeDomFromViewport(ele: string) {
    let index = this._stackViewportDOM.indexOf(ele);

    if (index !== -1) {
      this._stackViewportDOM.splice(index, 1);
      action(() => {
        this.emit('stackViewportDOM');
      });
    }
  }
  dispatchHandledEvent(event: any) {
    const originalEvent = event.nativeEvent ? event.nativeEvent : event;
    this.dispatchEventOnHitLayer(
      event.type,
      new DesignerMouseEvent(event.type, this, originalEvent, this),
      event
    );
  }

  handleNativeEvent(event: React.MouseEvent | MouseEvent) {
    this._middlewareRegistry.executeMiddleware(
      'Canvas.handleNativeEvent',
      event,
      (event: any) => {
        switch (event.type) {
          case 'click':
            // if (!this.dragging) {
            //   this.dispatchHandledEvent(event);
            // }
            break;
          case 'mousemove':
            if (this.isMouseDown && !this.dragging) {
              this.setDragging(true);
            }
            // if (!this.dragging) {
            this.dispatchHandledEvent(event);
            // }
            break;
          case 'mousedown':
            this.setMouseDown(true);
            this.dispatchHandledEvent(event);
            break;
          case 'mouseenter':
            this.dispatchHandledEvent(event);
            break;
          case 'mouseup':
            // event.type = 'click';

            this.dispatchHandledEvent(event);
            this.setMouseDown(false);
            if (!this.dragging) {
              event = new MouseEvent('click', event);
              // event.type = 'click';
              this.dispatchHandledEvent(event);
            }
            this.setDragging(false);
            break;
          case 'contextmenu':
          case 'dblclick':
            this.dispatchHandledEvent(event);
            break;
          case 'drop':
            this.dispatchEvent(
              event.type,
              new DesignerDropEvent(event.type, this, event.nativeEvent, this),
              event
            );
            break;
          default:
          // this.dispatchCanvasEvent(
          //   e.type,
          //   new Event(e.type, this, this),
          //   data
          // );
          // break;
        }
      }
    );
    // if (beforeHandleNativeEventMiddleware) {

    // });
    // beforeHandleNativeEventMiddleware[0](next);
    // console.log('middleware:: here', beforeHandleNativeEventMiddleware);
    // } else {
    //   this.handleEvent(e);
    // }
  }

  getContainerBoundingBox(layers: Array<Layer>) {
    return this.getBoundingBox(layers, true);
  }
  getContainerBoundingBoxWRTParent(layers: Array<Layer>) {
    return this.getBoundingBoxWRTParent(layers, true);
  }
  getBoundingBox(
    layers: Array<Layer>,
    withoutRotation: boolean = false,
    wrtParent: boolean = false
  ): IDimensionWithRotation {
    let box = { x: 0, y: 0, height: 0, width: 0, rotation: 0 };
    if (isEmpty(layers)) {
      return box;
    } else if (layers.length > 1 || withoutRotation) {
      let layerPoints: any = [];

      layers.map((layer: Layer) => {
        let dims = layer.getBoundingClientRect();
        let localBox = {
          x: dims.rootX,
          y: dims.rootY,
          width: dims.width,
          height: dims.height,
        };
        let rotation = layer.rotation;

        // let points = ModelUtils(
        //   layer.getRotatedDims(),
        //   0
        // );
        let points = layer.getRotatedPolygon().poly;
        if (wrtParent) {
          points = ModelUtils.getPointsFromBoundingBox(localBox, rotation);
        }
        // layer.getRotatedDims(); //ModelUtils.getPointsFromBoundingBox(localBox, rotation);

        layerPoints = layerPoints.concat(points);
      });

      let minXLayer: any = minBy(layerPoints, (layer: any) => {
        return layer[0];
      });
      let minYLayer: any = minBy(layerPoints, (layer: any) => {
        return layer[1];
      });
      let maxXLayer: any = maxBy(layerPoints, (layer: any) => {
        return layer[0];
      });
      let maxYLayer: any = maxBy(layerPoints, (layer: any) => {
        return layer[1];
      });
      minXLayer = minXLayer ? minXLayer[0] : 0;
      minYLayer = minYLayer ? minYLayer[1] : 0;
      maxXLayer = maxXLayer ? maxXLayer[0] : 0;
      maxYLayer = maxYLayer ? maxYLayer[1] : 0;

      box.x = minXLayer;
      box.y = minYLayer;
      box.width = maxXLayer ? maxXLayer - minXLayer : 0;
      box.height = maxYLayer ? maxYLayer - minYLayer : 0;
      box.rotation = 0;
    } else if (layers.length === 1) {
      let layer = layers[0];
      return layer.getRotatedDims();
    }
    return box;
  }
  getBoundingBoxWRTParent(
    layers: Array<Layer>,
    withoutRotation: boolean = false
  ) {
    let dims = this.getBoundingBox(layers, withoutRotation, true);
    let parentDims =
      layers.length && layers[0].parent
        ? layers[0].parent.getBoundingClientRect()
        : { rootX: 0, rootY: 0 };
    return {
      ...dims,
      x: dims.x - parentDims.rootX,
      y: dims.y - parentDims.rootY,
      rootX: dims.x,
      rootY: dims.y,
    };
  }

  enableMoveWithGrabHandle() {
    if (!this.mouseEventsBlocked) {
      this.moveWithGrabHandle = true;
      this.setMouseCursor(
        this.isMouseDown ? '-webkit-grabbing' : '-webkit-grab'
      );
      this.blockMouseEvents();
      action(() => {
        this.emit('moveWithGrabHandle');
      });
    }
  }

  disableMoveWithGrabHandle() {
    if (this.moveWithGrabHandle) {
      this.moveWithGrabHandle = false;
      this.setMouseCursor('default');
      this.unblockMouseEvents();
      action(() => {
        this.emit('moveWithGrabHandle');
      });
    }
  }
}
