import * as React from 'react';
import { Canvas } from '../models/Canvas';
import SetZoomEvent from 'events/SetZoomEvent';

type Props = {
  style?: any;
  canvas: Canvas;
  initialScroll: any;
};
export default class ZoomContainer extends React.Component<Props, any> {
  static defaultProps = { initialScroll: { x: 0, y: 0 } };
  containerRef: any;
  mouseDown: any;
  center: any;
  zoom: any;
  constructor(props: any) {
    super(props);
    this.zoom = props.canvas.getZoom();
    const scrollFactor: any =
      props.initialScroll === undefined
        ? ZoomContainer.defaultProps.initialScroll
        : props.initialScroll;

    const scrollXFactor: number = scrollFactor.x;
    const scrollYFactor: number = scrollFactor.y;

    this.state = {
      scroll: { x: scrollXFactor, y: scrollYFactor },
    };
  }
  componentDidMount() {
    document.addEventListener('keydown', function(e: any) {
      if (e.keyCode === 32 && e.target === document.body) {
        e.preventDefault();
      }
    });

    this.props.canvas.addEventListener('setZoom', this.onUpdateZoom);
    this.props.canvas.on('children', this.onUpdate);
  }

  onUpdateZoom = (event: any) => {
    this.zoom = event.zoom;
    this.forceUpdate();
  };

  onUpdate = () => {
    this.forceUpdate();
  };
  shouldComponentUpdate() {
    return false;
  }

  componentDidUpdate() {
    this.props.canvas.setZoom(this.zoom);
  }

  componentWillMount() {
    this.props.canvas.removeEventListener('setZoom', this.onUpdateZoom);
    // this.props.canvas.off('children', this.onUpdate);
  }

  getSceneStyle(): any {
    var style: any = {
      position: 'absolute',
    };
    style.transform = `scale(${this.zoom})`;
    // style.zoom = this.zoom;
    style.transformOrigin = '0 0';
    return style;
  }

  render() {
    let { initialScroll, ...props } = this.props;
    // console.log(this.zoom, '#####');
    return (
      <div
        ref={(c: any) => {
          this.containerRef = c;
        }}
        onClick={e => {}}
        style={this.getSceneStyle()}
        className="zoomContainer"
      >
        {React.Children.map(this.props.children, (child: any) => {
          if (child) {
            return React.cloneElement(child, {
              ...child.props,
              style: {
                ...child.props.style,
              },
            });
          }
          return null;
        })}
      </div>
    );
  }
}
