import { Canvas, IPlugin } from '../..';
import ImageRenderer from './ImageRenderer';
import ImageComponentDefinition from './ImageComponentDefinition';

export default class ImageRendererPlugin implements IPlugin {
  name = 'ImageRenderer';
  canvas!: Canvas;

  init(canvas: Canvas) {
    this.canvas = canvas;
  }

  activate() {
    this.canvas.addComponent('image', ImageRenderer, ImageComponentDefinition);
  }

  deactivate() {}
}
