import { Layer, Event } from '../../..';

export default class RulerLinesAddEvent extends Event {
  axis: 'x' | 'y';
  axisCoordinate: number;
  constructor(
    name: string,
    target: Layer,
    axis: 'x' | 'y',
    axisCoordinate: number,
    currentTarget?: Layer
  ) {
    super(name, target, currentTarget);
    this.axisCoordinate = axisCoordinate;
    this.axis = axis;
  }
}
