import * as React from 'react';
import ReactDOM from 'react-dom';
import {
  Canvas,
  IPlugin,
  Layer,
  Gradient,
  MouseEvent as DesignerMouseEvent,
} from '../..';
import { IDimensionWithRoot, IDimensionWithRotation } from 'interfaces';
import GradientEditorRenderer, {
  getGradientFromLineSegmentObject,
  LineSegmentGradient,
} from './EditorRenderer';
import CommandEvent from '../../events/CommandEvent';
export default class GradientEditorPlugin implements IPlugin {
  name = 'GradientEditorPlugin';
  nameSpace = 'GradientEditor';
  gradient?: Gradient;
  activeIndex?: number;
  initialShift?: { x: number; y: number };
  canvas!: Canvas;
  dispose: any;
  isOpened: boolean = false;
  wrapperLayer?: any;
  init(canvas: Canvas) {
    this.canvas = canvas;
  }
  getScrollShift = () => {
    const canvasScroll = this.canvas.getScroll();
    const zoom = this.canvas.getZoom();
    const viewPortLeftPos: number =
      canvasScroll.x *
      (this.canvas.layout.get('width') * zoom -
        this.canvas.getViewport().width);
    const viewPortTopPos: number =
      canvasScroll.y *
      (this.canvas.layout.get('height') * zoom -
        this.canvas.getViewport().height);
    return { x: viewPortLeftPos, y: viewPortTopPos };
  };
  render = (layer: Layer) => {
    this.gradient = layer.style.get('gradient');
    if (!this.gradient) {
      this.gradient = new Gradient(
        'LinearGradient',
        { x: 0.1, y: 0.1 },
        { x: 0.9, y: 0.9 },
        [
          { stopColor: '#E70B0B', offset: 0 },
          { stopColor: '#EEEEEE', offset: 1 },
        ],
        1
      );
      this.canvas.dispatchEvent(
        'setStyleAttribute',
        new CommandEvent('setStyleAttribute', this.canvas, {
          layer,
          key: 'gradient',
          value: this.gradient.toJS(),
          stylePropName: 'style',
        })
      );
    }
    const dims: IDimensionWithRoot &
      IDimensionWithRotation = layer.getBoundingClientRect();
    if (!this.wrapperLayer) {
      return;
    }
    this.isOpened = true;
    this.wrapperLayer.style.display = '';
    let scroll = this.getScrollShift();
    if (this.initialShift) {
      scroll = {
        x: scroll.x - this.initialShift.x,
        y: scroll.y - this.initialShift.y,
      };
    }
    ReactDOM.render(
      <GradientEditorRenderer
        layer={layer}
        dims={dims}
        zoom={this.canvas.getZoom()}
        scroll={scroll}
        canvas={this.canvas}
        gradient={this.gradient}
        onChange={(
          gradient: LineSegmentGradient,
          type: '' | 'Skip' | 'Forget'
        ) => {
          const gradientProps = getGradientFromLineSegmentObject(gradient);
          this.gradient = new Gradient(
            gradient ? gradient.gradientType : 'LinearGradient',
            gradientProps.from,
            gradientProps.to,
            gradientProps.stops,
            gradientProps.activeIndex
          );
          this.canvas.dispatchEvent(
            'setStyleAttribute',
            new CommandEvent(
              'setStyleAttribute',
              this.canvas,
              {
                layer,
                key: 'gradient',
                value: this.gradient.toJS(),
                stylePropName: 'style',
              },
              undefined,
              type
            )
          );
        }}
        onClickOutside={(e: any) => {
          this.closeEditor();
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperMouseDown={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperContexMenu={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperDoubleClick={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperMouseOut={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperDrop={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onDragOver={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperMouseMove={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
        onWrapperMouseUp={(e: any) => {
          this.canvas.dispatchEventOnHitLayer(
            e.type,
            new DesignerMouseEvent(e.type, this.canvas, e, this.canvas)
          );
        }}
      />,
      this.wrapperLayer
    );
  };
  rerender = () => {
    const layer = this.canvas.selectedLayers[0];
    if (layer && this.isOpened) {
      this.render(layer);
    }
  };
  closeEditor = () => {
    if (this.isOpened) {
      ReactDOM.unmountComponentAtNode(this.wrapperLayer);
      this.isOpened = false;
      this.wrapperLayer.style.display = 'none';
      this.canvas.off('zoom', this.rerender);
      this.canvas.off('scroll', this.rerender);
      this.canvas.removeFunction(`${this.nameSpace}.change`);
      this.canvas.removeFunction(`${this.nameSpace}.changeStopColor`);
    }
  };
  activate() {
    this.wrapperLayer = document.createElement('div');
    this.dispose = this.canvas.addDomToWorld(this.wrapperLayer);
    this.wrapperLayer.style.position = 'absolute';
    this.wrapperLayer.style.top = '0';
    this.wrapperLayer.style.left = '0';
    this.wrapperLayer.style.right = '0';
    this.wrapperLayer.style.bottom = '0';
    this.wrapperLayer.style.display = 'none';
    this.wrapperLayer.style.zIndex = 101;
    this.canvas.addEventListener(`${this.nameSpace}.open`, (data: any) => {
      if (!this.isOpened) {
        const layer = this.canvas.selectedLayers[0];
        this.initialShift = this.getScrollShift();
        if (layer) {
          this.render(layer);
        }
        this.canvas.on('zoom', this.rerender);
        this.canvas.on('scroll', this.rerender);
      }
    });
    this.canvas.addFunction(`${this.nameSpace}.isOpen`, (context: any) => {
      return this.isOpened;
    });
    this.canvas.addEventListener(`${this.nameSpace}.close`, this.closeEditor);
  }
  deactivate() {
    this.canvas.removeFunction(`${this.nameSpace}.isOpen`);
    this.canvas.removeEventListener(
      `${this.nameSpace}.close`,
      this.closeEditor
    );
    this.dispose();
  }
}
